import EventEmitter from "events";
import { ChatEvent, CHAT_EVENT_TYPE } from "../models";
import { ENCOUNTER_EVENT_TYPE } from "../consts/general";
import AppManager from "./appManager";

class ChatEventManager extends EventEmitter {
  private appManager: AppManager;
  private eventHistory: ChatEvent[] = [];

  constructor(appManager: AppManager) {
    super();
    this.appManager = appManager;
  }

  public addEvent(type: CHAT_EVENT_TYPE, title: string, subtitle?: string, data?: any): void {
    const event: ChatEvent = {
      type,
      title,
      subtitle,
      data,
      timestamp: Date.now()
    };

    this.eventHistory.push(event);
    
    // Emit the event to the chat system
    this.emit('chat-event', event);
  }

  public addCampaignStartEvent(campaignName: string): void {
    this.addEvent(CHAT_EVENT_TYPE.CAMPAIGN_START, campaignName);
  }

  public addEncounterEvent(eventType: ENCOUNTER_EVENT_TYPE, eventName?: string): void {
    const title = this.getEncounterEventDescription(eventType);
    this.addEvent(CHAT_EVENT_TYPE.ENCOUNTER_EVENT, title, eventName, { eventType });
  }

  public addLocationChangeEvent(locationName: string): void {
    this.addEvent(CHAT_EVENT_TYPE.LOCATION_CHANGE, "Location Change", locationName);
  }

  public addCombatStartEvent(enemyName?: string): void {
    this.addEvent(CHAT_EVENT_TYPE.COMBAT_START, "Combat Started", enemyName);
  }

  public addCombatEndEvent(outcome: 'victory' | 'defeat' | 'flee'): void {
    this.addEvent(CHAT_EVENT_TYPE.COMBAT_END, "Combat Ended", outcome);
  }

  public getEventHistory(): ChatEvent[] {
    return [...this.eventHistory];
  }

  private getEncounterEventDescription(eventType: ENCOUNTER_EVENT_TYPE): string {
    switch (eventType) {
      case ENCOUNTER_EVENT_TYPE.COMBAT:
        return "Combat";
      case ENCOUNTER_EVENT_TYPE.ROLEPLAY:
        return "Roleplaying";
      case ENCOUNTER_EVENT_TYPE.PUZZLE:
        return "Puzzle";
      case ENCOUNTER_EVENT_TYPE.DUNGEON:
        return "Dungeon Crawl";
      case ENCOUNTER_EVENT_TYPE.PERCEPTION:
        return "Perception Check";
      case ENCOUNTER_EVENT_TYPE.TOWN:
        return "Town Stuff";
      case ENCOUNTER_EVENT_TYPE.TRAVEL:
        return "Travel";
      case ENCOUNTER_EVENT_TYPE.CHALLENGE:
        return "Skill Challenge";
      case ENCOUNTER_EVENT_TYPE.TRAP:
        return "Trap";
      case ENCOUNTER_EVENT_TYPE.CHARACTER_BACKSTORY:
        return "Backstory";
      case ENCOUNTER_EVENT_TYPE.STORY:
        return "Story";
      case ENCOUNTER_EVENT_TYPE.EXPLORATION:
        return "Exploration";
      default:
        return "Unknown Event";
    }
  }
}

export default ChatEventManager;