import React from 'react';
import './notebook.scss';
import './tutorial.scss';
import { TutorialNotebookEntry } from '../../models';
import CImage from './cimage';
import { useAppManager, useUiManager } from '../managerContexts';

interface NoteBookTutorialProps {
    tutorialInfo: TutorialNotebookEntry;
}

const NotebookTutorial: React.FC<NoteBookTutorialProps> = ({ tutorialInfo }) => {
    const message = tutorialInfo.message;
    const icon = tutorialInfo.icon;
    const nextIndex = tutorialInfo.showNext;
    const prevIndex = tutorialInfo.showPrev;

    const uiManager = useUiManager();
    const appManager = useAppManager();
    const tutorialManager = appManager.getTutorialManager();

    const prevClicked = () => {
    if(prevIndex !== undefined && prevIndex >= 0)
         uiManager.showTutorialPage(prevIndex);
    }

    const nextClicked = () => {
        if(nextIndex !== undefined && nextIndex >= 0) {
            uiManager.showTutorialPage(nextIndex);
        }
        else {
            uiManager.closeTutorial();
        }
    }

    const clickedDisable = () => {
        uiManager.closeTutorial();
        tutorialManager.disableTutorial();
    }

    const nextOrClose = !nextIndex ? 'Close' : 'Next';

    return (
        <div className="notebook-tutorial-container">
            <div className="notebook-tutorial-contents left">
                <div className="tutorial-title-container">
                    <div className="tutorial-title">
                        {tutorialInfo.title}
                    </div>
                </div>
                <div className="notebook-icon-container"> 
                    <CImage imageId={icon} />
                </div>
                <div className="pill-button remove"> 
                    <div className="pill-button-disable" onClick={() => clickedDisable()}>DISABLE TUTORIAL</div>
                </div>
            </div>
            <div className="notebook-tutorial-contents right">
                <div className="tutorial-message-container">
                    <div className="tutorial-message" dangerouslySetInnerHTML={{ __html: message }}/>
                </div>
                
            
                <div className="notebook-tutorial-buttons">
                    {(prevIndex !== undefined && prevIndex >= 0) && <div className="nav-button prev" onClick={prevClicked}>Previous</div>}
                    <div className="nav-button next" onClick={nextClicked}>{nextOrClose}</div>
                </div>
            </div>
        </div>
    );
}



export default NotebookTutorial;