import React, { useEffect } from 'react';
import { Tag, FlavorTagSource, FlavorTagTheme } from '../../models';
import FlavorTagPill from './FlavorTagPill';
import './GameToast.scss';

export interface GameToastData {
  id: string;
  type: 'collection' | 'quest';
  title: string;
  subtitle?: string;
  tags: Tag[];
  newTag: Tag;
  isComplete?: boolean;
  isTagCollected: (tag: Tag) => boolean;
  getTagSource: (tag: Tag, isCollected: boolean) => FlavorTagSource;
  getTagTheme: (tag: Tag, isCollected: boolean) => FlavorTagTheme;
}

interface GameToastProps {
  data: GameToastData;
  onComplete: () => void;
  duration?: number;
}

const GameToast: React.FC<GameToastProps> = ({
  data,
  onComplete,
  duration = 5000
}) => {
  useEffect(() => {
    const timer = setTimeout(() => {
      onComplete();
    }, duration);

    return () => clearTimeout(timer);
  }, [onComplete, duration]);

  return (
    <div className={`game-toast ${data.type}-toast ${data.isComplete ? 'complete' : ''}`}>
      <div className="toast-header">
        <div className="toast-title">{data.title}</div>
        {data.subtitle && <div className="toast-subtitle">{data.subtitle}</div>}
      </div>
      <div className="tag-list">
        {data.tags.map((tag) => {
          const isCollected = data.isTagCollected(tag);
          return (
            <FlavorTagPill
              key={tag.name}
              tag={{
                tag,
                source: data.getTagSource(tag, isCollected),
                theme: data.getTagTheme(tag, isCollected)
              }}
              className={`
                ${tag.name === data.newTag.name ? 'pulse' : ''} 
                ${data.isComplete ? 'glow' : ''} 
                ${isCollected ? 'collected' : 'uncollected'}
              `.trim()}
            />
          );
        })}
      </div>
    </div>
  );
};

export default GameToast;
