import { Character, Encounter, NOTEBOOK_CATEGORY, NotebookState, Player, Location } from "../models";
import { EventEmitter } from 'events';
import { TUTORIALS } from '../consts/tutorials';
import AppManager from "./appManager";
import UiManager from "./uiManager";

class TutorialManager extends EventEmitter {
  private appManager: AppManager;
  private uiManager: UiManager;
  private seenTutorials: Set<keyof typeof TUTORIALS> = new Set();
  private turnOffTutorials: boolean = false;
  

  constructor(appManager: AppManager) {
    super();
    this.appManager = appManager;
    this.uiManager = this.appManager.getUiManager();
    this.seenTutorials = new Set();
  }

  startTutorial(tutorialId: keyof typeof TUTORIALS, force: boolean = false) {
    if(this.turnOffTutorials) {
      return;
    }
    if(this.seenTutorials.has(tutorialId) && !force){
      return;
    }
    this.uiManager.showTutorialMessage(TUTORIALS[tutorialId]);
  }

  disableTutorial() {
    this.turnOffTutorials = true;
  }
  
}



export default TutorialManager;