interface TreeShapeProps {
  size: number;
  color: string;
  style: React.CSSProperties;
}

export const TreeShape: React.FC<TreeShapeProps> = ({ size, color, style }) => {
  return (
    <div style={{ 
      position: 'absolute',
      width: size,
      height: size,
      ...style 
    }}>
      <div style={{
        position: 'absolute',
        width: size * 0.6,
        height: size * 0.6,
        borderRadius: '50%',
        backgroundColor: color,
        left: '20%',
        top: '20%'
      }} />
    </div>
  );
};