import React, { useEffect, useState } from 'react';
import './ModeContainer.scss';
import PlayerRecruitmentComponent from './playerRecruitment';
import CharacterCreationComponent from './characterCreation';
import CampaignSetupComponent from './campaignSetup';
import SessionPrepComponent from './sessionControl';
import SessionRunComponent from './sessionRun';
import FinalStatsComponent from './finalStats';
import MainMenuComponent from './mainMenu';
import { APP_STATE } from '../consts/states';
import CImage from './UI/cimage';
import { useAppManager, useCampaignManager, useUiManager } from './managerContexts';
import CampaignManager from '../managers/campaignManager';
import BottomBar from './GameRoom/bottomBar';


interface ModeContainerProps {
}

const ModeContainer: React.FC<ModeContainerProps> = ({}) => {

  const appManager = useAppManager();

  const [state, setState] = useState(appManager.state);
  const [transitioning, setTransitioning] = useState(false);
  const [transitionComplete, setTransitionComplete] = useState(false);

  const [showImage, setShowImage] = useState(false);

  const uiManager = useUiManager();
  const dataManager = appManager.getDataManager();
  const campaignManager = useCampaignManager();

  useEffect(() => {
    const handleStateChange = () => {
      setTransitionComplete(false);
      setTransitioning(false);
      setState(appManager.state);
      updateState(appManager.state);
      
    };

    appManager.on('stateChange', handleStateChange);
    return () => {
      appManager.off('stateChange', handleStateChange);
    };
  
  }, [appManager]);
  

  function updateState(state: APP_STATE) {

    if(!uiManager) {return}

    switch(state) {
      case APP_STATE.MAIN_MENU:
       
      break;
      case APP_STATE.PLAYER_RECRUITMENT:
      case APP_STATE.CHARACTER_CREATION: 
      case APP_STATE.CAMPAIGN_SETUP:
        setTransitioning(true);
        setTimeout(() => {
           setTransitioning(false);
           setTransitionComplete(true);
        }, 1000); 
      break;
      case APP_STATE.SESSION_OVERVIEW:
      break;
      case APP_STATE.SESSION:
        
      break;
      case APP_STATE.SCORE:
        
      break;
    }
   
  }

  function getModeComponent() {
    switch(state) {
      case APP_STATE.MAIN_MENU:
        return( 
          <MainMenuComponent
            appManager={appManager}
          />
         )
      case APP_STATE.PLAYER_RECRUITMENT:
       return( 
        <PlayerRecruitmentComponent
          appManager={appManager}
          playerManager={appManager.getPlayerManager()}
        />
       )
      case APP_STATE.CHARACTER_CREATION:
        return( 
        <CharacterCreationComponent/>
        );
      case APP_STATE.CAMPAIGN_SETUP:
        return( 
        <CampaignSetupComponent
          openCampaign={campaignManager.getOpenCampaignIndex()}
        />
        );
      case APP_STATE.SESSION_OVERVIEW:
        return( 
        <SessionPrepComponent
         
      />
        );
      case APP_STATE.SESSION:
        return( 
        <SessionRunComponent
          appManager={appManager}
      />
        );
      case APP_STATE.SCORE:
        return( 
        <FinalStatsComponent
          appManager={appManager}
      />
        );
    }
    return (
      <div>NOT YET IMPLEMENTED</div>
    )
  }

  const getBackgroundClass = () => {
    const transitionMode = dataManager.getStateProps().bg_transition_mode;
    switch(transitionMode) {
      case 'slide-left':
        return `${transitioning ? 'slide-left' : ''} ${transitionComplete ? 'left' : ''}`;
      case 'slide-right':
        return `${transitioning ? 'slide-right' : ''} ${transitionComplete ? 'right' : ''}`;
      case 'slide-down':
        return `${transitioning ? 'slide-down' : ''} ${transitionComplete ? 'down' : ''}`;
      default:
        return '';
    }
  }

  return (
    <div className="mode-container">
      <div className="bgImageContainer">
        <div className={`mode-background ${getBackgroundClass()}`}>
            <div className='mode-background-image' style={{ backgroundRepeat: 'repeat' }}>
              <CImage imageId={dataManager.getStateProps().background_image_id}/>
            </div>
          <div className='mode-background-overlay'>
              <CImage imageId={dataManager.getStateProps().background_image_id}/>
          </div>
          <div className='mode-background-bottom'>
              <CImage imageId={dataManager.getStateProps().secondary_bg_image_id}/>
          </div>
        </div>
      </div>
      <div className='mode-component'>
            {getModeComponent()}
        
      </div>

     
    </div>
  );
}

export default ModeContainer;
