import React, { useRef, useEffect, useState } from 'react';

interface FillBarProps {
    totalSegments: number;
    filledSegments: number;
    theme?: string;
    label?: string;
    buttonWhenFilled?: boolean;
    onClickFilled?: () => void;
    filledLabel?: string;
    labelStyle?: 'over' | 'normal';
}

const FillBar: React.FC<FillBarProps> = ({ totalSegments, filledSegments, theme = '', label, buttonWhenFilled = false, onClickFilled, filledLabel, labelStyle = 'normal' }) => {
    
    const previousFilledRef = useRef(filledSegments);
    const [animatingSegments, setAnimatingSegments] = useState<number[]>([]);

    // Track changes in filled segments and trigger animation
    useEffect(() => {
        const previousFilled = previousFilledRef.current;
        if (filledSegments > previousFilled) {
            // New segments were filled, animate the new ones
            const newSegments = [];
            for (let i = previousFilled; i < filledSegments; i++) {
                newSegments.push(i);
            }
            setAnimatingSegments(newSegments);
            
            // Clear animation after 500ms
            const timer = setTimeout(() => {
                setAnimatingSegments([]);
            }, 500);

            // Update the ref for next comparison
            previousFilledRef.current = filledSegments;

            return () => clearTimeout(timer);
        } else {
            // Update ref even if no animation needed
            previousFilledRef.current = filledSegments;
        }
    }, [filledSegments]);

    const segments = Array.from({ length: totalSegments }, (_, index) => {
        const isFilled = index < filledSegments;
        const isAnimating = animatingSegments.includes(index);
        
        return (
            <div
                key={index}
                className={`fill-bar-segment bg-theme-${theme} ${isFilled ? 'filled' : ''} ${isAnimating ? 'fill-glow-animation' : ''}`}
            />
        );
    });
      
    const filled = filledSegments >= totalSegments;
    const themeClass = 'theme-' + theme;
    if(labelStyle !== 'over') {
        return ( 
            <div className={`fill-bar`}>
                {label && <div className={`fill-bar-label text-theme-${theme || ''}`}>{label}</div>}
                <div className={`fill-bar-container ${filled ? 'filled' : ''} ${themeClass}`}>
                    {filled && buttonWhenFilled ? (
                        <div className={`fill-bar-filled-button ${themeClass}`} onClick={onClickFilled}>{filledLabel}</div>
                     ) : (
                     segments
                    )
                    }
                </div>
            </div>
            )
    }


 
    return (
        <div className={`fill-bar`}>
          <div className={`fill-bar-container ${filled ? themeClass : ''}`}>
            {label && labelStyle === 'over' && (
              <div className={`fill-bar-label-over text-theme-${theme || ''}`}>
                {label}
              </div>
            )}
            {filled && buttonWhenFilled ? (
              <div className={`fill-bar-filled-button ${themeClass}`} onClick={onClickFilled}>
                {filledLabel}
              </div>
            ) : (
              segments
            )}
          </div>
        </div>
      );
};
    
export default FillBar;
