import React from "react";
import { QuestReward, Quest, QuestNode } from "../../models";
import "./ActiveQuestsModal.scss";
import Cbox from "./cbox";
import CImage from "./cimage";
import RewardDisplay from "./RewardDisplay";

// Define ActiveQuest interface to match questManager
interface ActiveQuestData {
	quest: Quest;
	currentNode: QuestNode;
	completedTags: string[]; // Array of tag names
}

interface ActiveQuestsModalProps {
	activeQuests: ActiveQuestData[];
	onQuestSelect: (questId: string) => void;
	onClose: () => void;
	getCollectedTagsForQuest: (questId: string) => any[]; // Function to get actual tag objects
}

const ActiveQuestsModal: React.FC<ActiveQuestsModalProps> = ({
	activeQuests,
	onQuestSelect,
	onClose,
	getCollectedTagsForQuest,
}) => {
	// Helper function to count all possible tags in a quest
	const getAllTagsForQuest = (quest: Quest): number => {
		const allTags = new Set<string>();
		const countTags = (node: QuestNode) => {
			allTags.add(node.tag.name);
			for (const child of node.children) {
				countTags(child);
			}
		};
		countTags(quest.startingNode);
		return allTags.size;
	};

	return (
		<div className="active-quests-modal-overlay">
			<div className="quest-modal-container">
				<Cbox title="Active Quests" theme="quests">
					<div className="modal-header">
						<div className="quest-subheader">
							View your currently active side-stories and their progress.
						</div>
						<button className="close-button" onClick={onClose}></button>
					</div>

					{activeQuests.length === 0 ? (
						<div className="no-active-quests">
							<p>No active quests at the moment.</p>
							<p>Complete tag collections or advance the story to discover new quests!</p>
						</div>
					) : (
						<div className="quest-tiles">
							{activeQuests.map((activeQuest) => {
								const quest = activeQuest.quest;
								const collectedTags = getCollectedTagsForQuest(quest.id);
								const totalTags = getAllTagsForQuest(quest);
								const progressPercentage = Math.round(
									(collectedTags.length / totalTags) * 100
								);
								
								return (
									<Cbox
										key={quest.id}
										title={quest.name}
										theme={"quest"}
										onClick={() => onQuestSelect(quest.id)}
									>
										<div
											className="quest-tile-content active-quest"
											data-quest-icon={quest.icon || "default"}
										>
											<div className="quest-icon-background">
												{quest.icon && <CImage imageId={quest.icon} />}
											</div>

											<div className="quest-content-overlay">
												<div className="quest-info">
													<div className="quest-progress-bar">
														<div className="progress-label">
															Progress: {progressPercentage}%
														</div>
														<div className="progress-track">
															<div 
																className="progress-fill" 
																style={{ width: `${progressPercentage}%` }}
															/>
														</div>
														<div className="progress-details">
															{collectedTags.length} / {totalTags} tags collected
														</div>
													</div>
													
													<div className="quest-description">
														{quest.description}
													</div>

													<div className="quest-rewards">
														<div className="rewards-title">Potential Rewards:</div>
														<div className="quest-reward-row">
															{quest.rewards.map((reward: QuestReward, index: number) => (
																<span key={index} className="quest-reward">
																	<div className="quest-reward-treasure">
																		<RewardDisplay reward={reward} />
																	</div>
																</span>
															))}
														</div>
													</div>
												</div>

												
											</div>
										</div>
									</Cbox>
								);
							})}
						</div>
					)}
				</Cbox>
			</div>
		</div>
	);
};

export default ActiveQuestsModal;