import React, { useEffect, useState } from "react";
import { useCampaignManager, useAppManager } from "../managerContexts";
import { TAG_COLLECTIONS } from "../../consts/tagCollections";
import { FlavorTagSource, Tag, TAG_TYPE } from "../../models";
import FlavorTagPill from "./FlavorTagPill";
import "../../styles/tagCollections.scss";

type FilterType = "all" | "npc_races" | "enemies" | "travel" | "magic_item";

interface TagCollectionsScreenProps {
	onClose: () => void;
	isEmbedded?: boolean;
}

const TagCollectionsScreen: React.FC<TagCollectionsScreenProps> = ({
	onClose,
	isEmbedded = false,
}) => {
	const campaignManager = useCampaignManager();
	const appManager = useAppManager();
	const chatManager = appManager.getChatManager();
	const tagManager = appManager.getTagManager();
	const [activeFilter, setActiveFilter] = useState<FilterType>("all");

	const collectedTags = tagManager.getCollectedTags();
	const completedCollections = TAG_COLLECTIONS.filter((c) =>
		c.tags.every((t) => collectedTags.some((ct) => ct.name === t.name))
	);
	const incompleteCollections = TAG_COLLECTIONS.filter(
		(c) =>
			c.tags.some((t) => collectedTags.some((ct) => ct.name === t.name)) &&
			!c.tags.every((t) => collectedTags.some((ct) => ct.name === t.name))
	);

	const [tagSortAsc, setTagSortAsc] = useState(true);
	const [collectionSortAsc, setCollectionSortAsc] = useState(true);
	const [showIntroduced, setShowIncomplete] = useState(true);

	const filterTag = (tag: Tag): boolean => {
		switch (activeFilter) {
			case "npc_races":
				return !!tag.friendly;
			case "enemies":
				return !!tag.enemy || !!tag.friendly;
			case "travel":
				return !!tag.travel;
			case "magic_item":
				return tag.type === TAG_TYPE.GEAR;
			case "all":
				// We show these on the left
				return tag.type !== TAG_TYPE.GEAR;
			default:
				return true;
		}
	};

	// // Notify DM via chat for newly completed collections
	// useEffect(() => {
	// 	completedCollections.forEach((c) => {
	// 		chatManager.dmMessage(c.chat);
	// 	});
	// }, [chatManager, completedCollections]);

	// Prepare sorted data
	const sortedTags = [...collectedTags].sort((a, b) =>
		tagSortAsc ? a.name.localeCompare(b.name) : b.name.localeCompare(a.name)
	);
	const sortedCompleted = [...completedCollections].sort((a, b) =>
		collectionSortAsc
			? a.name.localeCompare(b.name)
			: b.name.localeCompare(a.name)
	);
	const sortedIntroduced = [...incompleteCollections].sort((a, b) =>
		collectionSortAsc
			? a.name.localeCompare(b.name)
			: b.name.localeCompare(a.name)
	);

	const expectedBackstoryTags = tagManager.getOutstandingBackstoryTags();
	const expectedMagicItemTags = tagManager.magicItemRumors;
	const discoveredMagicItemTags = tagManager.collectedTags.filter(
		(tag) => tag.type === TAG_TYPE.GEAR && !expectedMagicItemTags.includes(tag)
	);

	// Cash-in functionality
	const handleCashInCollection = (collectionName: string) => {
		// Mark collection as cashed in
		tagManager.cashInCollection(collectionName);
		
		// Add +1 DM ability point
		campaignManager.spendDMAbilityPoints(-1); // Negative to add points
		
		// Notify DM screen update so button counter refreshes
		campaignManager.emit('dmScreenUpdated');
		campaignManager.emit('dmAbilitiesUpdated', campaignManager.getDMAbilityPoints());

		console.log(`Cashed in collection: ${collectionName}, added +1 DM ability point`);
	};

	// Render functions for different sections
	const renderBackstoryLeads = () => {
		if (expectedBackstoryTags.length === 0) return null;
		
		return (
			<div className="minor-tag-collection">
				<div className="tag-collections-header">
					Backstory Leads
				</div>
				<div className="small-tags-container">
					<div className="tag-list">
						{expectedBackstoryTags.map((tag) => {
							const flavorTag = appManager
								.getTagManager()
								.tagToEncounterFlavorTag(tag);
							flavorTag.source = FlavorTagSource.BACKSTORY;
							return (
								<FlavorTagPill
									key={tag.name}
									tag={flavorTag}
									showTooltip={false}
								/>
							);
						})}
					</div>
				</div>
			</div>
		);
	};

	const renderRumoredMagicItems = () => {
		if (expectedMagicItemTags.length === 0) return null;
		
		return (
			<div className="minor-tag-collection">
				<div className="tag-collections-header">
					Rumored Magic Items
				</div>
				<div className="small-tags-container">
					<div className="tag-list">
						{expectedMagicItemTags.map((tag) => {
							const flavorTag = appManager
								.getTagManager()
								.tagToEncounterFlavorTag(tag);
							flavorTag.source = FlavorTagSource.MAGIC_ITEM_EXPECTED;
							return (
								<FlavorTagPill
									key={tag.name}
									tag={flavorTag}
									showTooltip={false}
								/>
							);
						})}
					</div>
				</div>
			</div>
		);
	};

	const renderQuestClues = () => {
		const outstandingQuestTags = appManager.getQuestManager().getOutstandingQuestTags();
		if (outstandingQuestTags.length === 0) return null;
		
		return (
			<div className="minor-tag-collection">
				<div className="tag-collections-header">
					Upcoming Quest Clues
				</div>
				<div className="small-tags-container">
					<div className="tag-list">
						{outstandingQuestTags.map((tag) => {
							const flavorTag = appManager
								.getTagManager()
								.tagToEncounterFlavorTag(tag);
							flavorTag.source = FlavorTagSource.QUEST;
							return (
								<FlavorTagPill
									key={tag.name}
									tag={flavorTag}
									showTooltip={false}
								/>
							);
						})}
					</div>
				</div>
			</div>
		);
	};

	const renderDiscoveredMagicItems = () => {
		if (discoveredMagicItemTags.length === 0) return null;
		
		return (
			<div className="minor-tag-collection">
				<div className="tag-collections-header">
					Discovered Magic Items
				</div>
				<div className="small-tags-container">
					<div className="tag-list">
						{discoveredMagicItemTags.map((tag) => {
							const flavorTag = appManager
								.getTagManager()
								.tagToEncounterFlavorTag(tag);
							flavorTag.source = FlavorTagSource.MAGIC_ITEM;
							return (
								<FlavorTagPill
									key={tag.name}
									tag={flavorTag}
									showTooltip={false}
								/>
							);
						})}
					</div>
				</div>
			</div>
		);
	};

	const renderSidePanel = () => {
		const hasContent =
			sortedCompleted.length > 0 ||
			sortedIntroduced.length > 0;
			
		if (!hasContent) return null;
		



		return (
			<div className="tag-collections-container narrow">
				<div className="side-panel">
					{sortedCompleted.length > 0 && renderEstablishedThemes()}
					{sortedIntroduced.length > 0 && renderIntroducedThemes()}
				
				</div>
			</div>
		);
	};

		const renderMainContent = () => {
		return (
			<div className="tag-collections-container">
				{!isEmbedded && (
					<div className="close-button" onClick={onClose}>
						
					</div>
				)}

				<div className="main-content">
					{renderEstablishedTopics()}
					<div className="minor-tag-containers">
						{renderBackstoryLeads()}
						{renderRumoredMagicItems()}
						{renderQuestClues()}
						{renderDiscoveredMagicItems()}
					</div>
				</div>
			</div>
		);
	};

	const renderFilterControls = () => {
		return (
			<div className="collections-controls">
				<div className="control-group">
					<div
						className={`filter-controls ${
							activeFilter === "all" ? "active" : ""
						}`}
						onClick={() => setActiveFilter("all")}
					>
						ALL
					</div>
					<div
						className={`filter-controls ${
							activeFilter === "npc_races" ? "active" : ""
						}`}
						onClick={() => setActiveFilter("npc_races")}
					>
						NPC
					</div>
					<div
						className={`filter-controls ${
							activeFilter === "enemies" ? "active" : ""
						}`}
						onClick={() => setActiveFilter("enemies")}
					>
						COMBATANT
					</div>
					<div
						className={`filter-controls ${
							activeFilter === "travel" ? "active" : ""
						}`}
						onClick={() => setActiveFilter("travel")}
					>
						TRAVEL
					</div>
				</div>
			</div>
		);
	};

	const renderEstablishedTopics = () => {
		return (
			<>
				<div className="tag-collections-header">Established Topics</div>
				<div className="collected-tags-container">
					{renderFilterControls()}
					<div className="tag-list">
						{sortedTags.filter(filterTag).map((tag) => {
							const flavorTag = appManager
								.getTagManager()
								.tagToEncounterFlavorTag(tag);
							flavorTag.source = FlavorTagSource.COLLECTION_COLLECTED;
							return (
								<FlavorTagPill
									key={tag.name}
									tag={flavorTag}
									showTooltip={false}
								/>
							);
						})}
					</div>
				</div>
			</>
		);
	};

	const renderEstablishedThemes = () => {
		return (
			<>
				<div className="tag-collections-header">Established Themes</div>
				<div className="collections-list">
					{sortedCompleted.map((c) => (
						<div key={c.name} className="collection-item complete">
							<div className="collection-header">
								<div className="collection-name completed">{c.name}</div>
								{!tagManager.isCollectionCashedIn(c.name) && (
									<button
										className="cash-in-button"
										onClick={() => handleCashInCollection(c.name)}
									>
										Cash In
									</button>
								)}
								{tagManager.isCollectionCashedIn(c.name) && (
									<span className="cashed-in-label">Cashed In</span>
								)}
							</div>
							<div className="tag-list">
								{c.tags.map((t) => {
									const owned = collectedTags.some(
										(ct) => ct.name === t.name
									);
									const flavorTag = appManager
										.getTagManager()
										.tagToEncounterFlavorTag(t);
									flavorTag.source = owned
										? FlavorTagSource.COLLECTION_COLLECTED
										: FlavorTagSource.COLLECTION;
									return (
										<FlavorTagPill
											key={t.name}
											tag={flavorTag}
											className={owned ? "collected" : "uncollected"}
											showTooltip={false}
										/>
									);
								})}
							</div>
						</div>
					))}
				</div>
			</>
		);
	};

	const renderIntroducedThemes = () => {
		if (!showIntroduced) return null;
		
		return (
			<>
				<div className="tag-collections-header">Introduced Themes</div>
				<div className="collections-list">
					{sortedIntroduced.map((c) => (
						<div key={c.name}>
							<div className="collection-name">{c.name}</div>
							<div className="collection-item">
								<div className="tag-list">
									{c.tags.map((t) => {
										const owned = collectedTags.some(
											(ct) => ct.name === t.name
										);
										const flavorTag = appManager
											.getTagManager()
											.tagToEncounterFlavorTag(t);
										flavorTag.source = owned
											? FlavorTagSource.COLLECTION_COLLECTED
											: FlavorTagSource.COLLECTION;
										return (
											<FlavorTagPill
												key={t.name}
												tag={flavorTag}
												className={owned ? "collected" : "uncollected"}
												showTooltip={false}
											/>
										);
									})}
								</div>
							</div>
						</div>
					))}
				</div>
			</>
		);
	};



	return (
		<div className="tag-collections-overlay green-theme">
			{renderMainContent()}
			{renderSidePanel()}
		</div>
	);
};

export default TagCollectionsScreen;
