interface ImageProps {
    imageId: string | undefined;
}

const CImage: React.FC<ImageProps> = ({ imageId }) => {
    if(imageId === "none") return null;
    const path = imageMap[imageId || ''];
    if (!path) {
        console.error(`No image found for imageId: ${imageId}`);
        return null;
    }
    return (
        <img src={path} alt="Icon" className="cimage" />
    );
}

export default CImage