import { GEAR, getCombinedAttributes } from '../consts/characterConsts';
import { ENEMIES } from '../consts/enemies';
import { CHARACTER_CLASS, RACE } from '../consts/general';
import { QUICKSTART_CHARACTERS } from '../consts/quickStart';
import { SCORE } from '../consts/score';
import { ATTRIBUTE_TYPE, Character, Tag } from '../models';
import { generateName } from '../utils/utils';
import AppManager from './appManager';

class CharacterManager {
  private characters: Character[];
  private uniqueID: number = 0;
  private selectedCharacters: number[];
  private appManager: AppManager;

  constructor(appManager: AppManager) {
    this.characters = [];
    this.selectedCharacters = [];
    this.appManager = appManager;
  }

  init() {
    this.generateRandomCharacters(12);
  }

  getCharacters() {
    return this.characters;
  }

  getPlayerCharacters() {
    const players = this.appManager.getPlayerManager().getPlayers();
    return players.flatMap((player) => player.characters);
  }
   
  getUFClass= (classId: CHARACTER_CLASS)  =>{
    switch(classId) {
      case CHARACTER_CLASS.CLERIC: return 'Cleric';
      case CHARACTER_CLASS.DRUID: return 'Druid';
      case CHARACTER_CLASS.FIGHTER: return 'Fighter';
      case CHARACTER_CLASS.PALADIN: return 'Paladin';
      case CHARACTER_CLASS.ROGUE: return 'Rogue';
      case CHARACTER_CLASS.WIZARD: return 'Wizard';
      default: return 'Unknown';
    }  
  }

   getUFRace= (raceId: RACE) => {
    switch(raceId) {
      case RACE.HUMAN: return 'Human';
      case RACE.ELF: return 'Elven';
      case RACE.DWARF: return 'Dwarven';
      case RACE.HALFLING: return 'Halfling';
      case RACE.HALF_ELF: return 'Half-Elf';
      case RACE.HALF_ORC: return 'Half-Orc';
      default: return 'Unknown';
    }
  }

  getUFAttributeName= (attributeId: ATTRIBUTE_TYPE) => {
    switch(attributeId) {
      case ATTRIBUTE_TYPE.COMBAT: return 'Combat';
      case ATTRIBUTE_TYPE.MAGIC: return 'Magic';
      case ATTRIBUTE_TYPE.TOOLS: return 'Tools';
      default: return 'Unknown';
    }
  }

  getUFAttributeSymbol= (attributeId: ATTRIBUTE_TYPE) => {
    switch(attributeId) {
      case ATTRIBUTE_TYPE.COMBAT: return '';
      case ATTRIBUTE_TYPE.MAGIC: return '';
      case ATTRIBUTE_TYPE.TOOLS: return '';
      default: return '';
    }
  }

  getUFInitials= (character: Character) => {
    return character.name.split(' ').map((n) => n[0]).join('');
  }

  setSelectedCharacters(selectedCharacters:number[]) {
    this.selectedCharacters = this.characters
      .filter((_, index) => selectedCharacters.includes(index))
      .map(character => character.id);
  }

  getCharacterByIndex(index: number) {
    return this.characters[index];
  }

  getCharacterById(id: number) {
    return this.characters.find(character => character.id === id) || this.characters[0];
  }


  getCharacterReferences() {
    return this.characters;
  }

  setAttributeUsed(character: Character, attributeType: ATTRIBUTE_TYPE, amount: number) {
    if(!character.attributeLoss) {
      character.attributeLoss = {};
    }
    character.attributeLoss[attributeType] = (character.attributeLoss[attributeType] || 0) + amount;
  }

  addUsedAttribute(character: Character, attributeType: ATTRIBUTE_TYPE, amount: number = 1) {
    // Add one or more points of attribute usage (default 1)
    this.setAttributeUsed(character, attributeType, amount);
  }

  useHalfRemainingAttributes(character: Character, attributeType: ATTRIBUTE_TYPE) {
    const remainingAttributes = getCombinedAttributes(character)[attributeType] - (character.attributeLoss?.[attributeType] || 0);
    const halfRemaining = Math.floor(remainingAttributes / 2);
    const reductionAmount = remainingAttributes - halfRemaining;
    this.setAttributeUsed(character, attributeType, reductionAmount);
    return reductionAmount;
  } 
  
  getRemainingAttributes(character: Character, attributeType: ATTRIBUTE_TYPE) {
    const totalAttributes = getCombinedAttributes(character)[attributeType] || 0;
    // need to add in the race attributes:
    const usedAttributes = character.attributeLoss?.[attributeType] || 0;
    return totalAttributes - usedAttributes;
  }

  getSelectedCharacters() {
    return this.selectedCharacters;
  }

  getRandomRace() {
    const races = Object.values(RACE);
    const randomIndex = Math.floor(Math.random() * races.length);
    return races[randomIndex];
  }

  getRandomClass() {
    const classes = Object.values(CHARACTER_CLASS);
    const randomIndex = Math.floor(Math.random() * classes.length);
    return classes[randomIndex];
  }

  getRandomIconNumber() {
    return Math.floor(Math.random() * 2) + 1;
  }

  getRandomIcon(characterRace: RACE) {
    // currently each race has exactly two icons, named race_1.png and race_2.png. 
    // This function should return one of those two names
    switch(characterRace) {
      case RACE.DWARF:
        return 'dwarf_' + this.getRandomIconNumber();
      case RACE.ELF:
        return 'elf_' + this.getRandomIconNumber();
      case RACE.HUMAN:
        return 'human_' + this.getRandomIconNumber();
      case RACE.HALF_ORC:
        return 'halfOrc_' + this.getRandomIconNumber();
      case RACE.HALF_ELF:
        return 'halfElf_' + this.getRandomIconNumber();
      case RACE.HALFLING: 
        return 'halfling_' + this.getRandomIconNumber();
      default:
        return 'human_' + this.getRandomIconNumber();
    }
  }

  generateStartingGear() {
    switch(Math.floor(Math.random() * 6)) {
      case 0: return [GEAR.SWORD_1];
      case 1: return [GEAR.WAND_5];
      case 2: return [GEAR.HELPFUL_IMP];
     default: return [];
    }
  }


  getRandomGear() {
    const gearNames = Object.keys(GEAR);
    const randomIndex = Math.floor(Math.random() * gearNames.length);
    return GEAR[gearNames[randomIndex]];
  }


  assignGear(character: Character, gearName: string) {
  
    if(GEAR[gearName] === undefined) {
      console.error('Gear not found: ' + gearName);
      // Check if the gearName is the value of the gear instead of the key
      gearName = Object.keys(GEAR).find(key => GEAR[key].name === gearName) || '';
    }
    
    const gear = GEAR[gearName];

    if(!gear) {
      console.error('Gear not found: ' + gearName);
      return;
    }

    character.gear.push(gear);
  }


  getAllExpectedBackstoryTags(): Tag[] {
    const backstoryTags = this.characters.flatMap((character) => {
      return character.backstory.filter((bs) => !bs.completed).map((backstory) => backstory.tag);
    })

    return [...new Set(backstoryTags)];
  }

  getCharactersWithBackstoryTag(tag: Tag): Character[] {
    return this.characters.filter((character) => character.backstory.some((bs) => bs.tag === tag));
  }

  assignBackstory(character: Character, tag: Tag) {
    const existingBackstory = character.backstory.find((backstory) => backstory.tag === tag);
    if(existingBackstory) {
      existingBackstory.completed = true;
      //this.appManager.getPartyManager().addPartyScore(SCORE.COMPLETE_BACKSTORY);
    } else {
      const newBackstory = {
        tag: tag,
        completed: false,
        description: undefined
      }
      character.backstory.push(newBackstory);
    }
    const player = this.appManager.getPlayerManager().getPlayerAssignedCharacter(character);
    if(player){
      this.appManager.getPlayerManager().addPlayerExp(player, 1);
    }
  }

  generateRandomCharacter(level?: number): Character {
    const newChar = this.generateSingleCharacter(level || 1);
    return newChar;
  }

  generateSingleCharacter(level: number): Character {
    const cclass = this.getRandomClass();
    const crace = this.getRandomRace();
    const newChar: Character = {
      name: generateName(true),
      race: crace,
      character_class: cclass,
      level: level || 1 + Math.floor(Math.random() * 4),
      experience: Math.floor(Math.random() * 50) + 0, // Random experience 20-69
      icon: this.getRandomIcon(crace),
      abilities: [],
      id: this.uniqueID++,
      gear: this.generateStartingGear(),
      backstory: []
    }
    return newChar;
  }


  generateRandomCharacters(nbChars: number, level?: number) {
    for (let p = 0; p < nbChars; p++) {
      this.characters.push(this.generateSingleCharacter(level || 1));
    }
   
  }

  generateNPC(npcTag: Tag) : Character {
    
    const newCharacter: Character = {
      race: RACE.NPC,
      character_class: CHARACTER_CLASS.NPC,
      abilities: [],
      level: 1,
      experience: 0, // NPCs start with 0 experience
      name: npcTag.name,
      id: this.uniqueID++,
      icon: `combat_${npcTag.name}`,
      gear: [],
      backstory: [],
    }

    return newCharacter;
  }


  initiateQuickStart() {
    
    this.characters = QUICKSTART_CHARACTERS;
  }

  /**
   * Add experience to a character, capped at 100
   * @param characterId - The ID of the character to give experience to
   * @param amount - The amount of experience to add (if not provided, adds 1-5 randomly)
   */
  characterAction(characterId: number, amount?: number): void {
    const character = this.getCharacterById(characterId);
    if (!character) {
      return;
    }

    const expToAdd = amount !== undefined ? amount : Math.floor(Math.random() * 5) + 1;
    character.experience = Math.min(100, character.experience + expToAdd);
  }

}

export default CharacterManager;