import React, { useState } from 'react';
import { Character, EffectData, ResultConfig, Attribute, ATTRIBUTE_TYPE } from '../../models';
import { 
  useAppManager, 
  useCharacterManager, 
  usePlayerManager 
} from '../managerContexts';
import { 
  getCharacterReadinessLevel, 
  CHARACTER_READINESS_LEVEL,
  getClassAttributes
} from '../../consts/characterConsts';
import ResultsDisplay from '../GameRoom/resultsDisplay';
import './PartyLevelUpModal.scss';
import PlayerCharacterLevelRow from './PlayerCharacterLevelRow';

interface PartyLevelUpModalProps {
  onClose: () => void;
}

const PartyLevelUpModal: React.FC<PartyLevelUpModalProps> = ({ onClose }) => {
  const [selectedCharacters, setSelectedCharacters] = useState<Set<number>>(new Set());
  const [showResults, setShowResults] = useState(false);
  const [resultConfig, setResultConfig] = useState<ResultConfig | null>(null);
  
  const appManager = useAppManager();
  const characterManager = useCharacterManager();
  const playerManager = usePlayerManager();
  const players = playerManager.getPlayers();

  const handleCharacterToggle = (characterId: number) => {
    const newSelected = new Set(selectedCharacters);
    if (newSelected.has(characterId)) {
      newSelected.delete(characterId);
    } else {
      newSelected.add(characterId);
    }
    setSelectedCharacters(newSelected);
  };

  const getAttributeGainForLevelUp = (character: Character): number => {
    // Standard attribute gain per level - this could be made more sophisticated
    // based on character class or level in the future
    return 2;
  };

  const getAttributeGains = (character: Character): Attribute => {
    // Get attributes at current level
    const currentLevelAttributes = getClassAttributes(character.character_class, character.level);
    
    // Get attributes at next level
    const nextLevelAttributes = getClassAttributes(character.character_class, character.level + 1);
    
    // Calculate the difference
    const gains: Attribute = {
      [ATTRIBUTE_TYPE.COMBAT]: nextLevelAttributes[ATTRIBUTE_TYPE.COMBAT] - currentLevelAttributes[ATTRIBUTE_TYPE.COMBAT],
      [ATTRIBUTE_TYPE.MAGIC]: nextLevelAttributes[ATTRIBUTE_TYPE.MAGIC] - currentLevelAttributes[ATTRIBUTE_TYPE.MAGIC],
      [ATTRIBUTE_TYPE.TOOLS]: nextLevelAttributes[ATTRIBUTE_TYPE.TOOLS] - currentLevelAttributes[ATTRIBUTE_TYPE.TOOLS]
    };
    
    return gains;
  };

  const generateLevelUpEffects = (): EffectData[] => {
    const effects: EffectData[] = [];
    const partyManager = appManager.getPartyManager();

    // Helper function to add results, combining effects for the same object
    const addResult = (object: any, effect: string, className: string) => {
      if (!object) {
        return;
      }

      // Check if the object already has a result in the effects array
      const existingResult = effects.find(result => result.object === object);
      if (existingResult && existingResult.effects) {
        existingResult.effects.push({
          effect: effect,
          class: className
        });
        return;
      }

      effects.push({
        object: object,
        effects: [{
          effect: effect,
          class: className
        }]
      });
    };

    // Get all characters that are leveling up
    const charactersToLevelUp = Array.from(selectedCharacters)
      .map(id => characterManager.getCharacterById(id))
      .filter(char => char !== undefined) as Character[];

    // Track which players own leveling characters
    const playersWithLevelingChars = new Set<number>();
    charactersToLevelUp.forEach(char => {
      const owner = players.find(p => p.characters.some(c => c.id === char.id));
      if (owner) {
        playersWithLevelingChars.add(owner.id);
      }
    });

    // Process each character leveling up
    charactersToLevelUp.forEach(character => {
      const owner = players.find(p => p.characters.some(c => c.id === character.id));
      if (!owner) return;

      const readinessLevel = getCharacterReadinessLevel(character.experience);
      
      // Show character level up details
      const newLevel = character.level + 1;
      const attributeGain = getAttributeGainForLevelUp(character);
      const attributeGains = getAttributeGains(character);
      
      addResult(character, `Level ${newLevel}`, "character-level");
      addResult(character, `+${attributeGain} Attributes`, "character-attributes");
      
      // Add attribute gains as a special effect that will render an AttributeDisplay
      addResult(character, JSON.stringify(attributeGains), "attribute-gains");

      // Give player full attention
      owner.playerStats.involvement = owner.playerStats.maxInvolvement;
      addResult(owner, "Full Attention", "player-attention");

      // Give player experience for leveling up their character
      playerManager.addPlayerExp(owner, 1);
      addResult(owner, "+1 Experience", "player-exp");

      // Apply happiness bonus based on readiness
      let happinessBonus = 0;
      switch (readinessLevel) {
        case CHARACTER_READINESS_LEVEL.VERY_UNREADY:
          happinessBonus = -2; // All other players lose happiness
          break;
        case CHARACTER_READINESS_LEVEL.UNREADY:
          happinessBonus = -1; // All other players lose happiness
          break;
        case CHARACTER_READINESS_LEVEL.READY:
          happinessBonus = 1;
          break;
        case CHARACTER_READINESS_LEVEL.PERFECT:
          happinessBonus = 2;
          break;
        case CHARACTER_READINESS_LEVEL.OVERREADY:
          happinessBonus = 0;
          break;
      }

      if (readinessLevel === CHARACTER_READINESS_LEVEL.VERY_UNREADY || readinessLevel === CHARACTER_READINESS_LEVEL.UNREADY) {
        // Apply negative happiness to all other players
        players.forEach(player => {
          if (player.id !== owner.id) {
            partyManager.addEnjoyment(player, happinessBonus);
            addResult(player, `${happinessBonus} Happiness`, "player-attitude");
          }
        });
      } else if (happinessBonus > 0) {
        // Apply positive happiness to the owner
        partyManager.addEnjoyment(owner, happinessBonus);
        addResult(owner, `+${happinessBonus} Happiness`, "player-attitude");
      }
    });

    // Apply overready penalty to players with overready characters when OTHER players level up
    players.forEach(player => {
      const hasOverreadyChar = player.characters.some(char => 
        getCharacterReadinessLevel(char.experience) === CHARACTER_READINESS_LEVEL.OVERREADY
      );
      
      // Only apply penalty if this player doesn't have any characters leveling up
      if (hasOverreadyChar && !playersWithLevelingChars.has(player.id)) {
        const numLevelingUp = charactersToLevelUp.length;
        const penalty = -1 * numLevelingUp;
        partyManager.addEnjoyment(player, penalty);
        addResult(player, `${penalty} Happiness (Overready Penalty)`, "player-attitude");
      }
    });

    // Add party exhaustion for level-up process
    const exhaustionAmount = 25;
    partyManager.exhaustParty(exhaustionAmount);
    addResult('party', `+${exhaustionAmount} Exhaustion`, "exhaustion");

    return effects;
  };

  const handleLevelUp = () => {
    const effects = generateLevelUpEffects();
    
    // Actually level up the characters (reset experience)
    selectedCharacters.forEach(charId => {
      const character = characterManager.getCharacterById(charId);
      if (character) {
        character.level += 1;
        character.experience = 0;
      }
    });

    // Create ResultConfig for the existing ResultsDisplay component
    const config: ResultConfig = {
      results: effects,
      title: "Level Up Results",
      description: `Successfully leveled up ${selectedCharacters.size} character${selectedCharacters.size === 1 ? '' : 's'}!`,
      tags: [], // No tags for level up results
      travelOption: {
        tagInstructions: ""
      },
      info: {}
    };

    setResultConfig(config);
    setShowResults(true);
  };

  const handleBackdropClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      onClose();
    }
  };

  const playersWithCharacters = players.filter(player => player.characters.length > 0);

  const renderResultInfo = () => {
    return null; // No additional info needed for level up results
  };

  if (showResults && resultConfig) {
    return (
      <div className="party-level-up-modal-overlay" onClick={handleBackdropClick}>
        <div className="party-level-up-modal-container">
          <div className="level-up-results-wrapper">
            <ResultsDisplay
              resultConfig={resultConfig}
              renderResultInfo={renderResultInfo}
              onClose={onClose}
              onTagClick={() => {}} // No tags to click for level up
              selectedTags={[]} // No tags for level up
              remainingClicks={0} // No tags to click
            />
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="party-level-up-modal-overlay" onClick={handleBackdropClick}>
      <div className="party-level-up-modal-container">
        <div className="party-level-up-modal-header">
          <h2>Character Level Up</h2>
          <button className="close-button" onClick={onClose}></button>
        </div>

        <div className="party-level-up-modal-content">
          <p className="selection-prompt">Select characters to level up:</p>
          
          <div className="players-list">
            {playersWithCharacters.map((player) => (
              <PlayerCharacterLevelRow
                key={player.id}
                player={player}
                selectedCharacters={selectedCharacters}
                onCharacterToggle={handleCharacterToggle}
              />
            ))}
          </div>
        </div>

        <div className="party-level-up-modal-footer">
          <button
            className={`confirm-button ${selectedCharacters.size === 0 ? 'exit' : ''}`}
            onClick={selectedCharacters.size === 0 ? onClose : handleLevelUp}
          >
            {selectedCharacters.size === 0 ? 'Exit' : `Level Up ${selectedCharacters.size} Characters`}
          </button>
        </div>
      </div>
    </div>
  );
};

export default PartyLevelUpModal;
