import { AVAILABILITY_RANGE } from '../consts/general';
import './playerRecruitment.scss';



interface ScheduleProps {
    dayData: number[];
    legend?: boolean;
}

const daysOfWeek = [
    { abbreviation: 'M', fullName: 'Monday' },
    { abbreviation: 'T', fullName: 'Tuesday' },
    { abbreviation: 'W', fullName: 'Wednesday' },
    { abbreviation: 'R', fullName: 'Thursday' },
    { abbreviation: 'F', fullName: 'Friday' },
    { abbreviation: 'St', fullName: 'Saturday' },
    { abbreviation: 'Sn', fullName: 'Sunday' },
  ];

  const availabiltyLegend = [
    { name: 'Yes', percentage: AVAILABILITY_RANGE.GOOD + 1},
    { name: 'Maybe', percentage: AVAILABILITY_RANGE.GOOD - 1},
    { name: 'No', percentage: AVAILABILITY_RANGE.BAD - 1},
  ]
  
  const getDayPercentage = (dayData: number[], dayIndex: number): number => {
    return dayData[dayIndex]
  };
  
  const getColorFromPercentage = (percentage: number): string => {
    const red = Math.min(255, Math.floor((100 - percentage) * 2.55));
    const green = Math.min(255, Math.floor(percentage * 2.55));
    return `rgba(${red}, ${green}, 0, 0)`;
    //return `rgb(${red}, ${green}, 0)`;
  };

const getSymbolForPercentage = (percentage: number): string => {
    if (percentage <= AVAILABILITY_RANGE.BAD) {
        return 'X';
    }
    if (percentage > AVAILABILITY_RANGE.GOOD) {
        return '';
    }
  
    return '';
}

const Schedule: React.FC<ScheduleProps> = ({dayData, legend = false}) => {
    return (
            <div className="week-display-days">
                {!legend && daysOfWeek.map((day, index) => {
                    const percentage = getDayPercentage(dayData, index);
                    const color = getColorFromPercentage(percentage);
                    return (
                    <div key={index} className="day-square" style={{ backgroundColor: color }}>
                        <div className="day-abbreviation">{day.abbreviation}</div>
                        <div className="day-symbol">{getSymbolForPercentage(percentage)}</div>
                        <div className="day-percentage">{percentage}%</div>
                    </div>
                    );
                })}
                {legend && availabiltyLegend.map((legendEntry, index) => {
                    const percentage =legendEntry.percentage;
                    const color = getColorFromPercentage(percentage);
                    return (
                    <div key={index} className="day-square" style={{ backgroundColor: color }}>
                        <div className="day-abbreviation">{legendEntry.name}</div>
                        <div className="day-symbol">{getSymbolForPercentage(percentage)}</div>
                    </div>
                    );
                })}
             </div>
    )
}

export default Schedule;