import React from 'react';
import './CContainer.scss';

interface CContainerProps {
  children: React.ReactNode;
  title?: string;
  variant?: string;
  className?: string;
  style?: React.CSSProperties;
}

const CContainer: React.FC<CContainerProps> = ({ 
  children, 
  title, 
  variant = 'primary', 
  className = '', 
  style 
}) => {
  const containerClasses = [
    'c-container',
    `c-container--${variant}`,
    className
  ].filter(Boolean).join(' ');

  return (
    <div className={containerClasses} style={style}>
      {title && (
        <div className="c-container__title-bar">
          <span className="c-container__title">{title}</span>
        </div>
      )}
      <div className="c-container__content">
        {children}
      </div>
    </div>
  );
};

export default CContainer;
