import { useState, useEffect } from 'react';
import { ModalConfig } from '../models';
import { useAppManager } from '../components/managerContexts';

export const useModal = () => {
  const [modalConfig, setModalConfig] = useState<ModalConfig | null>(null);
  const appManager = useAppManager();
  const uiManager = appManager.getUiManager();

  useEffect(() => {
    const handleModalStateChanged = (config: ModalConfig | null) => {
      setModalConfig(config);
    };

    uiManager.on('modalStateChanged', handleModalStateChanged);
    return () => {
      uiManager.off('modalStateChanged', handleModalStateChanged);
    };
  }, [uiManager]);

  return {
    modalConfig,
    handleOptionSelect: (optionId: string) => uiManager.handleModalOptionSelected(optionId),
    closeModal: () => uiManager.closeModal()
  };
};