import React from 'react';
import './ComingSoon.scss';

interface ComingSoonProps {
  /** The main label text (default: "LOCKED") */
  label?: string;
  /** The subtitle text (default: "Coming Soon") */
  subtitle?: string;
  /** Optional custom styling */
  className?: string;
  /** Whether to show the overlay (default: true) */
  visible?: boolean;
}

/**
 * ComingSoon Overlay Component
 * 
 * A reusable semi-transparent overlay that can be placed over any UI element
 * to indicate it's locked, disabled, or coming soon.
 * 
 * Usage Examples:
 * 
 * Basic usage:
 * <div style={{ position: 'relative' }}>
 *   <YourComponent />
 *   <ComingSoon />
 * </div>
 * 
 * Custom labels:
 * <ComingSoon label="DISABLED" subtitle="Feature in development" />
 * 
 * Conditional display:
 * <ComingSoon visible={!isUnlocked} />
 */
const ComingSoon: React.FC<ComingSoonProps> = ({
  label = "LOCKED",
  subtitle = "Coming Soon",
  className = "",
  visible = true
}) => {
  if (!visible) return null;

  return (
    <div className={`coming-soon-overlay ${className}`}>
      <div className="coming-soon-content">
        <div className="coming-soon-label">{label}</div>
        <div className="coming-soon-subtitle">{subtitle}</div>
      </div>
    </div>
  );
};

export default ComingSoon;