import React from 'react';
import { EncounterFlavorTag, ResultConfig, Tag } from '../../models';
import EffectDisplay from '../UI/effectDisplay';
import FlavorTagPill from '../UI/FlavorTagPill';
import TooltipWrapper from '../UI/TooltipWrapper';



interface ResultsDisplayProps {
  resultConfig: ResultConfig;
  renderResultInfo: () => React.ReactNode;
  onClose: () => void;
  onTagClick: (tag: EncounterFlavorTag) => void;
  selectedTags: Tag[];
  remainingClicks: number;
}

const ResultsDisplay: React.FC<ResultsDisplayProps> = ({
  resultConfig,
  renderResultInfo,
  onClose,
  onTagClick,
  selectedTags,
  remainingClicks
}) => {
  return (
    <div className="results-panel">
      <div className="results-panel-container">
        <div className="results-panel-header">
          {resultConfig.title}
          <div className="results-panel-description">
            {resultConfig.description}
          </div>
          <button
            className="close-button"
            onClick={onClose}
          >
            
          </button>
        </div>

        <div className="result-info">
          {renderResultInfo()}
        </div>

        <div className="travel-results">
          {resultConfig.results.map((result, idx) => (
            <EffectDisplay key={idx} effectData={result} />
          ))}
        </div>
        {resultConfig.tags.length > 0 && remainingClicks > 0 && (
          <div className="travel-tag-container">
            <div className="clicks-remaining">
              <TooltipWrapper
                content="Click on the tags to reveal them."
                maxWidth="200"
                preservePositioning={true}
                translate={[0, -80]}
              >
                {remainingClicks}
              </TooltipWrapper>
            </div>
            <div className="travel-tags-instructions">
              {resultConfig.travelOption.tagInstructions}
            </div>
            <div className="travel-tags-container">
              <div className="travel-tags">
                {resultConfig.tags.map((tag) => {
                  const tagUsed = selectedTags.includes(tag.tag);
                  return (
                    <FlavorTagPill
                      key={tag.tag.name}
                      tag={tag}
                      onClick={onTagClick}
                      className={`${tagUsed ? "used" : ""}`}
                      disabled={tagUsed}
                    />
                  );
                })}
              </div>
            </div>
          </div>
        )}

        {remainingClicks > 0 && resultConfig.tags.length === 0 && (
          <div className="remaining-clicks-warning">
            Nothing new to present to the players.
          </div>
      )}


        {(resultConfig.tags.length === 0 || remainingClicks === 0) && (
        <div className="bottom-container">
          
          <div
            className={`return-button ${remainingClicks > 0 ? 'disabled' : ''}`}
            onClick={onClose}
          >
            Return
          </div>
          
        </div> )}
      </div>
    </div>
  );
};

export default ResultsDisplay;
