import { LEVELUP_OPTION, MAX_CHARS_PER_PLAYER, PLAYER_EXP_LEVEL } from '../consts/general';
import { ATTITUDE, Character, ModalOption, Player, PREFERENCE, Tag } from '../models';
import { TAG } from '../consts/tags';
import { generateName } from '../utils/utils';
import { PLAYER_TRAITS } from '../consts/playerTraits';
import { QUICKSTART_PLAYERS } from '../consts/quickStart';
import UiManager from './uiManager';
import AppManager from './appManager';
import { channel } from 'diagnostics_channel';
import { ENEMIES } from '../consts/enemies';
import ChatManager from './chatManager';
import CharacterManager from './characterManager';


const CHANCE_FOR_PLAYER_TRAIT = 0.5;





class PlayerManager {
  private recruitablePlayers: Player[];
  private selectedPlayers: Player[];
  private players: Player[];
  private uniqueID: number = 0;
  private _currentPlayerIndex: number = 0;
  private appManager: AppManager;
  private uiManager: UiManager | undefined;
 

  constructor(appManager: AppManager) {
    this.recruitablePlayers = [];
    this.players = [];
    this.selectedPlayers = [];
    this.appManager = appManager;
  }


  getCurrentPlayer() {
    return this.players.length === 0 ? undefined : this.players[this._currentPlayerIndex];
  }

  isCurrentPlayerLast() {
    return this._currentPlayerIndex === this.players.length - 1;
  }

  init() {  
      this.uiManager = this.appManager.getUiManager();
      this.generateAvailablePlayers(8);
  }
  
  stressPlayers(amount: number) {
    
    this.players.forEach((player) => {
      player.playerStats.stress += amount;
    });
  
    this.uiManager?.updateParty();
  }

  getRecruitablePlayers() {
    return this.recruitablePlayers;
  }

  setSelectedPlayers(selectedPlayers:number[]) {
    this.selectedPlayers = this.recruitablePlayers.filter((_, index) => {
      return selectedPlayers.includes(index);
    });
  }
  
  getPlayers() {
    return this.players;
  }

  getSelectedPlayers() {
    return this.selectedPlayers;
  }

  getPlayerById(id: number): Player | undefined {
    return this.players.find((player) => player.id === id) || undefined;
  }

  getSelectedPlayerIds() {
    return this.selectedPlayers.map((player) => player.id);
  }

  generateIcon() {
    return "player_" + Math.ceil(Math.random() * 9.9);
  }

  generateSchedule() {
    const schedule = [];
    for(let i = 0; i<7; i++) {
      schedule.push(Math.ceil(Math.random() * 100));
    }
    return schedule;
  }

  generateAvailablePlayers(nbPlayer: number) {
    for(let p = 0; p<nbPlayer; p++){
      const newPlayer: Player = {
        name: generateName(false),
        id: this.uniqueID++,
        characters: [],
        icon: this.generateIcon(),
        schedule: this.generateSchedule(),
        preferences: this.generatePreferences(),
        traits: this.generatePlayerTraits(),
        playerStats: {
          stress: 50,
          enjoyment: 3,
          engagement: 50,
          involvement: 3,
          experience: 0,
          level: 1,
          maxEngagement: 5,
          maxInvolvement: 5,
          maxEnjoyment: 5,
          stressBuffer: 10,
        }
      }
      this.recruitablePlayers.push(newPlayer);
    }
  }

  // This should be a campaign setting maaybe....
  getMaxCharactersPerPlayer() {
    return MAX_CHARS_PER_PLAYER;
  }

  acceptSelectedPlayers() {
    this.players = this.selectedPlayers;
  }

  getPlayerWithAttitudeForTag(tag: Tag, attitude: ATTITUDE) {
    
    const playersWithAttitude = this.players.filter((player) => {
      return player.preferences.some((pref) => pref[0].name === tag.name && pref[1] === attitude);
    });
    if(playersWithAttitude.length > 0) {
      return playersWithAttitude[Math.floor(Math.random() * playersWithAttitude.length)];
    }
    return undefined;
  }


  generatePlayerTraits() {
    const allTraits = Object.values(PLAYER_TRAITS);
    
    if(Math.random() < CHANCE_FOR_PLAYER_TRAIT) {
      const randomTrait = allTraits[Math.floor(Math.random() * allTraits.length)];
      return [randomTrait];
    }
    return [];

  }

  generatePreferences(): [Tag, ATTITUDE][] {
    const preferences: [Tag, ATTITUDE][] = [];

    // Flatten the TAG object to get all tags
    const allTags: Tag[] = Object.values(TAG).flat();

    // Filter tags with preference = PREFERENCE.START
    const startTags = allTags.filter(tag => tag.preference === PREFERENCE.START);
    const mainTags = allTags.filter(tag => tag.preference === PREFERENCE.MAIN);

    // Select one main like and one main hate
    if (startTags.length > 1) {
      const mainLike = startTags.splice(Math.floor(Math.random() * startTags.length), 1)[0];
      const mainHate = startTags.splice(Math.floor(Math.random() * startTags.length), 1)[0];
      preferences.push([mainLike, ATTITUDE.LIKE]);
      preferences.push([mainHate, ATTITUDE.HATE]);
    }

    // Function to get random likes and hates
    const getRandomPreferences = (tags: Tag[], count: number, min: number, max: number) => {
      const selectedPreferences: [Tag, ATTITUDE][] = [];
      for (let i = 0; i < count; i++) {
        const randomTag = tags[Math.floor(Math.random() * tags.length)];
        const randomValue = Math.floor(Math.random() * (max - min + 1)) + min;
        selectedPreferences.push([randomTag, randomValue]);
      }
      return selectedPreferences;
    };

    // Get 0-2 random likes and hates
    const randomLikesCount = Math.floor(Math.random() * 3);
    const randomHatesCount = Math.floor(Math.random() * 3);

    const randomLikes = getRandomPreferences(mainTags, randomLikesCount, 1, 5);
    const randomHates = getRandomPreferences(mainTags, randomHatesCount, -5, -1);

    preferences.push(...randomLikes);
    preferences.push(...randomHates);

    return preferences;
  }

  exposeEncounterTag(tag: Tag) {
    // Player's happiness will go up or down by 5 points depending on their attitude towards the tag
    this.players.forEach((player) => {
      const attitude = player.preferences.find((pref) => pref[0].name === tag.name);
      if(attitude) {
        player.playerStats.enjoyment = Math.max(Math.min(player.playerStats.maxEnjoyment, player.playerStats.enjoyment + attitude[1]), 0);
      }
    });
    this.uiManager?.updateParty();
  }


  toggleSinglePlayerForCharacter(player: Player, character: Character) {
    if(player) {
      if(player.characters.includes(character)) {
        this.removeCharacterFromPlayer(player, character);
      }else{
        this.players.forEach((p) => {
          if(p.characters.includes(character)) {
            this.removeCharacterFromPlayer(p, character);
          }
        });
      player.characters.push(character);
     }
    }
  }

  getPlayerAssignedCharacter(character: Character| undefined): Player | undefined {
    if(!character) return undefined;
    let thePlayer: Player | undefined;
    this.players.forEach((p) => {
      if(p.characters.includes(character)) {
        thePlayer = p;
      }
    });
    return thePlayer;
  }

  assignCharactersToPlayer(player: Player, characters: Character[]) {
    if(player) {
     characters.forEach((character) => {
        player.characters.push(character);
      })
    }
  }

  isCharacterAssigned(character: Character) {
    return this.players.some((player) => player.characters.includes(character));
  }

  removeCharacterFromPlayer(player: Player, character: Character) {
    if(player) {
      const index = player.characters.indexOf(character);
      if(index > -1) {
        player.characters.splice(index, 1);
      }
    }
  }

  aggregateSchedule() {
    const dayData = [0, 0, 0, 0, 0, 0, 0];
    this.selectedPlayers.forEach((player) => {
      player.schedule.forEach((dayValue, index) => {
        dayData[index] += dayValue/this.selectedPlayers.length;
        dayData[index] = Math.ceil(dayData[index]);
      });
    });
    return dayData;
  }

  
  getGroupAttitudes(players: Player[] = this.selectedPlayers) {
    const groupAttitudes: { [key: string]: ATTITUDE } = {};
    players.forEach((player) => {
      player.preferences.forEach((pref) => {
        const tag = pref[0];
        const feel = pref[1];
        
        if(groupAttitudes[tag.name]) {
          switch(groupAttitudes[tag.name]) {
            case ATTITUDE.LIKE:
              if(feel < 0) {
                groupAttitudes[tag.name] = ATTITUDE.CONFLICTED;
              }
              break;
            case ATTITUDE.HATE:
              if(feel > 0) {
                groupAttitudes[tag.name] = ATTITUDE.CONFLICTED;
              }
              break;
          }
        } else {
          groupAttitudes[tag.name] = feel < 0 ? ATTITUDE.HATE : ATTITUDE.LIKE;
        }
      });
    });


  


       const likes = Object.keys(groupAttitudes).map(key => {
          if(groupAttitudes[key] === ATTITUDE.LIKE) return key
          return ''
        }
       ).filter((e) => e !== '')

       const hates = Object.keys(groupAttitudes).map(key => {
        if(groupAttitudes[key] === ATTITUDE.HATE) return key
        return ''
      }
     ).filter((e) => e !== '')

     const conflicted = Object.keys(groupAttitudes).map(key => {
      if(groupAttitudes[key] === ATTITUDE.CONFLICTED) return key
      return ''
    }
   ).filter((e) => e !== '')


    return {likes, hates, conflicted};
  }

  getGroupAttitudeForTag(players:Player[] ,tag: Tag) {
    const groupAttitudes = this.getGroupAttitudes(players);
    if(groupAttitudes.likes.includes(tag.name)) {
      return ATTITUDE.LIKE;
    } else if(groupAttitudes.hates.includes(tag.name)) {
      return ATTITUDE.HATE;
    } else if(groupAttitudes.conflicted.includes(tag.name)) {
      return ATTITUDE.CONFLICTED;
    }
    return ATTITUDE.NEUTRAL;
  }

  getPartyAttitudeForTag(tag: Tag) {
    return this.getGroupAttitudeForTag(this.players, tag);
  }


  // There are players in the group.
  readyForGame() {
    if(this.players.length === 0) return false;
    let allPlayersHaveCharacter = true;
    this.players.forEach((player) => {
      if(player.characters.length === 0){
        allPlayersHaveCharacter = false;
      }
    })
    return allPlayersHaveCharacter; 
  }

  initiateQuickStart() {
    
    this.recruitablePlayers = this.players = QUICKSTART_PLAYERS;
  }

  addPlayerExp(player: Player | undefined, amount: number) {
   if(!player) return;
      player.playerStats.experience += amount;
      if(player.playerStats.experience >= this.expNeededForLevel(player.playerStats.level)) {
        player.playerStats.level += 1;
        player.playerStats.experience = 0;
        console.log("player leveled up!!!!");
        this.appManager.getChatManager().dmMessage(`${player.name} learned something!`);
        this.handleLevelUp(player);
      }
    this.uiManager?.updateParty();
  }

  expNeededForLevel(level: number) {
    return PLAYER_EXP_LEVEL[level - 1] || 0;
  }

  getAllPlayerPreferences() {
    const allPreferences: Tag[] = [];
    this.players.forEach((player) => {
      player.preferences.forEach((pref) => {
        if(!allPreferences.includes(pref[0])) {
          allPreferences.push(pref[0]);
        }
      });
    });
    return allPreferences;
  }

  getAllPlayerCharacters(): Character[] {
    const allCharacters: Character[] = [];
    this.players.forEach((player) => {
      player.characters.forEach((character) => {
        if(!allCharacters.includes(character)) {
          allCharacters.push(character);
        }
      });
    });
    return allCharacters;
  }

  getRandomPlayer() {
    return this.players[Math.floor(Math.random() * this.players.length)];
  }

  getRandomCharacter(allowDead: boolean = false) {
    const allCharacters = this.getAllPlayerCharacters();
    if(allCharacters.length === 0) return undefined;
    if(!allowDead) {
      const aliveCharacters = allCharacters.filter((character) => !(character.injury === "dead"));
      if(aliveCharacters.length === 0) return undefined;
      return aliveCharacters[Math.floor(Math.random() * aliveCharacters.length)];
    }
    // If allowDead is true, return any character
    return allCharacters[Math.floor(Math.random() * allCharacters.length)];
  }


  getRandomCharacterForPlayer(player: Player, allowDead: boolean = false) {
    if(!player) return undefined;
    if(player.characters.length === 0) return undefined;
    if(!allowDead) {
      const aliveCharacters = player.characters.filter((character) => !(character.injury === "dead"));
      if(aliveCharacters.length === 0) return undefined;
      return aliveCharacters[Math.floor(Math.random() * aliveCharacters.length)];
    }
    // If allowDead is true, return any character
    return player.characters[Math.floor(Math.random() * player.characters.length)];
  }
  
  refreshPlayer(player: Player) {
    if(player) {
      player.playerStats.involvement = player.playerStats.maxInvolvement;
      player.playerStats.enjoyment = player.playerStats.maxEnjoyment;
      player.playerStats.stress = 0;
    }
    this.uiManager?.updateParty();
  }

  // player levelups give 1,2 or 3 points to either involvement, or enjoyment, or +5, +10, or +25 to stress buffer
  getPlayerLevelUpOption(player: Player) {
    let levelUpOption: ModalOption;

    const randomStat = Math.floor(Math.random() * 3) + 1; // 1 for enjoyment, 2 for involvement, 3 for stress
    const level = (() => {
      const rand = Math.random();
      if (rand < 0.5) return 1; // 50% chance
      if (rand < 0.9) return 2; // 40% chance
      return 3; // 10% chance
    })();
    const className =  (level === 1 ? 'common' : level === 2 ? 'rare' : 'legend');
    if (randomStat === 1) { // enjoyment
      levelUpOption = {
        id: LEVELUP_OPTION.MAX_HAPPINESS,
        title: (level === 1 ? '' : level === 2 ? 'Better ' : 'Great ') + 'Happiness',
        description: `+${level} max Enjoyment`,
        icon: '',
        level: level,
        class: className,
        callback: () => {
          player.playerStats.maxEnjoyment += level;
          this.uiManager?.updateParty();
        }
      };
    } else if(randomStat === 2) { // involvement
      levelUpOption = {
        id: LEVELUP_OPTION.MAX_ATTENTION,
        title: (level === 1 ? '' : level === 2 ? 'Better ' : 'Great ') + 'Attention',
        description: `+${level} max Attention`,
        icon: '',
        level: level,
        class: className,
        callback: () => {
          player.playerStats.maxInvolvement += level;
          this.uiManager?.updateParty();
        }
      };
    } else {
      levelUpOption = {
        id: LEVELUP_OPTION.STRESS_BUFFER,
        title: (level === 1 ? '' : level === 2 ? 'Better ' : 'Great ') + 'Stress Proof',
        description: `+${level * 5} Stress Buffer/Reduce`,
        icon: '',
        class: className,
        callback: () => {
          player.playerStats.stressBuffer += level * 5;
          player.playerStats.stress = Math.max(0, player.playerStats.stress - level * 5);
          this.uiManager?.updateParty();
        }
      };
    }

    return levelUpOption;
  }

  //This will eventully provide player traits but for now I want it to just generae a random chataracter and offer it as a level up option
  getFancyLevelUpOption(player: Player) {
    const randomCharacter = this.appManager.getCharacterManager().generateRandomCharacter(1)
    const levelUpOption: ModalOption = {
      id: LEVELUP_OPTION.CHARACTER,
      title: 'New Character',
      description: `Get ${randomCharacter.name}`,
      icon: `${randomCharacter.icon}`,
      class: 'level-up-option-character',
      callback: () => {
        player.characters.push(randomCharacter);
        this.uiManager?.updateParty();
        
      }
    };
    return levelUpOption;
  }



handleLevelUp(player: Player) {
  const levelUpOptions: ModalOption[] = []

  // Option to waive level up in favor of replenishing happiness, attention, and reducing stress. 
  levelUpOptions.push({
    id: LEVELUP_OPTION.REFRESH,
    title: 'Refresh',
    description: 'Fill Attitude, Attention. Reduce Stress.',
    icon: '',

    callback: () => this.refreshPlayer(player)
  });

  // get mid level up option
  levelUpOptions.push(this.getPlayerLevelUpOption(player));

  // get end level up option
  levelUpOptions.push(this.getFancyLevelUpOption(player));


  this.uiManager?.showModal({
    title: player.name + ' Level Up!',
    options: levelUpOptions,
    onClose: () => console.log('Modal closed'),
    type: 'levelup',
  });
  }

  /**
   * Evaluates player preferences for encounter options based on LIKE/HATE attitudes
   * Returns preference data for each encounter
   */
  evaluateEncounterPreferences(encounters: any[]): { 
    preferredEncounters: { [encounterName: string]: Player[] },
    notWantedEncounters: { [encounterName: string]: Player[] }
  } {
    const preferredEncounters: { [encounterName: string]: Player[] } = {};
    const notWantedEncounters: { [encounterName: string]: Player[] } = {};

    // Calculate like/hate indices for each encounter for each player
    const playerEncounterScores: { [playerName: string]: { [encounterName: string]: { likes: number, hates: number } } } = {};
    
    this.players.forEach(player => {
      playerEncounterScores[player.name] = {};
      
      encounters.forEach(encounter => {
        let likes = 0;
        let hates = 0;
        
        // Check encounter tags
        encounter.tags?.forEach((tag: any) => {
          const preference = player.preferences.find(pref => pref[0].name === tag.name);
          if (preference) {
            if (preference[1] === ATTITUDE.LIKE) likes++;
            if (preference[1] === ATTITUDE.HATE) hates++;
          }
        });
        
        // Check location tags
        encounter.location?.tags?.forEach((tag: any) => {
          const preference = player.preferences.find(pref => pref[0].name === tag.name);
          if (preference) {
            if (preference[1] === ATTITUDE.LIKE) likes++;
            if (preference[1] === ATTITUDE.HATE) hates++;
          }
        });
        
        playerEncounterScores[player.name][encounter.name] = { likes, hates };
      });
    });

    // Determine preferred encounters for each player
    this.players.forEach(player => {
      const scores = playerEncounterScores[player.name];
      let maxLikeScore = 0;
      let preferredEncounterNames: string[] = [];
      
      // Find encounters with highest like score
      Object.keys(scores).forEach(encounterName => {
        const score = scores[encounterName];
        const netScore = score.likes - score.hates; // Conflicting tags counteract each other
        
        if (score.likes > 0 && netScore > maxLikeScore) {
          maxLikeScore = netScore;
          preferredEncounterNames = [encounterName];
        } else if (score.likes > 0 && netScore === maxLikeScore) {
          preferredEncounterNames.push(encounterName);
        }
      });
      
      // Randomly select one if tied
      if (preferredEncounterNames.length > 0) {
        const selectedEncounter = preferredEncounterNames[Math.floor(Math.random() * preferredEncounterNames.length)];
        if (!preferredEncounters[selectedEncounter]) {
          preferredEncounters[selectedEncounter] = [];
        }
        preferredEncounters[selectedEncounter].push(player);
      }
      
      // Determine NOT-WANTED encounters
      Object.keys(scores).forEach(encounterName => {
        const score = scores[encounterName];
        const netScore = score.likes - score.hates;
        
        // Hated encounters (net negative score)
        if (netScore < 0) {
          if (!notWantedEncounters[encounterName]) {
            notWantedEncounters[encounterName] = [];
          }
          notWantedEncounters[encounterName].push(player);
        }
        // Non-preferred encounters have 25% chance of being NOT-WANTED (only if player has preferences)
        else if (!preferredEncounterNames.includes(encounterName) && 
                 player.preferences.length > 0 && 
                 Math.random() < 0.25) {
          if (!notWantedEncounters[encounterName]) {
            notWantedEncounters[encounterName] = [];
          }
          notWantedEncounters[encounterName].push(player);
        }
      });
    });

    return { preferredEncounters, notWantedEncounters };
  }

  /**
   * Returns all players for railroading penalty when only one option is available
   */
  playersAffectedByRailroading(): Player[] {
    return [...this.players];
  }

  /**
   * Apply encounter selection effects based on player preferences
   */
  applyEncounterSelectionEffects(selectedEncounter: any, encounters: any[]): void {
    // Delegate to PartyManager for stat updates
    this.appManager.getPartyManager().applyEncounterSelectionEffects(selectedEncounter, encounters);
  }

  /**
   * Calculate penalties for incomplete exploration
   */
  calculateIncompleteExplorationPenalties(tagManager: any): {
    locationTagPenalty: number;
    encounterTagPenalty: number;
    totalStressPenalty: number;
    totalAttentionPenalty: number;
  } {
    const outstandingLocationTags = tagManager.getOutstandingLocationTags();
    const outstandingEncounterTags = tagManager.getOutstandingEncounterTags();
    
    const locationTagPenalty = outstandingLocationTags.length * 5;
    const encounterTagPenalty = outstandingEncounterTags.length;
    
    // Stress penalty for any outstanding location tags
    const totalStressPenalty = locationTagPenalty;
    // -1 attention penalty for each outstanding encounter tag
    const totalAttentionPenalty = encounterTagPenalty;
    
    return {
      locationTagPenalty,
      encounterTagPenalty,
      totalStressPenalty,
      totalAttentionPenalty
    };
  }

  /**
   * Apply penalties for incomplete exploration when advancing encounters early
   */
  applyIncompleteExplorationPenalties(tagManager: any): void {
    // Delegate to PartyManager for stat updates
    this.appManager.getPartyManager().applyIncompleteExplorationPenalties(tagManager);
  }

  /**
   * Handle encounter selection - apply all effects and advance the encounter
   */
  handleNextEncounterSelection(selectedBranch: any, allBranches: any[], tagManager: any): void {
    // Delegate to PartyManager for encounter selection handling
    this.appManager.getPartyManager().handleNextEncounterSelection(selectedBranch, allBranches, tagManager);
  }

}

export default PlayerManager;



