import React, { useEffect, useState } from 'react';
import './characterCreation.scss';
import PlayerCard from './playerCard'; // Adjust the import path as needed
import CharacterCard from './characterCard'; // Adjust the import path as needed
import { useAppManager, useCharacterManager, usePlayerManager } from './managerContexts';
import { Character, NAV_ENTRY, Player } from '../models';
import { APP_STATE } from '../consts/states';
import NavBar from './UI/navBar';

interface CharacterCreationProps { 

}

const CharacterCreation: React.FC<CharacterCreationProps> = ({}) => {

  const playerManager = usePlayerManager()
  const characterManager = useCharacterManager();
  const appManager = useAppManager();

  const [selectedPlayer, setSelectedPlayer] = useState<Player | null>(null);
  const [players, setPlayers] = useState<Player[]>(playerManager.getPlayers());
  const [characters, setCharacters] = useState<Character[]>(characterManager.getCharacterReferences());
 const [update, setUpdate] = useState(false);

  const assignOrUnassignCharacter = (character: Character) => {
    if(isAssignedToOtherPlayer(character)) {
      return;
    }

    if (selectedPlayer) {
      if (selectedPlayer.characters.includes(character)) {
        // Unassign character
        playerManager.removeCharacterFromPlayer(selectedPlayer, character);
      } else if (selectedPlayer.characters.length < 4) {
        // Assign character
        playerManager.assignCharactersToPlayer(selectedPlayer, [character]);
      }
      setPlayers([...playerManager.getPlayers()]); // Update state to reflect changes
    }
  };


  // Select first player on mount: 
  useEffect(() => {
    if (!selectedPlayer && players.length) {
      setSelectedPlayer(players[0]);
    }
  }, [players]);


  const handlePlayerSelect = (player: Player) => {
    setSelectedPlayer(player);
  };


  const forceRender = () => {
    setUpdate(!update);
  }
  const isAssignedToOtherPlayer = (character: Character) => {
    return players.some(player => player.id !== selectedPlayer?.id && player.characters.includes(character));
  };

return (
    <div className="character-creation-container">
      <div className="character-creation-left-container">
      <NavBar buttonList={[NAV_ENTRY.PLAYER_RECRUITMENT, NAV_ENTRY.CAMPAIGN] } highlight={NAV_ENTRY.CAMPAIGN} />
      <div className="player-list-container">
     

        <div className="player-list">
          {players.map((player) => (
            <PlayerCard 
              key={player.id}
              player={player}
              isSelected={selectedPlayer?.id === player.id}
              onSelect={() => handlePlayerSelect(player)}
              showSchedule={false}
              showCharacters={true}
              parentUpdate={forceRender}
            >
            </PlayerCard>
          ))}
        </div>
      </div>
    </div>
      <div className="character-creation-right-container">
        <div className="character-grid">
          {characters.map((character) => (
            <CharacterCard
              key={character.id}
              character={character}
              isSelected={selectedPlayer?.characters.includes(character) ?? false}
              onSelect={() => assignOrUnassignCharacter(character)}
              showAbilities={true}
              isAssigned={selectedPlayer?.characters.includes(character)}
              isAssignedToOther={isAssignedToOtherPlayer(character)}

            />
          ))}
        </div>
      </div>
    </div>
  );
};

export default CharacterCreation;
