import React from 'react';
import { Encounter } from '../../models';
import EnhancedSkillChallenge from './enhancedSkillChallenge';
import EncounterHeader from './encounterHeader';
import { useCampaignManager } from '../managerContexts';

interface GameEncounterSkillChallengeProps {
  encounter: Encounter;
}

const GameEncounterSkillChallenge: React.FC<GameEncounterSkillChallengeProps> = ({ encounter }) => {
  const campaignManager = useCampaignManager();

  // Extract challenge configuration from encounter
  const challengeConfig = encounter.challenge?.[0];
  const challengeLength = 10; // Enhanced default length
  
  const handleChallengeComplete = () => {
    // Move to next phase or back to overview
    campaignManager.backToOverview();
  };

  return (
    <div className="game-encounter-skill-challenge">
      <EncounterHeader
        title={challengeConfig?.name || "Enhanced Skill Challenge"}
        description={challengeConfig?.description || encounter.location.name || "Select characters and challenge rows to overcome themed obstacles and collect skill points."}
        instructions={[true, false]} // Instructions are enabled/disabled flags
        childClass="skill-challenge-header"
      />
      
      <EnhancedSkillChallenge
        challengeLength={challengeLength}
        authoredTag={challengeConfig?.revealTag}
        authoredAssistTags={challengeConfig?.assistTags}
        skillThresholds={challengeConfig?.skillThresholds}
        onComplete={handleChallengeComplete}
        config={challengeConfig}
      />
    </div>
  );
};

export default GameEncounterSkillChallenge;
