import React from 'react';
import './campaign-display-body.css';
import EncounterDisplay from './encounter-display';
import { Encounter } from '../models';

interface CampaignDisplayBodyProps {
    processedEncounters: Encounter[][];
}

const CampaignDisplayBody: React.FC<CampaignDisplayBodyProps> = ({ processedEncounters }) => {
    
    if(!processedEncounters){
        return <div>No encounters!</div>
    }
    return (
        <div className="campaign-display-body">
            {processedEncounters.map((encounterRow, rowIndex) => (
                <div key={rowIndex} className="encounter-row">
                    {encounterRow.map((encounter, encounterIndex) => (
                       <EncounterDisplay 
                        key={encounterIndex}
                        encounter={encounter} />
                    ))}
                </div>
            ))}
        </div>
    );
};

export default CampaignDisplayBody;


