import React, { useState } from 'react';
import { Quest, QuestNode, ChatMessage, Player, ATTITUDE } from '../../models';
import './QuestTerminalModal.scss';

interface PlayerPreference {
  playerId: number;
  optionIndex: number;
  attitude: ATTITUDE;
}

interface QuestTerminalModalProps {
  quest: Quest;
  node: QuestNode;
  players: Player[];
  onComplete: () => void;
  onClose: () => void;
}

const QuestTerminalModal: React.FC<QuestTerminalModalProps> = ({
  quest,
  node,
  players,
  onComplete,
  onClose
}) => {
  const [selectedOption, setSelectedOption] = useState<number | null>(null);
  const [currentConversation, setCurrentConversation] = useState<ChatMessage[]>(node.conversation);
  
  // Generate random preferences for each player and each option
  const [playerPreferences] = useState<PlayerPreference[]>(() => {
    const preferences: PlayerPreference[] = [];
    const attitudes = [ATTITUDE.LIKE, ATTITUDE.HATE, ATTITUDE.NEUTRAL];
    
    if (quest.playerChoices) {
      for (let i = 0; i < quest.playerChoices.length; i++) {
        for (const player of players) {
          const randomAttitude = attitudes[Math.floor(Math.random() * 3)];
          preferences.push({
            playerId: player.id,
            optionIndex: i,
            attitude: randomAttitude
          });
        }
      }
    }
    
    return preferences;
  });

  const getPlayerPreferencesForOption = (optionIndex: number) => {
    return playerPreferences.filter(pref => pref.optionIndex === optionIndex);
  };

  const getAttitudeColor = (attitude: ATTITUDE) => {
    switch (attitude) {
      case ATTITUDE.LIKE: return '#4CAF50'; // Green
      case ATTITUDE.HATE: return '#F44336'; // Red
      case ATTITUDE.NEUTRAL: return '#999999'; // Grey
      default: return '#999999';
    }
  };

  const getPlayerIcon = (playerId: number) => {
    const player = players.find(p => p.id === playerId);
    return player?.icon || '';
  };

  const handleOptionClick = (optionIndex: number) => {
    setSelectedOption(optionIndex);
    if (quest.playerChoices && quest.playerChoices[optionIndex]) {
      setCurrentConversation(quest.playerChoices[optionIndex].conversation);
    }
  };

  const renderConversation = (conversation: ChatMessage[]) => {
    return conversation.map((message, index) => (
      <div key={index} className="conversation-message">
        <div className="speaker">
          {message.speaker.type === 'dm' ? 'DM' : 
           message.speaker.type === 'player' ? `Player ${message.speaker.id}` :
           message.speaker.type === 'character' ? `Character ${message.speaker.id}` : 'Unknown'}
        </div>
        <div className="message">{message.message}</div>
      </div>
    ));
  };

  return (
    <div className="quest-terminal-modal-overlay">
      <div className="quest-terminal-modal">
        <div className="modal-header">
          <h2>{quest.name} - Terminal</h2>
          <button className="close-button" onClick={onClose}></button>
        </div>
        
        <div className="modal-content">
          <div className="conversation-panel">
            <h3>Conversation</h3>
            <div className="conversation-content">
              {renderConversation(currentConversation)}
            </div>
          </div>
          
          <div className="options-panel">
            <h3>Player Choices</h3>
            {quest.playerChoices && quest.playerChoices.length > 0 ? (
              <div className="player-options">
                {quest.playerChoices.map((choice, index) => (
                  <div 
                    key={index}
                    className={`option-tile ${selectedOption === index ? 'selected' : ''}`}
                    onClick={() => handleOptionClick(index)}
                  >
                    <div className="option-content">
                      <h4>{choice.name}</h4>
                      <p>{choice.description}</p>
                    </div>
                    
                    <div className="player-preferences">
                      {getPlayerPreferencesForOption(index).map((pref, prefIndex) => (
                        <div 
                          key={prefIndex}
                          className="player-preference"
                          style={{ borderColor: getAttitudeColor(pref.attitude) }}
                          title={`${getPlayerIcon(pref.playerId)} - ${pref.attitude}`}
                        >
                          {getPlayerIcon(pref.playerId)}
                        </div>
                      ))}
                    </div>
                  </div>
                ))}
                
                {selectedOption !== null && (
                  <div className="accept-rewards">
                    <h4>Rewards:</h4>
                    <ul>
                      {quest.rewards.map((reward, index) => (
                        <li key={index}>
                          {reward.name || reward.type}: {reward.amount}
                          {reward.description && <span> - {reward.description}</span>}
                        </li>
                      ))}
                    </ul>
                    <button className="accept-button" onClick={onComplete}>
                      Accept Rewards
                    </button>
                  </div>
                )}
              </div>
            ) : (
              <div className="no-choices">
                <p>No player choices available for this quest.</p>
                <button className="accept-button" onClick={onComplete}>
                  Complete
                </button>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default QuestTerminalModal;
