import React, { useState, useEffect } from "react";
import { Encounter, Player, Character, EncounterFlavorTag, FlavorTagSource, TravelResultConfig, EffectData, ATTRIBUTE_TYPE, Tag } from '../../models';
import { useAppManager, useCampaignManager, usePlayerManager } from "../managerContexts";
import EncounterHeader from "./encounterHeader";
import { AttributeDisplay } from "../UI/attributeDisplay";
import Cimage from "../UI/cimage";
import "./gameRoom.scss";
import "./gameEncounterDungeonCrawl.scss";

interface GameEncounterDungeonCrawlProps {
  encounter: Encounter;
}

interface DungeonRoom {
  id: string;
  name: string;
  flavorText: string;
  tag?: Tag;
  combat: number;
  magic: number;
  tools: number;
  isCompleted: boolean;
}

interface RoomOption {
  id: string;
  type: 'party' | 'character';
  characters: Character[];
  players: Player[];
  combat: number;
  magic: number;
  tools: number;
  dmReward: number;
}

interface DungeonTotals {
  combat: number;
  magic: number;
  tools: number;
}

const GameEncounterDungeonCrawl: React.FC<GameEncounterDungeonCrawlProps> = ({ encounter }) => {
  // Configuration
  const [totalRooms] = useState(5);
  const [barMaxHeight] = useState(20);
  
  // State management
  const [currentRoomIndex, setCurrentRoomIndex] = useState(0);
  const [dungeonRooms, setDungeonRooms] = useState<DungeonRoom[]>([]);
  const [currentRoomOptions, setCurrentRoomOptions] = useState<RoomOption[]>([]);
  const [selectedOption, setSelectedOption] = useState<RoomOption | null>(null);
  const [dungeonTotals, setDungeonTotals] = useState<DungeonTotals>({ combat: 0, magic: 0, tools: 0 });
  const [hoveredTotals, setHoveredTotals] = useState<DungeonTotals | null>(null);
  const [nextRoomOptions, setNextRoomOptions] = useState<DungeonRoom[]>([]);
  const [showResults, setShowResults] = useState(false);
  const [resultConfig, setResultConfig] = useState<TravelResultConfig | null>(null);
  const [tagActivated, setTagActivated] = useState(false);

  const appManager = useAppManager();
  const campaignManager = useCampaignManager();
  const playerManager = usePlayerManager();
  const characterManager = appManager.getCharacterManager();
  const tagManager = appManager.getTagManager();
  const partyManager = appManager.getPartyManager();

  const players = playerManager.getPlayers();
  const allCharacters = players.flatMap(player => 
    player.characters.map(char => ({ character: char, player }))
  );

  // Initialize dungeon
  useEffect(() => {
    initializeDungeon();
  }, []);

  const initializeDungeon = () => {
    const startingRoom: DungeonRoom = {
      id: 'start',
      name: 'Dungeon Entrance',
      flavorText: 'You stand before the dark entrance to the ancient dungeon. The air is thick with mystery and danger.',
      combat: 0,
      magic: 0,
      tools: 0,
      isCompleted: false
    };
    
    setDungeonRooms([startingRoom]);
    setCurrentRoomIndex(0);
  };

  const generateRoomOptions = () => {
    const options: RoomOption[] = [];
    
    // Always include party option
    const partyOption: RoomOption = {
      id: 'party',
      type: 'party',
      characters: allCharacters.map(c => c.character),
      players: players,
      combat: calculatePartyCombinedAttribute(ATTRIBUTE_TYPE.COMBAT),
      magic: calculatePartyCombinedAttribute(ATTRIBUTE_TYPE.MAGIC),
      tools: calculatePartyCombinedAttribute(ATTRIBUTE_TYPE.TOOLS),
      dmReward: Math.floor(Math.random() * 3) + 1
    };
    options.push(partyOption);

    // Generate 3 random character options
    for (let i = 0; i < 3; i++) {
      const numCharacters = Math.floor(Math.random() * 3) + 1; // 1-3 characters
      const selectedCharacters: { character: Character, player: Player }[] = [];
      
      for (let j = 0; j < numCharacters && j < allCharacters.length; j++) {
        const randomChar = allCharacters[Math.floor(Math.random() * allCharacters.length)];
        if (!selectedCharacters.some(sc => sc.character.id === randomChar.character.id)) {
          selectedCharacters.push(randomChar);
        }
      }

      const option: RoomOption = {
        id: `option_${i}`,
        type: 'character',
        characters: selectedCharacters.map(sc => sc.character),
        players: selectedCharacters.map(sc => sc.player),
        combat: selectedCharacters.reduce((sum, sc) => sum + (characterManager.getRemainingAttributes(sc.character, ATTRIBUTE_TYPE.COMBAT) || 0), 0),
        magic: selectedCharacters.reduce((sum, sc) => sum + (characterManager.getRemainingAttributes(sc.character, ATTRIBUTE_TYPE.MAGIC) || 0), 0),
        tools: selectedCharacters.reduce((sum, sc) => sum + (characterManager.getRemainingAttributes(sc.character, ATTRIBUTE_TYPE.TOOLS) || 0), 0),
        dmReward: Math.floor(Math.random() * 3) + 1
      };
      options.push(option);
    }

    setCurrentRoomOptions(options);
  };

  const calculatePartyCombinedAttribute = (attributeType: ATTRIBUTE_TYPE): number => {
    return allCharacters.reduce((sum, { character }) => 
      sum + (characterManager.getRemainingAttributes(character, attributeType) || 0), 0
    );
  };

  const generateNextRoomOptions = () => {
    const roomOptions: DungeonRoom[] = [];
    
    for (let i = 0; i < 3; i++) {
      const room = generateRandomRoom();
      roomOptions.push(room);
    }
    
    setNextRoomOptions(roomOptions);
  };

  const generateRandomRoom = (): DungeonRoom => {
    // Placeholder room data - this would come from a data file
    const roomTemplates = [
      { name: "Ancient Library", flavorText: "Dusty tomes and forgotten knowledge fill this chamber." },
      { name: "Crystal Cavern", flavorText: "Glowing crystals illuminate the rocky walls." },
      { name: "Trapped Corridor", flavorText: "Pressure plates and dart holes line this narrow passage." },
      { name: "Throne Room", flavorText: "A decaying throne sits atop a dais of cracked marble." },
      { name: "Alchemist's Lab", flavorText: "Bubbling cauldrons and strange apparatus fill the room." },
      { name: "Guardian Chamber", flavorText: "Ancient statues watch over this circular room." },
      { name: "Treasure Vault", flavorText: "Golden glints catch the light in this secured chamber." },
      { name: "Ritual Circle", flavorText: "Arcane symbols are carved into the stone floor." }
    ];

    const template = roomTemplates[Math.floor(Math.random() * roomTemplates.length)];
    const useGeneratedTag = Math.random() < 0.7;
    
    return {
      id: `room_${Date.now()}_${Math.random()}`,
      name: template.name,
      flavorText: template.flavorText,
      tag: useGeneratedTag ? tagManager.generateRelevantFlavorTags(1)[0]?.tag : undefined,
      combat: Math.floor(Math.random() * 3), // 0-2
      magic: Math.floor(Math.random() * 3),  // 0-2
      tools: Math.floor(Math.random() * 3),  // 0-2
      isCompleted: false
    };
  };

  const handleStartingRoomOk = () => {
    const updatedRooms = [...dungeonRooms];
    updatedRooms[0].isCompleted = true;
    setDungeonRooms(updatedRooms);
    generateNextRoomOptions();
  };

  const handleOptionSelect = (option: RoomOption) => {
    setSelectedOption(option);
  };

  const handleTagActivate = () => {
    if (!selectedOption) return;
    
    const currentRoom = dungeonRooms[currentRoomIndex];
    if (!currentRoom) return;

    setTagActivated(true);

    // Update dungeon totals
    const newTotals = {
      combat: dungeonTotals.combat + selectedOption.combat,
      magic: dungeonTotals.magic + selectedOption.magic,
      tools: dungeonTotals.tools + selectedOption.tools
    };
    setDungeonTotals(newTotals);

    // Check if this is an encounter tag
    const isEncounterTag = encounter.location.tags.some(tag => 
      currentRoom.tag && tag.name === currentRoom.tag.name
    );

    if (isEncounterTag && currentRoom.tag) {
      // Trigger encounter modal (would integrate with existing encounter system)
      showResultsModal("Encounter Triggered", `You have triggered an encounter: ${currentRoom.tag.name}`, []);
    } else {
      // Calculate results
      const results: EffectData[] = [];
      
      // Add exhaustion to party
      //partyManager.addPartyExhaustion(5);
      results.push({
        object: 'party',
        effects: [{
          effect: "+5 Exhaustion",
          class: "party-exhaustion"
        }]
      });

      // Process player effects
      const involvedPlayerIds = selectedOption.players.map(p => p.id);
      
      players.forEach(player => {
        if (involvedPlayerIds.includes(player.id)) {
          // Give experience to involved players
          playerManager.addPlayerExp(player, 1);
          results.push({
            object: player,
            effects: [{
              effect: "+1 Experience",
              class: "player-exp"
            }]
          });
        } else {
          // Remove attention from uninvolved players
          partyManager.updateAttention(player, -1);
          results.push({
            object: player,
            effects: [{
              effect: "-1 Attention",
              class: "player-attention"
            }]
          });
        }
      });

      // Add DM reward if any
      if (selectedOption.dmReward > 0) {
        campaignManager.addDmExp(selectedOption.dmReward);
        results.push({
          object: 'party',
          effects: [{
            effect: `+${selectedOption.dmReward} DM Points`,
            class: "dm-points"
          }]
        });
      }

      // Expose tag if it exists
      if (currentRoom.tag) {
        const flavorTag: EncounterFlavorTag = {
          tag: currentRoom.tag,
          source: FlavorTagSource.LOCATION,
          theme: tagManager.getTheme()
        };
        campaignManager.exposeTag(flavorTag);
      }

      showResultsModal("Room Completed", `You have successfully navigated ${currentRoom.name}!`, results);
    }
  };

  const handleRoomComplete = () => {
    const updatedRooms = [...dungeonRooms];
    updatedRooms[currentRoomIndex].isCompleted = true;
    setDungeonRooms(updatedRooms);
    
    if (currentRoomIndex >= totalRooms - 1) {
      // Dungeon complete
      showDungeonCompleteModal();
    } else {
      // Show next room options
      generateNextRoomOptions();
    }
  };

  const handleNextRoomSelect = (room: DungeonRoom) => {
    const updatedRooms = [...dungeonRooms, room];
    setDungeonRooms(updatedRooms);
    setCurrentRoomIndex(currentRoomIndex + 1);
    setSelectedOption(null);
    setTagActivated(false);
    setNextRoomOptions([]);
    generateRoomOptions();
  };

  const showDungeonCompleteModal = () => {
    const results: EffectData[] = [{
      object: 'party',
      effects: [{
        effect: "Dungeon Completed!",
        class: "dungeon-complete"
      }]
    }];
    
    showResultsModal("Dungeon Complete", "You have successfully navigated the entire dungeon!", results);
  };

  const showResultsModal = (title: string, description: string, results: EffectData[]) => {
    const mockTravelOption = {
      id: "dungeon_result",
      name: title,
      description: description,
      time: 0,
      tags: 0,
      score: 0,
      boredom: 0,
      exhaustion: 0,
      tagInstructions: "",
      repeatable: false
    };

    const resultConfig: TravelResultConfig = {
      results: results,
      title: title,
      description: description,
      tags: [],
      travelOption: mockTravelOption,
      info: {
        score: 0,
        time: 0,
        boredom: 0,
        exhaustion: 0
      }
    };

    setResultConfig(resultConfig);
    setShowResults(true);
  };

  const handleResultsClose = () => {
    setShowResults(false);
    if (currentRoomIndex >= totalRooms - 1) {
      // Exit to encounter overview
      campaignManager.backToOverview();
    } else {
      handleRoomComplete();
    }
  };

  const renderDungeonTotalsUI = () => {
    const totalsToShow = hoveredTotals || dungeonTotals;
    
    return (
      <div className="dungeon-totals-ui">
        <div className="totals-bar combat">
          <div className="bar-fill" style={{ height: `${(totalsToShow.combat / barMaxHeight) * 100}%` }}>
            <div className="dm-reward" style={{ bottom: '50%' }}>6</div>
          </div>
          {hoveredTotals && (
            <div 
              className="bar-preview" 
              style={{ 
                height: `${((hoveredTotals.combat - dungeonTotals.combat) / barMaxHeight) * 100}%`,
                bottom: `${(dungeonTotals.combat / barMaxHeight) * 100}%`
              }}
            />
          )}
          <div className="bar-icon"></div>
        </div>
        <div className="totals-bar magic">
          <div className="bar-fill" style={{ height: `${(totalsToShow.magic / barMaxHeight) * 100}%` }}>
            <div className="dm-reward" style={{ bottom: '50%' }}>6</div>
          </div>
          {hoveredTotals && (
            <div 
              className="bar-preview" 
              style={{ 
                height: `${((hoveredTotals.magic - dungeonTotals.magic) / barMaxHeight) * 100}%`,
                bottom: `${(dungeonTotals.magic / barMaxHeight) * 100}%`
              }}
            />
          )}
          <div className="bar-icon"></div>
        </div>
        <div className="totals-bar tools">
          <div className="bar-fill" style={{ height: `${(totalsToShow.tools / barMaxHeight) * 100}%` }}>
            <div className="dm-reward" style={{ bottom: '50%' }}>6</div>
          </div>
          {hoveredTotals && (
            <div 
              className="bar-preview" 
              style={{ 
                height: `${((hoveredTotals.tools - dungeonTotals.tools) / barMaxHeight) * 100}%`,
                bottom: `${(dungeonTotals.tools / barMaxHeight) * 100}%`
              }}
            />
          )}
          <div className="bar-icon"></div>
        </div>
      </div>
    );
  };

  const renderCurrentRoom = () => {
    const currentRoom = dungeonRooms[currentRoomIndex];
    if (!currentRoom) return null;

    if (currentRoom.id === 'start' && !currentRoom.isCompleted) {
      return (
        <div className="dungeon-room starting-room">
          <div className="room-header">
            <h3>{currentRoom.name}</h3>
          </div>
          <div className="room-content">
            <p>{currentRoom.flavorText}</p>
            <button className="start-dungeon-button" onClick={handleStartingRoomOk}>
              Enter Dungeon
            </button>
          </div>
        </div>
      );
    }

    if (currentRoom.isCompleted) {
      return (
        <div className="dungeon-room completed">
          <div className="completed-room-summary">
            <span className="room-name">{currentRoom.name}</span>
            {currentRoom.tag && <span className="room-tag">{currentRoom.tag.name}</span>}
            <div className="room-attributes">
              <span>{currentRoom.combat}</span>
              <span>{currentRoom.magic}</span>
              <span>{currentRoom.tools}</span>
            </div>
          </div>
        </div>
      );
    }

    return (
      <div className="dungeon-room active">
        <div className="room-header">
          <h3>{currentRoom.name}</h3>
          <p>{currentRoom.flavorText}</p>
        </div>
        
        <div className="room-main">
          <div className="room-requirements">
            <div className="requirement-item">
              <span className="icon"></span>
              <span className="value">{selectedOption?.combat || currentRoom.combat}</span>
            </div>
            <div className="requirement-item">
              <span className="icon"></span>
              <span className="value">{selectedOption?.magic || currentRoom.magic}</span>
            </div>
            <div className="requirement-item">
              <span className="icon"></span>
              <span className="value">{selectedOption?.tools || currentRoom.tools}</span>
            </div>
          </div>

          <div className="room-tag-area">
            {currentRoom.tag && (
              <div 
                className={`room-tag ${tagActivated ? 'activated' : ''} ${selectedOption ? 'highlighted' : ''}`}
                onClick={selectedOption ? handleTagActivate : undefined}
              >
                {currentRoom.tag.name}
              </div>
            )}
          </div>

          <div className="room-options">
            {currentRoomOptions.map(option => (
              <div 
                key={option.id}
                className={`room-option ${selectedOption?.id === option.id ? 'selected' : ''}`}
                onClick={() => handleOptionSelect(option)}
              >
                <div className="option-attributes">
                  <span>{option.combat}</span>
                  <span>{option.magic}</span>
                  <span>{option.tools}</span>
                </div>
                <div className="option-characters">
                  {option.type === 'party' ? (
                    <span>Party</span>
                  ) : (
                    <div className="character-icons">
                      {option.characters.map((character, index) => (
                        <div key={character.id} className="character-icon" style={{ zIndex: option.characters.length - index }}>
                          <Cimage imageId={character.icon} />
                        </div>
                      ))}
                    </div>
                  )}
                </div>
                {option.dmReward > 0 && (
                  <div className="dm-reward-indicator">+{option.dmReward} DM</div>
                )}
              </div>
            ))}
          </div>
        </div>
      </div>
    );
  };

  const renderNextRoomOptions = () => {
    if (nextRoomOptions.length === 0) return null;

    return (
      <div className="next-room-options">
        <h4>Choose Next Room:</h4>
        <div className="room-options-grid">
          {nextRoomOptions.map(room => (
            <div 
              key={room.id}
              className="next-room-option"
              onClick={() => handleNextRoomSelect(room)}
              onMouseEnter={() => setHoveredTotals({
                combat: dungeonTotals.combat + room.combat,
                magic: dungeonTotals.magic + room.magic,
                tools: dungeonTotals.tools + room.tools
              })}
              onMouseLeave={() => setHoveredTotals(null)}
            >
              <h5>{room.name}</h5>
              <p>{room.flavorText}</p>
              <AttributeDisplay 
                attributes={{
                  [ATTRIBUTE_TYPE.COMBAT]: room.combat,
                  [ATTRIBUTE_TYPE.MAGIC]: room.magic,
                  [ATTRIBUTE_TYPE.TOOLS]: room.tools
                }}
                collapsed={true}
              />
            </div>
          ))}
        </div>
      </div>
    );
  };

  const renderCompletedRooms = () => {
    return dungeonRooms.filter((room, index) => index < currentRoomIndex && room.isCompleted).map((room, index) => (
      <div key={room.id} className="dungeon-room completed">
        <div className="completed-room-summary">
          <span className="room-name">{room.name}</span>
          {room.tag && <span className="room-tag">{room.tag.name}</span>}
          <div className="room-attributes">
            <span>{room.combat}</span>
            <span>{room.magic}</span>
            <span>{room.tools}</span>
          </div>
        </div>
      </div>
    ));
  };

  // Generate room options when room changes
  useEffect(() => {
    if (currentRoomIndex > 0 && !dungeonRooms[currentRoomIndex]?.isCompleted) {
      generateRoomOptions();
    }
  }, [currentRoomIndex]);

  return (
    <div className="game-encounter-dungeon-crawl">
      <EncounterHeader
        title={`Dungeon Crawl - ${encounter.location.name}`}
        description={`Navigate through ${totalRooms} rooms of this ancient dungeon!`}
        childClass="small-font"
      />
      
      <div className="dungeon-content">
        <div className="dungeon-main">
          <div className="dungeon-rooms">
            {renderCompletedRooms()}
            {renderCurrentRoom()}
            {renderNextRoomOptions()}
          </div>
        </div>
        
        <div className="dungeon-sidebar">
          {renderDungeonTotalsUI()}
          
          <div className="dungeon-progress">
            <h4>Progress</h4>
            <div className="progress-indicator">
              Room {currentRoomIndex + 1} of {totalRooms + 1}
            </div>
          </div>
        </div>
      </div>

      {showResults && resultConfig && (
        <div className="modal-overlay">
          <div className="modal-container">
            <div className="modal-header">
              {resultConfig.title}
              <button className="close-button" onClick={handleResultsClose}></button>
            </div>
            <div className="modal-description">{resultConfig.description}</div>
            <div className="dungeon-results">
              {resultConfig.results.map((result, index) => (
                <div key={index} className="effect-display">
                  <div className="effect-target">
                    {typeof result.object === 'string' ? result.object : result.object.name}
                  </div>
                  <div className="effect-list">
                    {result.effects?.map((effect, effectIndex) => (
                      <div key={effectIndex} className={`effect ${effect.class}`}>
                        {effect.effect}
                      </div>
                    ))}
                  </div>
                </div>
              ))}
            </div>
            <div className="modal-actions">
              <button onClick={handleResultsClose}>
                {currentRoomIndex >= totalRooms - 1 ? 'Exit Dungeon' : 'Continue'}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default GameEncounterDungeonCrawl;
