import React from 'react';
import { Player } from '../../models';
import CharacterLevelRow from './CharacterLevelRow';
import CImage from './cimage';
import './PlayerCharacterLevelRow.scss';

interface PlayerCharacterLevelRowProps {
  player: Player;
  selectedCharacters: Set<number>;
  onCharacterToggle: (characterId: number) => void;
}

const PlayerCharacterLevelRow: React.FC<PlayerCharacterLevelRowProps> = ({
  player,
  selectedCharacters,
  onCharacterToggle
}) => {
  
  const hasSelectedCharacter = player.characters.some(char => 
    selectedCharacters.has(char.id)
  );

  return (
    <div className={`player-character-level-row ${hasSelectedCharacter ? 'highlighted' : ''}`}>
      <div className="player-info">
        <div className="player-icon">
          <CImage imageId={player.icon} />
        </div>
        <div className="player-name">{player.name}</div>
      </div>
      
      <div className="characters-list">
        {player.characters.map((character) => (
          <CharacterLevelRow
            key={character.id}
            character={character}
            isSelected={selectedCharacters.has(character.id)}
            onToggle={() => onCharacterToggle(character.id)}
          />
        ))}
      </div>
    </div>
  );
};

export default PlayerCharacterLevelRow;
