import React, { useState, useRef, useEffect, ReactNode } from "react";
import "./TooltipWrapper.scss";

export interface TooltipWrapperProps {
  content: string | ReactNode;
  position?: "top" | "bottom" | "left" | "right";
  delay?: number;
  className?: string;
  maxWidth?: number | string;
  visible?: boolean | (() => boolean);
  trigger?: "hover" | "click" | "both";
  preservePositioning?: boolean; // Add this prop for absolute positioned elements
  showHelpIcon?: boolean; // Option to show/hide the help icon
  children: ReactNode;
  translate?: [number, number]; // Optional translation for the tooltip
}

const TooltipWrapper: React.FC<TooltipWrapperProps> = ({
  content,
  position = "top",
  delay = 200,
  className = "",
  maxWidth = 250,
  visible,
  trigger = "hover",
  preservePositioning = false,
  showHelpIcon = true,
  children,
  translate = [0, 0]
}) => {
  const [isVisible, setIsVisible] = useState(false);
  const [coordinates, setCoordinates] = useState({ x: 0, y: 0 });
  const tooltipRef = useRef<HTMLDivElement>(null);
  const containerRef = useRef<HTMLDivElement>(null);
  const timeoutRef = useRef<NodeJS.Timeout | null>(null);

  // Compute visibility state
  const shouldShow = visible !== undefined 
    ? (typeof visible === "function" ? visible() : visible) 
    : isVisible;  // Position the tooltip
  useEffect(() => {
    if (shouldShow && containerRef.current && tooltipRef.current) {
      // Check if the tooltip container is actually visible
      const containerStyle = window.getComputedStyle(containerRef.current);
      if (containerStyle.display === 'none' || containerStyle.visibility === 'hidden') {
        return; // Don't position the tooltip if the container is not visible
      }

      const containerRect = containerRef.current.getBoundingClientRect();
      const tooltipRect = tooltipRef.current.getBoundingClientRect();
      
      let x = 0;
      let y = 0;

      // Handle positioning differently if we're in preserve-positioning mode
      // This helps with absolutely positioned elements
      if (preservePositioning) {
        // For preservePositioning, we need to be more careful about the tooltip placement
        // and avoid overlapping with other elements
        switch (position) {
          case "top":
            x = window.scrollX + containerRect.left + (containerRect.width / 2) - (tooltipRect.width / 2);
            y = window.scrollY + containerRect.top - tooltipRect.height - 12;
            break;
          case "bottom":
            x = window.scrollX + containerRect.left + (containerRect.width / 2) - (tooltipRect.width / 2);
            y = window.scrollY + containerRect.bottom + 12;
            break;
          case "left":
            x = window.scrollX + containerRect.left - tooltipRect.width - 12;
            y = window.scrollY + containerRect.top + (containerRect.height / 2) - (tooltipRect.height / 2);
            break;
          case "right":
            x = window.scrollX + containerRect.right + 12;
            y = window.scrollY + containerRect.top + (containerRect.height / 2) - (tooltipRect.height / 2);
            break;
        }
      } else {
        // Original positioning for regular elements
        switch (position) {
          case "top":
            x = containerRect.left + containerRect.width / 2 - tooltipRect.width / 2;
            y = containerRect.top - tooltipRect.height - 8;
            break;
          case "bottom":
            x = containerRect.left + containerRect.width / 2 - tooltipRect.width / 2;
            y = containerRect.bottom + 8;
            break;
          case "left":
            x = containerRect.left - tooltipRect.width - 8;
            y = containerRect.top + containerRect.height / 2 - tooltipRect.height / 2;
            break;
          case "right":
            x = containerRect.right + 8;
            y = containerRect.top + containerRect.height / 2 - tooltipRect.height / 2;
            break;
        }
      }

      // Boundary detection to keep tooltip on screen
      const windowWidth = window.innerWidth;
      const windowHeight = window.innerHeight;

      if (x < 10) x = 10;
      if (x + tooltipRect.width > windowWidth - 10) x = windowWidth - tooltipRect.width - 10;
      if (y < 10) y = 10;
      if (y + tooltipRect.height > windowHeight - 10) y = windowHeight - tooltipRect.height - 10;

      setCoordinates({ x, y });
    }
  }, [shouldShow, position, preservePositioning]);
  const showTooltip = () => {
    if (timeoutRef.current) clearTimeout(timeoutRef.current);
    timeoutRef.current = setTimeout(() => setIsVisible(true), delay);
  };

  const hideTooltip = () => {
    if (timeoutRef.current) clearTimeout(timeoutRef.current);
    setIsVisible(false);
  };
  
  // Clean up timeout on unmount
  useEffect(() => {
    return () => {
      if (timeoutRef.current) {
        clearTimeout(timeoutRef.current);
      }
    };
  }, []);

  const toggleTooltip = () => {
    setIsVisible(!isVisible);
  };

  const getEventHandlers = () => {
    if (trigger === "hover" || trigger === "both") {
      return {
        onMouseEnter: showTooltip,
        onMouseLeave: hideTooltip,
        onFocus: showTooltip,
        onBlur: hideTooltip,
        onClick: trigger === "both" ? toggleTooltip : undefined
      };
    }
    if (trigger === "click") {
      return {
        onClick: toggleTooltip
      };
    }
    return {};
  };
  return (
    <div 
      className={`tooltip-wrapper-container ${preservePositioning ? 'preserve-positioning' : ''} ${className}`} 
      ref={containerRef}
      {...getEventHandlers()}
    >
      {children}

      {shouldShow && (        <div 
          className={`tooltip-wrapper tooltip-${position}`}
          ref={tooltipRef}
          style={{ 
            left: coordinates.x, 
            top: coordinates.y,
            maxWidth: typeof maxWidth === 'number' ? `${maxWidth}px` : maxWidth,
            zIndex: 10000, // Ensure the tooltip is above everything
            transform: `translate(${translate[0]}px, ${translate[1]}px)`,
          }}
        >          <div className="tooltip-content">{content}</div>
          {showHelpIcon && (
            <div className="tooltip-help-icon">
              <span className="tooltip-help-icon-text">i</span>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default TooltipWrapper;
