import React from 'react';
import { ModalConfig, Player, EncounterFlavorTag, FlavorTagSource, FlavorTagTheme } from '../../models';
import { useAppManager } from '../managerContexts';
import CImage from './cimage';
import FlavorTagPill from './FlavorTagPill';
import './NextEncounterModal.scss';

interface NextEncounterModalProps {
  modalConfig: ModalConfig;
  onClose: () => void;
  onOptionSelect: (optionId: string) => void;
}

const NextEncounterModal: React.FC<NextEncounterModalProps> = ({ modalConfig, onClose, onOptionSelect }) => {
  const appManager = useAppManager();
  const playerManager = appManager.getPlayerManager();
  const tagManager = appManager.getTagManager();
  
  if (!modalConfig.encounterData) return null;

  const { branches } = modalConfig.encounterData;

  // Use new preference evaluation system instead of the old branchPreferences
  const { preferredEncounters, notWantedEncounters } = playerManager.evaluateEncounterPreferences(branches);
  
  // Calculate incomplete exploration penalties
  const explorationPenalties = playerManager.calculateIncompleteExplorationPenalties(tagManager);
  const hasExplorationPenalties = explorationPenalties.totalStressPenalty > 0 || explorationPenalties.totalAttentionPenalty > 0;
  
  const getPlayerPreferences = (branchName: string): Player[] => {
    return preferredEncounters[branchName] || [];
  };

  const getPenaltyPlayers = (branchName: string): Player[] => {
    return notWantedEncounters[branchName] || [];
  };

  const isOnlyOneOption = branches.length === 1;

  // Helper function to convert Tag to EncounterFlavorTag
  const createFlavorTag = (tag: any, source: FlavorTagSource): EncounterFlavorTag => ({
    tag: tag,
    source: source,
    theme: FlavorTagTheme.NONE
  });

  return (
    <div className="modal-overlay">
      <div className="modal-container nextencounter">
        <div className="modal-header">
          <h2>{modalConfig.title}</h2>
          <button className="close-button" onClick={onClose}></button>
        </div>
        
        <div className="nextencounter-content">
          <div className="encounter-options">
            {branches.map((branch) => {
              const preferences = getPlayerPreferences(branch.name);
              const penaltyPlayers = getPenaltyPlayers(branch.name);
              
              return (
                <div
                  key={branch.name}
                  className="encounter-option"
                  onClick={() => onOptionSelect(branch.name)}
                >
                  <div className="encounter-card">
                    <div className="encounter-image">
                      <CImage imageId={branch.icon} />
                    </div>
                    
                    <div className="encounter-info">
                      <h3 className="encounter-name">{branch.name}</h3>
                      <p className="encounter-description">{branch.description}</p>
                      
                      <div className="encounter-location">
                        <strong>Location:</strong> {branch.location.name}
                      </div>
                       {branch.location.tags && branch.location.tags.length > 0 && (
                        <div className="encounter-tags">
                          <div className="tags-list">
                            {branch.location.tags.map((tag, index) => (
                              <FlavorTagPill
                                key={index}
                                tag={createFlavorTag(tag, FlavorTagSource.LOCATION)}
                                showTooltip={true}
                              />
                            ))}
                          </div>
                        </div>
                      )}
                      {branch.tags && branch.tags.length > 0 && (
                        <div className="encounter-tags">
                          <div className="tags-label">Elements:</div>
                          <div className="tags-list">
                            {branch.tags.map((tag, index) => (
                              <FlavorTagPill
                                key={index}
                                tag={createFlavorTag(tag, FlavorTagSource.ENCOUNTER)}
                                showTooltip={true}
                              />
                            ))}
                          </div>
                        </div>
                      )}
                    </div>
                    
                    <div className="player-preferences-container">
                      {preferences.length > 0 && (
                        <div className="player-preferences preferred">
                          <div className="preferences-label">Preferred by:</div>
                          <div className="preferences-effect">+1 Attitude</div>
                          <div className="player-icons">
                            {preferences.map((player) => (
                              <div
                                key={player.id}
                                className="player-icon"
                                title={player.name}
                              >
                                <CImage imageId={player.icon} />
                              </div>
                            ))}
                          </div>
                        </div>
                      )}
                      
                      {(penaltyPlayers.length > 0 || isOnlyOneOption || hasExplorationPenalties) && (
                        <div className="player-preferences not-wanted">
                          <div className="preferences-label">Penalties:</div>
                          
                          {/* Incomplete Exploration Penalties */}
                          {hasExplorationPenalties && (
                            <>
                              {explorationPenalties.totalStressPenalty > 0 && (
                                <div className="penalty">
                                  Undescribed location topics : +{explorationPenalties.totalStressPenalty} Stress 
                                </div>
                              )}
                              {explorationPenalties.totalAttentionPenalty > 0 && (
                                <div className="penalty">
                                 Need to describe {explorationPenalties.encounterTagPenalty} encounter topics : -{explorationPenalties.totalAttentionPenalty} Attention 
                                </div>
                              )}
                            </>
                          )}
                          
                          {/* Railroading Penalty */}
                          {isOnlyOneOption && (
                            <div className="penalty">Railroading: -1 Attention</div>
                          )}
                          
                          {/* Player Preference Penalties */}
                          {penaltyPlayers.length > 0 && !isOnlyOneOption && !hasExplorationPenalties && (
                            <div className="penalty">Player Dislikes: -1 Attitude</div>
                          )}
                          
                          <div className="player-icons">
                            {(hasExplorationPenalties || isOnlyOneOption ? 
                              playerManager.playersAffectedByRailroading() : 
                              penaltyPlayers
                            ).map((player: Player) => (
                              <div
                                key={player.id}
                                className="player-icon not-wanted-icon"
                                title={player.name}
                              >
                                <CImage imageId={player.icon} />
                              </div>
                            ))}
                          </div>
                        </div>
                      )}
                    </div>
                    
                    <div className="encounter-overlay">
                      <div className="select-text">Select Encounter</div>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
          
          <div className="selection-help">
            <p>Choose the next encounter for your party. Player preferences are shown based on their attitudes toward the encounter elements and preparation level.</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default NextEncounterModal;
