import { on } from "events";

interface CboxProps {
  title: string;
  theme?: string;
  children?: React.ReactNode;
  onClick?: () => void;
}
const Cbox: React.FC<CboxProps> = ({ title, theme, children, onClick }) => {
  return (
    <div className={`cbox ${theme}`} onClick={() => {onClick && onClick()}}>
      <div className={`cbox-text ${theme}`}>
        {title}
      </div>
      <div className="cbox-children">
        {children}
      </div>
    </div>
  );
};

export default Cbox;