import React from 'react';
import './gameRoom.scss';
import { useCampaignManager, usePartyManager } from '../managerContexts';

interface StressInfoProps {
  stress: number;
  displayNumber?: boolean;
}

const StressInfo: React.FC<StressInfoProps> = ({ stress, displayNumber }) => {
 
  const partyManager = usePartyManager();
  const campaignManager = useCampaignManager();

  

  const backgroundColor = campaignManager.getTensionColor(stress);


  const getStressDescription = (value: number): string => {
    return partyManager.getTensionDescription(value);
  };

  return (
    <div 
      className={`stress-info ${displayNumber ? 'number' : 'description'}`}
      style={{ 
        backgroundColor,
        transition: 'background-color 0.3s ease'
      }}
    >{displayNumber &&
      <div className="stress-value">{stress}</div>
    }
      {
        !displayNumber &&
        <div className="stress-description">
        {getStressDescription(stress)}
      </div>
      }
      
    </div>
  );
};

export default StressInfo;