import { Key, CSSProperties } from 'react';
import { ShapeConfig } from '../../models';
import { TreeShape } from './treeShapeProps';
import { SCENERY_THEME_CONFIGS } from '../../consts/sceneryConsts';
import './scenery.scss';

interface EncounterSceneryProps {
    theme: string;
    regenKey?: string;
}

const generateShapes = (config: ShapeConfig) => {
  const shapes = [];
  const count = Math.floor(Math.random() * (config.count.max - config.count.min + 1)) + config.count.min;

  for (let i = 0; i < count; i++) {
    const size = Math.floor(Math.random() * (config.maxSize - config.minSize + 1)) + config.minSize;
    
   
    let left = (Math.random() * 100) + '%';
    let top = (Math.random() * 100) + '%';
    if(config.type === 'wall') {
      left = '0%'; 
      top = '0%';
    }
    const style = {
      left,
      top,
      transform: config.rotate ? `rotate(${Math.random() * 360}deg)` : ''
    };

    shapes.push({
      id: i,
      size,
      style,
      type: config.type,
      color: config.color
    });
  }

  return shapes;
};

const EncounterScenery: React.FC<EncounterSceneryProps> = ({ theme, regenKey }) => {
  const themeConfig = SCENERY_THEME_CONFIGS[theme];
  const shapes = themeConfig.shapes.flatMap(generateShapes);




  return (
    <div 
      className={`encounter-scenery-container ${theme}`}
      style={{ backgroundColor: themeConfig.backgroundColor }}
    >
      <div className="encounter-scenery-background">
        {shapes.map((shape: { type: any; id: Key | null | undefined; size: number; color: any; style: CSSProperties | undefined; }) => {
          switch(shape.type) {
            case 'tree':
              return <TreeShape key={shape.id} {...shape} style={shape.style || {}} />;
            case 'rectangle':
                const width = shape.size + Math.random() * shape.size/2 + '%';
                const height = shape.size + Math.random() * shape.size/2 + '%';
              return (
                <div
                  key={shape.id}
                  style={{
                    position: 'absolute',
                    width: width,
                    height: height,
                    backgroundColor: shape.color,
                    ...shape.style
                  }}
                />
              );
              case 'wall':
                return (
                    <div
                        key={shape.id}
                        className="wall-pattern"
                        style={{
                            position: 'absolute',
                            width: shape.size + '%',
                            height: shape.size + '%',
                            opacity: 0.8,
                            left:  '0%',
                            top:  '0%',
                            ...shape.style
                        }}
                    />
                );
            default:
              return null;
          }
        })}
      </div>
    </div>
  );
};

export default EncounterScenery;