import { BUILDING_EFFECT_TYPE, BuildingEffect, Character, EncounterFlavorTag, Tag, TownBuilding } from "../../models";
import { useAppManager } from "../managerContexts";
import CImage from "../UI/cimage";
import DiceScore from "../UI/DiceScore";
import CharacterToken from "./characterToken";
import './town.scss';

interface BuildingCardProps {
    building: TownBuilding;
    onClick: (e: React.MouseEvent<HTMLDivElement>) => void;  // Update to pass event
    isSelectable: boolean;
    'data-building-id'?: string;  // Add this prop for animation targeting
    onCharacterClick: (character: Character, rect: DOMRect) => void;  // Add this
    onTagClick: (tag: EncounterFlavorTag, building: TownBuilding) => void;
  }
  

  const BuildingCard: React.FC<BuildingCardProps> = ({ 
    building, 
    onClick, 
    isSelectable,
    'data-building-id': buildingId ,
    onCharacterClick,
    onTagClick,
  }) => {
    const appManager = useAppManager();
    const townManager = appManager.getTownManager();
    const uiManager = appManager.getUiManager();


    const renderPositionEffect = (effect: BuildingEffect) => {
      const value = effect.value;
      const effectType = effect.type;
      let isIcon = false; 
      let iconText = '';
      let iconId = '';
      let textClass = ';'
      switch(effectType) {
        case BUILDING_EFFECT_TYPE.CAMPAIGN_POINT:
       //   isIcon = true;
       return (
        <DiceScore
          score = {effect.value}
          />
       )
         // iconId = 'icon_dmpoint';
          break;
        // case BUILDING_EFFECT_TYPE.ADD_PLAYER_POINT:
        //   isIcon = true;
        //   iconId = 'icon_playerpoint';
        //   break;
        case BUILDING_EFFECT_TYPE.ADD_TREASURE:
          isIcon = true;
          iconId = 'icon_treasure1';
          break;
         case BUILDING_EFFECT_TYPE.ADD_ENJOYMENT:
            iconText = value > 0 ? '' : ''
          break;
        case BUILDING_EFFECT_TYPE.ADD_TENSION:
           iconText = '' + value;  
          if(value > 0) {
            iconText = '+' + iconText;
              textClass = ' tension-up';
            }else{
              textClass = ' tension-down';
            }
            return (
              <div className='effect-score'>
                <div className ={'effect-score-label'}>
                  TENSION
                </div>
                <div className ={'effect-score-value'}>
                  {iconText}
                </div>
              </div>
            )
          break;
        // case BUILDING_EFFECT_TYPE.ADD_SCORE:
        //   return (
        //     <div className='effect-score'>
        //       <div className ={'effect-score-label'}>
        //         SCORE
        //       </div>
        //       <div className ={'effect-score-value'}>
        //         +{value}
        //       </div>
        //     </div>
        //   )
        //   break;
        case BUILDING_EFFECT_TYPE.INJURE:
          iconText = '';
          break;
      }

      if(isIcon) {
         return (
          <div className={`effect-display-icon ${textClass}`}>
            <CImage imageId={iconId} />
          </div>
        )

      } else {
        return (
          <div className={`effect-emoji-display ${textClass}`}>
            {iconText}
          </div>
        )
      }

     
    }



    const renderEffect = (effect: any) => {

      let effectString = '';
      let effectClass = '';
    
      switch(effect.type) {
        case BUILDING_EFFECT_TYPE.ADD_TENSION:
          effectString = (effect.value > 0 ? '+' : '') +  effect.value + ' tension';
          effectClass = 'tension-' + (effect.value > 0 ? 'up' : 'down');
          break;
        case BUILDING_EFFECT_TYPE.ADD_ENJOYMENT:
          effectString=  effect.value > 0 ? '' : '';
          break;
        // case BUILDING_EFFECT_TYPE.ADD_INVOLVEMENT:
        //   effectString = (effect.value > 0 ? '+' : '') +  effect.value + ' tension';
        //   effectClass = 'attention-' + (effect.value > 0 ? 'up' : 'down');

        //   break;
        // case BUILDING_EFFECT_TYPE.ADD_REWARDS:
        //   effectString = effect.value + ' rewards';
        //   break;
        case BUILDING_EFFECT_TYPE.REPLENISH_MAGIC:
          effectString = '';
          break;
        case BUILDING_EFFECT_TYPE.REPLENISH_TOOLS:
          effectString = '';
          break;
        // case BUILDING_EFFECT_TYPE.ADD_BACKSTORY:
        //   effectString = 'backstory';
        //   break;
        case BUILDING_EFFECT_TYPE.HEAL:
          effectString = '';
          break;
        case BUILDING_EFFECT_TYPE.RESURRECT:
          effectString = '';
          break;
        case BUILDING_EFFECT_TYPE.INJURE:
          effectString = '';
          break;
        case BUILDING_EFFECT_TYPE.USE_TOOLS:
          effectClass = 'use-tools';
          effectString = '-';
          break;
        case BUILDING_EFFECT_TYPE.USE_MAGIC:
          effectClass = 'use-magic';
          effectString = '-';
          break;
        //case BUILDING_EFFECT_TYPE.CAMPAIGN_POINT:
          // return (
          //   <div className="effect-display-icon">
          //     <CImage imageId="icon_dmpoint" />
          //   </div>
          // );
      //    effectClass = 'dm-point';
      //    effectString = '';
    
        // case BUILDING_EFFECT_TYPE.ADD_PLAYER_POINT:
        //   return (
        //     <div className="effect-display-icon">
        //       <CImage imageId="icon_playerpoint" />
        //     </div>
        //   );
      //  break;
        case BUILDING_EFFECT_TYPE.ADD_TREASURE:
          return (
            <div className="effect-display-icon">
              <CImage imageId="icon_treasure1" />
            </div>
          );
       


        default:
          effectString += effect.type;
      }

      return (
        <div className={`town-effect-display ${effectClass}`}>
          {effectString}
        </div>
      );

    }



    return (
        <div 
          className={`building-card ${isSelectable ? 'selectable' : ''}`}
          style={{
            left: `${building.position?.x}%`,
            top: `${building.position?.y}%`,
            width: `${building.size?.width}px`,
            height: `${building.size?.height}px`
          }}
          onClick={onClick}
          data-building-id={buildingId}
        >
        <div className="building-header">
          <div className="building-name">{building.building.name}</div>
       
          <div className="effect-row">
            {building.effects.map((effect, index) => (
                <div key={index} className={`effect ${effect.type}`}>
                  {renderEffect(effect)}
                </div>
            ))}
          </div>
                               
          </div>
          
       
        
        {/* <div className="building-tags">
        {building.tags.map((flavorTag, index) => {
          const isResolved = building.resolvedTags.some(t => t.name === flavorTag.tag.name);
          return (
            <div 
              key={index}
              className={`tag text-pill type-${flavorTag.source} ${uiManager.getPlayerAttitudesStyle(flavorTag.tag)} theme-${flavorTag.theme} ${building.isHighlighted ? 'clickable' : ''} ${isResolved ? 'resolved' : ''}`}// className={`text-pill type-${flavorTag.source} ${uiManager.getPlayerAttitudesStyle(flavorTag.tag)} theme-${flavorTag.theme}`}>
              onClick={() => {
                if (building.isHighlighted && !isResolved) {
                  onTagClick(flavorTag, building);
                }
              }}
            >
              {flavorTag.tag.name}
            </div>
          );
        })}
      </div> */}
        
        <div className="visitor-slots">
                {Array(building.capacity).fill(0).map((_, index) => (
                    <div key={index} className={`slot ${index < building.currentVisitors.length ? 'occupied' : ''}`}>
                        {building.currentVisitors[index] ? (
                          
                             <CharacterToken 
                                character={building.currentVisitors[index]} 
                                mini 
                                onClick={(e) => onCharacterClick(
                                    building.currentVisitors[index], 
                                    e.currentTarget.getBoundingClientRect()
                                )} 
                            />
                          
                           
                        ) : (
                          <div className="slot-effect">
                                {building.positionEffects[index].map((effect, index) => (
                                    <div key={index} className={`effect ${effect.type}`}>
                                      {renderPositionEffect(effect)}
                                    </div>
                                ))}
                            </div>
          
                        )}
                    </div>

                ))}
            </div>
            <div className={`building-effect ${building.isHighlighted ? 'hidden' : ''}`}>
            {building.building.description}
          </div>
            {building.isHighlighted && (
                <div className="interaction-hint">
                    Click on tags to gain their effects
                </div>
            )}
      </div>
    );
  };

export default BuildingCard;