import React, { useState, useEffect, useRef } from "react";
import {
	Tag,
	EncounterFlavorTag,
	RPEncounterFlavorTag,
	FlavorTagSource,
	FlavorTagTheme,
} from "../../models";
import {
	useAppManager,
	useCampaignManager,
	useUiManager,
} from "../managerContexts";
import CImage from "./cimage";
import "./TagPill.scss";
import AppManager from "../../managers/appManager";

export interface TagPillProps {
	tag: EncounterFlavorTag | RPEncounterFlavorTag;
	disabled?: boolean;
	highlighted?: boolean;
	theme?: string;
	onClick?: (flavorTag: EncounterFlavorTag) => void;
	className?: string;
	showTooltip?: boolean;
	tooltipText?: string;
	tooltipClassName?: string; // For custom tooltip styling
	off?: boolean;
	style?: React.CSSProperties;
}

const FlavorTagPill: React.FC<TagPillProps> = ({
	tag,
	disabled = false,
	highlighted = false,
	theme,
	onClick,
	className = "",
	showTooltip = true,
	tooltipText,
	tooltipClassName = "",
	off,
	style, // <-- style prop is already here
	...props
}) => {
	const [tooltipVisible, setTooltipVisible] = useState(false);
	const tagRef = useRef<HTMLDivElement>(null);
	const campaignManager = useCampaignManager();
	const uiManager = useUiManager();
	const appManager = useAppManager();
	const tagManager = appManager.getTagManager();

	// Determine if this is a flavor tag or regular tag
	const isRPFlavorTag = "character" in tag;

	// Get the actual Tag object regardless of input type
	const flavorTag = isRPFlavorTag
		? (tag as RPEncounterFlavorTag).tag
		: (tag as EncounterFlavorTag);

	// Handle tooltip positioning relative to viewport edges
	useEffect(() => {
		if (tooltipVisible && tagRef.current) {
			const tooltip = tagRef.current.querySelector(".tooltip") as HTMLElement;
		
			if (tooltip) {
				if (tooltipClassName) {
					tooltip.classList.add(tooltipClassName);
				}

				const tagRect = tagRef.current.getBoundingClientRect();
				const tooltipRect = tooltip.getBoundingClientRect();

				// Adjust if tooltip extends beyond the viewport
				if (tooltipRect.right > window.innerWidth) {
					tooltip.style.left = "auto";
					tooltip.style.right = "0";
					tooltip.style.transform = "translateX(0)";
				}

				if (tooltipRect.left < 0) {
					tooltip.style.left = "0";
					tooltip.style.right = "auto";
					tooltip.style.transform = "translateX(0)";
				}
			}
		}
	}, [tooltipVisible]);

	// Generate tooltip content
	const getTooltipContent = (): string => {
		if (tooltipText) return tooltipText;

		let content = "";

		// Get campaign tooltip if available
		const campaignTooltip = tagManager.getTooltipText({
			tag: flavorTag,
			type: "tag",
		});
		if (campaignTooltip) return campaignTooltip;

		// I think we should never get here!
		//return "HOW DID YOU GET HERE?!?! " + flavorTag?.tag.name || 'Unknown tag';

		// Otherwise build based on source
		switch (flavorTag?.source || "") {
		
			case FlavorTagSource.LOCATION:
				return (
					"HOW DID YOU GET HERE?!?! " + flavorTag?.tag.name || "Unknown tag"
				);
			case FlavorTagSource.PLOT:
				return (
					"HOW DID YOU GET HERE?!?! " + flavorTag?.tag.name || "Unknown tag"
				);
			case FlavorTagSource.PLAYERS:
				return (
					"HOW DID YOU GET HERE?!?! " + flavorTag?.tag.name || "Unknown tag"
				);
			case FlavorTagSource.BACKSTORY:
				return (
					"HOW DID YOU GET HERE?!?! " + flavorTag?.tag.name || "Unknown tag"
				);
			case FlavorTagSource.FORESHADOWING:
				return (
					"HOW DID YOU GET HERE?!?! " + flavorTag?.tag.name || "Unknown tag"
				);

			default:
				return (
					"HOW DID YOU GET HERE?!?! " + flavorTag?.tag.name || "Unknown tag"
				);
		}

		// // Add theme postfix if it exists
		// if (flavorTag && flavorTag.theme && flavorTag.theme !== FlavorTagTheme.NONE) {
		//   content += ` (${flavorTag.theme})`;
		// }

		return content;
	};

	// Determine CSS classes
	const getTagClasses = (): string => {
		let classes = "tag-pill text-pill";

		// Handle tag type classes
		if (flavorTag) {
			if (flavorTag.source) {
				classes += ` type-${flavorTag.source.toLowerCase()}`;
			}
			if (flavorTag.theme && flavorTag.theme !== FlavorTagTheme.NONE) {
				classes += ` theme-${flavorTag.theme.toLowerCase()}`;
			}
			// Apply player attitude styles
			const playerAttitudeStyle = uiManager.getPlayerAttitudesStyle(
				flavorTag.tag
			);
			if (playerAttitudeStyle) classes += ` ${playerAttitudeStyle}`;
		}

		// Add conditional classes
		if (disabled) classes += " disabled";
		if (highlighted) classes += " highlighted";
		if (theme) classes += ` theme-${theme}`;
		if (className) classes += ` ${className}`;

		if (off) classes += " off";

		return classes;
	};

	const handleClick = () => {
		if (!disabled && onClick && flavorTag) {
			onClick(flavorTag);
		}
	};
	// This is just a stylized round background
	const renderEncounterMedallion = () => {
		const expected = flavorTag?.source === FlavorTagSource.ENCOUNTER_EXPECTED;
		return (
			<div className={`encounter-medallion-outer tag-pill text-pill ${expected ? 'expected' : ''} ${off ? 'off' : ''}`} 
				style={style}
				onClick={handleClick}
				onMouseEnter={() => setTooltipVisible(true)}
				onMouseLeave={() => setTooltipVisible(false)}
			>
				{renderTag({ pointerEvents: 'none' }, false)}
				{!disabled && showTooltip && tooltipVisible && (
					<div className="tooltip medallion-tooltip">
						<div className="tooltip-content">{getTooltipContent()}</div>
					</div>
				)}
			</div>
		);
	}
	const renderTag = (style?: React.CSSProperties, mainChild = true) => (
		<div
			ref={tagRef}
			className={getTagClasses()}
			style={style}
			onClick={handleClick}
			onMouseEnter={ () => setTooltipVisible(true)}
			onMouseLeave={ () => setTooltipVisible(false)}
		>
			{flavorTag?.source === FlavorTagSource.TREASURE ? (
				<div className="treasure-icon-container">
					<CImage imageId="icon_treasure1" />
				</div>
			) : flavorTag?.source === FlavorTagSource.DM_POINT ? (
				<div className="dmpoint-icon-container">
					<CImage imageId="number_die_20" />
				</div>
			) : (
				flavorTag?.tag.name +
				(flavorTag?.source &&
				flavorTag.source === FlavorTagSource.FORESHADOWING_RANDOM
					? `?`
					: "")
			)}

			{!disabled && showTooltip && tooltipVisible && mainChild && (
				<div className="tooltip">
					<div className="tooltip-content">{getTooltipContent()}</div>
				</div>
			)}
		</div>
	);


	if(flavorTag?.source === FlavorTagSource.ENCOUNTER || flavorTag?.source === FlavorTagSource.ENCOUNTER_EXPECTED) {
		return renderEncounterMedallion();
	}

	return renderTag(style)
	
};

export default FlavorTagPill;
