import React, { useEffect } from 'react';
import './notebook.scss';
import { Encounter, NOTEBOOK_CATEGORY } from '../../models';
import { useUiManager } from '../managerContexts';
import NotebookEncounter from './notebookEncounter';
import NotebookCharacter from './notebookCharacter';
import NotebookLocation from './notebookLocation';
import NotebookPlayer from './notebookPlayer';
import NotebookGroup from './notebookGroup';
import GroupSchedule from './groupSchedule';
import NotebookTutorial from './notebookTutorial';


interface NoteBookProps {
    notebookPages: Record<string, any>[];
    singleColumn?: boolean;
    pageIndex?: number;

}

const Notebook: React.FC<NoteBookProps> = ({ notebookPages, singleColumn, pageIndex }) => {
    const uiManager = useUiManager();
    const [notebookIndex, setNotebookIndex] = React.useState(pageIndex || 0);


    const closeNotebook = () => {
        uiManager.closeNotebook();
    }


    useEffect(() => {
        setNotebookIndex(pageIndex || 0);
    }, [pageIndex]);

    const currentNotebookPage = notebookPages[notebookIndex];

    const removePlayerButton = () => {
        setNotebookIndex(0);
        uiManager.removePlayer(currentNotebookPage.content.player);
    }

    const getNotebookHeaderContent = () => {

        if (!currentNotebookPage) {
            return (
                <div className={`notebook-header `} />
            )
        }

        if (currentNotebookPage.category === NOTEBOOK_CATEGORY.ENCOUNTER || currentNotebookPage.category === NOTEBOOK_CATEGORY.LOCATION) {
            return (
                <div className={`notebook-header ${currentNotebookPage.category} `}>
                    <button className="pill-button" onClick={closeNotebook}>X</button>
                    <div className="notebook-header-title">{currentNotebookPage.category.toUpperCase()}</div>
                    {/* <div className="title-bar">
                        <div className="title-text">
                            {currentNotebookPage.title}
                        </div>
                        <div className="features-text">
                            FEATURES
                        </div>
                    </div> */}
                </div>
            )
        }

        if (currentNotebookPage.category === NOTEBOOK_CATEGORY.PLAYER) {
            return (
                <div className={`notebook-header player `}>
                    {singleColumn ? (
                        <button className="pill-button remove" onClick={removePlayerButton}>REMOVE

                        </button>
                    ) : (
                        <button className="pill-button" onClick={closeNotebook}>X</button>
                    )}
                    <div className="notebook-header-title">{currentNotebookPage.category.toUpperCase()}</div>
                    {/* <div className={`notebook-header-container`}>
                        <div className="title-text">
                            {currentNotebookPage.title}
                        </div>
                        <div className="player-preferences-container">
                            <div className="title-text player-preferences likes small">
                                Likes
                            </div>
                            <div className="title-text player-preferences hates small">
                                Hates
                            </div>
                        </div>
                    </div> */}
                </div>
            )
        }

        if (currentNotebookPage.category === NOTEBOOK_CATEGORY.CHARACTER) {
            return (
                <div className={`notebook-header character `}>

                    <button className="pill-button" onClick={closeNotebook}>X</button>
                    <div className="notebook-header-title">{currentNotebookPage.category.toUpperCase()}</div>
                    <div className={`notebook-header-container`}>
                        {/* <div className="title-text">
                            {currentNotebookPage.title}
                        </div> */}

                    </div>
                </div>
            )
        }
        if (currentNotebookPage.category === NOTEBOOK_CATEGORY.GROUP || currentNotebookPage.category === NOTEBOOK_CATEGORY.CHARACTER) {
            return (
                <div className={`notebook-header player ${currentNotebookPage.category}  `}>

                    <div className="notebook-header-title">{currentNotebookPage.category.toUpperCase()}</div>
                    <div className={`notebook-header-container`}>

                    </div>
                </div>
            )
        }


        return (
            <div className={`notebook-header  ${currentNotebookPage.category} `}>

                <button className="pill-button" onClick={closeNotebook}>X</button>
                <div className="title-bar">
                    <div className="title-text">
                        {currentNotebookPage.title}
                    </div>
                </div>
            </div>
        )
    }


    // reach out to todd

    const notebookTabs = notebookPages.map((page, index) => {

        return (
            <div className={`notebook-tab ${page.category} ${index === notebookIndex ? 'current' : ''}`} key={index} onClick={() => setNotebookIndex(index)}>
                <div className="notebook-tab-title">
                    {page.title}
                </div>
            </div>
        )
    })

    const getNotebookContent = () => {

        if (!currentNotebookPage) {
            return (
                <div className="notebook-body" />
            )
        }


        switch (currentNotebookPage.category) {
            case NOTEBOOK_CATEGORY.ENCOUNTER:
                return (
                    <NotebookEncounter
                        encounterInfo={currentNotebookPage.content}
                    />
                )

            case NOTEBOOK_CATEGORY.CHARACTER:
                return (
                    <NotebookCharacter
                        characterInfo={currentNotebookPage.content}
                    />
                )


            case NOTEBOOK_CATEGORY.LOCATION:
                return (
                    <NotebookLocation
                        locationInfo={currentNotebookPage.content}
                    />
                )

            case NOTEBOOK_CATEGORY.PLAYER:
                return (
                    <NotebookPlayer
                        playerInfo={currentNotebookPage.content}
                        singleColumn={singleColumn}
                        clickChar={(idx: number) => {
                            setNotebookIndex(idx+1)
                         }}
                    />
                )

            case NOTEBOOK_CATEGORY.MESSAGE:
                return (
                    <div className="notebook-category-message">
                        <div className="notebook-message-text">
                            {currentNotebookPage.content.message}
                        </div>
                    </div>
                )
            case NOTEBOOK_CATEGORY.TOC:
                return (
                    // <NotebookTOC
                    //     playerInfo = {currentNotebookPage.content}
                    // />
                    <div />
                )
            case NOTEBOOK_CATEGORY.GROUP:
                return (
                    <NotebookGroup
                        groupInfo={currentNotebookPage.content}
                    />
                )
            case NOTEBOOK_CATEGORY.TUTORIAL:
                return (
                    <NotebookTutorial
                        tutorialInfo={currentNotebookPage.content}
                    />
                )
        }
    }

    if (!currentNotebookPage) {
        return (
            <div className="notebook-modal" />
        );
    }
    return (

        <div className={`notebook-modal ${singleColumn ? 'single-column' : ''} ${currentNotebookPage.category === NOTEBOOK_CATEGORY.TUTORIAL ? 'tutorial' : ''}` }
        >
            {/* <div className="notebook-content o1">
                <div className="notebook-header" />
            </div>
            <div className="notebook-content o2" onClick={(e) => e.stopPropagation()}>
                <div className="notebook-tabs">
                    {notebookTabs}
                </div>
                <div className="notebook-header" />
            </div>
            <div className="notebook-content o3">
          
                <div className="notebook-header" />
            </div> */}
        {currentNotebookPage.category === NOTEBOOK_CATEGORY.TUTORIAL ? (
            <NotebookTutorial
            tutorialInfo={currentNotebookPage.content}
            />
            ) : (
            <div className="notebook-container">
                 <div className="notebook-tabs">
                        {notebookTabs}
                    </div>
                <div className={`notebook-content ${currentNotebookPage.category}`} onClick={(e) => e.stopPropagation()}>
                     
                        {getNotebookHeaderContent()}
                    <div className="notebook-body">
                        {getNotebookContent()}
                    </div>
                </div>
            </div>
            )}

            {singleColumn && (
                <div className="group-schedule-holder">
                    <GroupSchedule />
                </div>
            )}
        </div>
    );
};

export default Notebook;