import React from 'react';
import { Character } from '../../models';
import CImage from '../UI/cimage';

interface CharacterTokenProps {
    character: Character;
    isSelected?: boolean;
    onClick?: (e: React.MouseEvent<HTMLDivElement>) => void;
    mini?: boolean;
    style?: React.CSSProperties;
    'data-character-id'?: string;  // Add this prop for animation targeting
    className?:string;
    
  }

  const CharacterToken: React.FC<CharacterTokenProps> = ({ 
    character, 
    isSelected = false, 
    onClick,
    mini = false,
    style = {},
    'data-character-id': characterId,
    className = ''
  }) => {
    return (
      <div 
        className={`character-token ${isSelected ? 'selected' : ''} ${mini ? 'mini' : ''}  ${className}`}
        onClick={onClick}
        style={style}
        data-character-id={characterId}
      >
        <div className="character-icon">
          <CImage imageId={character.icon} />
        </div>
        {!mini && (
          <div className="character-name">
            {character.name}
          </div>
        )}
      </div>
    );
  };
export default CharacterToken;