import React from 'react';
import './campaign-display-body.css';
import { Campaign } from '../models';
import Cimage from './UI/cimage';

interface CampaignDisplayHeaderProps {
    campaign: Campaign;
}

const CampaignDisplayBody: React.FC<CampaignDisplayHeaderProps> = ({ campaign }) => {
    
    return (
        <div className='campaign-display-header'>
        <div className="campaign-icon">
          <Cimage imageId={campaign.icon} />
        </div>
        <div className="campaign-info">
          <div className="campaign-name"><strong>{campaign.name}</strong></div>
          <div className="campaign-description">{campaign.description}</div>
        </div>
        <div className="campaign-extra">
          <div>{campaign.nbCharacters[0]} - {campaign.nbCharacters[1]} characters</div>
          <div>{campaign.nbSessions[0]} - {campaign.nbSessions[1]} sessions</div>
        </div>
      </div>
    );
};

export default CampaignDisplayBody;