import { useEffect, useRef, useState } from "react";
import { useAppManager } from "../managerContexts";
import { CombatState, Tag } from "../../models";
import RangeCard from "./rangeCard";
import CombatModifiers from "./combatModifiers";
import ModeContainer from "../modecontainer";
import AddCombatants from "./addCombatants";
import { THEME_FILLED_REQ } from "../../consts/general";


interface CombatBarProps {
    combatState: CombatState;
}


const dangerLabels_default = [ 0, 0, 5, 10, 15, 25, 50, 75, 100];
const stressLabels_default = [ 0, 0, 1, 5, 5, 5, 10, 10, 20];  
const engagementBase = [-10, -1, +5, +10, +20, +10, +5, -1, -5];

const CombatBar: React.FC<CombatBarProps> = ({combatState}) => {


   
    const combatBarArrayLength = engagementBase.length;
    const appManager = useAppManager();
    const combatManager = appManager.getCombatManager();
    const partyManager = appManager.getPartyManager();
    const chatManager = appManager.getChatManager();

    const campaignManager = appManager.getCampaignManager();
    
    const getRange = (combatState: CombatState) => {
        const riskCenter = combatState.riskCenter;
        const range = [Math.max(riskCenter - combatState.nbEnemies, 0), Math.min(riskCenter + combatState.nbEnemies, combatBarArrayLength-1)];
        return range;
    }
    
    const themes = campaignManager.getCollectedTones();

    const [stressLabels, setStressLabels] = useState(stressLabels_default);
    const [dangerLabels, setDangerLabels] = useState(dangerLabels_default);
    const [engagementLabels, setEngagementLabels] = useState<number[]>([]);
  //  const [enemy, setEnemy] = useState(ENEMIES[combatState.enemyType.name]);
    const [range, setRange] = useState(getRange(combatState));

    const [highlightedSegment, setHighlightedSegment] = useState<number | null>(null);
    const [isAnimating, setIsAnimating] = useState(false);
    const [combatResultIndex, setCombatResultIndex] = useState<number | null>(null);
    const [randomResult, setRandomResult] = useState<number | null>(null);

    const applyGrimEffect = !!(themes.grim && themes.grim === THEME_FILLED_REQ);

    const labelsRef = useRef({
        danger: dangerLabels,
        stress: stressLabels,
        engagement: engagementLabels
      });
      
      useEffect(() => {
        labelsRef.current = {
          danger: dangerLabels,
          stress: stressLabels,
          engagement: engagementLabels
        };
      }, [dangerLabels, stressLabels, engagementLabels]);
    useEffect(() => {
        combatManager.setCombatBarAnimating(isAnimating);
        if(!isAnimating) {
           console.log("DONE ANIMATING" + combatResultIndex + " : " + combatState.round.phase);
           switch(combatState.round.phase) {
                case 'balance':
                    const newRange = getRange(combatState)
                    console.log("NEW RANGE: " + newRange);
                    setRange(newRange);
                    break
                case 'setup':
                    if(randomResult !== null) {
                        combatManager.modifyCombatRangeCenter(randomResult);
                        setCombatResultIndex(null);
                    }
                break;
                case 'heroes_attack':
                    break;
                case 'heroes_resolve':
                  
                    break;
                case 'enemies_attack':
                    // apply score and tension now
                    
                    break;
               
              
                case 'enemy_resolve_damage':
                     
                    break;
                default:
                    break;
            }
        }
    }, [isAnimating]);


    useEffect(() => {
        const handleCombatStateChange = (newCombatState: CombatState) => {
          if(combatManager) {
            setPeril(combatState);
           
            setEngagement(combatState);
            const newRange = getRange(combatState)
            setRange(newRange);
          }
        };
    let blah = false; 
    const handleCombatPhaseChange = (newCombatState: CombatState) => {
        console.log("Phase change ! " + newCombatState.round.phase);
        switch(newCombatState.round.phase) {
            case 'setup':   
            if(!isAnimating) { 
               
                console.log("WHATSUUUUP "+ blah)
                 blah = true; 
                setRandomResult(pickRandomInRange([0, combatBarArrayLength - 1]));
                setHighlightedSegment(null);
            }
                break;
            case 'heroes_attack':
              
                break;
         case 'heroes_resolve':
             
                break;
            case 'enemies_attack':
                setRandomResult( pickRandomInRange(getRange(newCombatState)));
             
                break;
           
        
            case 'enemy_resolve_damage':
           
                break;
            default:
                break;
            }
        };
        setEngagement(combatState);
        setPeril(combatState);
        combatManager.on('combatStateChange', handleCombatStateChange);
        combatManager.on('combatPhaseChange', handleCombatPhaseChange);
       
        
        return () => {
          combatManager.removeAllListeners('combatStateChange');
          combatManager.removeAllListeners('combatPhaseChange');
        };
      }, [combatManager]);
    
    const setPeril = (combatState: CombatState) => {
        // Determine how strong the enemy is versus the party
        const enemyStrength = combatState.enemyStats.attack[0];
        const partyStrength = partyManager.getPartyStrength();

        const riskArray = [0,1,5,10,25,50,75,80,90,100]
        const dangerCoefficient = (enemyStrength*enemyStrength) / (partyStrength*partyStrength);

        const perilLabels = riskArray.map((risk, index) => {
            let calcRisk = Math.ceil(risk * dangerCoefficient);
            
            if(combatState.nbEnemies === 1) {
                calcRisk *= .8;    
            } else if(combatState.nbEnemies === 3) {
                calcRisk *= 1.6;
            }
            
            if(index === 9) {
                calcRisk *= 2;
            }  
            if(index === 8) {
                calcRisk *= 1.5;
            }

            // round up to nearest 5
            calcRisk = Math.ceil(calcRisk/5)*5;

            //Max 100
            calcRisk = Math.min(calcRisk, 100);
            return calcRisk;
        });
        
        setDangerLabels(perilLabels);
        setStress(perilLabels)
        
    }

    const setPerilResult = (result: number) => {
        combatManager.setPerilResult([
          result, 
          labelsRef.current.danger[result], 
          labelsRef.current.stress[result], 
          labelsRef.current.engagement[result]
        ]);
      };

    const setStress = (perilLabels: number[]) => {
        const stressArray: number[] = [];
        perilLabels.forEach((label, index) => {
            const stress = getStressForPeril(label);
            stressArray.push(stress);
        });
        setStressLabels(stressArray);
    }

    const getStressForPeril = (peril: number) => {
        let stress = 0;
        if(peril <= 10) {
            stress = 1;
        } else if(peril <= 25) {
            stress = 5;
        } else if(peril <= 40) {
            stress = 10;
        } else if(peril <= 50) {
            stress = 15;
        } else if(peril <= 80) {
            stress = 20;
        } else if(peril <= 90) {
            stress = 25;
        } else {
            stress = 30;
        }
        return stress;
    }

    const setEngagement = (combatState: CombatState) => {
        const engagementArray:number[] = [];
        let maxEngagement = 0;
        engagementBase.forEach((base, index) => {
            const nbEnemenyCoefficient = .5 * combatState.nbEnemies;
            const basicEngagement = base * nbEnemenyCoefficient
            // round to lower int if negative or higher int if positive
            let roundedEngagement = basicEngagement < 0 ? Math.floor(basicEngagement) : Math.ceil(basicEngagement);
            if(roundedEngagement > maxEngagement) {
                maxEngagement = roundedEngagement;
               
            }else if(applyGrimEffect) {
                roundedEngagement = maxEngagement;
            }
            engagementArray.push(roundedEngagement);
        });
        setEngagementLabels(engagementArray);
    }


    const pickRandomInRange = (range:number[]) => {
        const result = Math.floor(Math.random() * (range[1] - range[0] + 1)) + range[0];
        animateRangePicker(range, result);
        setCombatResultIndex(result);
        setPerilResult(result);
        return result;
      }

      
      const generateColorsFromLabels = (numbers: number[], label: 'danger' | 'stress' | 'engagement'): string[] => {
        const min = Math.min(...numbers);
        const max = Math.max(...numbers);
        return numbers.map(n => combatManager.generateColorFromValue(n, min, max, label));
      };
      
      const dangerColors = generateColorsFromLabels(dangerLabels, 'danger');
      const stressColors = generateColorsFromLabels(stressLabels, 'stress');
      const engagementColors = generateColorsFromLabels(engagementLabels, 'engagement');

    const getSegments = (label: 'danger' | 'stress' | 'engagement') => {
        const segmentColors = label === 'danger' ? dangerColors : label === 'stress' ? stressColors : engagementColors;
        const labels = label === 'danger' ? dangerLabels : label === 'stress' ? stressLabels : engagementLabels;
        const initialSetup = combatState.round.phase === 'balance' || (combatState.round.phase === 'setup' && isAnimating);
        const segments = Array.from({ length: combatBarArrayLength }, (_, index) => (
            <div
            key={index}
            className={
                `combat-bar-segment  
                ${initialSetup || (index >= range[0] && index <= range[1]) ? 'in-range' : ''}
                ${index === highlightedSegment ? 'highlighted' : ''}
                ${isAnimating ? 'animating' : ''}
                ${combatResultIndex === null ? '' : index === combatResultIndex ? 'positive-result' : 'negative-result'}
            `}
            style={{ 
                backgroundColor: segmentColors[index] || '',
                color: (label === 'stress' && labels[index] > 10) ? '#777777ff' : ''
            }}
            >{(label!=='danger' &&  labels[index] > 0) ? '+' : ''}{labels[index]}{(label==='danger') ? '%':''}</div>
        ));
        return segments;
    }

    const HACKYHACKHACK = 8; // size of peril array
    
const animateRangePicker = (range: number[], finalNumber: number) => {
    //console.log(`Animating from ${range[0]} to ${range[1]} towards ${finalNumber}`);
    setIsAnimating(true);
    let direction = 1;
    let currentIndex = range[0];
    let delay = 50; // Start fast
    
    const animate = () => {
      setHighlightedSegment(currentIndex);
      if(combatManager.getCombatState()?.round.phase === 'setup') {

        setRange([Math.max(0, currentIndex - combatState.nbEnemies),Math.min(HACKYHACKHACK, currentIndex + combatState.nbEnemies)]);
    }
      //console.log(`Animating to ${finalNumber} from ${currentIndex}`);
      if (delay < 300) { // Still moving fast
        currentIndex += direction;
        
        if (currentIndex >= range[1]) {
          direction = -1;
          currentIndex = range[1];
        } else if (currentIndex <= range[0]) {
          direction = 1;
          currentIndex = range[0];
        }
        
        delay *= 1.10; // Slow down gradually
        setTimeout(animate, delay);
      } else {
        if(currentIndex !== finalNumber) {
            currentIndex += direction;
            if (currentIndex >= range[1]) {
                direction = -1;
                currentIndex = range[1];
              } else if (currentIndex <= range[0]) {
                direction = 1;
                currentIndex = range[0];
              }
            setTimeout(animate, delay);
        }else{
        // Final movement to target number
        setHighlightedSegment(finalNumber);
        setIsAnimating(false);
      }
    }
    };
    
    animate();
  };

    const combatModifiersCallback = (modifier: number, side: 'heroes' | 'enemies', flavor: Tag, index:number) => {
        combatManager.combatModifiersCallback(modifier, side, flavor, index);
    }

    const combatantCallback = (side: string, combatant: Tag, index: number) => {
        combatManager.combatantCallback(side, combatant, index);
    }


    return (
        <div className="combat-peril-row">
            <div className={`combat-bar-modifiers left ${combatState.round.phase === 'setup' ? 'show-arrow' : ''} ` }>
                {combatState.round.phase === 'balance' ? (
                    <AddCombatants
                        combatantCallback = {combatantCallback}
                        side={"heroes"}
                        combatState={combatState}
                        />
                    ) :
                    combatState.round.phase === 'setup' ? (
                    <CombatModifiers
                        modifierCallback={combatModifiersCallback}
                        side={"heroes"}
                        combatState={combatState}
                        disabled={isAnimating}
                        />
                    ) : null}
            </div>
            <div className="combat-bar-container">
            
                <div className = "combat-bar">
                    <div className="combat-bar-range-indicator">
                        <RangeCard min={range[0]} max={range[1]} totalSegments={combatBarArrayLength} isGrim={applyGrimEffect} />
                </div>
                    <div className="segment-container danger">
                        {getSegments('danger')}
                    </div>
                    <div className="segment-container stress">
                        {getSegments('stress')}
                    </div>
                    <div className="segment-container engagement">
                        {getSegments('engagement')}        
                    </div>
                </div>
            </div>
            <div className={`combat-bar-modifiers right ${combatState.round.phase === 'setup' ? 'show-arrow' : ''} ` }>
            {combatState.round.phase === 'balance' ? (
                 <AddCombatants
                        combatantCallback = {combatantCallback}
                        side={"enemies"}
                        combatState={combatState}
                        />
                    ) :
                    combatState.round.phase === 'setup' ? (
                <CombatModifiers
                    modifierCallback={combatModifiersCallback}
                    side={"enemies"}
                    combatState={combatState}
                    disabled={isAnimating}
                    />
            ) : null}
            </div>
        </div>
    );
}
export default CombatBar; 