import React from "react";
import { useUiManager } from "../managerContexts";
import DiceScore from "./DiceScore";
import FlavorTagPill from "./FlavorTagPill";

interface RewardDisplayProps {
	reward: {
		type: string;
		amount: number;
		description?: string;
	};
}

const RewardDisplay: React.FC<RewardDisplayProps> = ({ reward }) => {
	const uiManager = useUiManager();
	const config = uiManager.getRewardDisplayConfig(reward);

	switch (config.type) {
		case "dice":
			return <DiceScore score={config.score} />;
		case "treasurePills":
			return (
				<>
					{Array.from({ length: config.amount }, (_, idx) => (
						<FlavorTagPill
							key={idx}
							tag={config.tagConfig}
							highlighted={false}
							showTooltip={false}
							className="encounter-flavor-tag"
						/>
					))}
				</>
			);
		case "text":
		default:
			return <span>{config.text}</span>;
	}
};

export default RewardDisplay;