import { useEffect, useState } from "react";
import { Encounter, EncounterEvent, SESSION_STATE } from "../../models";
import { useCampaignManager, useUiManager } from "../managerContexts";
import EncounterEventDisplay from "./encounterEventDisplay";
//import GameEncounterIntro from "./encounterIntro";
import GameEncounterOverview from "./gameEncounterOverview";
import { ENCOUNTER_EVENT_TYPE } from "../../consts/general";
import GameEncounterCombat from "./gameEncounterCombat";
import GameEncounterExploration from "./gameEncounterExploration";
import GameEncounterPerception from "./gameEncounterPerception";
import GameEncounterRoleplay from "./gameEncounterRoleplay";
import GameEncounterTown from "./gameEncounterTown";
import GameEncounterTrap from "./gameEncounterTrap";
import GameEncounterSkillChallenge from "./gameEncounterSkillChallenge";
import GameEncounterPuzzle from "./gameEncounterPuzzle";
import GameEncounterStory from "./gameEncounterStory";
import GameEncounterTravel from "./gameEncounterTravel";
import EncounterScenery from "../UI/encounterScenery";
import CImage from "../UI/cimage";
import GameEncounterDungeonCrawl from "./gameEncounterDungeonCrawl";


interface GameEncounterProps {
    encounter: Encounter;
    showOverview?: boolean;
}


const GameEncounter: React.FC<GameEncounterProps> = ({encounter, showOverview}) => {

  const [events, setEvents] = useState<EncounterEvent[]>(encounter.events);
   const [update, setUpdate] = useState(false);
  const campaignManager = useCampaignManager();
  const uiManager = useUiManager();
  const theme = encounter.location.themes[0];   
  const [encounterState, setEncounterState] = useState(campaignManager.getEncounterState());
  const [showImageForEvent, setShowImageForEvent] = useState(uiManager.showImageForEvent());
  const [eventImageOpacity, setEventImageOpacity] = useState(uiManager.getEventImageOpacity());


  useEffect(() => {
    const updateEncounterState = () => {
      const newState = campaignManager.getEncounterState();
      if (newState?.sessionState !== undefined && newState.eventIndex !== undefined) {
        setEncounterState({...newState});
      }
    };

    const updateEvent = () => {
      setShowImageForEvent(uiManager.showImageForEvent());
      setEventImageOpacity(uiManager.getEventImageOpacity());
    };


    uiManager.on('updateEncounterState', updateEncounterState);
    uiManager.on('eventUpdated', updateEvent);
    return () => {
      uiManager.off('updateEncounterState', updateEncounterState);
    };
  }, [campaignManager, uiManager]);


  const getCurrentEventComponent = (eventIndex: number) => {
    if(!encounterState) {
      return null;
    }
    const events = encounter.events;
    
    const currentEncounterEvent = events[encounterState.eventIndex];

    switch(currentEncounterEvent.type) {
      case ENCOUNTER_EVENT_TYPE.COMBAT:
        return <GameEncounterCombat encounter={encounter} />;
      case ENCOUNTER_EVENT_TYPE.ROLEPLAY:
        return <GameEncounterRoleplay encounter={encounter} />;
      case ENCOUNTER_EVENT_TYPE.PUZZLE:
        return <GameEncounterPuzzle encounter={encounter} />;
      case ENCOUNTER_EVENT_TYPE.PERCEPTION:
        return <GameEncounterPerception encounter={encounter} />;
      case ENCOUNTER_EVENT_TYPE.TOWN:
        return <GameEncounterTown encounter={encounter} />;
      case ENCOUNTER_EVENT_TYPE.TRAVEL:
        return <GameEncounterTravel encounter={encounter} />;
      case ENCOUNTER_EVENT_TYPE.CHALLENGE:
        return <GameEncounterSkillChallenge encounter={encounter} />;
      case ENCOUNTER_EVENT_TYPE.TRAP:
        return <GameEncounterTrap encounter={encounter} />;
      case ENCOUNTER_EVENT_TYPE.STORY:
        return <GameEncounterStory encounter={encounter} />;
      case ENCOUNTER_EVENT_TYPE.EXPLORATION:
        return <GameEncounterExploration encounter={encounter} />;
case ENCOUNTER_EVENT_TYPE.DUNGEON:
      return <GameEncounterDungeonCrawl encounter={encounter} />;
      
      default:
        return null;
    }
  }

  const getCurrentEventComponentParent = () => {
   
    if(!encounterState) {
      return null;
    }
    
    switch(encounterState.sessionState) {
      // case SESSION_STATE.LOCATION_INTRO:
      //     return (
      //     <GameEncounterIntro
      //       encounter={encounter} />
      //     );
      case SESSION_STATE.INTRO_EVENTS:
         return getCurrentEventComponent(0);
      case SESSION_STATE.INTRO_MAIN:
          return <div>Intro Main</div>;
      case SESSION_STATE.MAIN_EVENTS:
        return getCurrentEventComponent(1);
      case SESSION_STATE.INTRO_EXIT:
          return <div>Intro Exit</div>;
      case SESSION_STATE.EXIT_EVENTS:
        return getCurrentEventComponent(2);
      case SESSION_STATE.EXIT:
          return <div>Exit</div>;
      default:
        break;  
      }
  }

  return (
    <div className={`table-encounter-container ${theme}`}>
       {showOverview ? (
        <GameEncounterOverview encounter={encounter} />
      ): (
        <div className='table-encounter-scene'>
          <div className="scenery-opacity-container" style={{opacity: showImageForEvent ? 1 - eventImageOpacity : 1}}>
            <EncounterScenery theme={theme} />
          </div>
          {showImageForEvent && (
            <div className="encounter-icon-container" style={{ opacity: eventImageOpacity }}>
              <CImage imageId={encounter.location.icon}  />
            </div>
          )}
        
        
          <div className='table-encounter-title'>
            {encounter.name}
          </div>
          <div className='table-encounter-session'>
            {getCurrentEventComponentParent()}
          </div>
        </div>
      )}
    </div>
  );
};

export default GameEncounter;