import React from "react";
import { APP_STATE } from "../../consts/states";
import { useAppManager, useCampaignManager, useUiManager } from "../managerContexts";
import GameEncounter from "./gameEncounter";
import RewardModal from "./rewardModal";

interface GameTableProps {

}




const GameTable: React.FC<GameTableProps> = () => {

  const campaignManager = useCampaignManager();
  const encounter = campaignManager.getCurrentEncounter();
  const appManager = useAppManager();



  if(!encounter) {
    return null;
  }

  return (
	 
        <div className = "game-table-slab">
         
          <GameEncounter 
            encounter = {encounter}
            showOverview = {appManager.state === APP_STATE.SESSION_OVERVIEW}
          />
          <RewardModal
            rewardsAcceptedCallback={campaignManager.rewardsAccepted}         
          />
        </div>
	
  );
}

export default GameTable;
