import { EffectData, Attribute, ATTRIBUTE_TYPE } from "../../models";
import CImage from "./cimage";

interface EffectDisplayProps {
	effectData: EffectData;
}

const EffectDisplay: React.FC<EffectDisplayProps> = ({ effectData }) => {
	const renderAttributeGains = (attributeGains: Attribute) => {
		const gains = [];
		
		if (attributeGains[ATTRIBUTE_TYPE.COMBAT] && attributeGains[ATTRIBUTE_TYPE.COMBAT] > 0) {
			gains.push(
				<div key="combat" className="attribute-gain-item">
					<span className="attribute-icon combat"></span>
					<span className="attribute-value">+{attributeGains[ATTRIBUTE_TYPE.COMBAT]}</span>
				</div>
			);
		}
		
		if (attributeGains[ATTRIBUTE_TYPE.MAGIC] && attributeGains[ATTRIBUTE_TYPE.MAGIC] > 0) {
			gains.push(
				<div key="magic" className="attribute-gain-item">
					<span className="attribute-icon magic"></span>
					<span className="attribute-value">+{attributeGains[ATTRIBUTE_TYPE.MAGIC]}</span>
				</div>
			);
		}
		
		if (attributeGains[ATTRIBUTE_TYPE.TOOLS] && attributeGains[ATTRIBUTE_TYPE.TOOLS] > 0) {
			gains.push(
				<div key="tools" className="attribute-gain-item">
					<span className="attribute-icon tools"></span>
					<span className="attribute-value">+{attributeGains[ATTRIBUTE_TYPE.TOOLS]}</span>
				</div>
			);
		}
		
		return gains.length > 0 ? (
			<div className="attribute-gains-container">
				{gains}
			</div>
		) : null;
	};

	const renderEffect = (effect: any, idx: number) => {
		// Special handling for attribute gains
		if (effect.class === 'attribute-gains') {
			try {
				const attributeGains: Attribute = JSON.parse(effect.effect);
				const gainsDisplay = renderAttributeGains(attributeGains);
				
				if (!gainsDisplay) {
					return null; // No gains to show
				}
				
				return (
					<div key={idx} className={`effect-display-effect ${effect.class}`}>
						{gainsDisplay}
					</div>
				);
			} catch (e) {
				// If parsing fails, fall back to normal text display
				return (
					<div key={idx} className={`effect-display-effect ${effect.class}`}>
						{effect.effect}
					</div>
				);
			}
		}
		
		// Normal effect rendering
		return (
			<div key={idx} className={`effect-display-effect ${effect.class}`}>
				{effect.effect}
			</div>
		);
	};

	return (
		<div className="effect-display">
			<div className="effect-display-header">
				<div className="effect-display-name">{effectData.object === 'party' ? 'Party' : (effectData.object as any).name}</div>
				{effectData.object !== 'party' && (effectData.object as any).icon && <CImage imageId={(effectData.object as any).icon} />}
			</div>

			{effectData.effects?.map((effect, idx) => renderEffect(effect, idx))}
		</div>
	);
};

export default EffectDisplay;
