import React, { useState } from 'react';
import { useCharacterManager, useAppManager, useCampaignManager } from '../managerContexts';
import FillBar from './fillBar';
import { ATTRIBUTE_TYPE } from '../../models';
import DiceScore from './DiceScore';

interface TreasureDisplayProps {
  treasureLevel: number;
  treasurePoints: number;
}



const TreasureDisplay: React.FC<TreasureDisplayProps> = ({treasureLevel, treasurePoints}) => {
   const appManager = useAppManager();
    const partyManager = appManager.getPartyManager();
   // const campaignManager = useCampaignManager();

  // Calculate current level and next threshold
  while (partyManager.getTreasureThreshold(treasureLevel + 1) <= treasurePoints) {
    treasureLevel++;
  }
  
  const nextThreshold = partyManager.getTreasureThreshold(treasureLevel + 1);
  const previousThreshold = partyManager.getTreasureThreshold(treasureLevel);
  const progressToNext = (treasurePoints - previousThreshold) / (nextThreshold - previousThreshold);
  
  const nextReward = partyManager.getTreasureReward(treasureLevel);

  return (
    <div className="treasure-display-container">
      <div className="treasure-header">
        <span className="treasure-title">TREASURE</span>
      </div>
      
      <div className="treasure-reward">
       
        <span className="reward-value">
          {nextReward.type === 'score' ? (
            <DiceScore
              score={nextReward.value}
             />
          ): (
            <span>+{nextReward.value} Attitude</span>)}
          
        </span>
      </div>
      
      <div className="treasure-progress">
        <span className="progress-text">
          {treasurePoints}/{nextThreshold}
        </span>
        <FillBar 
          totalSegments={nextThreshold}
          filledSegments={treasurePoints}
          theme={"treasure"}
        />
      </div>
    </div>
  );
};

export default TreasureDisplay;