import React, { useEffect } from 'react';
import './globalUi.scss';
import { APP_STATE } from '../../consts/states';
import { useAppManager, useUiManager } from '../managerContexts';
import Notebook from './notebook';
import { Modal } from './modal';
import SelectQuestModal from './SelectQuestModal';
import QuestTerminalModal from './QuestTerminalModal';
import QuestProgressModal from './QuestProgressModal';
import ActiveQuestsModal from './ActiveQuestsModal';

interface GlobalUiProps {
 
}

const GlobalUi: React.FC<GlobalUiProps> = () => {

  const appManager = useAppManager();
  const uiManager = useUiManager();

  const [state, setState] = React.useState(appManager.state);
  const [notebookState, setNotebookState] = React.useState(undefined)
  const [tutorialPage, setTutorialPage] = React.useState(0)
  const [selectQuestModalVisible, setSelectQuestModalVisible] = React.useState(false);
  const [activeQuestsModalVisible, setActiveQuestsModalVisible] = React.useState(false);
  const [questTerminalModal, setQuestTerminalModal] = React.useState<{
    quest: any;
    node: any;
  } | null>(null);
  const [questProgressModalQuestId, setQuestProgressModalQuestId] = React.useState<string | null>(null);

  useEffect(() => {
    const handleStateChange = () => {
      setState(appManager.state);
    };

    const handleSetNotebookState = (newState: any) => {
      setTutorialPage(0)
      setNotebookState(newState);
    }

    const handleTutorialPage = (page: number) => {
      setTutorialPage(page)
    }

    const handleShowSelectQuestModal = (show: boolean) => {
      setSelectQuestModalVisible(show);
    };

    const handleShowActiveQuestsModal = (show: boolean) => {
      setActiveQuestsModalVisible(show);
    };

    const handleShowQuestTerminalModal = (data: { quest: any; node: any } | null) => {
      setQuestTerminalModal(data);
    };

    const handleShowQuestProgressModal = (questId: string | null) => {
      setQuestProgressModalQuestId(questId);
    };

    appManager.on('stateChange', handleStateChange);
    uiManager.on('setNotebookState', handleSetNotebookState)
    uiManager.on('setTutorialPage', handleTutorialPage)
    uiManager.on('showSelectQuestModal', handleShowSelectQuestModal);
    uiManager.on('showActiveQuestsModal', handleShowActiveQuestsModal);
    uiManager.on('showQuestTerminalModal', handleShowQuestTerminalModal);
    uiManager.on('showQuestProgressModal', handleShowQuestProgressModal);
    
    return () => {
      uiManager.off('setNotebookState', handleSetNotebookState);
      appManager.off('stateChange', handleStateChange);
      uiManager.off('setTutorialPage', handleTutorialPage)
      uiManager.off('showSelectQuestModal', handleShowSelectQuestModal);
      uiManager.off('showActiveQuestsModal', handleShowActiveQuestsModal);
      uiManager.off('showQuestTerminalModal', handleShowQuestTerminalModal);
      uiManager.off('showQuestProgressModal', handleShowQuestProgressModal);
    };
  
  }, [appManager, uiManager]);
  
  function getStateComponent( state: APP_STATE) {
    switch(state) {
      case APP_STATE.PLAYER_RECRUITMENT:
      case APP_STATE.CHARACTER_CREATION:
      case APP_STATE.MAIN_MENU:
      case APP_STATE.CAMPAIGN_SETUP:
      case APP_STATE.SESSION_OVERVIEW:
      case APP_STATE.SESSION:
      case APP_STATE.SCORE:
      default:
        return  getDefaultStateComponent()
    }
  }

  // not yet implemented state UI
  function getDefaultStateComponent() {
    return <div></div>
  }

  return (
    <div className="global-ui-container">
      <div className="state-display">
        {getStateComponent(state)}
      </div>
        {
        notebookState ?  
        <Notebook
            notebookPages={notebookState}
            pageIndex={tutorialPage}
        /> : null}
        <Modal />
        
        {selectQuestModalVisible && (
          <SelectQuestModal
            quests={[
              ...appManager.getQuestManager().getAvailableQuests(),
              ...appManager
                .getQuestManager()
                .getActiveQuests()
                .map((aq) => aq.quest),
            ]}
            activeQuestIds={appManager
              .getQuestManager()
              .getActiveQuests()
              .map((aq) => aq.quest.id)}
            onQuestSelect={(questId: string) => {
              // Close the selection modal and show the quest progress modal
              uiManager.questSelected(questId);
            
            }}
            onClose={() => uiManager.hideSelectQuestModal()}
          />
        )}

        {activeQuestsModalVisible && (
          <ActiveQuestsModal
            activeQuests={appManager.getQuestManager().getActiveQuests()}
            onQuestSelect={(questId: string) => {
              // Close the active quests modal and show the quest progress modal
              uiManager.hideActiveQuestsModal();
              uiManager.showQuestProgressModal(questId);
            }}
            onClose={() => uiManager.hideActiveQuestsModal()}
            getCollectedTagsForQuest={(questId: string) => appManager.getQuestManager().getCollectedTagsForQuest(questId)}
          />
        )}

        {questTerminalModal && (
          <QuestTerminalModal
            quest={questTerminalModal.quest}
            node={questTerminalModal.node}
            players={appManager.getPlayerManager().getPlayers()}
            onComplete={() => {
              // Handle quest terminal completion
              appManager
                .getQuestManager()
                .advanceQuest(
                  questTerminalModal.quest.id,
                  questTerminalModal.node.id
                );
              uiManager.hideQuestTerminalModal();
            }}
            onClose={() => uiManager.hideQuestTerminalModal()}
          />
        )}
        
        {questProgressModalQuestId && (
          <QuestProgressModal
            quest={(() => {
              // Find quest in available or active quests
              const availableQuest = appManager.getQuestManager().getAvailableQuests().find(q => q.id === questProgressModalQuestId);
              if (availableQuest) return availableQuest;
              
              const activeQuest = appManager.getQuestManager().getActiveQuests().find(aq => aq.quest.id === questProgressModalQuestId);
              return activeQuest?.quest;
            })()!}
            currentNode={(() => {
              // Get current node for the quest
              const activeQuest = appManager.getQuestManager().getActiveQuests().find(aq => aq.quest.id === questProgressModalQuestId);
              if (activeQuest) return activeQuest.currentNode;
              
              // If not active, use starting node
              const quest = appManager.getQuestManager().getAvailableQuests().find(q => q.id === questProgressModalQuestId);
              return quest?.startingNode;
            })()!}
            collectedTags={appManager.getQuestManager().getCollectedTagsForQuest(questProgressModalQuestId)}
            onClose={() => uiManager.hideQuestProgressModal()}
            onAccept={() => {
              appManager.getQuestManager().launchQuest(questProgressModalQuestId);
              uiManager.hideQuestProgressModal();
            }}
          />
        )}
        
        {/* <Chat /> */}
    </div>
  );
}

export default GlobalUi;
