

import { useCallback, useEffect, useState } from 'react';
import { ATTRIBUTE_CONTRIBUTOR, CombatState, Encounter, EnemyAttribute, EnemyAttributeInstance, HeroCombatant, PERIL_INDEX, Tag } from '../../models';
import { useAppManager, useCampaignManager, useUiManager } from '../managerContexts';
import AttributeDisplay from '../UI/attributeDisplay';
import CImage from '../UI/cimage';
import CombatBar from './combatBar';
import './combat.scss';
import NumberDie from '../UI/numderDie';
import CombatManager from '../../managers/combatManager';
import { ThemedButton } from '../UI/themedButton';


interface GameEncounterCombatProps {
	encounter: Encounter;
}

  const GameEncounterCombat: React.FC<GameEncounterCombatProps> = ({encounter}) => {

	const appManager = useAppManager();
	const uiManager = useUiManager();
	const combatManager = appManager.getCombatManager();
	const characterManager = appManager.getCharacterManager();

	const partyManager = appManager.getPartyManager();

	const combatState = combatManager?.generateCombatState(encounter, 0) || CombatManager.GetDefaultCombatState();

	const [swapVisible, setSwapVisible] = useState(false);
	
	const [enemyAttributes, setEnemyAttributes] = useState<EnemyAttributeInstance>();
	const [nbEnemies, setNbEnemies] = useState(combatState.nbEnemies);
	const [enemyType, setEnemyType] = useState(combatState.enemyType);
	const [perilResult, setPerilResult] = useState(combatState.perilResult);
	const [combatBarAnimating, setCombatBarAnimating] = useState(false);
	const [combatMessage, setCombatMessage] = useState<string>();
	const [rewards, setRewards] = useState<number>(combatState.rewards || 1);

	
	const [heroCombatants, setHeroCombatants] = useState<HeroCombatant[]>(partyManager.generateHeroes(combatState.npcs));
	const [heroActed, setHeroActed] = useState(heroCombatants.map(() => 'false'));

	const getEnemyAttributeInstance = (enemyStats: EnemyAttribute): EnemyAttributeInstance => {
			const attributeIndex = (combatManager?.getCombatState()?.nbEnemies || 1) - 1;
			const nbEnemies = combatManager?.getCombatState()?.nbEnemies || 1;
			const enemyAttributes: EnemyAttributeInstance = {
				health: enemyStats.health[attributeIndex] || enemyStats.health[0] * nbEnemies,
				attack: enemyStats.attack[attributeIndex] || enemyStats.attack[0] * nbEnemies,
				defense: enemyStats.defense[attributeIndex] || enemyStats.defense[0] * nbEnemies,
				magic_defense: enemyStats.magic_defense[attributeIndex] || enemyStats.magic_defense[0] * nbEnemies,
			};
			return enemyAttributes;
		}
	useEffect(() => {
		
		const handleCombatStateChange = (newCombatState: CombatState) => {
			setNbEnemies(newCombatState.nbEnemies);
			setEnemyType(newCombatState.enemyType);
			setHeroCombatants(partyManager.generateHeroes(newCombatState.npcs));
			setPerilResult(newCombatState.perilResult);
			setRewards(newCombatState.rewards || 1);
			if(newCombatState.round.phase === 'balance') {
				setEnemyAttributes(getEnemyAttributeInstance(newCombatState.enemyStats))
			}else {
				setEnemyAttributes(newCombatState.enemyStatsInstance);
			}

			if(newCombatState.round.phase === 'setup') {
				// reset acted
				setHeroActed(Object.keys(newCombatState.heroAttacks).map(() => 'false'))
				
			}
			setCombatMessage(newCombatState.combatMessage);
			setCombatBarAnimating(newCombatState.barAnimating || false);
			//setCombatStatePhase(newCombatState);
		  
		};
	  
		combatManager.on('combatStateChange', handleCombatStateChange);
		setEnemyAttributes(getEnemyAttributeInstance(combatState.enemyStats))
		
		return () => {
		  combatManager.removeAllListeners('combatStateChange');
		};
	  }, [combatManager, encounter]);


	const enemyAttributesDisplay = () => {
		return (
			<div className = "combat-encounter-enemies-attributes-display">
				<div className="combat-enemy-attribute defense">
					<div className="combat-enemy-attribute-title">Defense</div>
					<div className="combat-enemy-attribute-value">{enemyAttributes?.defense}</div>
				</div>
				<div className="combat-enemy-attribute magic-defense">
					<div className="combat-enemy-attribute-title">Magic Defense</div>
					<div className="combat-enemy-attribute-value">{enemyAttributes?.magic_defense}</div>
				</div>
				<div className="combat-enemy-attribute health">
					<div className="combat-enemy-attribute-title">Health</div>
					<div className="combat-enemy-attribute-value">{enemyAttributes?.health}</div>
				</div>
			</div>
		)
	}

	const generateEnemy = () => {
		  const icons = [];
		  //const symbol = uiManager.getShortenedName(enemy.name);
		  const nbIcons = [0,1,3,5,7];
		  
		  for (let i = 0; i < nbIcons[nbEnemies]; i++) {
			icons.push(
				<div key={`${i}`} className={`enemy-icon enemy-type-${enemyType.name}`}>
					<CImage imageId={`combat_${enemyType.name}`} />
				</div>
				);
		  }
		  return icons;
		};

	const modifyEnemies = (nb: number) => {
		if(nb < 1) {
			combatManager.reduceEnemies();
		}
		else {
			combatManager.increaseEnemies();
		}
	}
	



	const getRoundAdvanceLabel = () => {
		switch(combatState.round.phase) {
			case 'balance':
				return 'Begin Fight'
			case 'setup':
			
				return `Heroes Attack`;
			case 'heroes_attack':
				if(combatState.enemyStatsInstance.health <= 0) {
					return 'End Combat'
				}
				if(heroCombatants.every((hero, index) => heroActed[index] !== 'false')) {
					return 'Continue';
				}
				return 'Skip Hero Attack';
			case 'heroes_resolve':
					return 'Enemies Attack';
			case 'enemies_attack':
				if(combatState.barAnimating) {
					return '...';
				}
				return 'Resolve Peril';
			
			case 'enemy_resolve_damage':
					return 'End Round';
			case 'end_combat':
					return '';
			default: 	
			return '';
		}
	}
	

	const description = encounter.combat ? encounter.combat[0]?.description : '';
	const getCombatMessage = () => {

		if(combatState.round.phase === 'heroes_attack') {
			return (
				<div className = "combat-message-container">
					<div className = "combat-message-legend">
						<div className="combat-message-legend-item">:  1- to Health or Defense.</div>
						<div className="combat-message-legend-item">:  1 to Magic Defense.</div>
						<div className="combat-message-legend-item">:  1 to anything</div>
						<div className="combat-message-legend-item">:  Chance to crit for </div>
						<div className="combat-message-legend-item">:  2-4 Damage to anything.</div>
						<div className="combat-message-legend-item">:  at higher levels</div>
						<br></br>
						<div className="combat-message-legend-item"> / : limited uses</div>
					</div>
				</div>
			)
		}


		let combatMessage = combatManager.getCombatMessage();
		return (
			<div className = "combat-message-container">
				<div className = "combat-message">
					{combatMessage}
				</div>
			</div>
		)
	}
		
	const heroAttackPressed = (hero: HeroCombatant, index: number) => {
		const newHeroActed = [...heroActed];
		newHeroActed[index] = combatManager?.heroAttack(hero) || 'false';
		
		setHeroActed(newHeroActed);
	}

	const rewardDisplay = () => {
		if(rewards) {
			return (
				<div className="combat-encounter-rewards">
					<div className="combat-encounter-rewards-title">Players will expect</div>
					<div className="combat-encounter-rewards-list">{rewards} rewards</div>
				</div>
			)
		}
		return null;
	}

	  return (
		<div className="game-encounter-combat">
			
			
			<div className="combat-encounter-upper-container">
				<div className="combat-encounter-header">		
					<div className="combat-encounter-header-title">Combat Event in {encounter.location.name}</div>
					<div className="combat-encounter-description">{description}</div>
				</div>
			</div>


		    <div className="combat-encounter-body-container">
				<CombatBar combatState = {combatState}/>
				<div className="combat-encounter-enemies-row">
					{getCombatMessage()}
					
					<div className="combat-encounter-enemies">
						<div className= "combat-encounter-enemies-number">
							{combatState.round.phase === 'balance' && (
							<div className="combat-enemies-ui"  onClick={() => modifyEnemies(-1)}>
								<NumberDie baseDie={20} displayNumber={'1'}/>
								less
							</div>)}
							<div className="combat-encounter-enemies-list">
								{generateEnemy()}
							</div>
							{combatState.round.phase === 'balance' && (
							<div className="combat-enemies-ui"  onClick={() => modifyEnemies(1)}>
								<NumberDie baseDie={20} displayNumber={'1'}/>
								more
							</div>)}
						</div>

					
						
						
							
						<div className="combat-encounter-enemies-attributes">
							{enemyAttributesDisplay()}
						</div>
						<div className="combat-encounter-enemies-header">
						<div className='combat-encounter-enemy-name'>
							{enemyType.name}
						</div>
						
					</div>	
				</div>

				<div className = "combat-advance-box">
					<div className ={`combat-encounter-advance-button-container ${combatBarAnimating ? 'disabled' : ''} `} onClick={() => combatManager?.advanceRound()}>
						<div className="combat-encounter-advance-button">{getRoundAdvanceLabel()}
							{combatState.round.phase === 'enemy_resolve_damage' && combatManager.getAttackActionCost() > 0 &&
							<div className="combat-button-exhaustion-label">+{combatManager.getAttackActionCost()} Exhaustion</div>
							}
							{combatState.round.phase === 'heroes_attack' && combatState.enemyStatsInstance.health <= 0 &&
							<div className="combat-button-exhaustion-label">+{combatManager.getCombatState()?.perilResult[PERIL_INDEX.SCORE]} Score</div>
							}
							{combatState.round.phase === 'enemies_attack' && !combatState.barAnimating && 
								<div>
									<div className="combat-button-exhaustion-label">+{combatManager.getCombatState()?.perilResult[PERIL_INDEX.STRESS]} Tension</div>
									<div className="combat-button-exhaustion-label">+{combatManager.getCombatState()?.perilResult[PERIL_INDEX.SCORE]} Score</div>
								</div>
							}
						</div>
					</div>
				</div>
				
			</div>
			{rewardDisplay()}

			<div className="combat-encounter-heroes">
				{heroCombatants && heroCombatants.map((hero, index) => {
					return (
						<div key={hero.name} className="combat-encounter-hero-row">
							{(hero.npcTag || 
							(hero.character && hero.character.injury !== 'dead')) &&
							combatState.round.phase === 'heroes_attack' &&
							<div className="combat-hero-attack-results-container">
								<ThemedButton className={`combat-hero-button ${heroActed[index] !== 'false' ? 'acted' : ''}`} onClick={() => heroAttackPressed(hero, index)}>
									{heroActed[index] === 'false' ? combatManager.getHeroAttackDecisionText(hero) : heroActed[index]}
								</ThemedButton>
								<div className={`combat-hero-button ${heroActed[index] === 'false' ? '' : 'hide'} `} onClick={() => combatManager?.heroRethinkAttack(hero)}>
									<div className="combat-sure-text">
										'Are you Sure?'
									</div>
									<NumberDie baseDie={20} displayNumber={'1'} />
								</div>

							</div>
							}
							<div className = "combat-encounter-hero-container">
								<div className="combat-encounter-hero">
									<div className="combat-encounter-hero-name">{uiManager.getHeroFirstName(hero.name)}</div>
									<div className="combat-character-icon">
										<CImage imageId={hero.icon} />
									</div>
									{ hero.character && hero.character.injury === 'injured' && (
										<div className="combat-encounter-hero-injury"></div>
									)}
									{ hero.character && hero.character.injury === 'dead' && (
										<div className="combat-encounter-hero-injury dead"></div>
									)}
								</div>
								<div className="combat-encounter-hero-attributes">
									<AttributeDisplay hero={hero} category={ATTRIBUTE_CONTRIBUTOR.ALL} showAttributeLoss={true} />
								</div>
							</div>
						</div>
					)})}	
				</div>
			</div>
		</div>
	  );
	};

export default GameEncounterCombat;