import React from 'react';
import CImage from './cimage';
import { ChatMessage } from '../../models';
import { useUiManager } from '../managerContexts';

interface ChatMessageDisplayProps {
  chatMessage: ChatMessage;
  newMessage?: boolean;
}

const ChatMessageDisplay: React.FC<ChatMessageDisplayProps> = ({chatMessage, newMessage}) => {

  const uiManager = useUiManager();

  const message = chatMessage.message;
  const iconId = uiManager.getMessageIconId(chatMessage.speaker);
  
  let speakerName = uiManager.getMessageSpeakerName(chatMessage.speaker);


  return (
    <div className={`chat-message-display ${chatMessage.speaker.type}`}>
      <div className='chat-message-speaker-container'>
        <div className="chat-message-speaker under-icon">
            {speakerName}
          </div>
        <div className="chat-message-icon">
          <CImage imageId={iconId} />
        </div>
      </div>
      <div className = 'chat-messager-content'>
        <div className="chat-message-speaker  over-message">
          {speakerName}
        </div>
        <div className = {`chat-message-text ${chatMessage.speaker.type}`}>
          <div 
            className="chat-message-words"
            dangerouslySetInnerHTML={{ __html: message }}
          />
         
        </div>
      </div>
    </div> 
  );
}

export default ChatMessageDisplay;