import React from 'react';
import './groupSchedule.scss';
import { usePlayerManager } from '../managerContexts';
import Schedule from '../schedule';

const GroupSchedule: React.FC = () => {
    const playerManager = usePlayerManager();
    const dayData = playerManager.aggregateSchedule();

    const dayDataToSymbol = (percentage: number) => {
        if(percentage === 0) {
            return '-';
        } else 
        if (percentage < 33) {
            return 'X';
        } else if (percentage < 65) {
            return ' ';
        } else {
            return '';
        }
    }



    return (
        <div className="group-schedule-container">
            <div className="group-schedule-tab">Scheduling</div>
            <div className="week-display-days">
                {dayData.map((percentage, index) => (
                    <div key={index} className="day-box">
                        <div className="day-abbreviation">{['M', 'T', 'W', 'R', 'F', 'St', 'Sn'][index]}</div>
                        <div className="day-percentage">{dayDataToSymbol(percentage)}</div>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default GroupSchedule;