import React, { useEffect, useState, useRef } from "react";
import Chat from "../UI/chat";
import {
  useAppManager,
  useCampaignManager,
  useUiManager,
} from "../managerContexts";
import ToneTracker from "./toneTracker";
import DMCollectionsModal from "../UI/DMCollectionsModal";
import LLMConfigModal from "../UI/LLMConfigModal";
import { ChatRuleTestPanel } from "../ChatRuleTestPanel";
import { THEME_FILLED_REQ } from "../../consts/general";
import { TAG_COLLECTIONS } from "../../consts/tagCollections";
import GameToast, { GameToastData } from "../UI/GameToast";
import { Tag, FlavorTagSource, FlavorTagTheme } from "../../models";
import FillBar from "../UI/fillBar";
import NeedleGauge from "../UI/needleGauge";
import StressInfo from "./stressInfo";
import "./rightSidebar.scss";
import "./dmScreen.scss"; // Import for toast container styles

interface RightSidebarProps {}

const RightSidebar: React.FC<RightSidebarProps> = () => {
  const appManager = useAppManager();
  const campaignManager = useCampaignManager();
  const uiManager = useUiManager();
  const questManager = appManager.getQuestManager();
  const tagManager = appManager.getTagManager();
  const partyManager = appManager.getPartyManager();
  const [flavorTagThemes, setFlavorTagThemes] = useState(
    campaignManager.getCollectedTones()
  );

  // Player tension and exhaustion state
  const [partyExhaustion, setPartyExhaustion] = useState(
    partyManager.getPartyExhaustion()
  );
  const [partyTension, setPartyTension] = useState(
    partyManager.getPartyTension()
  );
  const [needleGaugeUpdate, setNeedleGaugeUpdate] = useState(0); // Force needle gauge re-render

  // State for modals
  const [showDMCollectionsModal, setShowDMCollectionsModal] = useState(false);
  const [dmModalInitialView, setDMModalInitialView] = useState<
    "collections" | "abilities"
  >("collections");
  const [showLLMConfig, setShowLLMConfig] = useState(false);
  const [showChatRuleTest, setShowChatRuleTest] = useState(false);

  // Track DM ability points for badge updates
  const [dmAbilityPoints, setDMAbilityPoints] = useState(
    campaignManager.getDMAbilityPoints()
  );

  // Toast functionality
  const [gameToasts, setGameToasts] = useState<GameToastData[]>([]);
  const processedTagsRef = useRef<Set<string>>(new Set());

  // Effects for player tension and exhaustion updates
  useEffect(() => {
    const updateGameInfo = () => {
      setPartyExhaustion(partyManager.getPartyExhaustion());
    };
    partyManager.on("dmScreenUpdated", updateGameInfo);
    return () => {
      partyManager.off("dmScreenUpdated", updateGameInfo);
    };
  }, [partyManager]);

  useEffect(() => {
    const forceUpdate = () => {
      setPartyTension(partyManager.getPartyTension());
    };
    uiManager.on("playerUpdate", forceUpdate);
    return () => {
      uiManager.off("playerUpdate", forceUpdate);
    };
  }, [uiManager, partyManager]);

  // Listen for tension descriptions updates to refresh needle gauge
  useEffect(() => {
    const updateNeedleGauge = () => {
      setNeedleGaugeUpdate((prev) => prev + 1); // Force needle gauge re-render
    };
    campaignManager.on("tensionDescriptionsUpdated", updateNeedleGauge);
    return () => {
      campaignManager.off("tensionDescriptionsUpdated", updateNeedleGauge);
    };
  }, [campaignManager]);

  // Helper function to create collection toast data
  const createCollectionToastData = (
    collection: string,
    newTag: Tag,
    isComplete: boolean,
    tagManager: any
  ): GameToastData => {
    const collectionData = TAG_COLLECTIONS.find((c) => c.name === collection);
    if (!collectionData) throw new Error(`Collection not found: ${collection}`);

    const isTagCollected = (tag: Tag): boolean =>
      tagManager
        .getCollectedTags()
        .some((collectedTag: Tag) => tag.name === collectedTag.name);

    return {
      id: `collection-${collection}-${newTag.name}`,
      type: "collection",
      title: collection,
      subtitle: isComplete ? "Collection Complete!" : "New Topic Collected",
      tags: collectionData.tags,
      newTag,
      isComplete,
      isTagCollected,
      getTagSource: (tag: Tag, isCollected: boolean) =>
        isCollected
          ? FlavorTagSource.COLLECTION_COLLECTED
          : FlavorTagSource.COLLECTION,
      getTagTheme: (tag: Tag, isCollected: boolean) =>
        isComplete ? FlavorTagTheme.GLOW : FlavorTagTheme.NONE,
    };
  };

  // Helper function to create quest toast data
  const createQuestToastData = (questData: {
    quest: any;
    newTag: any;
    completedTags: string[];
    currentNode: any;
  }): GameToastData => {
    const { quest, newTag, completedTags, currentNode } = questData;

    const isTagCollected = (tag: Tag): boolean => {
      return completedTags.includes(tag.name);
    };

    const getAllQuestTags = (): Tag[] => {
      const allTags: Tag[] = [];
      for (const child of currentNode.children) {
        allTags.push(child.tag);
      }
      return allTags;
    };

    const isQuestComplete = currentNode.children.length === 0;

    return {
      id: `quest-${quest.id}-${newTag.name}`,
      type: "quest",
      title: quest.name,
      subtitle: isQuestComplete ? "Quest Complete!" : "Quest Progress",
      tags: getAllQuestTags(),
      newTag,
      isComplete: isQuestComplete,
      isTagCollected,
      getTagSource: (tag: Tag, isCollected: boolean) => FlavorTagSource.QUEST,
      getTagTheme: (tag: Tag, isCollected: boolean) => FlavorTagTheme.NONE,
    };
  };

  const handleToastComplete = () => {
    setGameToasts((prev) => prev.slice(1));
  };
  useEffect(() => {
    const updateFlavorTags = () => {
      setFlavorTagThemes({ ...campaignManager.getCollectedTones() });
    };

    const updateDMAbilityPoints = (points: number) => {
      setDMAbilityPoints(points);
    };

    campaignManager.on("dmScreenUpdated", updateFlavorTags);
    campaignManager.on("dmAbilitiesUpdated", updateDMAbilityPoints);

    return () => {
      campaignManager.off("dmScreenUpdated", updateFlavorTags);
      campaignManager.off("dmAbilitiesUpdated", updateDMAbilityPoints);
    };
  }, [campaignManager, questManager]);

  // Collection toast handling - listen to dmScreenUpdated events
  useEffect(() => {
    const handleTagUpdate = () => {
      const currentCollectedTags = tagManager.getCollectedTags();

      const currentTagCount = currentCollectedTags.length;
      const lastKnownCount = processedTagsRef.current.size;

      console.log('RightSidebar: Collection toast check triggered', {
        currentTagCount,
        lastKnownCount,
        processedTagsList: Array.from(processedTagsRef.current)
      });

      // Only process if we actually have more tags than we've processed before
      if (currentTagCount <= lastKnownCount) {
        console.log('RightSidebar: No new tags detected, skipping toast logic');
        return;
      }

      // Find new tags that haven't been processed yet
      const newlyCollectedTags = currentCollectedTags.filter(
        (tag) => !processedTagsRef.current.has(tag.name)
      );

      console.log('RightSidebar: Newly collected tags:', newlyCollectedTags.map(t => t.name));

      if (newlyCollectedTags.length > 0) {
        // Process each new tag
        newlyCollectedTags.forEach((newTag) => {
          // Check which collections this tag contributes to
          TAG_COLLECTIONS.forEach((collection) => {
            const hasTag = collection.tags.some((t) => t.name === newTag.name);
            if (hasTag) {
              const isComplete = collection.tags.every((t) =>
                currentCollectedTags.some((ct) => ct.name === t.name)
              );

              const toastData = createCollectionToastData(
                collection.name,
                newTag,
                isComplete,
                tagManager
              );
              setGameToasts((prev) => [...prev, toastData]);
            }
          });

          // Add to processed tags set
          processedTagsRef.current.add(newTag.name);
        });
      }
    };

    // Listen to the same event that updates other DM screen elements
    campaignManager.on("dmScreenUpdated", handleTagUpdate);

    return () => {
      campaignManager.off("dmScreenUpdated", handleTagUpdate);
    };
  }, [campaignManager, tagManager]);

  // Quest toast handling
  useEffect(() => {
    const handleShowQuestToast = (questData: {
      quest: any;
      newTag: any;
      completedTags: string[];
      currentNode: any;
    }) => {
      const toastData = createQuestToastData(questData);
      setGameToasts((prev) => [...prev, toastData]);
    };

    campaignManager.on("showQuestToast", handleShowQuestToast);

    return () => {
      campaignManager.off("showQuestToast", handleShowQuestToast);
    };
  }, [campaignManager]);

  const redeemTheme = (theme: string) => {
    campaignManager.redeemTheme(theme);
  };

  const backgroundColor = campaignManager.getTensionColor(partyTension);

  return (
    <div className="right-sidebar">
      <div className="right-sidebar-content">
        {/* DM Control Panel */}
        {/* Tone Tracker */}
       
        {/* Player Tension */}
        <div className="player-tension-holder">
          <div
            className="stress-needle"
            onClick={() => partyManager.setPartyTension(partyTension + 4)}
            style={{
              borderColor: backgroundColor,
              transition: "background-color 0.3s ease",
            }}
          >
            <StressInfo stress={partyTension} />
            <NeedleGauge
              key={needleGaugeUpdate}
              value={partyTension / 10}
              max={10}
              colors={campaignManager.getTensionColors()}
              descriptions={campaignManager
                .getTensionDescriptions()
                .map((desc) => desc.description)}
              segmentRanges={campaignManager
                .getTensionRanges()
                .map((range) => range / 10)}
            />
            <div className="stress-label-containers">
              <div className="stress-labels">
                <div className="stress-label title">PARTY TENSION</div>
                <StressInfo stress={partyTension} displayNumber />
              </div>
              <div className="stress-labels bottom">
                <div className="stress-label">
                  {partyManager.getTensionDescription2(partyTension)}
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* Player Exhaustion */}
        <div className="player-row-exhaustion">
          <div className="party-exhaustion">
            <div className="party-exhaustion-label-row">
              <div className="party-exhaustion-label">0</div>
              <div className="party-exhaustion-label">EXHAUSTION</div>
              <div className="party-exhaustion-label">
                {partyManager.getMaxPartyExhaustion()}
              </div>
            </div>

            <FillBar
              totalSegments={20}
              filledSegments={partyExhaustion / 5}
              theme="exhaustion"
              label=""
            />
            <div className="party-exhaustion-label number">
              {partyExhaustion}
            </div>
          </div>
        </div>
         {Object.entries(flavorTagThemes).length > 0 && (
          <ToneTracker
            themes={flavorTagThemes}
            maxSegments={THEME_FILLED_REQ}
            onRedeem={redeemTheme}
          />
        )}
        {/* Chat - Main content area */}
        <div className="chat-container">
          <Chat />
        </div>{" "}
        <div className="dm-control-panel">
          <button
            className="dm-control-button collections-button"
            onClick={() => {
              setDMModalInitialView("collections");
              setShowDMCollectionsModal(true);
            }}
           
          >
            <span>TOPICS</span>
            {tagManager.getAvailableCollectionRewards() > 0 && (
              <div className="reward-count">
                {tagManager.getAvailableCollectionRewards()}
              </div>
            )}
          </button>

          <button
            className="dm-control-button skill-button"
            onClick={() => {
              setDMModalInitialView("abilities");
              setShowDMCollectionsModal(true);
            }}
           
          >
            <span>SKILLS</span>
            {dmAbilityPoints > 0 && (
              <div className="reward-count">{dmAbilityPoints}</div>
            )}
          </button>
             <button
            className="dm-control-button quest-button"
            onClick={() => {
              questManager.activateQuest.length && uiManager.showActiveQuestsModal();
            }}
           
          >
            <span>QUESTS</span>
            {questManager.getActiveQuests().length > 0 && (
              <div className="reward-count">{questManager.getActiveQuests().length}</div>
            )}
          </button>
        </div>
        {/* Settings Panel */}
        <div className="settings-panel">
          <button
            className="settings-button llm-button"
            onClick={() => setShowLLMConfig(true)}
            title="Configure AI-powered contextual chat responses"
          >
            <span> LLM</span>
            {appManager.getChatManager().getLLMService().isEnabled() && (
              <div className="status-indicator enabled"></div>
            )}
          </button>

          {/* <button
            className="settings-button chat-rules-button"
            onClick={() => setShowChatRuleTest(true)}
            title="Test and configure chat behavior rules"
          >
            <span> Rules</span>
          </button> */}
        </div>
      </div>

      {/* Modals */}
      {showDMCollectionsModal && (
        <DMCollectionsModal
          initialView={dmModalInitialView}
          onClose={() => setShowDMCollectionsModal(false)}
        />
      )}

      {showLLMConfig && (
        <LLMConfigModal
          isOpen={showLLMConfig}
          onClose={() => setShowLLMConfig(false)}
        />
      )}

      {showChatRuleTest && (
        <ChatRuleTestPanel onClose={() => setShowChatRuleTest(false)} />
      )}

      {/* Toast container - positioned near TOPICS button */}
      <div className="game-toasts-container">
        {gameToasts.map((toastData, index) => (
          <div key={toastData.id}>
            <GameToast data={toastData} onComplete={handleToastComplete} />
          </div>
        ))}
      </div>
    </div>
  );
};

export default RightSidebar;
