import React, { useEffect, useState, useMemo } from 'react';
import CImage from './cimage';
import './RewardFillBar.scss';
import DiceScore from './DiceScore';

interface RewardFillBarProps {
    initialCampaignScore: number; // Current campaign score before adding new points
    newScore: number; // New points being added this encounter
    onRewardClaim: (rewardIndex: number) => void;
    claimedRewards: number[]; // Array of reward indices that have been claimed
    onDMPointClaim?: (dmPointIndex: number) => void; // Callback for claiming DM points
    claimedDMPoints?: number[]; // Array of DM point indices that have been claimed
    showScore?: boolean; // Whether to show the updated score (default: true)
    onThresholdCrossed?: (threshold: number) => void; // Callback when crossing reward thresholds
}

const RewardFillBar: React.FC<RewardFillBarProps> = ({ 
    initialCampaignScore, 
    newScore,
    onRewardClaim, 
    claimedRewards = [],
    onDMPointClaim,
    claimedDMPoints = [],
    showScore = true,
    onThresholdCrossed
}) => {
    // Calculate final campaign score
    const finalCampaignScore = initialCampaignScore + newScore;
    
    // Animation state - starts at initial campaign score, animates to final
    const [animatingScore, setAnimatingScore] = useState(initialCampaignScore);
    const [flashingThresholds, setFlashingThresholds] = useState<number[]>([]);
    
    // Constants for the reward system
    const MAX_SCORE = 120;
    const D6_INTERVAL = 4;
    const REWARD_INTERVAL = 24; // D20 interval and reward threshold
    
    // Use displayed score for calculations
    const displayedScore = showScore ? finalCampaignScore : initialCampaignScore;
    
    // Calculate which reward thresholds have been reached
    const rewardThresholds = useMemo(() => [24, 48, 72, 96, 120], []);
    const availableRewards = rewardThresholds
        .map((threshold, index) => ({ threshold, index }))
        .filter(({ threshold, index }) => 
            displayedScore >= threshold && !claimedRewards.includes(index)
        );
    
    // Calculate which DM point thresholds have been reached
    const dmPointThresholds = useMemo(() => [12, 36, 60, 84, 108], []); // Every 12 points, excluding quest reward points
    const availableDMPoints = dmPointThresholds
        .map((threshold, index) => ({ threshold, index }))
        .filter(({ threshold, index }) => 
            displayedScore >= threshold && !claimedDMPoints.includes(index)
        );
    
    // Check if any reward is available but not claimed
    const hasUnclaimedReward = availableRewards.length > 0;
    const hasUnclaimedDMPoint = availableDMPoints.length > 0;
    
    // Animate score changes and detect threshold crossings
    useEffect(() => {
        if (!showScore) {
            // During scoring animation, keep at initial score
            setAnimatingScore(initialCampaignScore);
            return;
        }
        
        // Animate from initial to final campaign score
        const targetScore = finalCampaignScore;
        if (targetScore !== animatingScore) {
            // Check for threshold crossings during animation
            const allThresholds = [...rewardThresholds, ...dmPointThresholds];
            const thresholdsCrossed = allThresholds.filter(threshold => 
                animatingScore < threshold && targetScore >= threshold
            );
            
            if (thresholdsCrossed.length > 0) {
                // Flash the crossed thresholds
                setFlashingThresholds(thresholdsCrossed);
                thresholdsCrossed.forEach(threshold => {
                    onThresholdCrossed?.(threshold);
                });
                
                // Remove flash after animation
                setTimeout(() => setFlashingThresholds([]), 1500);
            }
            
            // Animate to target score with delay
            const delay = (animatingScore === initialCampaignScore && targetScore > initialCampaignScore) ? 500 : 100;
            const timer = setTimeout(() => {
                setAnimatingScore(targetScore);
            }, delay);
            return () => clearTimeout(timer);
        }
    }, [finalCampaignScore, animatingScore, showScore, initialCampaignScore, rewardThresholds, dmPointThresholds, onThresholdCrossed]);
    
    // Calculate fill percentage (0-100%)
    const fillPercentage = Math.min((animatingScore / MAX_SCORE) * 100, 100);
    const bubblePosition = fillPercentage;
    
    // Generate milestone markers
    const milestones = useMemo(() => {
        const markers = [];
        
        // Add d6 markers every 4 points
        for (let i = D6_INTERVAL; i <= MAX_SCORE; i += D6_INTERVAL) {
            if (i % REWARD_INTERVAL !== 0) { // Don't overlap with d20 markers
                // Check if this is a DM point threshold
                const dmPointIndex = dmPointThresholds.indexOf(i);
                const isDMPoint = dmPointIndex !== -1;
                const isAvailableDMPoint = isDMPoint && displayedScore >= i && !claimedDMPoints.includes(dmPointIndex);
                
                markers.push({
                    position: (i / MAX_SCORE) * 100,
                    type: 'd6' as const,
                    score: i,
                    isDMPoint: isDMPoint,
                    isAvailableDMPoint: isAvailableDMPoint
                });
            }
        }
        
        // Add d20 markers every 24 points (reward thresholds)
        for (let i = REWARD_INTERVAL; i <= MAX_SCORE; i += REWARD_INTERVAL) {
            markers.push({
                position: (i / MAX_SCORE) * 100,
                type: 'd20' as const,
                score: i
            });
        }
        
        return markers;
    }, [MAX_SCORE, D6_INTERVAL, REWARD_INTERVAL, dmPointThresholds, displayedScore, claimedDMPoints]);
    
    const handleRewardClaim = () => {
        if (hasUnclaimedReward && availableRewards.length > 0) {
            // Claim the first available reward
            const firstAvailableReward = availableRewards[0];
            onRewardClaim(firstAvailableReward.index);
        }
    };
    
    const handleDMPointClaim = () => {
        if (hasUnclaimedDMPoint && availableDMPoints.length > 0 && onDMPointClaim) {
            // Claim the first available DM point
            const firstAvailableDMPoint = availableDMPoints[0];
            onDMPointClaim(firstAvailableDMPoint.index);
        }
    };
    
    return (
        <div className="reward-fill-bar">
            <div className="reward-fill-container">
                {/* Custom progress bar */}
                <div className="reward-progress-bar">
                    <div className="progress-track">
                        <div 
                            className="progress-fill"
                            style={{ width: `${fillPercentage}%` }}
                        />
                    </div>
                    
                    {/* Milestone markers */}
                    <div className="milestone-markers">
                        {milestones.map((milestone, index) => (
                            <div
                                key={index}
                                className={`milestone-marker ${milestone.type} ${
                                    flashingThresholds.includes(milestone.score) ? 'flashing' : ''
                                } ${
                                    milestone.isDMPoint ? 'dmpoint-dice' : ''
                                } ${
                                    milestone.isAvailableDMPoint ? 'available-dmpoint' : ''
                                }`}
                                style={{ left: `${milestone.position}%` }}
                                onClick={milestone.isAvailableDMPoint ? handleDMPointClaim : undefined}
                            >
                                <CImage 
                                    imageId={milestone.type === 'd6' ? 'die_d6' : 'die_d20'} 
                                />
                            </div>
                        ))}
                    </div>
                    
                    {/* Score bubble */}
                    <div 
                        className="score-bubble"
                        style={{ left: `${bubblePosition}%` }}
                    >
                        <div className="bubble-arrow"></div>
                        <div className="bubble-content">
                            {hasUnclaimedReward ? (
                                <button 
                                    className="reward-claim-button"
                                    onClick={handleRewardClaim}
                                >
                                    QUEST
                                </button>
                            ) : hasUnclaimedDMPoint ? (
                                <button 
                                    className="dmpoint-claim-button"
                                    onClick={handleDMPointClaim}
                                >
                                    DM POINT
                                </button>
                            ) : (
                                <span className="score-text">
                                    <DiceScore
                                        score={animatingScore}
                                    />
                                </span>
                            )}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default RewardFillBar;
