import React from 'react';
import CImage from './cimage';

interface NumberDieProps {
    baseDie: number;
    displayNumber: number | string;
    onClick?: () => void;
}

const NumberDie: React.FC<NumberDieProps> = ({baseDie, displayNumber, onClick}) => {
    

    const imageId = `number_die_${baseDie}`;
   
    
    return ( 
        <div className="number-die" onClick={ onClick }>
            <CImage imageId={imageId} />
            <div className = "number-die-text">
                {displayNumber}
            </div>
      </div>
    )
};
    
export default NumberDie;
