import React, { useState } from 'react';
import NumberDie from '../UI/numderDie';
import { CombatState, Tag } from '../../models';


interface AddCombatantsProps {
    combatantCallback: (side: string, combatant :Tag, index: number) => void;
    side:'heroes' | 'enemies';
    combatState: CombatState;
  }
  
  const AddCombatants: React.FC<AddCombatantsProps> = ({ combatantCallback, side, combatState }) => {
    
    const onFriendlyTagClick = (index: number) => {
      combatantCallback(side, combatState.friendlyTags[index], index);
    }

    const onHostileTagClick = (index: number) => {
      combatantCallback(side, combatState.hostileTags[index], index);
    }

    const getCombatantTags = () => {


     if(side === 'heroes' && combatState.friendlyTags.length <= 0) {
      return (
        <div className="combatant-tags">
          <div className="none-available">No valid established NPC Races available.</div>
        </div>
      )
     } else if(side === 'enemies' &&  combatState.friendlyTags.length <= 0 && combatState.hostileTags.length <= 0) {
      return (
        <div className="combatant-tags">
          <div className="none-available">No valid established Enemy Races available.</div>
        </div>
      )
    }



      return (
        <div className={`combatant-tags ${side}`}>
         {combatState.friendlyTags.map((tag, index) => {
          return (
            <div key={index} className={`text-pill combatant ${side}`} onClick={()=>onFriendlyTagClick(index)}>
              {tag.name}
            </div>
          )})}
           {side === "enemies" && combatState.hostileTags.map((tag, index) => {
          return (
            <div key={index} className={`text-pill combatant ${side}`} onClick={()=>onHostileTagClick(index)}>
              {tag.name}
            </div>
          )})}
        </div>
      )
    }

    return (
      <div className={`combat-combatants ${side}`}>
        <div className="combat-combatants-label">
          {side === 'heroes' ? 'Add allied Combatants' : 'Swap Enemies'} 
        </div>
        <div className = "combat-combatants-row">
          <div className="combat-combatants-tags">
            {getCombatantTags()}
          </div>
        </div>
       
      </div>
    )
  };

  export default AddCombatants;