import EventEmitter from 'events';
import { ATTRIBUTE_TYPE, Character, CHAT_TYPE, CombatState, Encounter, EnemyAttribute, EnemyAttributeInstance, HeroCombatant, PERIL_INDEX, Tag } from '../models';
import AppManager from './appManager';
import CampaignManager from './campaignManager';
import CharacterManager from './characterManager';
import PlayerManager from './playerManager';
import UiManager from './uiManager';
import { ENEMIES } from '../consts/enemies';
import ChatManager from './chatManager';
import PartyManager from './partyManager';
import { getCombinedAttributes } from '../consts/characterConsts';

const defaultStats: EnemyAttribute = {
  health: [1],
  attack: [1],
  defense: [1],
  magic_defense: [1],
}


class CombatManager extends EventEmitter {

  
  private appManager: AppManager | undefined;
  private characterManager: CharacterManager | undefined;
  private campaignManager: CampaignManager | undefined;
  private playerManager: PlayerManager | undefined;
  private uiManager: UiManager | undefined;
  private chatManager: ChatManager | undefined;

  private combatState: CombatState | undefined;
  private partyManager: PartyManager | undefined;

  constructor(appManager: AppManager) {
    super();
    setTimeout(() => {
      this.appManager = appManager;
      this.characterManager = appManager.getCharacterManager();
      this.campaignManager = appManager.getCampaignManager();
      this.playerManager = appManager.getPlayerManager();
      this.uiManager = appManager.getUiManager();
      this.chatManager = appManager.getChatManager();
      this.partyManager = appManager.getPartyManager();
    }, 0);

   
    
  }

  getCombatState() {
    return this.combatState;
  }

  generateCombatRewards() {
    
    if(!this.combatState) {
      return;
    }
    
    switch(this.combatState.nbEnemies) {
      case 1:
        this.combatState.rewards = 1;
        break;
      case 2:
        this.combatState.rewards =  3;
        break;
      case 3:
        this.combatState.rewards =  5;
        break;
      default:
        this.combatState.rewards =  1;
        break;
    }

  }

  
 getCombatMessage = () => {
	if(!this.combatState) return;

  switch(this.combatState.round.phase) {
    case 'balance':
      return  'Balance the combat by changing the number or type of enemies and/or add NPCs.';
    case 'setup':
      if(this.combatState.barAnimating) {
        return `The players and ${this.combatState.enemyType.name} ready themselves themselves and we see how dangerous this round will be... `;
      }
      return 'Balance the fight by using established elements of the environment to make the combat more or less dangerous. Or simply fudge the dice...';
    case 'heroes_attack':
     return 'done in component'
    case 'heroes_resolve':
      return 'Now the enemies will attack! This will determine the chance of injury to the party.';
    case 'enemies_attack':
      if(this.combatState.barAnimating) {
        return `Determining the danger and excitement of this round...`;
      }
      return `There is a ${this.combatState.perilResult[PERIL_INDEX.DANGER]}% chance of a party injury.`;
   
    case 'enemy_resolve_damage':
      return this.combatState.combatMessage;
    
    default:
      return '';
  }
}


  generateCombatState(encounter: Encounter, combatIndex: number): CombatState | undefined {
    
    if(!encounter.combat || !encounter.combat[combatIndex]){
      return;
    }

    if(this.combatState) {
      return this.combatState;
    } 

    const combat = encounter.combat[combatIndex];
    const enemies = combat.enemy;
    const nbEnemies = combat.nbEnemies;

    const newCombatState: CombatState = {
      round: {
       number: 0,
       phase: 'balance'
      },
      attacker: "heroes",
      currentCharacterIndex: 0,
      currentEnemyIndex: 0,
      nbEnemies: nbEnemies,
      enemyType: enemies,
      enemyStats: ENEMIES[enemies.name] || {...defaultStats},
      riskCenter: 4,
      usedTags: this.appManager?.getTagManager().locationTagsCollected.map(() => false) || [],
      hostileTags:this.campaignManager?.getHostileCombatantTags(enemies.name) || [],
      friendlyTags:this.campaignManager?.getFriendlyCombatantTags() || [],
      npcs: [],
      rewards: 1,
      perilResult: [0,0,0,0],
      heroAttacks: {},
      enemyStatsInstance:  {
        health: 1,
        attack: 1,
        defense: 1,
        magic_defense: 1,
      }
     // usedFriendlyTags:Array(this.campaignManager?.getFriendlyCombatantTags().length).fill(false),
     // usedHostileTags:Array(this.campaignManager?.getHostileCombatantTags().length).fill(false),
    };
   

    this.updateCombatState(newCombatState);
    this.generateCombatRewards();
    return newCombatState;
  }

  static GetDefaultCombatState(): CombatState {
    return {
      round: {
        number: 0,
        phase: 'balance'
      },
      attacker: "heroes",
      currentCharacterIndex: 0,
      currentEnemyIndex: 0,
      nbEnemies: 1,
      enemyType: {name: 'none'},
      enemyStats: {...defaultStats},
      riskCenter: 5,
      usedTags: [],
      hostileTags:[],
      friendlyTags:[],
      npcs:[],
      perilResult: [0,0,0,0],
      heroAttacks: {},
      rewards: 1,
      enemyStatsInstance:  {
        health: 1,
        attack: 1,
        defense: 1,
        magic_defense: 1,
      }
     // usedFriendlyTags:[],
     // usedHostileTags:[],
    }
  
  }

  modifyCombatRangeCenter(value: number) {
    
    if(!this.combatState) {
      return;
    }
    this.combatState.riskCenter = value;
    this.emit('combatStateChange', this.combatState);
  }

  updateCombatState(combatState: CombatState) {
    this.combatState = combatState;
    this.emit('combatStateChange', this.combatState);
  }

  applyPerilResultsToParty() {
    
    if(!this.combatState) {
      return;
    }
    const stress = this.combatState.perilResult[PERIL_INDEX.STRESS];
    //const engagement = this.combatState.perilResult[PERIL_INDEX.ENGAGEMENT];
   // const score = this.combatState.perilResult[PERIL_INDEX.SCORE];
    this.partyManager?.addPartyTension(stress);
  //  this.partyManager?.addPartyScore(score);

  
  }

  setCombatBarAnimating(isAnimating: boolean) {
    if(!this.combatState) { 
      return;
    }
    this.combatState.barAnimating = isAnimating;
    this.emit('combatStateChange',  this.combatState);
  }

   advanceRound() {
    
    if(!this.combatState) {
      return;
    }
    switch(this.combatState.round.phase) {
        case 'balance':
          this.generateEnemyAttributeInstance();
          this.combatState.round.phase = 'setup';
          this.generateHeroAttacks();
          this.chatManager?.combatIntroMessage(this.combatState.enemyType);
          break;
        case 'setup':
          
          this.combatState.round.phase = 'heroes_attack';
          
         // this.campaignManager?.exhaustParty(this.getAttackActionCost());
          
          
          this.combatState.round.number++;

          break;  
        case 'heroes_attack':
                    
          if(this.combatState.enemyStatsInstance.health <= 0) {
           // this.partyManager?.addPartyScore(this.combatState.perilResult[PERIL_INDEX.SCORE]);
            this.uiManager?.showRewardModal(this.combatState.rewards || 0, true);
          }else {
            this.combatState.round.phase = 'heroes_resolve';
          }
          break;
        case 'heroes_resolve':
        
          this.combatState.round.phase = 'enemies_attack';
         
          break;

				case 'enemies_attack':
        //  this.applyPerilResultsToParty();
          this.perilCheck();
          this.combatState.round.phase = 'enemy_resolve_damage';
          this.applyPerilResultsToParty();
          break;

        case 'enemy_resolve_damage':
          this.combatState.round.phase = 'setup';
          this.generateHeroAttacks();
          this.partyManager?.exhaustParty(this.getAttackActionCost());
          break;

				default: 
          console.log("Unknown phase");
		}

    console.log("Changing phase to "  + this.combatState.round.phase);
    this.emit('combatPhaseChange', this.combatState);
    this.emit('combatStateChange', this.combatState);
   }


   perilCheck() {
    const chanceOfPeril = this.combatState?.perilResult;
    if(!chanceOfPeril) return;
    const randomRoll = Math.floor(Math.random() * 100);
    if(randomRoll < chanceOfPeril[PERIL_INDEX.DANGER]) {
      this.activatePeril();
    } else{
      this.chatManager?.dmMessage(`You guys are safe this round.`);
    }

    if(this.combatState)
      this.combatState.combatMessage = `${chanceOfPeril[PERIL_INDEX.DANGER]}% chance of injury and rolled a ${randomRoll}.`;

   }


   activatePeril() {
    if(this.combatState === undefined) {
      return;
    }
    let combatants = this.partyManager?.generateHeroes(this.combatState.npcs || []);
    if(combatants === undefined) {
      return;
    }
    let injuredIndex = Math.floor(Math.random() * combatants.length);

    if(injuredIndex < this.combatState.npcs.length) {
      this.combatState.npcs.splice(injuredIndex, 1);
      this.chatManager?.dmMessage(`The ${combatants[injuredIndex].name} were frigtened off!`);
    } else {
      let injury = ''
      if(combatants[injuredIndex].character) {
        injury = this.partyManager?.addInjury(combatants[injuredIndex].character) || 'unknown injury';
      }

      this.chatManager?.dmMessage(`Oh no! ${combatants[injuredIndex].name} is ${injury}`);
    }
   }



    generateEnemyAttributeInstance() {
      const attributeIndex = (this.getCombatState()?.nbEnemies || 1) - 1;
      const nbEnemies = this.getCombatState()?.nbEnemies || 1;
      const enemyStats = this.getCombatState()?.enemyStats || {...defaultStats};
      const enemyAttributes: EnemyAttributeInstance = {
              health: enemyStats.health[attributeIndex] || enemyStats.health[0] * nbEnemies,
              attack: enemyStats.attack[attributeIndex] || enemyStats.attack[0] * nbEnemies,
              defense: enemyStats.defense[attributeIndex] || enemyStats.defense[0] * nbEnemies,
              magic_defense: enemyStats.magic_defense[attributeIndex] || enemyStats.magic_defense[0] * nbEnemies,
            };
      if(this.combatState) {
        this.combatState.enemyStatsInstance = enemyAttributes;
      }
    }
  
    generateHeroAttacks() {
      if(!this.combatState) {
        return;
      } 
      const heroes = this.partyManager?.generateHeroes(this.combatState?.npcs || []);
      heroes?.forEach((hero) => {
        if(!this.combatState) {
          return;
        } 
        this.combatState.heroAttacks[hero.name] = this.generateAttack(hero);
      });
  }

  heroRethinkAttack(hero: HeroCombatant) { 
    if(!this.combatState) {
      return;
    }
    this.campaignManager?.spendDMPoints(1); 
    let currentAttack = this.combatState.heroAttacks[hero.name];

    let maxRetries = 10;
    while(currentAttack === this.combatState.heroAttacks[hero.name] && maxRetries-- > 0) {
      this.combatState.heroAttacks[hero.name] = this.generateAttack(hero);
    }

    let chatMessage = `${hero.name} should really think of a different tactic.`;
    switch(Math.floor(Math.random() * 3)) {
      case 0:
       chatMessage = `Do you really want ${hero.name} to do that?`;
        break;
      case 1:
        chatMessage = `That's stupid.`;
        break;
      case 2:
        chatMessage = `You know ${hero.name} has better options, right?`;
        break;
    }

    this.chatManager?.dmMessage(chatMessage);

    this.emit('combatStateChange', this.combatState);
  }

  getHeroAttackDecisionText(hero: HeroCombatant) {
    const attackType = this.combatState?.heroAttacks[hero.name];
    if(!attackType) {
      return 'vxvxcvx';
    }
    switch(attackType) {
      case 'combat':
        return `${hero.name} attacks with their weapon!`;
      case 'tools':
        return `${hero.name} uses their tools!`;
      case 'magic':
        return `${hero.name} casts a spell!`;
      default:
        return 'vxvxcvxvxvxc';
    }
  }

  generateAttack(hero: HeroCombatant) {

    let allAbilities: Record<string, number> = {};
    if(hero.npcTag) {
      hero.character = this.characterManager?.generateNPC(hero.npcTag);
      allAbilities = this.getNPCAttributes(hero.npcTag);
    } 
    else{
      allAbilities = getCombinedAttributes(hero.character as Character, true);
    } 

   
        if(allAbilities[ATTRIBUTE_TYPE.MAGIC] && allAbilities[ATTRIBUTE_TYPE.MAGIC] > 0) {
          if(Math.random() > .666) 
            return 'magic'
        }
        if(allAbilities[ATTRIBUTE_TYPE.TOOLS] && allAbilities[ATTRIBUTE_TYPE.TOOLS] > 0) {
          if(Math.random() > .5) 
            return 'tools'
        }
        return 'combat';
  }

  getNPCAttributes(npcTag: Tag) {   
    let enemyStats = ENEMIES[npcTag.name];
    return { 
      [ATTRIBUTE_TYPE.COMBAT]: Math.max(1, enemyStats.attack[0]), 
      [ATTRIBUTE_TYPE.MAGIC]: enemyStats.magic_defense[0],
      [ATTRIBUTE_TYPE.TOOLS]: enemyStats.defense[0]
      }
  }

  heroAttack(hero: HeroCombatant) {
    if(!this.combatState) {
      return;
    }
  
   
   

    let player;
    if(hero.character) {
      player = this.playerManager?.getPlayerAssignedCharacter(hero.character);
      if(player){
        this.partyManager?.playersTakeAction([player.name], false);
        this.partyManager?.stressAction([player]);
        this.chatManager?.playerMessage(this.getHeroAttackDecisionText(hero), player.id );
      }
    }
    let abilities = getCombinedAttributes(hero.character as Character, true);

    if(hero.npcTag){
      abilities = this.getNPCAttributes(hero.npcTag);
    }


    const fullAbilities  = getCombinedAttributes(hero.character as Character);
    let actualDamage = 0;
    let damageType = 'combat';
    let nbIcons = 0;
    let target = '';
    let crit = false;
    switch(this.combatState.heroAttacks[hero.name]) {
      case 'combat':
        let damage = Math.floor(Math.random() * (abilities[ATTRIBUTE_TYPE.COMBAT] || 0)) + 1;
        if(this.combatState.enemyStatsInstance.defense > 0){
          target = 'Defense';
          this.combatState.enemyStatsInstance.defense = Math.max(0, this.combatState.enemyStatsInstance.defense - damage);
        } else if (this.combatState.enemyStatsInstance.magic_defense > 0) {
          target = 'Magic Defense';
          damage = 1;
          this.combatState.enemyStatsInstance.magic_defense = Math.max(0, this.combatState.enemyStatsInstance.magic_defense - 1);
        } else {
          target = 'Health';
          this.combatState.enemyStatsInstance.health = Math.max(0, this.combatState.enemyStatsInstance.health - damage);
        }
        nbIcons = damage;
        actualDamage = damage;
        damageType = 'combat';
        break;
      case 'tools':
        let toolsDamage = 1;
        if(Math.random() < .25) {
          toolsDamage = fullAbilities[ATTRIBUTE_TYPE.TOOLS];
          crit = true;
        }
        if(this.combatState.enemyStatsInstance.defense > 0){
          target = 'Defense';
          this.combatState.enemyStatsInstance.defense = Math.max(0, this.combatState.enemyStatsInstance.defense - toolsDamage);
        } else if (this.combatState.enemyStatsInstance.magic_defense > 0) {
          target = 'Magic Defense';
          this.combatState.enemyStatsInstance.magic_defense = Math.max(0, this.combatState.enemyStatsInstance.magic_defense - toolsDamage);
        } else {
          target = 'Health';
          this.combatState.enemyStatsInstance.health = Math.max(0, this.combatState.enemyStatsInstance.health - toolsDamage);
        }
        this.characterManager?.setAttributeUsed(hero.character as Character, ATTRIBUTE_TYPE.TOOLS, 1);
        actualDamage = toolsDamage;
        damageType = 'tools';
        nbIcons = toolsDamage;
        break;
      case 'magic':
        const nbSpells = Math.min(abilities[ATTRIBUTE_TYPE.MAGIC] || 1,  hero.character?.level || 1);
        const spellDamage = nbSpells * (Math.floor(Math.random() * 3) + 2);
       if (this.combatState.enemyStatsInstance.magic_defense > 0) {
        target = 'Magic Defense';
          this.combatState.enemyStatsInstance.magic_defense = Math.max(0, this.combatState.enemyStatsInstance.magic_defense - spellDamage);
        } else  if(this.combatState.enemyStatsInstance.defense > 0){
          target = 'Defense';
          this.combatState.enemyStatsInstance.magic_defense = Math.max(0, this.combatState.enemyStatsInstance.defense - spellDamage);
        } else {
          target = 'Health';
          this.combatState.enemyStatsInstance.health = Math.max(0, this.combatState.enemyStatsInstance.health - spellDamage);
        }
        this.characterManager?.setAttributeUsed(hero.character as Character, ATTRIBUTE_TYPE.MAGIC, nbSpells);
        actualDamage = spellDamage;
        damageType = 'magic';
        nbIcons = nbSpells;
        break;
      
      default:
        console.log('Unknown attack type');
    }

    let icons ='';
    switch(damageType) {
      case 'combat':
        icons = ''.repeat(nbIcons);
        break;
      case 'tools':
        icons = ''.repeat(nbIcons);
        break;
      case 'magic':
        icons = ''.repeat(nbIcons);
        break;
    }
    
    let attackString = `${crit === true ? 'Crit! ' : ''}  ${icons} = ${actualDamage} damage ${target}.`;
    this.emit('combatStateChange', this.combatState);
    return attackString;
  }

  getAttackActionCost() {
    if(!this.combatState)
        return 0;
    
    return (this.combatState.round.number) * Object.keys(this.combatState.heroAttacks).length;
  }

  reduceEnemies() {
    if(this.combatState) {
      if(this.combatState.nbEnemies > 1) {
        this.combatState.nbEnemies--;
        this.campaignManager?.spendDMPoints(1);
        this.generateCombatRewards();
        this.emit('combatStateChange', this.combatState);
      }
    }
  }

  increaseEnemies() {
    if(this.combatState) {
      if(this.combatState.nbEnemies < 3) {
        this.combatState.nbEnemies++;
        this.campaignManager?.spendDMPoints(1);
        this.generateCombatRewards();
        this.emit('combatStateChange', this.combatState);
      }
    }
  }

  combatModifiersCallback (modifier: number, side: 'heroes' | 'enemies', flavor: Tag, index:number) {
    if(!this.combatState) {
      return;
    }

    if(side === 'heroes') {
        this.combatState.riskCenter -= modifier;
    } else {
        this.combatState.riskCenter += modifier;
    }
    if(flavor.name !== 'DM') {
        this.combatState.usedTags[index] = !this.combatState.usedTags[index];
    }

    this.emit('combatStateChange', this.combatState);

    this.chatManager?.sayCombatModifiedMessage(side, flavor, this.combatState.enemyType, modifier);
}

    combatantCallback(side: string, clickedCombatant: Tag, index: number) {
      
      if(!this.combatState) {
        return;
      }

      const isCombatantFriendly = clickedCombatant.friendly;  
      // Remove clickedCombatant from available lists
      if(isCombatantFriendly) {
         this.combatState.friendlyTags.splice(index, 1);
      } else {
        this.combatState.hostileTags.splice(index, 1);
      }
      // add existing enemy back to available lists depending on friendly or not. 
      if(side === 'enemies') {
        if(this.combatState.enemyType.friendly) {
          this.combatState.friendlyTags.push(this.combatState.enemyType);
        } else {
          this.combatState.hostileTags.push(this.combatState.enemyType);
        }
        this.setEnemyType(clickedCombatant);
      } else {
        // Otherwise this is being added to the NPC list.
        this.combatState.npcs.push(clickedCombatant); 
       
      }
      this.generateCombatRewards();
      this.emit('combatStateChange', this.combatState);
    }

    setEnemyType(enemy: Tag) {
      if(!this.combatState) {
        return;
      }
      this.combatState.enemyType = enemy;
      this.combatState.enemyStats = ENEMIES[enemy.name] || {...defaultStats};
      this.emit('combatStateChange', this.combatState);
    }


    setPerilResult(result: number[]) {
      
      if(!this.combatState) {
        return;
      }
      this.combatState.perilResult = result;
      this.emit('combatStateChange', this.combatState);
    }


  // perceptionEventReveal(character: Character) {
  //   // We need to bump the player's engagement, and decreae all other players engamgement
  //   const players = this.playerManager?.getPlayers();
  //   players?.forEach((player) => {
  //     if(player.characters.includes(character.id)) {
  //       player.playerStats.engagement += 1;
  //     } else {
  //       player.playerStats.engagement -= 1;
  //     }
  //   });
  //   this.uiManager?.updateParty();
  // }

   generateColorFromValue(value: number, min: number, max: number, label: string ) {
    // Normalize value to 0-1 range
    const normalized = (value - min) / (max - min);
    
    // Define color stops
    let colors =  {
        negative: { r: 255, g: 61, b: 61 },  
        neutral: { r: 255, g: 139, b: 105 }, 
        positive: { r: 255, g: 0, b: 0 }  
    }
    
    switch(label) {
        case 'danger':
            colors = {
                negative: { r: 255, g: 255, b: 255 }, 
                neutral: { r: 179, g: 139, b: 105 }, 
                positive: { r: 200, g: 25, b: 20 }   
            }
            break;
        case 'stress':
            colors = {
                negative: { r: 255, g: 255, b: 255 },  
                neutral: { r: 130, g: 130, b: 130 }, 
                positive: { r: 0, g: 0, b: 0 }   
            }
            break;
        case 'engagement':
            colors = {
                negative: { r: 139, g: 61, b: 61 }, 
                neutral: { r: 179, g: 139, b: 105 }, 
                positive: { r: 92, g: 148, b: 87 }   
            }
            break
            case 'reward':
                colors = {
                  negative: { r: 255, g: 255, b: 255 }, 
                  neutral: { r: 255, g: 255, b: 255 }, 
                  positive: { r: 255, g: 215, b: 0 }   // Gold color
                }
              break
    
    }
    
  
    let result;
    if (value < 0) {
      // Interpolate between negative and neutral
      const t = (value - min) / (0 - min);
      result = {
        r: Math.round(colors.negative.r + (colors.neutral.r - colors.negative.r) * t),
        g: Math.round(colors.negative.g + (colors.neutral.g - colors.negative.g) * t),
        b: Math.round(colors.negative.b + (colors.neutral.b - colors.negative.b) * t)
      };
    } else {
      // Interpolate between neutral and positive
      const t = value / max;
      result = {
        r: Math.round(colors.neutral.r + (colors.positive.r - colors.neutral.r) * t),
        g: Math.round(colors.neutral.g + (colors.positive.g - colors.neutral.g) * t),
        b: Math.round(colors.neutral.b + (colors.positive.b - colors.neutral.b) * t)
      };
    }
    
    return `#${result.r.toString(16).padStart(2,'0')}${result.g.toString(16).padStart(2,'0')}${result.b.toString(16).padStart(2,'0')}`;
  };

}

export default CombatManager;