import { useEffect, useState } from "react";
import { Encounter, EncounterEvent } from "../../models";
import { useCampaignManager, useUiManager } from "../managerContexts";
import EncounterEventDisplay from "./encounterEventDisplay";


interface GameEncounterOverviewProps {
    encounter: Encounter;
}


const GameEncounterOverview: React.FC<GameEncounterOverviewProps> = ({encounter}) => {

  const [events, setEvents] = useState<EncounterEvent[]>(encounter.events);
  const [update, setUpdate] = useState(false);
  const campaignManager = useCampaignManager();
  const uiManager = useUiManager();


 

  const renderEventColumn = () => {

    return events.map((event, eventIndex) => (
          <EncounterEventDisplay
            key={`${eventIndex}`}
            event={event}
          />
        ))}
      

  return (
        <div className='table-encounter-divisions'>
          <div className="event-column open">
            {renderEventColumn()}
            <div className="event-column-label">
               
            </div>
          </div>
         
        </div>
  );
};

export default GameEncounterOverview;