import { Tag, ATTITUDE } from "../models";

export interface ChatRule {
  id: string;
  name: string;
  description: string;
  condition: ChatRuleCondition;
  action: ChatRuleAction;
  priority: number; // Higher number = higher priority
  enabled: boolean;
}

export interface ChatRuleCondition {
  type: 'always' | 'chance' | 'attitude' | 'theme' | 'mood' | 'context' | 'speaker_type' | 'tag_type';
  value?: any; // Specific value to check against
  chance?: number; // For chance-based rules (0-100)
  operator?: 'equals' | 'contains' | 'not_equals' | 'greater_than' | 'less_than';
}

export interface ChatRuleAction {
  type: 'modify_prompt' | 'set_speaker_preference' | 'add_instruction' | 'set_style';
  instruction: string; // The instruction to add to the prompt
  weight?: number; // How strongly to apply this rule (0-1)
}

