export class ChatRuleEngine {
  private rules: ChatRule[] = [];
  private debugMode: boolean = true; // Set to true to see which rules are applied

  constructor(rules: ChatRule[] = []){//DEFAULT_CHAT_RULES) {
    this.rules = [...rules];
  }

  // Enable/disable debug mode
  setDebugMode(enabled: boolean): void {
    this.debugMode = enabled;
  }

  isDebugMode(): boolean {
    return this.debugMode;
  }

}
