import React from 'react';

interface InstructionsPanelProps {
  instructions: string[];
}

const InstructionsPanel: React.FC<InstructionsPanelProps> = ({ instructions }) => {
  return (
    <div className="encounter-instructions">
      <div className="tooltip-help-icon">
        <span className="tooltip-help-icon-text">i</span>
      </div>
      {instructions.map((instruction, index) => (
        <div key={`instruction-${index}`} className="encounter-instructions-text">
          {instruction}
        </div>
      ))}
    </div>
  );
};

export default InstructionsPanel;
