import React, { useState, useEffect, useCallback } from "react";
import {
  Character,
  ATTRIBUTE_TYPE,
  Tag,
  FlavorTagSource,
  FlavorTagTheme,
  Challenge,
} from "../../models";
import {
  useAppManager,
  useCampaignManager,
  useCharacterManager,
} from "../managerContexts";
import { getCombinedAttributes } from "../../consts/characterConsts";
import FillBar from "../UI/fillBar";
import CImage from "../UI/cimage";
import CContainer from "../UI/CContainer";
import "./enhancedSkillChallenge.scss";
import TagPill from "../UI/TagPill";
import FlavorTagPill from "../UI/FlavorTagPill";
import DiceScore from "../UI/DiceScore";
import { render } from "@testing-library/react";

// Animation timing constants
const ANIMATION_TIMINGS = {
  SELECTION_DURATION: 2500, // Total time for spot selection animation
  CYCLE_SPEED: 100, // Time between highlight changes during cycling
  FINAL_PULSE_DURATION: 800, // Time the final selected spot pulses before resolution
  SPOT_HIGHLIGHT_SCALE: 1.15, // Scale factor for highlighted spots during cycling
} as const;

// Enhanced types for the new skill challenge system
export enum CSPOT_TYPE {
  USE_ATTRIBUTE_COMBAT = "use_attribute_combat",
  USE_ATTRIBUTE_MAGIC = "use_attribute_magic",
  USE_ATTRIBUTE_TOOLS = "use_attribute_tools",
  UNLOCK_ASSIST = "free_spot", // Special spots with no attribute cost
}

export interface CSpot {
  id: string;
  type: CSPOT_TYPE;
  position: number;
  used: boolean;
  icon: string;
  characterId?: string; // ID of character who used this spot
  scorePoints?: number; // Optional dice score points (1-3)
  challengePoints?: number; // Challenge difficulty (1-3 circles)
}

export interface ChallengeRow {
  id: string;
  theme: "COMBAT" | "GEAR" | "MAGIC";
  name: string;
  description: string;
  cspots: CSpot[];
  selected: boolean;
  animatingSelection?: boolean;
}

export interface CharacterPosition {
  characterId: string;
  rowIndex: number;
  position: number;
}

interface SkillTotals {
  combat: number;
  magic: number;
  gear: number;
}

interface SkillThresholds {
  combat: number;
  magic: number;
  gear: number;
}

interface RewardLevel {
  id: string;
  name: string;
  description: string;
  tensionChange: number;
  attentionChange: number;
  scoreBonus: number;
}

interface AssistTag {
  id: string;
  tag: Tag;
  state: "AVAILABLE" | "ACTIVE" | "USED" | "UNAVAILABLE";
  isLocationTag: boolean;
}

interface EnhancedSkillChallengeProps {
  challengeLength?: number; // Number of spots in each challenge row (12-20)
  authoredTag?: Tag; // Main tag associated with this challenge
  skillThresholds?: SkillThresholds; // Thresholds for combat/magic/gear (default 5 each)
  onComplete?: () => void;
  authoredAssistTags?: Tag[]; // Tags that can assist in the challenge
  generateRandomAssistTags?: boolean; // If true, will generate random assist tags
  config? : any; // Additional configuration object
}

const EnhancedSkillChallenge: React.FC<EnhancedSkillChallengeProps> = ({
  challengeLength = 10,
  authoredTag,
  skillThresholds = { combat: 5, magic: 5, gear: 5 },
  onComplete,
  authoredAssistTags = [],
  generateRandomAssistTags = false,
  config,
}) => {
  const appManager = useAppManager();
  const campaignManager = useCampaignManager();
  const characterManager = useCharacterManager();
  const tagManager = appManager.getTagManager();
  const chatManager = appManager.getChatManager();

  // Core state
  const [characters] = useState<Character[]>(characterManager.getCharacters());
  const [selectedCharacter, setSelectedCharacter] = useState<Character | null>(
    null
  );
  const [challengeRows, setChallengeRows] = useState<ChallengeRow[]>([]);
  const [characterPositions, setCharacterPositions] = useState<
    CharacterPosition[]
  >(
    characters.map((char) => ({
      characterId: String(char.id),
      rowIndex: -1,
      position: -1,
    }))
  );
  const successTag =
    authoredTag ||
    tagManager.getOutstandingEncounterTags()[0] ||
    tagManager.generateRelevantFlavorTags(1)[0]?.tag;

  // Enhanced tracking state
  const [skillTotals, setSkillTotals] = useState<SkillTotals>({
    combat: 0,
    magic: 0,
    gear: 0,
  });
  const [assistTags, setAssistTags] = useState<AssistTag[]>([]);
  const [currentRewardLevel, setCurrentRewardLevel] = useState<number>(0);
  const [movesCompleted, setMovesCompleted] = useState<number>(0);
  const [challengeComplete, setChallengeComplete] = useState<boolean>(false);
  const [assistBonus, setAssistBonus] = useState<number>(0); // Track number of assist tags used

  // Check if we're in failure state (no skills completed)
  const isFailureState = () => {
    return skillsCompleted() === 0;
  };

  // UI state
  const [highlightedSpot, setHighlightedSpot] = useState<{
    rowIndex: number;
    spotIndex: number;
  } | null>(null);
  const [finalSelectedSpot, setFinalSelectedSpot] = useState<{
    rowIndex: number;
    spotIndex: number;
  } | null>(null);

  // Reward levels configuration
  const rewardLevels: RewardLevel[] = [
    {
      id: "failure",
      name: "Failure",
      description: "-50 Tension, -1 Attention",
      tensionChange: -50,
      attentionChange: -1,
      scoreBonus: 0,
    },
    {
      id: "simple",
      name: "Simple",
      description: "-5 Tension",
      tensionChange: -5,
      attentionChange: 0,
      scoreBonus: 1,
    },
    {
      id: "challenging",
      name: "Challenging",
      description: "+5 Tension",
      tensionChange: 5,
      attentionChange: 0,
      scoreBonus: 2,
    },
    {
      id: "hard",
      name: "Hard",
      description: "+15 Tension, +1 Attention",
      tensionChange: 15,
      attentionChange: 1,
      scoreBonus: 3,
    },
  ];

  // Helper functions
  const getIconForSpotType = (type: CSPOT_TYPE, points: number = 1): string => {
    // Return circle indicators instead of emoji icons
    const circle = "";
    return Array(points).fill(circle).join("");
  };

  const getIconForTheme = (theme: "COMBAT" | "GEAR" | "MAGIC"): string => {
    switch (theme) {
      case "COMBAT":
        return "";
      case "MAGIC":
        return "";
      case "GEAR":
        return "";
      default:
        return "";
    }
  };

  const getThemeColorClass = (type: CSPOT_TYPE): string => {
    switch (type) {
      case CSPOT_TYPE.USE_ATTRIBUTE_COMBAT:
        return "theme-combat";
      case CSPOT_TYPE.USE_ATTRIBUTE_MAGIC:
        return "theme-magic";
      case CSPOT_TYPE.USE_ATTRIBUTE_TOOLS:
        return "theme-gear";
      case CSPOT_TYPE.UNLOCK_ASSIST:
        return "theme-neutral";
      default:
        return "theme-neutral";
    }
  };

  // Generate dice pip pattern for challenge spots
  const renderDicePips = (
    points: number,
    includeAssistBonus: boolean = true
  ) => {
    const appliedAssistBonus = includeAssistBonus ? assistBonus : 0;
    const totalPoints = points + appliedAssistBonus;
    const clampedPoints = Math.min(Math.max(totalPoints, 1), 6); // Clamp between 1-6 for dice patterns

    const pipPositions = {
      1: ["center"],
      2: ["top-left", "bottom-right"],
      3: ["top-left", "center", "bottom-right"],
      4: ["top-left", "top-right", "bottom-left", "bottom-right"],
      5: ["top-left", "top-right", "center", "bottom-left", "bottom-right"],
      6: [
        "top-left",
        "top-right",
        "middle-left",
        "middle-right",
        "bottom-left",
        "bottom-right",
      ],
    };

    const positions = pipPositions[
      clampedPoints as keyof typeof pipPositions
    ] || ["center"];

    return (
      <div className="dice-face">
        {positions.map((position, index) => {
          const isAssistBonus = index >= points && appliedAssistBonus > 0;
          return (
            <div
              key={index}
              className={`pip ${position} ${
                isAssistBonus ? "assist-bonus" : ""
              }`}
            />
          );
        })}
      </div>
    );
  };

  const getAttributeForSpotType = (type: CSPOT_TYPE): ATTRIBUTE_TYPE => {
    switch (type) {
      case CSPOT_TYPE.USE_ATTRIBUTE_COMBAT:
        return ATTRIBUTE_TYPE.COMBAT;
      case CSPOT_TYPE.USE_ATTRIBUTE_MAGIC:
        return ATTRIBUTE_TYPE.MAGIC;
      case CSPOT_TYPE.USE_ATTRIBUTE_TOOLS:
        return ATTRIBUTE_TYPE.TOOLS;
      default:
        return ATTRIBUTE_TYPE.COMBAT;
    }
  };

  const getSkillKeyForType = (type: CSPOT_TYPE): keyof SkillTotals => {
    switch (type) {
      case CSPOT_TYPE.USE_ATTRIBUTE_COMBAT:
        return "combat";
      case CSPOT_TYPE.USE_ATTRIBUTE_MAGIC:
        return "magic";
      case CSPOT_TYPE.USE_ATTRIBUTE_TOOLS:
        return "gear";
      default:
        return "combat";
    }
  };

  const getAttributeForChallengeRowType = (
    rowIndex: number
  ): ATTRIBUTE_TYPE => {
    const row = challengeRows[rowIndex];
    switch (row.theme) {
      case "COMBAT":
        return ATTRIBUTE_TYPE.COMBAT;
      case "MAGIC":
        return ATTRIBUTE_TYPE.MAGIC;
      case "GEAR":
        return ATTRIBUTE_TYPE.TOOLS;
      default:
        return ATTRIBUTE_TYPE.COMBAT;
    }
  };

  const canPlayerAffordRow = (character: Character, rowIndex: number): boolean => {
    const attributeType = getAttributeForChallengeRowType(rowIndex);
    const remainingAttributes = characterManager.getRemainingAttributes(character, attributeType);
    return remainingAttributes >= 1;
  };

  const getAttributeCostText = (rowIndex: number): string => {
    const attributeType = getAttributeForChallengeRowType(rowIndex);
    const attributeName = characterManager.getUFAttributeName(attributeType);
    return `Character uses one ${attributeName}`;
  };

  const getUserFacingTheme = (theme: "COMBAT" | "GEAR" | "MAGIC"): string => {
    switch (theme) {
      case "COMBAT":
        return "Might";
      case "GEAR":
        return "Skill";
      case "MAGIC":
        return "Magic";
      default:
        return "Unknown";
    }
  };

  // Initialize the challenge field with 3 themed rows
  const initializeChallengeField = (totalUnlockSpotsNeeded: number): void => {
    const themes: Array<"COMBAT" | "GEAR" | "MAGIC"> = [
      "COMBAT",
      "GEAR",
      "MAGIC",
    ];
    const rows: ChallengeRow[] = [];

    const unlockSpotsPerRow = Math.ceil(totalUnlockSpotsNeeded / themes.length);
    let totalUnlockSpotsAdded = 0;

    themes.forEach((theme, rowIndex) => {
      const rowSpots: CSpot[] = [];

      for (let i = 0; i < challengeLength; i++) {
        let spotType: CSPOT_TYPE;
        const themeChance = Math.random();

        if (themeChance < 0.7) {
          switch (theme) {
            case "COMBAT":
              spotType = CSPOT_TYPE.USE_ATTRIBUTE_COMBAT;
              break;
            case "GEAR":
              spotType = CSPOT_TYPE.USE_ATTRIBUTE_TOOLS;
              break;
            case "MAGIC":
              spotType = CSPOT_TYPE.USE_ATTRIBUTE_MAGIC;
              break;
          }
        } else {
          const otherTypes = [
            CSPOT_TYPE.USE_ATTRIBUTE_COMBAT,
            CSPOT_TYPE.USE_ATTRIBUTE_MAGIC,
            CSPOT_TYPE.USE_ATTRIBUTE_TOOLS,
          ];
          spotType = otherTypes[Math.floor(Math.random() * otherTypes.length)];
        }

        // Add random score points to one spot per row
        const hasScore = i === Math.floor(Math.random() * challengeLength);
        const scorePoints = hasScore
          ? Math.floor(Math.random() * 3) + 1
          : undefined;

        // Determine challenge points (1-3 circles for difficulty)
        const challengePoints = Math.floor(Math.random() * 3) + 1;
        rowSpots.push({
          id: `${theme.toLowerCase()}_row_spot_${i}`,
          type: spotType,
          position: i,
          used: false,
          icon: getIconForSpotType(spotType, challengePoints),
          scorePoints,
          challengePoints, // Add this to track difficulty
        });
      }

      rows.push({
        id: `row_${rowIndex}`,
        theme,
        name: `${getUserFacingTheme(theme)}`,
        description: `Focus on ${theme.toLowerCase()} skills`,
        cspots: rowSpots,
        selected: false,
      });
    });

    // Now that our rows are populoared we can randomly distribute the ASSIST UNLOCKS
    while (totalUnlockSpotsAdded < totalUnlockSpotsNeeded) {
      // get a random row
      const randomRowIndex = Math.floor(Math.random() * rows.length);
      const row = rows[randomRowIndex];
      // find a random position that is not already an UNLOCK ASSIST
      const availablePositions = row.cspots
        .map((spot, index) =>
          spot.type !== CSPOT_TYPE.UNLOCK_ASSIST ? index : -1
        )
        .filter((index) => index !== -1);
      if (availablePositions.length === 0) continue; // all spots are UNLOCK ASSIST, skip
      const randomPosIndex = Math.floor(
        Math.random() * availablePositions.length
      );
      const pos = availablePositions[randomPosIndex];
      // replace that spot with an UNLOCK ASSIST
      row.cspots[pos] = {
        id: `${row.theme.toLowerCase()}_row_spot_${pos}_unlock`,
        type: CSPOT_TYPE.UNLOCK_ASSIST,
        position: pos,
        used: false,
        icon: getIconForSpotType(CSPOT_TYPE.UNLOCK_ASSIST),
        challengePoints: 0,
      };
      totalUnlockSpotsAdded++;
    }

    setChallengeRows(rows);
  };

  // Initialize assist tags (8 total: location tags + random tags)
  const initializeAssistTags = () => {
    const tags: AssistTag[] = [];
    if (authoredAssistTags && authoredAssistTags.length > 0) {
      authoredAssistTags.forEach((tag, index) => {
        tags.push({
          id: `authored_assist_${index}`,
          tag: tag,
          state: tagManager.isTagCollected(tag) ? "AVAILABLE" : "UNAVAILABLE",
          isLocationTag: true,
        });
      });
    }
    if (generateRandomAssistTags) {
      // Add location tags from current encounter (if any)
      // This would need to be populated from encounter context
      const locationTags = tagManager.getOutstandingLocationTags();
      locationTags.forEach((tag, index) => {
        tags.push({
          id: `location_${index}`,
          tag: tag,
          state: "UNAVAILABLE",
          isLocationTag: true,
        });
      });

      const collectedLocationTags = tagManager.getCollectedLocationTags();
      collectedLocationTags.forEach((tag, index) => {
        tags.push({
          id: `collected_location_${index}`,
          tag: tag,
          state: "AVAILABLE",
          isLocationTag: true,
        });
      });

      // Add random tags to reach total of 5
      const randomTags = tagManager.generateRelevantFlavorTags(5);
      randomTags.forEach((tag, index) => {
        tags.push({
          id: `assist_${index}`,
          tag: tag.tag,
          state: tagManager.isTagCollected(tag.tag)
            ? "AVAILABLE"
            : "UNAVAILABLE", // 60% available initially
          isLocationTag: false,
        });
      });
    }
    const tagsToAdd = tags.slice(0, 5);
    setAssistTags(tagsToAdd);

    const unavailableAssistTags = tagsToAdd.filter(
      (tag) => tag.state === "UNAVAILABLE"
    );

    initializeChallengeField(unavailableAssistTags.length);
  };

  // Initialize on mount
  useEffect(() => {
    initializeAssistTags();
  }, []); // Empty dependency array means this runs once on mount

  const nbAvailableAssistTags = assistTags.filter(
    (tag) => tag.state === "AVAILABLE"
  ).length;

  // Character selection
  const handleCharacterClick = (character: Character) => {
    if (selectedCharacter && selectedCharacter.id === character.id) {
      // Deselect if already selected
      setSelectedCharacter(null);

      // Deactivate all active assist tags and reset bonus
      setAssistTags((prev) =>
        prev.map((tag) =>
          tag.state === "ACTIVE" ? { ...tag, state: "AVAILABLE" } : tag
        )
      );
      setAssistBonus(0);
      return;
    }
    setSelectedCharacter(character);
  };

  // Row selection with animation
  const handleRowClick = async (rowIndex: number) => {
    if (!selectedCharacter) return;

    // Check if player can afford this row
    if (!canPlayerAffordRow(selectedCharacter, rowIndex)) {
      return;
    }

    // get player from charachter: 
    const player = appManager.getPlayerManager().getPlayerAssignedCharacter(selectedCharacter);
   // Expose the tag in the campaign system
          appManager.getPartyManager().playersTakeAction(((player && [player.name]) || undefined), true);
    // Clear any previous highlight states
    setHighlightedSpot(null);
    setFinalSelectedSpot(null);

    // Start selection animation
    setChallengeRows((prev) =>
      prev.map((row, index) => ({
        ...row,
        selected: index === rowIndex,
        animatingSelection: index === rowIndex,
      }))
    );

    const row = challengeRows[rowIndex];
    const availableSpots = row.cspots.filter(spot => !spot.used);
    
    // Pre-select the target spot
    const randomSpotIndex = Math.floor(Math.random() * availableSpots.length);
    const targetSpot = availableSpots[randomSpotIndex];
    const targetSpotIndex = row.cspots.findIndex(spot => spot.id === targetSpot.id);
    
    // Cycling animation
    let currentHighlightIndex = 0;
    let startTime = Date.now();
    let shouldStop = false;
    
    const cycleInterval = setInterval(() => {
      if (availableSpots.length > 0) {
        const spotIndex = row.cspots.findIndex(spot => spot.id === availableSpots[currentHighlightIndex].id);
        setHighlightedSpot({ rowIndex, spotIndex });
        
        // Check if we should stop cycling (after minimum time has passed)
        const elapsedTime = Date.now() - startTime;
        if (elapsedTime >= ANIMATION_TIMINGS.SELECTION_DURATION - ANIMATION_TIMINGS.FINAL_PULSE_DURATION) {
          shouldStop = true;
        }
        
        // Move to next spot
        currentHighlightIndex = (currentHighlightIndex + 1) % availableSpots.length;
        
        // Stop when we land on the target spot (and minimum time has passed)
        if (shouldStop && spotIndex === targetSpotIndex) {
          clearInterval(cycleInterval);
          setHighlightedSpot(null);
          
          // Set final selected spot for pulsing
          setFinalSelectedSpot({ rowIndex, spotIndex: targetSpotIndex });
          
          // After pulse, resolve the selection
          setTimeout(() => {
            setChallengeRows((prev) =>
              prev.map((r, idx) => ({
                ...r,
                animatingSelection: false,
              }))
            );
            
            setFinalSelectedSpot(null);
            resolveSpotSelection(rowIndex, targetSpot, selectedCharacter);
          }, ANIMATION_TIMINGS.FINAL_PULSE_DURATION);
        }
      }
    }, ANIMATION_TIMINGS.CYCLE_SPEED);
  };

  // Resolve spot selection and effects
  const resolveSpotSelection = (
    rowIndex: number,
    spot: CSpot,
    character: Character
  ) => {
    // Note: For now, skip attention reduction as ATTENTION is not in ATTRIBUTE_TYPE enum
    // This would need to be implemented when attention system is available

    // Mark spot as used and assign to character
    setChallengeRows((prev) =>
      prev.map((row, index) =>
        index === rowIndex
          ? {
              ...row,
              cspots: row.cspots.map((s) =>
                s.id === spot.id
                  ? { ...s, used: true, characterId: String(character.id) }
                  : s
              ),
            }
          : row
      )
    );

    // Handle UNLOCK_ASSIST spots
    if (spot.type === CSPOT_TYPE.UNLOCK_ASSIST) {
      // Show assist options modal
      renderAssistOptionsResults();
    } else {
      // Add skill points for non-assist spots
      const skillKey = getSkillKeyForType(spot.type);
      setSkillTotals((prev) => ({
        ...prev,
        [skillKey]: prev[skillKey] + spot.challengePoints! + assistBonus,
      }));

      // Add used attribute to character
      const attributeType = getAttributeForChallengeRowType(rowIndex);
      characterManager.addUsedAttribute(character, attributeType);
    }

    // Update moves completed and reward level
    setMovesCompleted((prev) => {
      const newCount = prev + 1;
      updateRewardLevel(newCount);
      return newCount;
    });

    // Clear selections
    setSelectedCharacter(null);
    setChallengeRows((prev) =>
      prev.map((row) => ({ ...row, selected: false }))
    );

    // Convert all ACTIVE assist tags to USED and reset bonus
    setAssistTags((prev) =>
      prev.map((tag) =>
        tag.state === "ACTIVE" ? { ...tag, state: "USED" } : tag
      )
    );
    setAssistBonus(0);
    
    if (spot.scorePoints) {
      // Add score to campaign via campaign manager
      campaignManager.addEncounterPoints(spot.scorePoints);
      console.log(
        `Character ${character.name} gained ${spot.scorePoints} encounter points!`
      );
    }
  };

  const skillsCompleted = () => {
    const magicCompleted = skillTotals.magic >= skillThresholds.magic;
    const combatCompleted = skillTotals.combat >= skillThresholds.combat;
    const gearCompleted = skillTotals.gear >= skillThresholds.gear;
    return (
      (magicCompleted ? 1 : 0) +
      (combatCompleted ? 1 : 0) +
      (gearCompleted ? 1 : 0)
    );
  };

  // Update reward level based on progress
  const updateRewardLevel = (moves: number) => {
    const skills = skillsCompleted();

    if (skills === 0) {
      setCurrentRewardLevel(0); // Failure
    } else if (skills === 1) {
      setCurrentRewardLevel(1); // Simple
    } else if (skills === 2) {
      setCurrentRewardLevel(2); // Challenging
    } else {
      setCurrentRewardLevel(3); // Hard
    }
  };

  // Calculate current reward level reactively
  const getCurrentRewardLevel = () => {
    const skills = skillsCompleted();

    if (skills === 0) {
      return 0; // Failure
    } else if (skills === 1) {
      return 1; // Simple
    } else if (skills === 2) {
      return 2; // Challenging
    } else {
      return 3; // Hard
    }
  };

  // Handle assist tag click
  const handleAssistTagClick = (tagId: string) => {
    const clickedTag = assistTags.find((tag) => tag.id === tagId);

    if (clickedTag) {
      if (clickedTag.state === "AVAILABLE") {
        // Activate the tag and increment bonus
        setAssistTags((prev) =>
          prev.map((tag) =>
            tag.id === tagId ? { ...tag, state: "ACTIVE" } : tag
          )
        );
        setAssistBonus((prev) => prev + 1);
        chatManager.triggerChallengeAssistChat(
          clickedTag.tag,
          selectedCharacter || undefined
        );
      } else if (clickedTag.state === "ACTIVE") {
        // Deactivate the tag and decrement bonus
        setAssistTags((prev) =>
          prev.map((tag) =>
            tag.id === tagId ? { ...tag, state: "AVAILABLE" } : tag
          )
        );
        setAssistBonus((prev) => Math.max(0, prev - 1));
      }
    }

    // // Show tag resolution (implementation depends on tag system)
    // const tag = assistTags.find((t) => t.id === tagId);
    // if (tag) {
    //   setCurrentResultTag(tag.tag);
    //   setShowResults(true);
    // }
  };

  // Accept challenge completion
  const handleAcceptChallenge = () => {
    setChallengeComplete(true);

    // Apply reward effects
    const currentReward = rewardLevels[currentRewardLevel];

    // Apply tension/attention changes (implementation depends on campaign system)
    console.log(`Challenge completed with reward: ${currentReward.name}`);

    // Show completion modal or expose authored tag
    if (authoredTag) {
      // Use TagModal instead of setting state for ResultsDisplay
      appManager.getUiManager().showTagModal({
        title: `Challenge Completed`,
        description: config?.successText,
        tags: [
          tagManager.tagToEncounterFlavorTag(
            authoredTag,
            FlavorTagSource.ENCOUNTER,
            FlavorTagTheme.NONE
          )
        ],
        effects: [], // Could add challenge completion effects here
        selectedTags: [], // No previously selected tags
        remainingClicks: 1, // Allow clicking the tag to acknowledge

        onTagClick: (tag) => {
          // Expose the tag and complete the challenge
          
          // Use setTimeout to ensure the tag click completes before closing modal
          setTimeout(() => {
            appManager.getUiManager().closeModal();
            if (onComplete) onComplete();
            campaignManager.exposeTag(tag);
          }, 100);
        },
        onClose: () => {
          // If they close without clicking, still complete
          if (onComplete) onComplete();
        }
      });
    } else {
      // Default completion with attention bonus
      if (onComplete) onComplete();
    }
  };

  const renderAssistOptionsResults = () => {
    const unavailableAssistTags = assistTags.filter(
      (tag) => tag.state === "UNAVAILABLE"
    );

    if (unavailableAssistTags.length === 0) return;

    // Convert unavailable assist tags to EncounterFlavorTag format
    const tagsForModal = unavailableAssistTags.map(assistTag => 
      tagManager.tagToEncounterFlavorTag(
        assistTag.tag,
        campaignManager.getPotentialTagSource(assistTag.tag),
        FlavorTagTheme.NONE
      )
    );

    appManager.getUiManager().showTagModal({
      title: "Exposition Opportunity",
      description: "During the challenge, an opportunity to introduce one of the following topics comes up.",
      tags: tagsForModal,
      effects: [], // No effects for assist unlock
      selectedTags: [], // No previously selected tags
      remainingClicks: 1, // Only allow one tag click
   //   tagInstructions: "Select one assistance option to unlock",
      onTagClick: (tag) => {
        // Find the corresponding assist tag and update its state
        const assistTag = unavailableAssistTags.find(at => at.tag.name === tag.tag.name);
        if (assistTag) {
          setAssistTags(prev => 
            prev.map(at => 
              at.id === assistTag.id 
                ? { ...at, state: "AVAILABLE" }
                : at
            )
          );
       


          campaignManager.exposeTag(tag);
          // Use setTimeout to ensure the tag click completes before closing modal
          setTimeout(() => {
            appManager.getUiManager().closeModal();
          }, 100);
        }
      },
      onClose: () => {
        // Modal closed, nothing special needed
        console.log("Assist options modal closed");
      }
    });
  };

  // Render character list
  const renderCharacterList = (isActive: boolean) => (
    <CContainer title="Heroes" variant={isActive ? "primary" : "secondary"}>
      {characters.map((character) => {
        // Get total attributes from class/race combination
        const totalAttributes = getCombinedAttributes(character);

        // Calculate used attributes from attributeLoss
        const combatUsed =
          character.attributeLoss?.[ATTRIBUTE_TYPE.COMBAT] || 0;
        const magicUsed = character.attributeLoss?.[ATTRIBUTE_TYPE.MAGIC] || 0;
        const toolsUsed = character.attributeLoss?.[ATTRIBUTE_TYPE.TOOLS] || 0;

        // Calculate remaining attributes
        const combatTotal = totalAttributes[ATTRIBUTE_TYPE.COMBAT] || 0;
        const magicTotal = totalAttributes[ATTRIBUTE_TYPE.MAGIC] || 0;
        const toolsTotal = totalAttributes[ATTRIBUTE_TYPE.TOOLS] || 0;

        return (
          <div
            key={character.id}
            className={`character-item ${
              selectedCharacter?.id === character.id
                ? "selected"
                : selectedCharacter
                ? "disabled"
                : ""
            }`}
            onClick={() => handleCharacterClick(character)}
          >
            <div className="left-side">
              <div className="character-name">{character.name}</div>
              <div className="character-icon">
                <CImage imageId={character.icon} />
              </div>
            </div>

            <div className="character-info">
              {/* Combat attribute row */}
              <div className="attribute-row">
                <span className="attribute-label"></span>
                <div className="attribute-icons">
                  {Array.from(
                    { length: Math.max(1, combatTotal) },
                    (_, index) => (
                      <span
                        key={`combat-${index}`}
                        className={`attribute-icon ${
                          combatTotal === 0 || index >= combatTotal - combatUsed
                            ? "used"
                            : "available"
                        }`}
                      >
                        
                      </span>
                    )
                  )}
                </div>
              </div>

              {/* Magic attribute row */}
              <div className="attribute-row">
                <span className="attribute-label"></span>
                <div className="attribute-icons">
                  {Array.from(
                    { length: Math.max(1, magicTotal) },
                    (_, index) => (
                      <span
                        key={`magic-${index}`}
                        className={`attribute-icon ${
                          magicTotal === 0 || index >= magicTotal - magicUsed
                            ? "used"
                            : "available"
                        }`}
                      >
                        
                      </span>
                    )
                  )}
                </div>
              </div>

              {/* Tools attribute row */}
              <div className="attribute-row">
                <span className="attribute-label"></span>
                <div className="attribute-icons">
                  {Array.from(
                    { length: Math.max(1, toolsTotal) },
                    (_, index) => (
                      <span
                        key={`tools-${index}`}
                        className={`attribute-icon ${
                          toolsTotal === 0 || index >= toolsTotal - toolsUsed
                            ? "used"
                            : "available"
                        }`}
                      >
                        
                      </span>
                    )
                  )}
                </div>
              </div>
            </div>
          </div>
        );
      })}
    </CContainer>
  );

  // Render challenge rows
  const renderChallengeRows = (active: boolean) => (
    <CContainer
      title="Methods and Tactics"
      variant={active ? "primary" : "secondary"}
    >
      <div className="challenge-rows">
        {challengeRows.map((row, rowIndex) => {
          const isRowDisabled = selectedCharacter && !canPlayerAffordRow(selectedCharacter, rowIndex);
          const canAffordRow = selectedCharacter && canPlayerAffordRow(selectedCharacter, rowIndex);
          
          return (
            <div
              key={row.id}
              className={`challenge-row theme-${row.theme.toLowerCase()} 
                ${row.selected ? "selected" : ""} 
                ${row.animatingSelection ? "animating" : ""}
                ${!selectedCharacter ? "disabled" : ""}
                ${isRowDisabled ? "attribute-disabled" : ""}
                ${canAffordRow ? "show-cost" : ""}`}
              data-cost-text={canAffordRow ? getAttributeCostText(rowIndex) : ""}
              onClick={() => handleRowClick(rowIndex)}
            >
            <div className="row-header">
              <div className="row-icon">{getIconForTheme(row.theme)}</div>
              <div className="row-label">{row.name}</div>
            </div>

            <div className="challenge-spots">
              {row.cspots.map((spot, spotIndex) => {
                const isHighlighted = highlightedSpot?.rowIndex === rowIndex && highlightedSpot?.spotIndex === spotIndex;
                const isFinalSelected = finalSelectedSpot?.rowIndex === rowIndex && finalSelectedSpot?.spotIndex === spotIndex;
                
                return (
                  <div
                    key={spot.id}
                    className={`challenge-spot ${
                      spot.used ? "used" : "available"
                    } ${
                      isHighlighted ? "cycling-highlight" : ""
                    } ${
                      isFinalSelected ? "final-selected" : ""
                    } ${getThemeColorClass(spot.type)}`}
                    data-row={rowIndex}
                    data-spot-position={spot.position}
                  >
                    <div className="spot-icon">
                      {!spot.used && spot.type === CSPOT_TYPE.UNLOCK_ASSIST ? (
                        <FlavorTagPill
                          showTooltip={true}
                          disabled={false}
                          tag={tagManager.tagToEncounterFlavorTag(
                            {
                              name: "?",
                            },
                            FlavorTagSource.NONE,
                            FlavorTagTheme.NONE
                          )}
                        />
                      ) : (
                        !spot.used && renderDicePips(spot.challengePoints || 1)
                      )}
                    </div>

                    {!spot.used && spot.scorePoints && (
                      <div className="score-indicator">
                        <DiceScore score={spot.scorePoints}></DiceScore>
                      </div>
                    )}

                    {spot.used && spot.characterId && (
                      <div className="character-marker">
                        <CImage
                          imageId={
                            characters.find(
                              (c) => String(c.id) === spot.characterId
                            )?.icon
                          }
                        />
                      </div>
                    )}
                  </div>
                );
              })}
            </div>
          </div>
          );
        })}
      </div>
    </CContainer>
  );

  // Render assist tags panel
  const renderAssistTags = (active: boolean) => (
    <CContainer
      title="Potential Assistance"
      variant={active ? "primary" : "secondary"}
    >
      <div
        className={`assist-tags-container ${
          !selectedCharacter ? "disabled" : ""
        }`}
      >
        {assistTags.map((assistTag) => (
          <div
            key={assistTag.id}
            className={`assist-tag ${assistTag.state.toLowerCase()}`}
            onClick={() =>
              (assistTag.state === "AVAILABLE" ||
                assistTag.state === "ACTIVE") &&
              handleAssistTagClick(assistTag.id)
            }
          >
            <span className="tag-name">
              <FlavorTagPill
                showTooltip={false}
                disabled={
                  assistTag.state !== "ACTIVE" &&
                  assistTag.state !== "AVAILABLE"
                }
                tag={tagManager.tagToEncounterFlavorTag(
                  assistTag.tag,
                  campaignManager.getPotentialTagSource(assistTag.tag),
                  FlavorTagTheme.NONE
                )}
              />
            </span>
            <span className="tag-state">{assistTag.state}</span>
          </div>
        ))}
      </div>
    </CContainer>
  );

  // Render totals column
  const renderTotalsColumn = () => (
    <div className="totals-column">
      <CContainer title="Challenge Status" variant="primary-container">
        <div className="skill-totals">
          <div className="skill-bar">
            <FillBar
              totalSegments={skillThresholds.combat}
              filledSegments={skillTotals.combat}
              label={`${skillTotals.combat}/${skillThresholds.combat}`}
              theme="combat"
            />
            <div
              className={`skill-icon ${
                skillTotals.combat >= skillThresholds.combat ? "completed" : ""
              }`}
            >
              <div className="skill-circle combat-circle" />
            </div>
          </div>
          <div className="skill-bar">
            <FillBar
              totalSegments={skillThresholds.gear}
              filledSegments={skillTotals.gear}
              label={`${skillTotals.gear}/${skillThresholds.gear}`}
              theme="gear"
            />
            <div
              className={`skill-icon ${
                skillTotals.gear >= skillThresholds.gear ? "completed" : ""
              }`}
            >
              <div className="skill-circle gear-circle" />
            </div>
          </div>
          <div className="skill-bar">
            <FillBar
              totalSegments={skillThresholds.magic}
              filledSegments={skillTotals.magic}
              label={`${skillTotals.magic}/${skillThresholds.magic}`}
              theme="magic"
            />
            <div
              className={`skill-icon ${
                skillTotals.magic >= skillThresholds.magic ? "completed" : ""
              }`}
            >
              <div className="skill-circle magic-circle" />
            </div>
          </div>

          <div
            className={`result-tag-container ${
              isFailureState() ? "failure" : "success"
            }`}
            onClick={handleAcceptChallenge}
            style={
              !isFailureState()
                ? {
                    backgroundColor: "var(--ui-color-primary)",
                    border: "6px solid white",
                  }
                : {
                    backgroundColor: "var(--ui-color-failure)",
                    border: "6px solid var(--ui-color-failure-border)",
                  }
            }
          >
            {authoredTag && (
              <div className="tag-content">
                <FlavorTagPill
                  showTooltip={false}
                  disabled={isFailureState()}
                  tag={tagManager.tagToEncounterFlavorTag(
                    successTag,
                    tagManager.isTagCollected(successTag)
                      ? FlavorTagSource.ENCOUNTER
                      : FlavorTagSource.ENCOUNTER_EXPECTED,
                    FlavorTagTheme.NONE
                  )}
                />
                {isFailureState() && <div className="diagonal-stripe"></div>}
              </div>
            )}
            <div className="accept-label">
              {isFailureState() ? "Accept Failure" : "Accept Results"}
            </div>
          </div>
        </div>
      </CContainer>

      <div className="results-meter">
        {rewardLevels.map((level, index) => (
          <div
            key={level.id}
            className={`reward-level ${
              index === getCurrentRewardLevel() ? "current" : ""
            }`}
          >
            <div className="reward-indicators">
              {[0, 1, 2].map((dotIndex) => (
                <div
                  key={dotIndex}
                  className={`indicator-dot ${
                    dotIndex < index ? "filled" : ""
                  }`}
                />
              ))}
            </div>
            <div className="reward-content">
              <span className="level-name">{level.name}</span>
              <span className="level-description">{level.description}</span>
            </div>
          </div>
        ))}
        <div className="results-label">CURRENT RESULT</div>
      </div>
    </div>
  );

  return (
    <div className="enhanced-skill-challenge">
      <div className="challenge-layout">
        {/* Left side: Character list and challenge rows */}
        <div className="left-section">
          <div className="character-column">
            <div className="character-section">
              {renderCharacterList(!selectedCharacter)}
            </div>
          </div>
          <div className="challenge-column">
            <div className="challenge-section">
              {renderChallengeRows(!!selectedCharacter)}
            </div>

            <div className="assist-section">
              {renderAssistTags(
                !!selectedCharacter && nbAvailableAssistTags > 0
              )}
            </div>
          </div>
          <div className="right-section">{renderTotalsColumn()}</div>
        </div>
      </div>
    </div>
  );
};

export default EnhancedSkillChallenge;
