import React from 'react';
import { Tag, EncounterFlavorTag, FlavorTagSource, FlavorTagTheme } from '../../models';
import FlavorTagPill from './FlavorTagPill';

interface TagPillProps {
  tag: Tag;
  source?: FlavorTagSource;
  theme?: FlavorTagTheme;
  className?: string;
  disabled?: boolean;
  highlighted?: boolean;
  onClick?: (tag: EncounterFlavorTag) => void;
  showTooltip?: boolean;
  tooltipText?: string;
  off?: boolean;
  tooltipClassName?: string;
}

const TagPill: React.FC<TagPillProps> = ({
  tag,
  source = FlavorTagSource.NONE,
  theme = FlavorTagTheme.NONE,
  className,
  disabled,
  highlighted,
  onClick,
  showTooltip,
  tooltipText,
  tooltipClassName,
  off,
}) => {
  const flavorTag: EncounterFlavorTag = {
    tag,
    source,
    theme,
  };

  return (
    <FlavorTagPill
      tag={flavorTag}
      className={className}
      disabled={disabled}
      highlighted={highlighted}
      onClick={onClick ? () => onClick(flavorTag) : undefined}
      showTooltip={showTooltip}
      tooltipText={tooltipText}
      tooltipClassName={tooltipClassName}
      off={off}
    />
  );
};

export default TagPill;
