import React from 'react';
import { ATTRIBUTE_CONTRIBUTOR, Character, NOTEBOOK_CATEGORY } from '../models';
import Cimage from './UI/cimage';
import './characterCard.scss';
import MoreInfo from './UI/moreInfoButton';
import { useAppManager, useCharacterManager, useUiManager } from './managerContexts';
import { APP_STATE } from '../consts/states';
import AttributeDisplay from './UI/attributeDisplay';


interface CharacterCardProps {
  character: Character;
  isSelected: boolean;
  onSelect: () => void;
  showAbilities: boolean;
  collapsed?: boolean;
  isAssigned?: boolean;
  isAssignedToOther?: boolean;
}

const CharacterCard: React.FC<CharacterCardProps> = ({ character, isSelected, onSelect, showAbilities, collapsed, isAssigned, isAssignedToOther }) => {

  const uiManager = useUiManager();
  const characterManager = useCharacterManager();
  const appManager = useAppManager();



  const clickedMoreInfo = () => {
    uiManager.showBasicNotebook(NOTEBOOK_CATEGORY.CHARACTER, character)
  }

  const showInfoOnClick = appManager.state !== APP_STATE.CHARACTER_CREATION;;

  return (
   
    collapsed ? (
      <div className="character-card collapsed" onClick={onSelect}>
        <div className="character-icon">
          <Cimage imageId={character.icon} />
            <div className="character-initials">{characterManager.getUFInitials(character)}</div>
        </div>
        <div className='character-collapsed-overlay'>
        {showInfoOnClick ? 
          <MoreInfo 
            callback={clickedMoreInfo}
          /> : 
          <div className="character-collapsed-unassign">X</div>
        }
        </div>

    </div>
    ) : (
    <div className={`character-card ${isSelected ? 'selected' : ''} ${isAssignedToOther ? 'assigned-to-other' : ''}`} onClick={onSelect}>
      <div className={`character-card-container ${isAssigned ? 'assigned' : ''} `}>
        <div className="character-card-header">
          <div className="character-info">
            <div className="character-name">{character.name}</div>
            <div className="character-details">
              <div className="character-race">{characterManager.getUFRace(character.race)} {characterManager.getUFClass(character.character_class)}</div>
            </div>
            <AttributeDisplay hero={character} category={ATTRIBUTE_CONTRIBUTOR.ALL} collapsed={false} />
          </div>
          <div className="character-icon">
            <Cimage imageId={character.icon} />
            <div className="character-level">Level {character.level}</div>
          </div>
        </div>
        {showAbilities && (
          <div className="character-card-body">
            <div className="abilities-list">
              <ul>
                {character.abilities.map((ability, index) => (
                  <li key={index}>
                    <strong>{ability}</strong>
                  </li>
                ))}
              </ul>
            </div>
          </div>
        )}
      </div>
      <MoreInfo
        callback={clickedMoreInfo}
      />
      {isAssigned && <div className="character-assigned">Assigned</div>}
    </div>
    )

  );
};

export default CharacterCard;