import { EventEmitter } from 'events';
import CampaignManager from './campaignManager';
import PlayerManager from './playerManager';
import SessionManager from './sessionManager';
import PartyManager from './partyManager';
import UiManager from './uiManager';
import DataManager from './dataManager';
import { APP_STATE } from "../consts/states";
import CharacterManager from './characterManager';
import TutorialManager from './tutorialManager';
import ChatManager from './chatManager';
import ChatEventManager from './chatEventManager';
import CombatManager from './combatManager';
import { SESSION_STATE } from '../models';
import TownManager from './townManager';
import TagManager from './tagManager';
import QuestManager from './questManager';



const ENABLE_TUTORIAL = false;


class AppManager extends EventEmitter {


  private _state: APP_STATE = APP_STATE.MAIN_MENU;
  private lastTime: number = 0;

  private campaignManager : CampaignManager;
  private playerManager : PlayerManager;
  private sessionManager : SessionManager;
  private dataManager :   DataManager;
  private partyManager :   PartyManager;
  private uiManager : UiManager;
  private characterManager : CharacterManager;
  private tutorialManager : TutorialManager;
  private chatManager : ChatManager;
  private chatEventManager : ChatEventManager;
  private combatManager : CombatManager;
  private townManager : TownManager
  private tagManager : TagManager;
  private questManager : QuestManager;
  constructor() {
    super();
    // First phase: Initialize managers that don't have circular dependencies
    this.playerManager = new PlayerManager(this);
    this.campaignManager = new CampaignManager(this);
    this.characterManager = new CharacterManager(this);
    this.sessionManager = new SessionManager();
    this.dataManager = new DataManager(this);
    this.partyManager = new PartyManager(this);
    this.uiManager = new UiManager(this);
    this.tutorialManager = new TutorialManager(this);
    this.combatManager = new CombatManager(this);
   

    // Second phase: Initialize managers with circular dependencies
    this.tagManager = new TagManager(this.campaignManager, this.playerManager, undefined, this);
    this.chatManager = new ChatManager(this);
    this.chatEventManager = new ChatEventManager(this);
    this.questManager = new QuestManager();
    
    // Third phase: Resolve circular dependencies after both are created
    this.tagManager.setChatManager(this.chatManager);
    this.townManager = new TownManager(this);
    this.init();
  }

  init() {
    this.state = APP_STATE.MAIN_MENU;
   
  
    this.playerManager.init();
    this.characterManager.init();


    if(ENABLE_TUTORIAL) {
    setTimeout(() => {
      this.tutorialManager.startTutorial('MAIN_MENU');
    }, 100);

  }
    this.startGameLoop();

    this.initiateQuickStartSetup();
    this.state = APP_STATE.SESSION_OVERVIEW;
    
  }


  
  initiateQuickStartSetup() {

    this.playerManager.initiateQuickStart();
    this.characterManager.initiateQuickStart();
    this.campaignManager.initiateQuickStart();
    this.chatManager.startSessionEvent();

    // Show welcome modal
    setTimeout(() => {
    this.uiManager.showWelcomeModal();
    this.chatManager.startWelcomeConversation();
    }, 1000);

  }


  startGameLoop() {
    const gameLoop = (currentTime: number) => {
      const timeDt = currentTime - this.lastTime;
      this.lastTime = currentTime;

      this.update(timeDt);

      requestAnimationFrame(gameLoop);
    };

    requestAnimationFrame(gameLoop);
  }

  update(timeDt: number) {
   // console.log(`Updating game state with time delta: ${timeDt}ms`);
    // Update game state and pass timeDt to other controllers
    this.uiManager?.update(timeDt);
  }

 // Getter for state
 get state(): APP_STATE {
  return this._state;
}

// Setter for state
set state(newState: APP_STATE) {
  console.log("Changing state from", this._state, "to", newState);
 switch(this._state){

    case APP_STATE.PLAYER_RECRUITMENT:
      this.playerManager.acceptSelectedPlayers();  
    break;
    case APP_STATE.MAIN_MENU:
    case APP_STATE.CHARACTER_CREATION:
    case APP_STATE.CAMPAIGN_SETUP:
      console.log("Changing state from ", this._state);
      break;
 }

 switch(newState){
  case APP_STATE.MAIN_MENU:
    break;
  case APP_STATE.PLAYER_RECRUITMENT:
    
    this.tutorialManager.startTutorial('PLAYER_RECRUITMENT');
    break;
  case APP_STATE.CHARACTER_CREATION:
    this.tutorialManager.startTutorial('CHARACTER_CREATION');
    break;
  case APP_STATE.CAMPAIGN_SETUP:
    this.tutorialManager.startTutorial('CAMPAIGN_SETUP');
    break;
  case APP_STATE.SESSION_OVERVIEW:
    //this.tutorialManager.startTutorial('SESSION_PREP');
    this.campaignManager.setSessionState(SESSION_STATE.LOCATION_INTRO);
    break;
  case APP_STATE.SESSION:
   // this.tutorialManager.startTutorial('SESSION');
    this.campaignManager.setSessionState(SESSION_STATE.INTRO_EVENTS);
    //this.chatManager.startSessionEvent();
    break;

}
  this._state = newState;
  this.emit('stateChange');
}

advanceState() {
  this.state = this.dataManager?.getStateProps().nextState || APP_STATE.MAIN_MENU;
}

getUiManager() {
  return this.uiManager;
}
getDataManager() {
  return this.dataManager;
}

getPlayerManager () {
  return this.playerManager;
}

getCharacterManager() {
  return this.characterManager;
}

getSessionManager() {
  return this.sessionManager;
};

getPartyManager() {

  return this.partyManager;
};

getTagManager() {
  return this.tagManager;
}

getCombatManager() {
  return this.combatManager;
}

getCampaignManager() {
  return this.campaignManager;
};

getTutorialManager() {
  return this.tutorialManager;
}

getChatManager() {
  return this.chatManager;
}

getChatEventManager() {
  return this.chatEventManager;
}

getTownManager() {
  return this.townManager;
}

getQuestManager() {
  return this.questManager;
}


}

export default AppManager;