import React, { useState } from 'react';
import CombatBar from './combatBar';
import NumberDie from '../UI/numderDie';
import { useAppManager, useCampaignManager } from '../managerContexts';
import { CombatState, Tag } from '../../models';


interface CombatModifiersProps {
  modifierCallback: (modifier: number, side: 'heroes' | 'enemies', flavor :Tag, index: number) => void;
    side:'heroes' | 'enemies';
    combatState: CombatState;
    disabled?: boolean;
  }
  
  const CombatModifiers: React.FC<CombatModifiersProps> = ({ modifierCallback, side, combatState, disabled }) => {
    
    const campaignManager = useCampaignManager();
    const appManager = useAppManager();
    const combatManager = appManager.getCombatManager();
    const [locationTags, setLocationTags] = useState(appManager.getTagManager().locationTagsCollected);
    // usedTags should ONLY keep track of the used-tags for THIS component, because it determines if THIS component activated it. 
  
   
    
    const dmDie = () => {
      campaignManager.spendDMPoints(1);
      modifierCallback(1, side, {name: 'DM'}, 0);
    }

    const onTagClick = (index: number) => {
      if(!combatState.usedTags) {
        combatState.usedTags = locationTags.map(() => false);
      }
      const tagSet = !combatState.usedTags[index];
      modifierCallback(tagSet ? 1 : -1, side, locationTags[index], index);
    }

    const getLocationTags = () => {

      return (
        <div className={`location-tags ${side}`}>
         {locationTags.map((tag, index) => {
          return (
            <div key={index} className={`text-pill type-location ${combatState.usedTags[index] ? 'expected' : ''}`} onClick={()=>onTagClick(index)}>
              {tag.name}
            </div>
          )})}
        </div>
      )

    }



    return (
      <div className={`combat-modifiers ${side}`}>
        <div className="combat-modifiers-label">
          Assist {side === 'heroes' ? 'Heroes' : 'Enemies'} 
        </div>
        <div className = "combat-modifier-row">
          <div className="combat-modifiers-tags">
            {getLocationTags()}
          </div>
          <div className = {`combat-modifiers-die ${disabled ? 'disabled' : ''}`}>
            <NumberDie 
              baseDie={20}
              displayNumber={'1'}
              onClick={() => dmDie()}/>
          </div>
        </div>
       
      </div>
    )
  };

  export default CombatModifiers;