import { EventEmitter } from 'events';
import { Quest, QuestNode, Tag, ATTITUDE, Player, EncounterFlavorTag, FlavorTagSource } from '../models';
import { RICKY, RICKY2 } from '../consts/quests';

interface ActiveQuest {
  quest: Quest;
  currentNode: QuestNode;
  completedTags: string[];
}

interface PlayerPreference {
  playerId: number;
  optionIndex: number;
  attitude: ATTITUDE;
}

class QuestManager extends EventEmitter {
  private activeQuests: Map<string, ActiveQuest> = new Map();
  private availableQuests: Quest[] = [RICKY, RICKY2];

  getAvailableQuests(): Quest[] {
    return this.availableQuests.filter(quest => !this.activeQuests.has(quest.id));
  }

  getActiveQuests(): ActiveQuest[] {
    return Array.from(this.activeQuests.values());
  }

  launchQuest(questId: string): boolean {
    const quest = this.availableQuests.find(q => q.id === questId);
    if (!quest || this.activeQuests.has(questId)) {
      return false;
    }

    this.activeQuests.set(questId, {
      quest,
      currentNode: quest.startingNode,
      completedTags: []
    });



    this.emit('questLaunched', quest);
    return true;
  }

  isQuestActive(questId: string): boolean {
    return this.activeQuests.has(questId);
  }

  getCurrentQuestTags(): EncounterFlavorTag[] {
    const questTags: EncounterFlavorTag[] = [];

    for (const activeQuest of this.activeQuests.values()) {
      // Add tags from current node's children
      for (const child of activeQuest.currentNode.children) {
        const isTerminal = child.children.length === 0;
        questTags.push({
          tag: child.tag,
          source: isTerminal ? FlavorTagSource.QUEST_TERMINAL : FlavorTagSource.QUEST,
          theme: 'none' as any // Will be styled with bright pink
        });
      }
    }

    return questTags;
  }

  getOutstandingQuestTags(): Tag[] {
    const outstandingTags: Tag[] = [];

    for (const activeQuest of this.activeQuests.values()) {
      // Add tags from current node's children that haven't been completed
      for (const child of activeQuest.currentNode.children) {
        if (!activeQuest.completedTags.includes(child.tag.name)) {
          outstandingTags.push(child.tag);
        }
      }
    }

    return outstandingTags;
  }

  getExhaustionForQuest(questId: string): number {
    const quest = this.getQuestById(questId);
    if (!quest) return 0;
    let totalCost: number = 0;
    quest.cost.forEach(cost => {
      if (cost.type === 'exhaustion') {
        totalCost += cost.amount;
      }
    });
    return totalCost;
  }

  getQuestById(questId: string): Quest | null {
    const activeQuest = this.activeQuests.get(questId);
    if (activeQuest) return activeQuest.quest;
    return this.availableQuests.find(q => q.id === questId) || null;
  }

  advanceQuest(questId: string, tagName: string): QuestNode | null {
    const activeQuest = this.activeQuests.get(questId);
    if (!activeQuest) return null;

    // Find the node with the matching tag in the entire quest tree
    const targetNode = this.findNodeByTag(activeQuest.quest.startingNode, tagName);
    if (!targetNode) return null;

    // Mark the tag as completed if not already done
    if (!activeQuest.completedTags.includes(tagName)) {
      activeQuest.completedTags.push(tagName);
    }

    // If the exposed tag matches a child of the current node, advance to that child
    const matchingChild = activeQuest.currentNode.children.find(child => child.tag.name === tagName);
    if (matchingChild) {
      // Advance to the child node
      activeQuest.currentNode = matchingChild;
      
      if (matchingChild.children.length === 0) {
        // This is a terminal node
        this.emit('questTerminalReached', { quest: activeQuest.quest, node: matchingChild });
      } else {
        this.emit('questAdvanced', { quest: activeQuest.quest, node: matchingChild });
      }
      
      return matchingChild;
    }

    // If the exposed tag matches the current node itself, just mark as visited
    // This typically happens when showing the conversation for the current node
    if (activeQuest.currentNode.tag.name === tagName) {
      this.emit('questAdvanced', { quest: activeQuest.quest, node: activeQuest.currentNode });
      return activeQuest.currentNode;
    }

    // Tag doesn't match current progression - this shouldn't normally happen
    console.warn(`Quest ${questId}: Tag "${tagName}" doesn't match current quest progression from node "${activeQuest.currentNode.tag.name}"`);
    return null;
  }

  getQuestForTag(tagName: string): { quest: Quest; node: QuestNode } | null {
    // Search in all active quests
    for (const activeQuest of this.activeQuests.values()) {
      const foundNode = this.findNodeByTag(activeQuest.quest.startingNode, tagName);
      if (foundNode) {
        return { quest: activeQuest.quest, node: foundNode };
      }
    }
    
    // Also search in available quests (for initial quest activation)
    for (const quest of this.availableQuests) {
      const foundNode = this.findNodeByTag(quest.startingNode, tagName);
      if (foundNode) {
        return { quest, node: foundNode };
      }
    }
    
    return null;
  }

  // Helper method to recursively find a node by tag name
  private findNodeByTag(node: QuestNode, tagName: string): QuestNode | null {
    if (node.tag.name === tagName) {
      return node;
    }
    
    for (const child of node.children) {
      const found = this.findNodeByTag(child, tagName);
      if (found) return found;
    }
    
    return null;
  }

  generatePlayerPreferences(numOptions: number, players: Player[]): PlayerPreference[] {
    const preferences: PlayerPreference[] = [];
    const attitudes = [ATTITUDE.LIKE, ATTITUDE.HATE, ATTITUDE.NEUTRAL];

    for (let i = 0; i < numOptions; i++) {
      for (const player of players) {
        const randomAttitude = attitudes[Math.floor(Math.random() * 3)];
        preferences.push({
          playerId: player.id,
          optionIndex: i,
          attitude: randomAttitude
        });
      }
    }

    return preferences;
  }

  completeQuest(questId: string): void {
    if (this.activeQuests.has(questId)) {
      const activeQuest = this.activeQuests.get(questId)!;
      this.activeQuests.delete(questId);
      this.emit('questCompleted', activeQuest.quest);
    }
  }

  // New methods for quest modal system
  isQuestComplete(questId: string): boolean {
    const activeQuest = this.activeQuests.get(questId);
    if (!activeQuest) return false;
    
    // Quest is complete if current node has no children (terminal node)
    return activeQuest.currentNode.children.length === 0;
  }

  getNextQuestNode(questId: string): QuestNode | null {
    const activeQuest = this.activeQuests.get(questId);
    if (!activeQuest || activeQuest.currentNode.children.length === 0) {
      return null;
    }

    // Return the first child node as the next topic
    return activeQuest.currentNode.children[0];
  }

  activateQuest(questId: string): boolean {
    // This is the same as launchQuest but with different name for clarity
    return this.launchQuest(questId);
  }

  getCollectedTagsForQuest(questId: string): Tag[] {
    const activeQuest = this.activeQuests.get(questId);
    if (!activeQuest) return [];

    // Find all nodes that have been completed
    const collectedTags: Tag[] = [];
    const findCompletedTags = (node: QuestNode, visited: Set<string> = new Set()) => {
      if (visited.has(node.tag.name)) return;
      visited.add(node.tag.name);

      if (activeQuest.completedTags.includes(node.tag.name)) {
        collectedTags.push(node.tag);
      }

      for (const child of node.children) {
        findCompletedTags(child, visited);
      }
    };

    findCompletedTags(activeQuest.quest.startingNode);
    return collectedTags;
  }
}

export default QuestManager;
