import React, { useEffect, useState } from 'react';
import { FlavorTagTheme } from '../models';
import { useCampaignManager } from './managerContexts';

const THEME_CHANCE = .25;// chance to apply a theme

interface WithRandomThemeProps {
  onClick?:(theme: string | null) => void; // Add onClick to the interface
  className?: string;
  regenerateKey?: number; 
}

export const withRandomTheme = <P extends object>(
  WrappedComponent: React.ComponentType<P>
) => {

  return (props: P & WithRandomThemeProps) => {
      const campaignManager = useCampaignManager();
    const [theme, setTheme] = useState<string | null>(null);

    const handleClick = (e: React.MouseEvent) => {
      if (props.onClick) {
        props.onClick(theme);
      }

      if(theme) {
          campaignManager.themedUiClicked(theme);
      }
      setTheme(null);

    };

    useEffect(() => {
      if (Math.random() < THEME_CHANCE) {
        const themes = Object.values(FlavorTagTheme) as string[];
        const randomTheme = themes[Math.floor(Math.random() * themes.length-1)];
        setTheme(randomTheme);
      }
    }, [props.regenerateKey]);

 

    const combinedClassName = theme 
      ? `${props.className || ''} theme-${theme}`
      : props.className;

    return (
      <div className="theme-wrapper" onClick={handleClick}>
        <WrappedComponent {...props} className={combinedClassName} />
        {theme && (
          <div className="tag-tooltip">
            {`This action will be done in a ${theme} way`}
          </div>
        )}
      </div>
    );
  };
};