

import { useState } from 'react';
import { Character, Encounter, EncounterFlavorTag, TAGS_TYPE, PerceptionTags, Tag } from '../../models';
import { useAppManager, useCampaignManager, useCharacterManager, usePlayerManager, useUiManager } from '../managerContexts';
import EncounterHeader from './encounterHeader';
import CImage from '../UI/cimage';
import NumberDie from '../UI/numderDie';
import { GEAR } from '../../consts/characterConsts';
import PartyManager from '../../managers/partyManager';
import Tooltip from '../UI/tooltip';



interface GameEncounterPerceptionProps {
	encounter: Encounter;
}

const GameEncounterPerception: React.FC<GameEncounterPerceptionProps> = ({encounter}) => {
   const playerManager = usePlayerManager();
   const campaignManager = useCampaignManager();
   const uiManager = useUiManager();
   const appManager = useAppManager();
   const chatManager = appManager.getChatManager();
   const tagManager = appManager.getTagManager();
	const partyManager = appManager.getPartyManager();
   const allCharacters: Character[] = playerManager.getAllPlayerCharacters();
  const [perceptionRevealed, setPerceptionRevealed] = useState<boolean[]>(allCharacters.map(() => false));
  const [perceptionAccepted, setPerceptionAccepted] = useState<boolean[]>(allCharacters.map(() => false));

  const [perceptionTags, setPerceptionTags] = useState<PerceptionTags[]>([]);
  


  const rerollCharacterPerception = (character: Character, index: number) => {
	revealOrRerollCharacterPerceptions(character, index);
	campaignManager.spendDMPoints(1);
	
  }

  const revealOrRerollCharacterPerceptions = (character: Character, index: number) => {
	const newPerceptionRevealed = [...perceptionRevealed];
	const newPerceptionTags = [...perceptionTags];

	newPerceptionTags[index] = campaignManager.getPerceptionTags(character) as PerceptionTags;
	
	if(!perceptionRevealed[index]) {
		if(perceptionRevealed.filter((p) => p).length !== 0) {
		   partyManager.perceptionEventReveal(character);
		}
		newPerceptionRevealed[index] = true;
	}
	

	setPerceptionRevealed(newPerceptionRevealed);
	
	setPerceptionTags(newPerceptionTags);
	
  };

  const acceptCharacterPerception = (character: Character, index: number) => {
	const newPerceptionAccepted = [...perceptionAccepted];
	newPerceptionAccepted[index] = true;
	setPerceptionAccepted(newPerceptionAccepted);

	campaignManager.acceptPerceptionTags(perceptionTags[index], character);

	chatManager.perceptionEvent(character, perceptionTags[index]);

}

  const getPlayerAttitudesStyle = (tag: Tag) => {
	return uiManager.getPlayerAttitudesStyle(tag);
  }

  const getTooltipMessage = (flavorTag: EncounterFlavorTag, perceptionTagsIndex: number, tagIndex: number) => {
		let messageString = '';


		const encounterTooltip = campaignManager.getCampaignTooltipForTag(flavorTag);
		if (encounterTooltip) {
			return encounterTooltip;
		}

		switch(perceptionTags[perceptionTagsIndex].type){
			case TAGS_TYPE.CURRENT_LOCATION:
				messageString = `The character carefully examines ${flavorTag.tag.name}.`;
				break;
			case TAGS_TYPE.BRANCH_LOCATION:
				messageString = `Reveal hints connecting ${flavorTag.tag.name} to an upcoming encounter.`;
				break;
			case TAGS_TYPE.CHARACTER_BACKSTORY:
				messageString = `Connect the player's backstory to this new topic.`;
				break;
			case TAGS_TYPE.CHARACTER_BACKSTORY_PROGRESS:
				messageString = `Reveal ${flavorTag.tag.name}, which relates to a character's backstory.`;
				break;
			case TAGS_TYPE.MAGIC_ITEM:
				messageString = `Reveal a magic item.`;
				break;
		}


	 switch(flavorTag.theme) {
		  case 'comedic':
			messageString += " The players find something funny about this."
		   break; 
		  case 'grim':
			messageString += " this is discussed in a grim manner"
			
			break; 
		  case 'fantasy':
			messageString += " the discussion around this is fantastical"
		   break; 
		  case 'heroic':
			messageString += " this evokes a feeling of heroism"
			
		   break; 
		  case 'horror':
			messageString += " something about this is scary"
			
		   break; 
		  case 'romantic':
			messageString += " this is discussed with romantic undertones"
		   break; 
		  case 'serious':
			messageString += " the players take this very seriously"
		   break; 
		  case 'dramatic':
			messageString += " the table gets very dramatic about this"
			break; 
		}

		return messageString;
  }



const getPerceptionTagTypeDisplay = (index: number) => {

	switch (perceptionTags[index].type) {
		case TAGS_TYPE.CURRENT_LOCATION:
			return (
				<div className="perception-display current-location">
					<div className='perception-description'>
						Nothing interesting found. Simply learned basic info about {perceptionTags[index].name}:
					</div>
					<div className="perception-tags">
						{perceptionTags[index].tags.map((flavorTag, indexb) => (
							<div key={indexb} className={`perception-tag text-pill type-${flavorTag.source} ${getPlayerAttitudesStyle(flavorTag.tag)} theme-${flavorTag.theme}`}>
								{flavorTag.tag.name}
								{flavorTag.theme && <Tooltip theme={''} tag={flavorTag.tag} message={getTooltipMessage(flavorTag, index, indexb)} />}
							</div>
						))}
					</div>
				</div>
			)
		case TAGS_TYPE.BRANCH_LOCATION:
			return (
				<div className="perception-display branch-location">
					<div className='perception-description'>
						Learned some things about an upcoming encounter called {perceptionTags[index].name} which may be interesting:
					</div>
					<div className="perception-tags">
						{perceptionTags[index].tags.map((flavorTag, indexb) => (
							<div key={indexb} className={`perception-tag text-pill type-foreshadowing ${getPlayerAttitudesStyle(flavorTag.tag)} theme-${flavorTag.theme}`}>
								{flavorTag.tag.name}
								{flavorTag.theme && <Tooltip theme={''} tag={flavorTag.tag}  message={getTooltipMessage(flavorTag, index, indexb)} />}
							</div>
						))}
					</div>
				</div>
			)
		case TAGS_TYPE.CHARACTER_BACKSTORY:
			return (
				<div className="perception-display backstory">
					<div className='perception-description'>
						Shocking new information revealed about {perceptionTags[index].name}'s Backstory!
					</div>
					<div className="perception-tags">
						{perceptionTags[index].tags.map((flavorTag, indexb) => (
							<div key={indexb} className={`perception-tag text-pill expected ${getPlayerAttitudesStyle(flavorTag.tag)} theme-${flavorTag.theme}`}>
								{flavorTag.tag.name}
								{flavorTag.theme && <Tooltip theme={''} tag={flavorTag.tag}  message={getTooltipMessage(flavorTag, index,indexb)} />}
							</div>
						))}
					</div>
				</div>
			)
			case TAGS_TYPE.CHARACTER_BACKSTORY_PROGRESS:
				return (
					<div className="perception-display backstory">
						<div className='perception-description'>
							Confirmation regarding {perceptionTags[index].name}'s Backstory:
						</div>
						<div className="perception-tags">
							{perceptionTags[index].tags.map((flavorTag, indexb) => (
								<div key={indexb} className={`perception-tag text-pill expected ${getPlayerAttitudesStyle(flavorTag.tag)} theme-${flavorTag.theme}`}>
									{flavorTag.tag.name}
									{flavorTag.theme && <Tooltip theme={''} tag={flavorTag.tag} message={getTooltipMessage(flavorTag, index, indexb)} />}
								</div>
							))}
						</div>
					</div>
				)
		case TAGS_TYPE.MAGIC_ITEM:
			let expectsMagicItem = tagManager.magicItemRumors.includes(perceptionTags[index].tags[0].tag);
			// Make sure magic item isn't one that was accpeted already
			perceptionTags.forEach((perceptionTag, i) => {
				if(perceptionAccepted[i] && perceptionTag.type === TAGS_TYPE.MAGIC_ITEM) {
					perceptionTag.tags.forEach(flavorTag => {
						if(flavorTag.tag.name === perceptionTags[index].tags[0].tag.name) {
							expectsMagicItem = false;
						}
					});
				}
			});

			return (
				<div className="perception-display magic-item">
					<div className='perception-description'>
						{expectsMagicItem ? 'Discovered a Magic Item you have heard of!' : 'Learned about a nearby Magic Item.'}
					</div>
					<div className="perception-tags">
						{perceptionTags[index].tags.map((flavorTag, indexb) => (
							<div key={indexb} className={`perception-tag text-pill ${expectsMagicItem ? '' : 'expected'}  type-${flavorTag.source} ${getPlayerAttitudesStyle(flavorTag.tag)} theme-${flavorTag.theme}`}>
								{flavorTag.tag.name}
								{flavorTag.theme && <Tooltip theme={''} tag={flavorTag.tag} message={getTooltipMessage(flavorTag, index, indexb)} />}
							</div>
						))}
					</div>
				</div>
			)
		case TAGS_TYPE.PLOT_HOOK:
			return (
				<div className="perception-display-campaign-container">
					<div className='perception-description'>
							{campaignManager.getCampaignName()} Progress:
					</div>
					<div className="perception-display plot-hook">
						<div className="campaign-icon">
							<CImage imageId={campaignManager.getCampaignIcon()}/>
						</div>
						<div className="campaign-plot-hook">
							<b>{perceptionTags[index].tags[0].tag.name}</b> {`${campaignManager.getNextPlotHookText()}`}
						</div>
					</div>
				</div>
			)
	}
}

	  return (
		<div className="game-encounter-perception">
			<EncounterHeader
				title={`Perception in ${encounter.location.name}`}
				description="Characters can search for clues and information"
				rightContent={
					<div className="perception-stats">
						Revealed: {perceptionRevealed.filter(p => p).length}/{allCharacters.length}
					</div>
				}
			/>
			<div className="perception-container">
				<div className='character-list'>
					{allCharacters.map((character, index) => (
					<div key={index} className="perception-row">
						<div className="character-container">
							<div className='character-display'>	
								<div className='character-name'>{character.name.split(' ')[0]}</div>
								<div className='character-icon'>
									<CImage imageId={character.icon}/>
								</div>
							</div>
						</div>
							{perceptionRevealed[index] ? (
								<div className='character-perception-tags-container'>
									{getPerceptionTagTypeDisplay(index)}
									{perceptionAccepted[index] ? 
									(
										<div className='character-perception-buttons'>
											<div className='character-perception-accepted'>
												
											</div>
										</div>
									) : 
									(
									<div className='character-perception-buttons'>
										<div className='character-perception-reroll-button' onClick={() => rerollCharacterPerception(character, index)}>
											REROLL
											<NumberDie baseDie={20} displayNumber={1}/>
										</div>
										<div className='character-perception-accept-button' onClick={() => acceptCharacterPerception(character, index)}>
											ACCEPT
										</div>
									</div>
								)}
								</div>
							) : 
							(
								<div className='character-perception-reveal-button' onClick={() => revealOrRerollCharacterPerceptions(character, index)}>
									<div className='reveal-title'>
										PERCEPTION CHECK
									</div>
								</div>
							)}
						</div>
					
					))}
				</div>
			</div>
		</div>
	  );
	};

export default GameEncounterPerception;