import { FlavorTagTheme } from "../../models";

interface RangeCardProps {
    min: number;
    max: number;
    totalSegments: number;
    isGrim?: boolean;
  }
  
  const RangeCard: React.FC<RangeCardProps> = ({ min, max, totalSegments, isGrim }) => {
    
    max++;
    const leftPosition = ((min / totalSegments) * 100) -1;
    const width = (((max - min) / totalSegments) * 100) +1.5;
  
    return (
     
      <div 
        className={`range-card`} 
        style={{
          left: `${leftPosition}%`,
          width: `${width}%`
        }}
      >
         <div className="combat-bar-label peril">PERIL</div>
         <div className="combat-bar-label stress">TENSION</div>
         <div className="combat-bar-label engagement">{isGrim ? 'GRIM ' : ' '}SCORE</div>
      </div>
    );
  };

  export default RangeCard;