import React from "react";
import "./notebook.scss";
import { PlayerNotebookEntry } from "../../models";
import UiManager from "../../managers/uiManager";
import Schedule from "../schedule";
import CImage from "./cimage";
import { useCharacterManager } from "../managerContexts";
import FillBar from "./fillBar";
import { click } from "@testing-library/user-event/dist/click";
import Cbox from "./cbox";
import { PLAYER_EXP_LEVEL } from "../../consts/general";

interface NoteBookPlayerProps {
  playerInfo: PlayerNotebookEntry;
  clickChar?: (idx: number) => void;
  singleColumn?: boolean;
}

const NotebookPlayer: React.FC<NoteBookPlayerProps> = ({
  playerInfo,
  singleColumn,
  clickChar = () => {},
}) => {
  const characterManager = useCharacterManager();

  const likesList = playerInfo.player.preferences.filter(
    (preference) => preference[1] > 0
  );
  const hatesList = playerInfo.player.preferences.filter(
    (preference) => preference[1] < 0
  );

  const getLikesList = () => {
    return likesList.map((preference, idx) => {
      return (
        <div key={idx} className="text-pill like">
          <div className="player-preference">- {preference[0].name}</div>
        </div>
      );
    });
  };

  const getHatesList = () => {
    return hatesList.map((preference, idx) => {
      return (
        <div key={idx} className="text-pill hate">
          <div className="player-preference">- {preference[0].name}</div>
        </div>
      );
    });
  };

  const getTraitsList = () => {
    return playerInfo.player.traits.map((trait, idx) => (
      <div key={idx}>
        <Cbox
          title={trait.name}
            theme="trait"
        >
        <div className="trait-description">{trait.description}</div>
        </Cbox>
      </div>
     
    ));
  };

  const clickCharacter = (idx: number) => {
    clickChar(idx);
  }


  return (
    <div className="notebook-player-container">

<div className="player-notebook-upper">

      {!singleColumn && (
        <div className="player-notebook-left-content">
          <div className="notebook-player-topbar">
            <div className="notebook-player-icon">
              <CImage imageId={playerInfo.player.icon} />
            </div>
            <div className="notebook-player-name">{playerInfo.player.name}</div>
          </div>

          <div className="player-notebook-mid-content">
            <div className="player-notebook-mid-left">
            <Cbox
                    title="STATS"
                        theme="stats"
                        >
                    <div className="character-level">
                        <FillBar
                        filledSegments={playerInfo.player.playerStats.experience}
                        totalSegments={PLAYER_EXP_LEVEL[playerInfo.player.playerStats.level]}
                        theme="player-level"
                        label={`LEVEL ${playerInfo.player.playerStats.level}`}
                        />
                    </div>
                    <div className="character-attitude">
                        <FillBar
                        filledSegments={playerInfo.player.playerStats.enjoyment}
                        totalSegments={playerInfo.player.playerStats.maxEnjoyment}
                        theme="player-level"
                        label={`ATTITUDE ${playerInfo.player.playerStats.enjoyment}/${playerInfo.player.playerStats.maxEnjoyment}`}
                        />
                    </div>
                    <div className="character-attention">
                        <FillBar
                        filledSegments={playerInfo.player.playerStats.involvement}
                        totalSegments={playerInfo.player.playerStats.maxInvolvement}
                        theme="player-level"
                        label={`ATTENTION ${playerInfo.player.playerStats.involvement}/${playerInfo.player.playerStats.maxInvolvement}`}
                        />
                    </div>
                    <Cbox
                        title="STRESS"
                        theme="stress"
                        >
                        <div className="character-stress">
                            CURRENT STRESS{" "}
                            {Math.max(
                            0,
                            playerInfo.player.playerStats.stress -
                                playerInfo.player.playerStats.stressBuffer
                            )}
                            %
                        </div>
                        <div className="character-stress">
                            RESILIENCE{" "}
                            {Math.min(
                            playerInfo.player.playerStats.stressBuffer,
                            playerInfo.player.playerStats.stress
                            )}
                            /{playerInfo.player.playerStats.stressBuffer}
                        </div>
                    </Cbox>

</Cbox>

                </div>

                


            <div className="player-notebook-mid-right">
            <Cbox
                    title="PREFERENCES"
                    theme="preferences"
                >
           
            <Cbox
                
                    title="LIKES"
                    theme="likes"
                    >
              <div className="player-preferences likes">
                {getLikesList()}
              </div>
            </Cbox>
             <Cbox
                    title="HATES"
                    theme="hates"
                >
              <div className="player-preferences hates">
               
                {getHatesList()}
              </div>
            
            </Cbox>
            </Cbox>
            </div>

            
          </div>

         


        </div>
      )}
      <div className="player-notebook-right-content">
        {!singleColumn && playerInfo.player.characters.length > 0 && (
          <Cbox 
            title="CHARACTERS"
            theme="characters"
          >
            {playerInfo.player.characters.map((char, idx) => {
              //const char = characterManager.getCharacterById(character);
              return (
                <div key={idx} className="player-character-container" onClick={() => clickCharacter(idx)}>
                  <div className={`player-character-hero-image`}>
                    <div className="challenge-character-icon">
                      <CImage imageId={char.icon} />
                    </div>
                    {char.injury === "injured" && (
                      <div className="player-character-hero-injury"></div>
                    )}
                    {char.injury === "dead" && (
                      <div className="player-character-hero-injury dead"></div>
                    )}
                  </div>
                  <div className="player-character-text-container">
                    <div className="player-character-name-container">
                      <div className="player-character-name">{char.name}</div>
                    </div>
                    <div className="player-character-info">
                      {char.race} {char.character_class}
                    </div>

                    <div className="player-character-info">
                      Level {char.level}
                    </div>
                  </div>
                </div>
              );
            })}
          </Cbox>
        )}
      </div>

</div>

      <div className="player-notebook-bottom">
            <Cbox 
             title="PLAYER TRAITS"
            theme="traits"
            >
                {playerInfo.player.traits.length === 0 ? (
                    <div className="no-traits">
                        level up to unlock traits
                        </div>
                        ) : (

                <div className="player-notebook-traits">{getTraitsList()}</div>
                    
                    )}
            </Cbox>
          </div>

    </div>
  );
};

export default NotebookPlayer;
