import React from "react";
import {
	Attribute,
	ATTRIBUTE_CONTRIBUTOR,
	ATTRIBUTE_TYPE,
	Character,
	HeroCombatant,
} from "../../models";
import {
	useAppManager,
	useCharacterManager,
} from "../managerContexts";
import {
	getRaceAttributes,
	getClassAttributes,
	getCombinedAttributes,
} from "../../consts/characterConsts";

// Generic attribute display props - can work with raw attributes
interface GenericAttributeDisplayProps {
	attributes: Attribute;
	lostAttributes?: Attribute;
	collapsed?: boolean;
	showAttributeLoss?: boolean;
}

// Character-specific attribute display props
interface CharacterAttributeDisplayProps {
	hero: HeroCombatant | Character;
	category: ATTRIBUTE_CONTRIBUTOR;
	collapsed?: boolean;
	showAttributeLoss?: boolean;
	overviewDisplay?: boolean;
}

const isHeroCombatant = (
	hero: HeroCombatant | Character
): hero is HeroCombatant => {
	return "character" in hero || "npcTag" in hero;
};

// Generic component for displaying raw attributes
const AttributeDisplay: React.FC<GenericAttributeDisplayProps> = ({
	attributes,
	lostAttributes,
	collapsed = false,
	showAttributeLoss = false,
}) => {
	const characterManager = useCharacterManager();

	const generateIcons = (attributeType: ATTRIBUTE_TYPE, value: number) => {
		const icons = [];
		const symbol = characterManager.getUFAttributeSymbol(attributeType);
		for (let i = 0; i < value; i++) {
			icons.push(
				<span
					key={`${attributeType}-${i}`}
					className={`attribute-icon ${attributeType}`}
				>
					{symbol}
				</span>
			);
		}
		return icons;
	};

	if (collapsed) {
		const allIcons = Object.keys(attributes).flatMap((key) =>
			generateIcons(
				key as ATTRIBUTE_TYPE,
				attributes[key as keyof Attribute] || 0
			)
		);
		return <div className="collapsed-attributes">{allIcons}</div>;
	}

	return (
		<div className="attribute-display-container">
			{Object.keys(attributes).map((key) => (
				<div key={key} className="attribute-display">
					<div className="attribute-title">
						<b>{characterManager.getUFAttributeName(key as ATTRIBUTE_TYPE)}</b>:
					</div>
					{lostAttributes && showAttributeLoss ? (
						<div className="attribute-container">
							<div className="attributes-retained">
								{generateIcons(
									key as keyof Attribute,
									attributes[key as keyof Attribute] || 0
								)}
							</div>
							<div className="attributes-lost">
								{generateIcons(
									key as keyof Attribute,
									lostAttributes[key as keyof Attribute] || 0
								)}
							</div>
						</div>
					) : (
						<div className="attribute-container">
							{generateIcons(
								key as keyof Attribute,
								attributes[key as keyof Attribute] || 0
							)}
						</div>
					)}
				</div>
			))}
		</div>
	);
};

// Character-specific component with all the complex logic
const CharacterAttributeDisplay: React.FC<CharacterAttributeDisplayProps> = ({
	hero,
	category,
	collapsed,
	showAttributeLoss,
	overviewDisplay = false,
}) => {
	const characterManager = useCharacterManager();
	const appManager = useAppManager();
	const combatManager = appManager.getCombatManager();

	let npcAttributes = {
		[ATTRIBUTE_TYPE.COMBAT]: 0,
		[ATTRIBUTE_TYPE.MAGIC]: 0,
		[ATTRIBUTE_TYPE.TOOLS]: 0,
	};
	let character;

	if (isHeroCombatant(hero) && hero.npcTag) {
		npcAttributes = combatManager.getNPCAttributes(hero.npcTag);
		hero.character = characterManager.generateNPC(hero.npcTag);

		category = ATTRIBUTE_CONTRIBUTOR.NPC;
	}

	character = isHeroCombatant(hero) ? (hero.character as Character) : hero;

	const raceAttributes = getRaceAttributes(character);
	const combinedattributesWithLoss = getCombinedAttributes(character, true);
	const lostAttributes = character.attributeLoss || {
		[ATTRIBUTE_TYPE.COMBAT]: 0,
		[ATTRIBUTE_TYPE.MAGIC]: 0,
		[ATTRIBUTE_TYPE.TOOLS]: 0,
	};
	const combinedAttributes = getCombinedAttributes(character);

	const generateIcons = (attributeType: ATTRIBUTE_TYPE, value: number) => {
		const icons = [];
		const symbol = characterManager.getUFAttributeSymbol(attributeType);
		for (let i = 0; i < value; i++) {
			icons.push(
				<span
					key={`${attributeType}-${i}`}
					className={`attribute-icon ${attributeType}`}
				>
					{symbol}
				</span>
			);
		}
		return icons;
	};

	const getGenericAttributesDisplay = (
		attributes: Attribute,
		lostAttributes?: Attribute
	) => {
		if (collapsed) {
			const allIcons = Object.keys(attributes).flatMap((key) =>
				generateIcons(
					key as ATTRIBUTE_TYPE,
					attributes[key as keyof Attribute] || 0
				)
			);
			return <div className="collapsed-attributes">{allIcons}</div>;
		} else {
			return Object.keys(attributes).map((key) => (
				<div key={key} className="attribute-display">
					<div className="attribute-title">
						<b>{characterManager.getUFAttributeName(key as ATTRIBUTE_TYPE)}</b>:
					</div>
					{lostAttributes && showAttributeLoss ? (
						<div className="attribute-container">
							<div className="attributes-retained">
								{generateIcons(
									key as keyof Attribute,
									attributes[key as keyof Attribute] || 0
								)}
							</div>
							<div className="attributes-lost">
								{generateIcons(
									key as keyof Attribute,
									lostAttributes[key as keyof Attribute] || 0
								)}
							</div>
						</div>
					) : (
						<div className="attribute-container">
							{generateIcons(
								key as keyof Attribute,
								attributes[key as keyof Attribute] || 0
							)}
						</div>
					)}
				</div>
			));
		}
	};

	const getGearAttributesDisplay = () => {
		if (character.gear.length === 0) {
			return <div></div>;
		}

		return character.gear.map((gear) => {
			return (
				<div key={gear.name} className="gear-attributes">
					<div className="gear-title">
						<b>{gear.name}: </b>
					</div>
					{getGenericAttributesDisplay(gear.attributes)}
				</div>
			);
		});
	};

	const getBackstoryElement = () => {
		const backstory = character.backstory;
		const nbCompleted = character.backstory.filter((bs) => bs.completed).length;
		const placeholdersNeeded = 4 - backstory.length;
		return (
			<div className="backstory-element">
				<div className="backstory-title">
					<b>Backstory : </b>
					{nbCompleted} / 4
				</div>
				<div className="backstory-tags">
					{backstory.map((bs, index) => (
						<div
							key={`backstory-${index}`}
							className={`tag-pill backstory-item ${
								bs.completed ? "type-backstory" : "expected"
							}`}
						>
							{bs.tag.name}
						</div>
					))}
					{[...Array(placeholdersNeeded)].map((_, index) => (
						<div
							key={`placeholder-${index}`}
							className="tag-pill backstory-item expected"
						>
							?
						</div>
					))}
				</div>
			</div>
		);
	};

	const getClassAttributesDisplay = () => {
		const levels = Array.from({ length: character.level }, (_, i) => i + 1);

		return levels.map((level) => {
			const classAttributes = getClassAttributes(
				character.character_class,
				level
			);

			return (
				<div key={level} className="level-attributes">
					<div className="level-title">
						<b>Level {level}:</b>{" "}
					</div>
					{getGenericAttributesDisplay(classAttributes)}
				</div>
			);
		});
	};

	const getAttributesDisplay = () => {
		switch (category) {
			case ATTRIBUTE_CONTRIBUTOR.CHARACTER_CLASS:
				return getClassAttributesDisplay();
			case ATTRIBUTE_CONTRIBUTOR.GEAR:
				return getGearAttributesDisplay();
			case ATTRIBUTE_CONTRIBUTOR.RACE:
				return getGenericAttributesDisplay(raceAttributes);
			case ATTRIBUTE_CONTRIBUTOR.ALL:
				if (showAttributeLoss) {
					return getGenericAttributesDisplay(
						combinedattributesWithLoss,
						lostAttributes
					);
				}
				return getGenericAttributesDisplay(combinedAttributes);
			case ATTRIBUTE_CONTRIBUTOR.NPC:
				return getGenericAttributesDisplay(npcAttributes);
			default:
				return null;
		}
	};

	return (
		<div className="attribute-display-container">
			{getAttributesDisplay()}
			{overviewDisplay && getGearAttributesDisplay()}
			{overviewDisplay && getBackstoryElement()}
		</div>
	);
};

// Export both components - keep CharacterAttributeDisplay as default for backwards compatibility
export default CharacterAttributeDisplay;
export { AttributeDisplay };
