import { NB_EXPECTED_ENCOUNTER_TAGS } from "../consts/campaigns";
import { TAG } from "../consts/tags";
import { ATTITUDE, FlavorTagSource, FlavorTagTheme, RPEncounterFlavorTag, EncounterFlavorTag, Tag, Encounter, TAG_TYPE, Campaign, Player, CHAT_TYPE } from "../models";
import AppManager from "./appManager";
import CampaignManager from "./campaignManager";
import ChatManager from "./chatManager";
import PlayerManager from "./playerManager";


class TagManager {

  private campaignManager: CampaignManager;
  private playerManager: PlayerManager;
  private chatManager: ChatManager;
  private appManager: AppManager;

  private _locationTagsCollected: Tag[] = [];
  private _locationTagsExpected: Tag[] = [];
  private _encounterTagsCollected: Record<string, Tag[]> = {};
  private _encounterTagsExpected: Record<string, Tag[]> = {};
  private _magicItemRumors: Tag[] = [];
  private _collectedTags: Tag[] = [];
  private _cashedInCollections: string[] = [];


  set locationTagsCollected(value: Tag[]) {
    this._locationTagsCollected = value;
  }

  get locationTagsExpected(): Tag[] {
    return this._locationTagsExpected;
  }

  set locationTagsExpected(value: Tag[]) {
    this._locationTagsExpected = value;
  }

  get encounterTagsCollected(): Record<string, Tag[]> {
    return this._encounterTagsCollected;
  }

  set encounterTagsCollected(value: Record<string, Tag[]>) {
    this._encounterTagsCollected = value;
  }

  get encounterTagsExpected(): Record<string, Tag[]> {
    return this._encounterTagsExpected;
  }

  set encounterTagsExpected(value: Record<string, Tag[]>) {
    this._encounterTagsExpected = value;
  }

  addMagicItemRumor(tag: Tag): void {
    if (!this._magicItemRumors.some(t => t.name === tag.name)) {
      this._magicItemRumors.push(tag);
    }
  }


  get magicItemRumors(): Tag[] {
    return this._magicItemRumors;
  }

  set magicItemRumors(value: Tag[]) {
    this._magicItemRumors = value;
  }

  get collectedTags(): Tag[] {
    return this._collectedTags;
  }

  set collectedTags(value: Tag[]) {
    this._collectedTags = value;
  }

  collectLocationTag(tag: Tag): void {
    if (!this._locationTagsCollected.some(t => t.name === tag.name)) {
      this._locationTagsCollected.push(tag);
      this.collectTag(tag);
    }

    // Check if all location tags are collected and trigger scene set modal
    if (this._locationTagsCollected.length >= this._locationTagsExpected.length) {
      this.triggerSetSceneModal();
      this.appManager.getUiManager().setEventImageOpacity(1);
    }

  }

  collectEncounterTag(tag: Tag): boolean {
    // check the current encounter, then the connected branches
    const currentEncounter = this.campaignManager.getCurrentEncounter();
    let satisfiedNeededTag = false;
    if (!currentEncounter) return false;  
    const expectedTags = currentEncounter.tags || [];
    if (expectedTags.some(t => t.name === tag.name) && !this.encounterTagsCollected[currentEncounter.name]?.some(t => t.name === tag.name)) {

      // If the tag is expected, add it to the collected tags for that branch
      if (!this.encounterTagsCollected[currentEncounter.name]) {
        this.encounterTagsCollected[currentEncounter.name] = [];
      }
      this.encounterTagsCollected[currentEncounter.name].push(tag);
      satisfiedNeededTag = true;
      
      // Trigger encounter tag modal
      this.triggerEncounterTagModal(tag);
    }
   
    return satisfiedNeededTag;
  }

  isTagCollected(tag: Tag): boolean {
    return this.collectedTags.some(t => t.name === tag.name);
  }


  introduceNewTagForUpcomingBranch(branch: Encounter, tag: Tag) {
    // check the current encounter, then the connected branches
    if(!branch || !this.campaignManager.getCurrentEncounter()) return;
    if(!branch.tags.includes(tag) && this.encounterTagsExpected[branch.name] && !this.encounterTagsExpected[branch.name].includes(tag)) {
     
      this.encounterTagsExpected[branch.name].push(tag);
     
    }
  }

  collectUpcomingEncounterTag(tag: Tag): boolean {
    // check the current encounter, then the connected branches
    const currentEncounter = this.campaignManager.getCurrentEncounter();
    let satisfiedNeededTag = false;
    if (!currentEncounter) return false;
    const branches = currentEncounter.branches || [];
    branches.forEach(branch => {
      if (this.encounterTagsExpected[branch.name]) {
        const expectedTags = this.encounterTagsExpected[branch.name];
        if (expectedTags.some(t => t.name === tag.name) && !this.encounterTagsCollected[branch.name]?.some(t => t.name === tag.name)) {
          // If the tag is expected, add it to the collected tags for that branch
          if (!this.encounterTagsCollected[branch.name]) {
            this.encounterTagsCollected[branch.name] = [];
          }
          this.encounterTagsCollected[branch.name].push(tag);
          satisfiedNeededTag = true;
        }
      }
    });
 

    return satisfiedNeededTag;
  }
  // Functions to get tags for various reasons but which do not ask a collection of tags directly. 

  getOutstandingQuestTags(): Tag[] {
    return this.appManager?.getQuestManager()?.getOutstandingQuestTags() || [];
  } 
  // Get tags needed for backstories

  getOutstandingBackstoryTags() {
    return this.appManager.getCharacterManager().getAllExpectedBackstoryTags();
  }

  getOutstandingLocationTags() {
    return this._locationTagsExpected.filter(tag => !this._locationTagsCollected.some(t => t.name === tag.name));
  }

  getCollectedLocationTags(): Tag[] {
    return this._locationTagsCollected;
  }

  getEstablishedFriendlyTags() {
    // get number of unique friendly tags from the list:
    const allTags: Tag[] = Object.values(TAG).flat();
    const friendlyTags = allTags.filter(tag => tag.friendly);
    const establishedFriendlyTags = this.collectedTags.filter(tag => friendlyTags.includes(tag));
    return establishedFriendlyTags;
  }

  getRandomFriendlyTags(nbTags: number, allowCollected: boolean = true): Tag[] {
    // get number of unique friendly tags from the list:
    const allTags: Tag[] = Object.values(TAG).flat();
    if (!allowCollected) {
      const collectedFriendlyTags = this.getEstablishedFriendlyTags();
      const uncollectedFriendlyTags = allTags.filter(tag => tag.friendly && !collectedFriendlyTags.includes(tag));
      return this.getRandomTags(nbTags, uncollectedFriendlyTags);
    }
    const friendlyTags = allTags.filter(tag => tag.friendly);
    const randomFriendlyTags = this.getRandomTags(nbTags, friendlyTags);
    return randomFriendlyTags;
  }

  getPotentialCombatantsTags() {
    // get number of unique friendly tags from the list:
    const allTags: Tag[] = Object.values(TAG).flat();
    const combatantTags = allTags.filter(tag => tag.enemy || tag.friendly);
    return combatantTags;
  }

  getEstablishedCombatantTags() {
    // get number of unique friendly tags from the list:
    const allTags: Tag[] = Object.values(TAG).flat();
    const combatantTags = allTags.filter(tag => tag.enemy || tag.friendly);
    const establishedCombatantTags = this.collectedTags.filter(tag => combatantTags.includes(tag));
    return establishedCombatantTags;
  }


  getRandomMagicItemTag(nbTags: number): Tag[] {

    const expectedGearTags = [...this.magicItemRumors];
    // determine if we should get a new random element, or one of the outstanding ones

    while (expectedGearTags.length < nbTags + 2) {
      const newGear = this.appManager.getCharacterManager().getRandomGear();
      const gearTag = { name: newGear.name, type: TAG_TYPE.GEAR };
      expectedGearTags.push(gearTag);
    }
    const randomTags = this.getRandomTags(nbTags, expectedGearTags);
    return randomTags;

  }



  get locationTagsCollected(): Tag[] {
    return this._locationTagsCollected;
  }


  initializeNewCampaign(campaign: Campaign) {
    this.locationTagsCollected = [];
    this.encounterTagsCollected = {};
    this.magicItemRumors = [];
    this.collectedTags = [];
    this.cashedInCollections = [];
    // Add all the authored tags for encounters to the expected tags. 
    this.encounterTagsExpected = {};
    if (campaign) {
      campaign.encounters.forEach(encounter => {
        this.encounterTagsExpected[encounter.name] = encounter.tags || [];
        this.encounterTagsCollected[encounter.name] = [];
      });
    }

  }


  initializeNewEncounter(encounter: Encounter) {
    this.locationTagsCollected = [];
    this.locationTagsExpected = encounter.location.tags || [];
  }










  constructor(
    campaignManager: CampaignManager,
    playerManager: PlayerManager,
    chatManager: ChatManager | undefined,
    appManager: AppManager
  ) {
    this.playerManager = playerManager;
    this.chatManager = chatManager as ChatManager; // Will be set later if undefined
    this.campaignManager = campaignManager;
    this.appManager = appManager;
  }

  // Method to set chat manager after construction
  setChatManager(chatManager: ChatManager) {
    this.chatManager = chatManager;
  }

  getTooltipText(rpFlavorTag: RPEncounterFlavorTag) {
    let tooltipText = "";

    if (rpFlavorTag.tag) {
      if (this.campaignManager.getCampaignTooltipForTag(rpFlavorTag.tag)) {
        return this.campaignManager.getCampaignTooltipForTag(rpFlavorTag.tag);
      }

      const flavorTag = rpFlavorTag.tag;
      const playerLikes = this.playerManager.getPartyAttitudeForTag(flavorTag.tag);
      switch (flavorTag.source) {
        case FlavorTagSource.LOCATION:
          switch (Math.floor(Math.random() * 2)) {
            case 0:
              tooltipText = `The players interact with ${flavorTag.tag.name}`;
              break;
            case 1:
              tooltipText = `You describe some nearby ${flavorTag.tag.name}`;
              break;
          }

          break;
        case FlavorTagSource.MAGIC_ITEM_EXPECTED:
          tooltipText = "Hint about a magic item: " + flavorTag.tag.name;
          break;
        case FlavorTagSource.FORESHADOWING:
          if (playerLikes === ATTITUDE.LIKE) {
            tooltipText = `Some players will be happy to know that ${flavorTag.tag.name} seems to be part of an upcoming encounter`;
          } else if (playerLikes === ATTITUDE.HATE) {
            tooltipText = `Much to one player's annoyaince - you imply that ${flavorTag.tag.name} will be expected in an upcoming encounter`;
          } else {
            tooltipText = `You foreshadow ${flavorTag.tag.name} for an upcoming encounter`;
          }
          break;
        case FlavorTagSource.FORESHADOWING_RANDOM:
          if (playerLikes === ATTITUDE.LIKE) {
            tooltipText = `The players like ${flavorTag.tag.name}...add it on a whim to an upcoming encounter`;
          } else if (playerLikes === ATTITUDE.HATE) {
            tooltipText = `${flavorTag.tag.name} will feature in an upcoming encounter - even though some players hate ${flavorTag.tag.name}!`;
          } else {
            tooltipText = `Ad-lib ${flavorTag.tag.name} into an upcoming encounter`;
          }
          break;
        case FlavorTagSource.RUMOR:
          // check if this is an expected tag for a branch
          const isExpectedForCurrentEncounter = this.campaignManager
            .getExpectedEncounterTags()
            .includes(flavorTag.tag);
          tooltipText = isExpectedForCurrentEncounter
            ? `You reinforce the importance of ${flavorTag.tag.name}.`
            : `You confirm the players' questions about ${flavorTag.tag.name}`;
          break;
        case FlavorTagSource.PLAYERS:
          tooltipText = `The players have feelings about ${flavorTag.tag.name}`;
          break;
        case FlavorTagSource.QUESTS:
          tooltipText = `While questing, the players learn about ${flavorTag.tag.name}`;
          break;
        case FlavorTagSource.ALL:
          tooltipText = `For no specific reason, the players discuss ${flavorTag.tag.name}`;
          break; case FlavorTagSource.MAGIC_ITEM:
          if(this.magicItemRumors.includes(flavorTag.tag)) {
            tooltipText = `You award the players the ${flavorTag.tag.name} they have been looking for!`;
          } else {
            tooltipText = `You tell the players about a magic item: ${flavorTag.tag.name}`;
          }
          break;
        case FlavorTagSource.BACKSTORY:
          tooltipText = `The ${flavorTag.tag.name} is integral to a character's backstory!`;
          break;
        case FlavorTagSource.PLOT:
          tooltipText = `${flavorTag.tag.name} will be important to the story`;
          break;
        case FlavorTagSource.ENEMY:
          tooltipText = `Introduce some ${flavorTag.tag.name} as a new enemy for this campaign.`;
          break;
        case FlavorTagSource.TREASURE:
          tooltipText = `The players discover valuable treasure!`;
          break;
        case FlavorTagSource.NONE:
          tooltipText = flavorTag?.tag.name + ' comes up for no specific reason.';
          break;
        case FlavorTagSource.ENCOUNTER:
          tooltipText = flavorTag?.tag.name + ' is a very important topic. Maxim should have Authored a tooltip for this! BAD MAXIM';
          tooltipText = "LAUNCH ENCOUNTER SCENE:" + tooltipText;
          break;
        case FlavorTagSource.QUEST:
          tooltipText = `Revealing or discussing this will progress a quest`;
          break;
         case FlavorTagSource.QUEST_TERMINAL:
          tooltipText = `Revealing or discussing this will complete a quest`;
          break; 
        default:
          tooltipText = "";
          break;
      }

      tooltipText += this.chatManager.getThemePostFix(flavorTag);

      return tooltipText;
    } else if (rpFlavorTag.character) {
      tooltipText = `Spend some time focusing on ${rpFlavorTag.character.name}`;
      return tooltipText;
    } else if (rpFlavorTag.type === "treasure") {
      tooltipText = `TREASURE: award the players a bit of treasure...`;
      return tooltipText;
    } else if (rpFlavorTag.type === "dmpoint") {
      tooltipText = `DM POINT: You have a great idea which will inform future decisions and make you an overall better dm`;
      return tooltipText;
    }
  };

  /**
   * Convert a simple Tag into an EncounterFlavorTag with default source/theme
   */
  public tagToEncounterFlavorTag(tag: Tag, source: FlavorTagSource = FlavorTagSource.ALL, theme: FlavorTagTheme = FlavorTagTheme.NONE, isHighlighted = false): EncounterFlavorTag {
    return { tag, source, theme };
  }

  public collectTag(tag: Tag | EncounterFlavorTag): void {
    const flavorTag = ('tag' in tag) ? tag : this.tagToEncounterFlavorTag(tag);
    const baseTag = flavorTag.tag;


    if (!this.collectedTags.some(t => t.name === baseTag.name)) {
      this.collectedTags.push(baseTag);
    }

  }

  public getCollectedTags(): Tag[] {
    return this.collectedTags;
  }

  public isCurrentEncounterTag(tagName: string): boolean {
    const currentEncounter = this.campaignManager.getCurrentEncounter();
    if (!currentEncounter) return false;
    return this.encounterTagsExpected[currentEncounter.name].some(t => t.name === tagName);
  }


 public getCollectedEncounterTags(): Tag[] {
    const currentEncounter = this.campaignManager.getCurrentEncounter();
    if (!currentEncounter) return [];
    return this.encounterTagsCollected[currentEncounter.name] || [];
  }

  public getOutstandingEncounterTags(): Tag[] {
    const currentEncounter = this.campaignManager.getCurrentEncounter();
    
    if (!currentEncounter) return [];
    const expectedTags = this.encounterTagsExpected[currentEncounter.name] || [];
    return expectedTags.filter(tag => !this.encounterTagsCollected[currentEncounter.name]?.some(t => t.name === tag.name));
  }

  public getUpcomingEncounterTags(): Tag[] {
    const currentEncounter = this.campaignManager.getCurrentEncounter();
    if (!currentEncounter) return [];
    const upcomingEncounters = currentEncounter.branches || [];
    return upcomingEncounters.flatMap(encounter => this.encounterTagsExpected[encounter.name] || []);
  }

  public isUpcomingEncounterTag(tagName: string): boolean {
    const currentEncounter = this.campaignManager.getCurrentEncounter();
    const upcomingEncounters = currentEncounter?.branches;;
    if (!upcomingEncounters || upcomingEncounters.length === 0) return false;
    return upcomingEncounters.some(encounter =>
      this.encounterTagsExpected[encounter.name].some(t => t.name === tagName)
    );
  }

  public showNextEncounterModal(): void {
    this.triggerNextEncounterModal();
  }
  // Check against magic items, backstories, etc and change the location based on that. This will then set the style
  getFormattedSource = (tag: Tag, source: FlavorTagSource) => {
    let formattedSource = source;
    if (this.magicItemRumors.includes(tag)) {
      formattedSource = FlavorTagSource.MAGIC_ITEM;
    } else if(tag.type === TAG_TYPE.GEAR) {
      formattedSource = FlavorTagSource.MAGIC_ITEM_EXPECTED;
    } 
    
    else if (this.getOutstandingBackstoryTags().includes(tag)) {
      formattedSource = FlavorTagSource.BACKSTORY;
    }
    else if (this.locationTagsExpected.includes(tag)) {
      formattedSource = FlavorTagSource.LOCATION;
    } else if (this.isCurrentEncounterTag(tag.name)) {
      //  debugger
      formattedSource = FlavorTagSource.ENCOUNTER;
    }
    else if (this.isUpcomingEncounterTag(tag.name)) {
      //  debugger
      formattedSource = FlavorTagSource.FORESHADOWING;
    }
    return formattedSource;
  }

  couldAssignRandomTagToBranch(encounter: Encounter) {

    return NB_EXPECTED_ENCOUNTER_TAGS > this.encounterTagsExpected[encounter.name].length;
  }


  // Check if we might be able to assign a randome tag to any branch 
  couldAssignRandomTag(currentEncounter: Encounter | undefined): boolean {
    if (!currentEncounter) {
      return false;
    }

    const upcomingBranches = currentEncounter.branches || [];
    let couldAssignRandomTagToAnyBranch = false;
    upcomingBranches.forEach((branch) => {
      if (this.couldAssignRandomTagToBranch(branch)) {
        couldAssignRandomTagToAnyBranch = true;
      }
    });
    return couldAssignRandomTagToAnyBranch;
  }

  tagToFlavorTag(tag: Tag, theme: FlavorTagTheme = FlavorTagTheme.NONE): EncounterFlavorTag {
    const source = this.getFormattedSource(tag, FlavorTagSource.ALL);
    return {
      tag,
      source,
      theme
    };
  }

  convertRpTagToFlavorTag(rpTag: RPEncounterFlavorTag): EncounterFlavorTag | undefined {
    // Usually thie RP tag just contains a flavor tag, but sometimes they are special and we neex to eract the flavortag and add some shit

    if (rpTag.type === "treasure") {
      return {
        tag: {
          name: "Treasure",
        },
        source: FlavorTagSource.TREASURE,
        theme: FlavorTagTheme.NONE,
      };
    }

    if (rpTag.type === "dmpoint") {
      return {
        tag: {
          name: "DM Point",
        },
        source: FlavorTagSource.DM_POINT,
        theme: FlavorTagTheme.NONE,
      };
    }

    if (rpTag.type === "character") {
      return {
        tag: {
          name: rpTag.character!.name,
        },
        source: FlavorTagSource.PLAYERS,
        theme: FlavorTagTheme.NONE,
      };
    }

    return rpTag.tag;
  }



  generateRelevantFlavorTags(nbTags: number, omitSources?: FlavorTagSource[]): EncounterFlavorTag[] {

    const encounter = this.campaignManager.getCurrentEncounter();
    if (!encounter) {
      return [];
    }

    const outstandingLocationTags = this.getOutstandingLocationTags();
    const outstandingEncounterTags = this.encounterTagsExpected[encounter.name] || [];
    const upcomingEncounterTagsToAdd = this.getUpcomingEncounterTags();
    const magicItemsToAdd = this.magicItemRumors;
    const backstoryTagsToAdd = this.getOutstandingBackstoryTags();
    const playerPreferenceTagsToAdd = this.playerManager.getAllPlayerPreferences();
    const questTagsToAdd = this.appManager?.getQuestManager()?.getCurrentQuestTags() || [];

    let allTagsSoFar: any[] = [];

    //Convert the locationTags into EncounterFlavorTags
    const locationFlavorTags: EncounterFlavorTag[] = outstandingLocationTags.map(tag => {
      return {
        tag: tag,
        source: this.getFormattedSource(tag, FlavorTagSource.LOCATION),
        theme: this.getTheme()
      }
    });
    allTagsSoFar = [...allTagsSoFar, ...outstandingLocationTags];


    // MAXIM TODO: Why do we get so many of these in RP events?!
    const encounterFlavorTags: EncounterFlavorTag[] = outstandingEncounterTags.map(tag => {
      return {
        tag: tag,
        source: this.getFormattedSource(tag, FlavorTagSource.ENCOUNTER),
        theme: FlavorTagTheme.NONE
      }
    });
    // we only want to add one encounter flavor tag, so get a random one, 
    if (encounterFlavorTags.length > 0) {
      const randomEncounterTag = encounterFlavorTags[Math.floor(Math.random() * encounterFlavorTags.length)];
      allTagsSoFar.push(randomEncounterTag);
    }


    //CONVERT THE UPCOMING TAGS INTO FORESHADOWING TAGS
    const upcomingEncounterFlavorTags: EncounterFlavorTag[] = upcomingEncounterTagsToAdd.map(tag => {
      return {
        tag: tag,
        source: this.getFormattedSource(tag, FlavorTagSource.FORESHADOWING),
        theme: this.getTheme()
      }
    });

    allTagsSoFar = [...allTagsSoFar, ...upcomingEncounterTagsToAdd];


    //Convert the magic items into EncounterFlavorTags
    const magicItemFlavorTags: EncounterFlavorTag[] = magicItemsToAdd.map(tag => {
      return {
        tag: tag,
        source: this.getFormattedSource(tag, FlavorTagSource.MAGIC_ITEM),
        theme: this.getTheme()
      }
    });

    allTagsSoFar = [...allTagsSoFar, ...magicItemsToAdd];
    //Convert the backstory tags into EncounterFlavorTags
    const backstoryFlavorTags: EncounterFlavorTag[] = backstoryTagsToAdd.map(tag => {
      return {
        tag: tag,
        source: this.getFormattedSource(tag, FlavorTagSource.BACKSTORY),
        theme: this.getTheme()
      }
    });
    allTagsSoFar = [...allTagsSoFar, ...backstoryTagsToAdd];

    // Helper function to generate extra random flavor tags
    const generateExtraRandomFlavorTags = (count: number, existingTags: Tag[]): EncounterFlavorTag[] => {
      const extraTags = this.getRandomTags(count, this.getAllAllTags(), existingTags);
      return extraTags.map(tag => {
        if (tag === undefined) {
          debugger
        }
        if (omitSources && !omitSources.includes(FlavorTagSource.FORESHADOWING_RANDOM)  && this.couldAssignRandomTag(this.campaignManager.getCurrentEncounter()) && Math.random() < 0.25) {
          return {
            tag: tag,
            source: this.getFormattedSource(tag, FlavorTagSource.FORESHADOWING_RANDOM),
            theme: this.getTheme()
          }
        }

        return {
          tag: tag,
          source: this.getFormattedSource(tag, FlavorTagSource.ALL),
          theme: this.getTheme()
        }
      });
    };

    // Now we can get random tags. These can be in favor of player favorites or hates, or things to complete a collection, etc....

    // This is how we get the random tags.
    const randomPlayerTags: Tag[] =  this.getRandomTags(4, playerPreferenceTagsToAdd, allTagsSoFar);
    const initialExtraFlavorTags = generateExtraRandomFlavorTags(4, allTagsSoFar);
    const randomPlayerFlavorTags: EncounterFlavorTag[] = randomPlayerTags.map(tag => {
      if (tag === undefined) {
        debugger
      }
      return {
        tag: tag,
        source: this.getFormattedSource(tag, FlavorTagSource.ALL),
        theme: this.getTheme()
      }
    });

    let allFlavorTags = [...locationFlavorTags, ...encounterFlavorTags, ...upcomingEncounterFlavorTags, ...magicItemFlavorTags, ...backstoryFlavorTags, ...questTagsToAdd, ...randomPlayerFlavorTags, ...initialExtraFlavorTags];
    allFlavorTags = allFlavorTags.filter(tag => this.collectedTags.includes(tag.tag) === false);
    if (omitSources) {
      allFlavorTags = allFlavorTags.filter(tag => !omitSources.includes(tag.source));
    }

    const randomizedTags = allFlavorTags.sort(() => Math.random() - 0.5).slice(0, nbTags);

    // If we don't have enough tags to fulfill our order, generate additional random tags
    if (randomizedTags.length < nbTags) {
      const shortfall = nbTags - randomizedTags.length;
      const allUsedTags = allFlavorTags.map(ft => ft.tag);
      const additionalFlavorTags = generateExtraRandomFlavorTags(shortfall * 2, allUsedTags); // Generate extra to account for filtering
      
      // Filter out collected tags and omitted sources
      let filteredAdditionalTags = additionalFlavorTags.filter(tag => this.collectedTags.includes(tag.tag) === false);
      if (omitSources) {
        filteredAdditionalTags = filteredAdditionalTags.filter(tag => !omitSources.includes(tag.source));
      }
      
      // Add the additional tags to fill the shortfall
      randomizedTags.push(...filteredAdditionalTags.slice(0, shortfall));
    }

    return randomizedTags;
   
  }



  getRandomTags(nbTags: number | number[], tagList: Tag[], omittedTags?: Tag[]) {

    if (Array.isArray(nbTags)) {
      // if nbTags is a range, get a random amount selected from that range
      nbTags = Math.floor(Math.random() * (nbTags[1]) + nbTags[0]);
    }
    if (nbTags <= 0) {
      return [];
    }
    if (omittedTags) {
      const omittedNames = omittedTags.map(tag => tag.name);
      tagList = tagList.filter(tag => !omittedNames.includes(tag.name));
    }
    const randomTags = tagList.sort(() => Math.random() - 0.5).slice(0, nbTags);
    return randomTags;
  }

  getAllAllTags(): Tag[] {
     return Object.values(TAG).flat();
  }
  

 getTheme(): FlavorTagTheme {

    // 1 in 5 chance of having a theme
    if (Math.floor(Math.random() * 6) !== 0) {
      return FlavorTagTheme.NONE;
    }


    const availableTones = this.campaignManager.getUnlockedTones();

    if (availableTones.length === 0) {
      return FlavorTagTheme.NONE;
    }

    // Randomly select one of the available tones
    const themeRoll = Math.floor(Math.random() * availableTones.length);
    return availableTones[themeRoll];

  }

  // Modal trigger methods
  triggerSetSceneModal(): void {
    const currentEncounter = this.campaignManager.getCurrentEncounter();
    if (!currentEncounter) return;

    const uiManager = this.appManager.getUiManager();
    uiManager.showModal({
      title: 'Location Described',
      type: 'setscene',
      options: [{
        id: 'ok',
        title: 'Continue',
        description: 'Proceed with the encounter',
        callback: () => {
          this.campaignManager.setCurrentEncounterSceneComplete();
        }
      }],
      // Store additional data for the modal component
      sceneData: {
        location: currentEncounter.location,
        encounter: currentEncounter
      }
    } as any);
  }

  triggerEncounterTagModal(tag: Tag): void {
    const currentEncounter = this.campaignManager.getCurrentEncounter();
    if (!currentEncounter) return;

    // Get chat information for the tag
    const chatTrigger = this.campaignManager.getEncounterChatTrigger(tag);
    
    // Send conversation directly to chat instead of showing modal
    if (chatTrigger) {
      const chatManager = this.appManager.getChatManager();
      
      // Handle different chat trigger types with fast speed for modal conversations
      if (Array.isArray(chatTrigger)) {
        // Array of messages
        chatManager.fireNewChatMessage(chatTrigger, { speed: 'fast' });
      } else if (typeof chatTrigger === 'string') {
        // Simple string message
        chatManager.fireNewChatMessage(chatTrigger, { speed: 'fast' });
      } else if (chatTrigger.message) {
        // Message object
        chatManager.fireNewChatMessage({
          message: chatTrigger.message,
          speaker: chatTrigger.speaker || { type: CHAT_TYPE.DM, id: 0 }
        }, { speed: 'fast' });
      }
    }
    
    // Still show a simple modal for scoring/rewards if needed, but without conversation
    const uiManager = this.appManager.getUiManager();
    uiManager.showModal({
      title: `Tag Discovered: ${tag.name}`,
      type: 'encountertag',
      options: [{
        id: 'ok',
        title: 'Continue',
        description: 'Proceed with the encounter',
        callback: () => {
          // Modal will close automatically
        }
      }],
      // Store additional data for the modal component
      tagData: {
        tag: tag,
        chatTrigger: chatTrigger, // Keep conversation in modal AND send to chat
        encounter: currentEncounter
      }
    } as any);
  }

  triggerNextEncounterModal(): void {
    const currentEncounter = this.campaignManager.getCurrentEncounter();
    if (!currentEncounter || !currentEncounter.branches || currentEncounter.branches.length === 0) return;

    const players = this.playerManager.getPlayers();
    const branches = currentEncounter.branches;
    
    // Calculate player preferences for each branch
    const branchPreferences = this.calculateBranchPreferences(branches, players);
    
    const uiManager = this.appManager.getUiManager();
    uiManager.showModal({
      title: 'Choose Next Encounter',
      type: 'nextencounter',
      options: branches.map(branch => ({
        id: branch.name,
        title: branch.name,
        description: branch.description,
        callback: () => {
          this.handleNextEncounterSelection(branch);
        }
      })),
      // Store additional data for the modal component
      encounterData: {
        branches: branches,
        branchPreferences: branchPreferences,
        currentEncounter: currentEncounter
      }
    } as any);
  }

  private calculateBranchPreferences(branches: Encounter[], players: Player[]): { [branchName: string]: Player[] } {
    const preferences: { [branchName: string]: Player[] } = {};
    
    branches.forEach(branch => {
      preferences[branch.name] = [];
      const expectedTags = this.encounterTagsExpected[branch.name] || [];
      const collectedTags = this.encounterTagsCollected[branch.name] || [];
      
      players.forEach(player => {
        const score = this.calculatePlayerBranchScore(player, expectedTags, collectedTags);
        if (score > 0) {
          preferences[branch.name].push(player);
        }
      });
    });
    
    // Ensure each player has at least one preference (random if none)
    players.forEach(player => {
      const hasPreference = Object.values(preferences).some(prefs => prefs.includes(player));
      if (!hasPreference) {
        const randomBranch = branches[Math.floor(Math.random() * branches.length)];
        preferences[randomBranch.name].push(player);
      }
    });
    
    return preferences;
  }

  private calculatePlayerBranchScore(player: Player, expectedTags: Tag[], collectedTags: Tag[]): number {
    let score = 0;
    
    // Higher score for more collected tags
    const collectedCount = collectedTags.length;
    if (collectedCount >= 3) score += 4;
    else if (collectedCount >= 1) score += 2;
    else return 0; // Never consider branches with zero collected tags
      // Factor in player preferences
    expectedTags.forEach(tag => {
      const preference = player.preferences.find((pref: [Tag, ATTITUDE]) => pref[0].name === tag.name);
      if (preference) {
        score += preference[1]; // ATTITUDE.LIKE = 1, ATTITUDE.HATE = -1
      }
    });
    
    return Math.max(0, score);
  }

  private handleNextEncounterSelection(selectedBranch: Encounter): void {
    // Get the current encounter branches for evaluation
    const currentEncounter = this.campaignManager.getCurrentEncounter();
    const branches = currentEncounter?.branches || [];
    
    // Let PartyManager handle all encounter selection effects and advancement
    this.appManager.getPartyManager().handleNextEncounterSelection(selectedBranch, branches, this);
  }

  get cashedInCollections(): string[] {
    return this._cashedInCollections;
  }

  set cashedInCollections(value: string[]) {
    this._cashedInCollections = value;
  }

  cashInCollection(collectionName: string): void {
    if (!this._cashedInCollections.includes(collectionName)) {
      this.chatManager.triggerCollectionCompleteChat(collectionName);
      this._cashedInCollections.push(collectionName);
    }
  }

  isCollectionCashedIn(collectionName: string): boolean {
    return this._cashedInCollections.includes(collectionName);
  }

  getAvailableCollectionRewards(): number {
    const { TAG_COLLECTIONS } = require('../consts/tagCollections');
    const completedCollections = TAG_COLLECTIONS.filter((c: any) =>
      c.tags.every((t: Tag) => this._collectedTags.some((ct: Tag) => ct.name === t.name))
    );
    return completedCollections.filter((c: any) => !this.isCollectionCashedIn(c.name)).length;
  }
}

export default TagManager;