import GameTable from "./gameTable";
import { useAppManager, usePlayerManager, useUiManager } from '../managerContexts';

import './gameRoom.scss';
import BottomBar from './bottomBar';
import RightSidebar from './rightSidebar';
import { useEffect, useState } from 'react';

import PlayerDisplay from './PlayerDisplay';
import TreasureDisplay from '../UI/treasureDisplay';
import LevelUpButton from '../UI/LevelUpButton';
import PartyLevelUpModal from '../UI/PartyLevelUpModal';





interface GameRoomProps {
	overviewMode?: boolean;
}

const GameRoom: React.FC<GameRoomProps> = ({overviewMode = false}) => {

	const appManager = useAppManager();
	const playerManager = usePlayerManager(); 
	const players = playerManager.getPlayers();

	const uiManager = useUiManager();
	const partyManager = appManager.getPartyManager();
	const [isLevelUpModalOpen, setIsLevelUpModalOpen] = useState(false);
	
	//const [partyScore, setPartyScore] = useState(partyManager.getPartyScore());

	const [treasureLevel, setTreasureLevel] = useState(partyManager.getTreasureLevel());
	const [treasurePoints, setTreasurePoints] = useState(partyManager.getTreasurePoints());	
	//const [campaignScore, getCampaignScore] = useState(campaignManager.getCampaignScore());


	
  useEffect(() => {
	const forceUpdate = () => {
	  setTreasureLevel(partyManager.getTreasureLevel());
	  setTreasurePoints(partyManager.getTreasurePoints());
	};
	uiManager.on('playerUpdate', forceUpdate);
	return () => {
	  uiManager.off('playerUpdate', forceUpdate);
	};
  }, [uiManager, partyManager]);



	const playerRowDisplay = () => {
		return (
		<div className="gameroom-player-row">
			{players.map((player) => (
				<PlayerDisplay 
					key={player.id}
					player={player}
				/>
			))}
		</div>)
	}



  return (
	<div className="gameroom-container">
		{/* New 2-Panel Layout */}
		<div className="gameroom-layout">
			{/* Main Content Area */}
			<div className="gameroom-main-content">
				{/* Top Container - Player Info */}
				<div className="gameroom-top-container">
					<div className="player-display-container">
						<div className="player-row">
							{playerRowDisplay()}
							<LevelUpButton 
								characters={appManager.getCharacterManager().getPlayerCharacters()} 
								onClick={() => setIsLevelUpModalOpen(true)} 
							/>
							<TreasureDisplay 
								treasureLevel={treasureLevel}
								treasurePoints={treasurePoints}
							/>

						</div>
					</div>
				</div>
				
				{/* Game Table Content */}
				{!overviewMode && (
					<div className="gameroom-table-container">
						<GameTable />
					</div>
					
		

				)}	{!overviewMode && (
				
						<BottomBar />
				
					
		

				)}
			</div>
			
			
			{/* Right Sidebar - Chat & DM Controls */}
			{!overviewMode && <RightSidebar />}
		</div>
		
		
		{isLevelUpModalOpen && (
			<PartyLevelUpModal 
				onClose={() => setIsLevelUpModalOpen(false)}
			/>
		)}


	</div>
  );
}

export default GameRoom;