import { Character, Encounter, NOTEBOOK_CATEGORY, NotebookState, Player, Location, TutorialStep, ChatSpeaker, CHAT_TYPE, EncounterState, ATTITUDE, Tag, ModalConfig, ResultConfig, EncounterFlavorTag, EffectData, FlavorTagSource, FlavorTagTheme } from "../models";
import { EventEmitter } from 'events';
import AppManager from "./appManager";
import PlayerManager from "./playerManager";
import CharacterManager from "./characterManager";
import QuestManager from "./questManager";

class UiManager extends EventEmitter {
  private appManager: AppManager;
  private playerManager: PlayerManager;
  private characterManager: CharacterManager;
  public renderImageForEvent: boolean = true;
  public eventImageOpacity: number = 0;
  private activeModal: ModalConfig | null = null;

  constructor(appManager: AppManager) {
    super();
    this.init();
    this.appManager = appManager;
    this.playerManager = this.appManager.getPlayerManager();
    this.characterManager = this.appManager.getCharacterManager();
  }

  showModal(config: ModalConfig) {
    this.activeModal = config;
    this.emit('modalStateChanged', this.activeModal);
  }

  closeModal() {
    if (this.activeModal?.onClose) {
      this.activeModal.onClose();
    }
    this.activeModal = null;
    this.emit('modalStateChanged', null);
  }

  showWelcomeModal() {
    this.showModal({
      title: 'Welcome to Dungeon Planner',
      type: 'welcome',
      options: [{
        id: 'continue',
        title: 'Begin Adventure',
        description: 'Start your campaign experience',
        callback: () => {
          // Close the modal
        }
      }]
    });
  
  }

  
  handleModalOptionSelected(optionId: string) {
    const option = this.activeModal?.options.find((opt: { id: string; }) => opt.id === optionId);
    if (option) {
      option.callback();
      this.closeModal();
    }
  }


  // TODO: Put all this in some kinda message thinger
  closeNotebook() {
    this.emit('setNotebookState', undefined);
  }
  closeTutorial() {
    this.closeNotebook();
  }

  updateEncounterState(encounterState: EncounterState) {
    this.emit('updateEncounterState', encounterState);
  }

  showNotebook(newNotebookStateArray: NotebookState[]) {
    this.emit('setNotebookState', newNotebookStateArray)
  }

  removePlayer(player: Player) {
    this.emit('removePlayer', player);
  }

  getEventIcon(eventType: string) {
    return `event_${eventType}`;
}   

  showTutorialPage(page: number) {
    this.emit('setTutorialPage', page);
  }

  changeEventTimer(time: number) {

    this.emit('setupTimeChange', time);
  }


  showRewardModal(rewards: number, advanceEvent: boolean) {
    this.emit('showRewardModal', rewards, advanceEvent);
  }

  // Quest Modal Management
  showSelectQuestModal() {
    this.emit('showSelectQuestModal', true);
  }

  hideSelectQuestModal() {
    this.emit('showSelectQuestModal', false);
  }

  showQuestTerminalModal(quest: any, node: any) {
    this.emit('showQuestTerminalModal', { quest, node });
  }

  hideQuestTerminalModal() {
    this.emit('showQuestTerminalModal', null);
  }

  showQuestProgressModal(questId: string) {
    this.emit('showQuestProgressModal', questId);
  }

  hideQuestProgressModal() {
    this.emit('showQuestProgressModal', null);
  }

  // Active Quests Modal Management
  showActiveQuestsModal() {
    this.emit('showActiveQuestsModal', true);
  }

  hideActiveQuestsModal() {
    this.emit('showActiveQuestsModal', false);
  }

  showResultModal(resultConfig: ResultConfig) {
    this.emit('showResultModal', resultConfig);
  }

  hideResultModal() {
    this.emit('showResultModal', undefined);
  }

  toggleImageForEvent() {
    this.eventImageOpacity = 1;;
    this.renderImageForEvent = !this.renderImageForEvent;
    this.emit('eventUpdated');
  }

  questSelected(questId: string) {
    const questExhaustion = this.appManager.getQuestManager().getExhaustionForQuest(questId);
    const partyManager = this.appManager.getPartyManager();
    if(questExhaustion > 0) {
      partyManager.exhaustParty(questExhaustion); 
    }

    this.hideSelectQuestModal();
    this.showQuestProgressModal(questId);
  }


  getMessageIconId(speaker: ChatSpeaker) {
    let iconId;
    switch(speaker.type) {
      case CHAT_TYPE.PLAYER:
        const player = this.playerManager.getRecruitablePlayers()[speaker.id]; 
        iconId = player.icon;
        break;
      case CHAT_TYPE.CHARACTER:
        const character = this.characterManager.getCharacterById(speaker.id);
        iconId = character.icon;
        break;
      case CHAT_TYPE.DM:
        iconId = 'chat_dm';
        break;
      case CHAT_TYPE.HELPER:
      default:
        iconId = 'chat_imp';
        break;
    }
    return iconId;
  }

  getMessageSpeakerName(speaker: ChatSpeaker) {
    let name;
    switch(speaker.type) {
      case CHAT_TYPE.PLAYER:
        const player = this.playerManager.getRecruitablePlayers()[speaker.id]; 
        name = player.name;
        break;
      case CHAT_TYPE.CHARACTER:
        const character = this.characterManager.getCharacterById(speaker.id);
        name = character.name;
        break;
      case CHAT_TYPE.DM:
        name = 'You';
        break;
      case CHAT_TYPE.HELPER:
      default:
        name = 'Impy';
        break;
    }
    return name;
  }

  getShortenedName(name: string) {
    return name.split(' ').map((n) => n[0]).join('');
  }

  showImageForEvent() {
    return this.renderImageForEvent;
  }

  setEventImageOpacity(opacity: number) {
    this.eventImageOpacity = opacity;
    this.emit('eventUpdated'); 
    }

  getEventImageOpacity() {
    return this.eventImageOpacity;
  }

  getHeroFirstName(charName: string) {
    return charName.split(' ')[0];
  }
  


  showBasicNotebook(category: string, item: Character | Encounter | Location | Player, startPage: number = 0) {
    let content;
    let notebookTitle ='';
    
    let secondaryEntries = []
    
    // We might need to add more to this payload...? This might be unneccesary I dont know. I also dont
    // know how to spell unessecary. 
    switch(category){
      case NOTEBOOK_CATEGORY.CHARACTER:
        content = {character: item as Character};
        notebookTitle = content.character.name;
        let playerInfo = this.playerManager.getPlayerAssignedCharacter(content.character)
        if(playerInfo) {
          secondaryEntries.push({
            category: NOTEBOOK_CATEGORY.PLAYER,
            content: {
              player: playerInfo,
              message: ''
            },
            title: playerInfo.name
          })
        }
      break;
      case NOTEBOOK_CATEGORY.LOCATION:
        content = {location: item as Location};
        notebookTitle = content.location.name
      break;
      case NOTEBOOK_CATEGORY.ENCOUNTER:
        content = {encounter: item as Encounter};
        notebookTitle = content.encounter.name;
        secondaryEntries.push({
          category: NOTEBOOK_CATEGORY.LOCATION,
          content: {location: content.encounter.location},
          title: content.encounter.location.name
        })
      break;
      case NOTEBOOK_CATEGORY.PLAYER:
        content = {player: item as Player};
        notebookTitle = content.player.name;
        //const characterManager = this.appManager.getCharacterManager();

        content.player.characters.forEach((character) => {
       
        secondaryEntries.push({
          category: NOTEBOOK_CATEGORY.CHARACTER,
          content: {
            character: character,
            message: ''
          },
          title: character.name
        })
      });

      break;
      default:
        console.error("BAD SHOW BASIC NOTEBOOK CALLED")
        return;
    }
    const notebookState: NotebookState = {
      category,
      content: content,
      title: notebookTitle,
    } 

    if(secondaryEntries.length > 0) {
      this.showNotebook([notebookState, ...secondaryEntries])
    } else {
      this.showNotebook([notebookState])
    }

  }


  showTutorialMessage(tutorialStep: TutorialStep) {
    const notebookState: NotebookState = {
      category: NOTEBOOK_CATEGORY.TUTORIAL,
      content: {
        message: tutorialStep.content[0],
        showNext:  tutorialStep.content.length > 1 ? 1 : undefined,
        icon: tutorialStep.icons ? tutorialStep.icons[0] : undefined,
        title: tutorialStep.title
      },
      title: tutorialStep.title,
    }
    const secondaryEntries = tutorialStep.content.slice(1).map((content, index) => {
      return {
        category: NOTEBOOK_CATEGORY.TUTORIAL,
        content: {
          message: content,
          showNext: index < tutorialStep.content.length - 2 ? index + 2 : undefined,
          showPrev:  index,
          icon: tutorialStep.icons ? tutorialStep.icons[index + 1] : undefined,
          title: tutorialStep.title
        },
        title: tutorialStep.title,
      }
    })
  
    if(secondaryEntries.length > 0) {
      this.showNotebook([notebookState, ...secondaryEntries])
    } else {
      this.showNotebook([notebookState])
    }

  }

  getPlayerAttitudesStyle(tag: Tag) {
    const playerAttitudes = this.playerManager.getGroupAttitudeForTag(this.playerManager.getPlayers(), tag);
    switch(playerAttitudes) {
      case ATTITUDE.LIKE:
        return 'player-like';
      case ATTITUDE.CONFLICTED:
        return 'player-conflicted';
      case ATTITUDE.HATE:
        return 'player-hate';
      default:
        return '';
    }
  }

  updateParty() {
    
    this.emit('playerUpdate');
  }


  showAcceptCampaignModal(campaignID: number) {
    console.log('showing accept campaign modal...');
  }

  init() {
    console.log('init uiManager...');
    // Logic to guide players through the graph
  }

  update(timeDt: number) {
   // this.updateColorLerps(timeDt);
  }

  showTagModal(config: {
    title: string;
    description?: string;
    tags: EncounterFlavorTag[];
    effects?: EffectData[];
    selectedTags: Tag[];
    remainingClicks: number;
    tagInstructions?: string;
    onTagClick: (tag: EncounterFlavorTag) => void;
    onClose: () => void;
  }) {
    this.showModal({
      title: config.title,
      type: 'tagmodal',
      options: [], // No options needed as the modal handles its own interactions
      tagModalConfig: {
        title: config.title,
        description: config.description,
        tags: config.tags,
        effects: config.effects,
        selectedTags: config.selectedTags,
        remainingClicks: config.remainingClicks,
        tagInstructions: config.tagInstructions,
        onTagClick: config.onTagClick,
        onClose: config.onClose
      }
    });
  }

  static getPlayerEmail(playerName: string) {
    const seed = this.hashString(playerName);
    const random = this.seededRandom(seed);
    const email = `${playerName.replace(' ', '.').toLowerCase()}${this.randomDigit(random)}${this.randomDigit(random)}${this.randomDigit(random)}@gmail.com`;
    return email;
  }

    static getPlayerPhone(playerName: string) {
      const seed = this.hashString(playerName);
      const random = this.seededRandom(seed);
      const phoneNumber = `(${this.randomDigit(random)}${this.randomDigit(random)}${this.randomDigit(random)}) ${this.randomDigit(random)}${this.randomDigit(random)}${this.randomDigit(random)}-${this.randomDigit(random)}${this.randomDigit(random)}${this.randomDigit(random)}${this.randomDigit(random)}`;
      return phoneNumber;
    }

   private static hashString(str: string): number {
      let hash = 0;
      for (let i = 0; i < str.length; i++) {
        const char = str.charCodeAt(i);
        hash = (hash << 5) - hash + char;
        hash |= 0; // Convert to 32bit integer
      }
      return hash;
    }

    private static seededRandom(seed: number): () => number {
      return function() {
        const x = Math.sin(seed++) * 10000;
        return x - Math.floor(x);
      };
    }

    private static randomDigit(random: () => number): number {
      return Math.floor(random() * 10);
    }

  /**
   * Returns configuration for displaying quest rewards
   */
  getRewardDisplayConfig(reward: {
    type: string;
    amount: number;
    description?: string;
  }): 
    | { type: 'dice'; score: number }
    | { type: 'treasurePills'; amount: number; tagConfig: { tag: { name: string }; source: FlavorTagSource; theme: FlavorTagTheme } }
    | { type: 'text'; text: string }
  {
    switch (reward.type) {
      case "score":
        return {
          type: 'dice',
          score: reward.amount
        };
      case "treasure":
        return {
          type: 'treasurePills',
          amount: reward.amount,
          tagConfig: {
            tag: { name: "Treasure" },
            source: FlavorTagSource.TREASURE,
            theme: FlavorTagTheme.NONE,
          }
        };
     
      default:
        return {
          type: 'text',
          text: `${reward.description || "Reward"}: ${reward.amount}`
        };
    }
  }
  }


  // Other methods to manage sessions


export default UiManager;