import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.scss';
import App from './App';

import AppManager from './managers/appManager';

import { AppManagerProvider, CampaignManagerProvider, CharacterManagerProvider, DataManagerProvider, PartyManagerProvider, PlayerManagerProvider, SessionManagerProvider, UiManagerProvider } from './components/managerContexts';

// Initialize managers

const appManager = new AppManager();

const dataManager = appManager.getDataManager();
const campaignManager = appManager.getCampaignManager();
const sessionManager = appManager.getSessionManager();
const PartyManager = appManager.getPartyManager();
const uiManager = appManager.getUiManager();
const characterManager = appManager.getCharacterManager();
const playerManager = appManager.getPlayerManager();


const root = ReactDOM.createRoot(document.getElementById('root') as HTMLElement);
root.render(
  <React.StrictMode>
    <CharacterManagerProvider manager={characterManager}>
      <PlayerManagerProvider manager={playerManager}>
        <AppManagerProvider manager={appManager}>
          <DataManagerProvider manager={dataManager}>
            <CampaignManagerProvider manager={campaignManager}>
              <SessionManagerProvider manager={sessionManager}>
                <PartyManagerProvider manager={PartyManager}>
                  <UiManagerProvider manager={uiManager}>
                    <App />
                  </UiManagerProvider>
                </PartyManagerProvider>
              </SessionManagerProvider>
            </CampaignManagerProvider>
          </DataManagerProvider>
        </AppManagerProvider>
      </PlayerManagerProvider>
    </CharacterManagerProvider>
  </React.StrictMode>
);