import React from 'react';
import './DiceScore.scss';
import CImage from './cimage';

interface DiceScoreProps {
  score: number;
  direction?: 'row' | 'column';
  className?: string;
}

interface DiceRepresentation {
  type: 'd4' | 'd6' | 'd20';
  count?: number; // For d20s, this represents the multiplier shown in the center
}

const DiceScore: React.FC<DiceScoreProps> = ({ 
  score, 
  direction = 'row',
  className = '' 
}) => {
  
  const calculateDiceRepresentation = (totalScore: number): DiceRepresentation[] => {
    if (totalScore <= 0) return [];
    
    const dice: DiceRepresentation[] = [];
    let remaining = totalScore;
    
    // Handle d20s first (scores of 24+)
    if (remaining >= 24) {
      const d20Count = Math.floor(remaining / 24);
      dice.push({ type: 'd20', count: d20Count });
      remaining = remaining % 24;
    }
    
    // Handle d6s (scores of 4-23, or remaining after d20s)
    if (remaining >= 4) {
      const d6Count = Math.floor(remaining / 4);
      for (let i = 0; i < d6Count; i++) {
        dice.push({ type: 'd6' });
      }
      remaining = remaining % 4;
    }
    
    // Handle d4s (scores of 1-3, or remaining after d6s)
    if (remaining > 0) {
      for (let i = 0; i < remaining; i++) {
        dice.push({ type: 'd4' });
      }
    }
    
    return dice;
  };

  const renderDie = (die: DiceRepresentation, index: number) => {
    const baseClass = `dice-icon dice-${die.type}`;
    
    return (
      <div 
        key={`${die.type}-${index}`} 
        className={baseClass}
        style={{
          zIndex: die.type === 'd20' ? 10 : 5 - index // d20s on top, then reverse order for overlap
        }}
      >
        {/* Placeholder icons - these would be replaced with actual SVG icons */}
        {die.type === 'd4' && (
          <CImage imageId="die_d4"/>
        )}
        {die.type === 'd6' && (
           <CImage imageId="die_d6"/>
        )}        {die.type === 'd20' && (
          <div className="d20-container">
            <CImage imageId="die_d20"/>
            {die.count && die.count > 1 && (
              <span className="d20-number">{die.count}</span>
            )}
          </div>
        )}
      </div>
    );
  };

  const dice = calculateDiceRepresentation(score);
  
  if (dice.length === 0) {
    return <div className={`dice-score empty ${className}`}>-</div>;
  }

  return (
    <div className={`dice-score ${direction} ${className}`}>
      {dice.map((die, index) => renderDie(die, index))}
    </div>
  );
};

export default DiceScore;
