import React from "react";
import { Character } from "../../models";
import {
	getCharacterReadinessLevel,
	getCharacterReadinessSymbol,
	CHARACTER_READINESS_LEVEL,
} from "../../consts/characterConsts";
import "./LevelUpButton.scss";

interface LevelUpButtonProps {
	characters: Character[];
	onClick: () => void;
}

const LevelUpButton: React.FC<LevelUpButtonProps> = ({
	characters,
	onClick,
}) => {
	const getReadinessClass = (experience: number): string => {
		const level = getCharacterReadinessLevel(experience);
		switch (level) {
			case CHARACTER_READINESS_LEVEL.VERY_UNREADY:
				return "unready2";
			case CHARACTER_READINESS_LEVEL.UNREADY:
				return "unready1";
			case CHARACTER_READINESS_LEVEL.READY:
				return "ready";
			case CHARACTER_READINESS_LEVEL.PERFECT:
				return "perfect";
			case CHARACTER_READINESS_LEVEL.OVERREADY:
				return "overready";
			default:
				return "unknown";
		}
	};

	return (
		<div className="level-up-button" onClick={onClick}>
			<div className="inner-content">
				<div className="level-up-label">Level Up!</div>
				<div className="character-readiness-row">
					{characters.map((character, index) => (
						<div
							key={character.id}
							className={`readiness-symbol ${getReadinessClass(
								character.experience
							)}`}
						>
							{getCharacterReadinessSymbol(character.experience)}
						</div>
					))}
				</div>
			</div>
		</div>
	);
};

export default LevelUpButton;
