import { Attribute, ATTRIBUTE_TYPE, Character, Gear } from "../models"
import { CHARACTER_CLASS, RACE } from "./general";

// Character level-up readiness constants
export const CHARACTER_READINESS_THRESHOLDS = {
  VERY_UNREADY: 20,
  UNREADY: 50,
  READY_START: 50,
  READY_END: 64,
  PERFECT_START: 65,
  PERFECT_END: 74,
  READY_SECOND_START: 75,
  READY_SECOND_END: 90,
  OVERREADY: 91
} as const;

export enum CHARACTER_READINESS_LEVEL {
  VERY_UNREADY = 'very-unready',
  UNREADY = 'unready',
  READY = 'ready',
  PERFECT = 'perfect',
  OVERREADY = 'overready'
}

// Helper function to get character readiness level from experience
export function getCharacterReadinessLevel(experience: number): CHARACTER_READINESS_LEVEL {
  if (experience < CHARACTER_READINESS_THRESHOLDS.VERY_UNREADY) {
    return CHARACTER_READINESS_LEVEL.VERY_UNREADY;
  }
  if (experience < CHARACTER_READINESS_THRESHOLDS.UNREADY) {
    return CHARACTER_READINESS_LEVEL.UNREADY;
  }
  if (experience < CHARACTER_READINESS_THRESHOLDS.PERFECT_START) {
    return CHARACTER_READINESS_LEVEL.READY;
  }
  if (experience < CHARACTER_READINESS_THRESHOLDS.READY_SECOND_START) {
    return CHARACTER_READINESS_LEVEL.PERFECT;
  }
  if (experience <= CHARACTER_READINESS_THRESHOLDS.READY_SECOND_END) {
    return CHARACTER_READINESS_LEVEL.READY;
  }
  return CHARACTER_READINESS_LEVEL.OVERREADY;
}

// Helper function to get readiness symbol for UI display
export function getCharacterReadinessSymbol(experience: number): string {
  const level = getCharacterReadinessLevel(experience);
  switch (level) {
    case CHARACTER_READINESS_LEVEL.VERY_UNREADY:
    case CHARACTER_READINESS_LEVEL.UNREADY:
      return '';
    case CHARACTER_READINESS_LEVEL.READY:
      return '';
    case CHARACTER_READINESS_LEVEL.PERFECT:
      return '!';
    case CHARACTER_READINESS_LEVEL.OVERREADY:
      return '!';
    default:
      return '?';
  }
}

// Helper function to get happiness modifier for level up
export function getCharacterLevelUpHappinessModifier(experience: number): number {
  const level = getCharacterReadinessLevel(experience);
  switch (level) {
    case CHARACTER_READINESS_LEVEL.VERY_UNREADY:
      return -2; // All other players lose happiness
    case CHARACTER_READINESS_LEVEL.UNREADY:
      return -1; // All other players lose happiness
    case CHARACTER_READINESS_LEVEL.READY:
      return 1;
    case CHARACTER_READINESS_LEVEL.PERFECT:
      return 2;
    case CHARACTER_READINESS_LEVEL.OVERREADY:
      return 0;
    default:
      return 0;
  }
}

// Helper function to get CSS class name for readiness level
export function getCharacterReadinessClass(experience: number): string {
  return getCharacterReadinessLevel(experience);
}

// Helper function to get readiness description for tooltips
export function getCharacterReadinessDescription(experience: number): string {
  const level = getCharacterReadinessLevel(experience);
  switch (level) {
    case CHARACTER_READINESS_LEVEL.VERY_UNREADY:
      return 'Very Unready: All other players -2 happiness';
    case CHARACTER_READINESS_LEVEL.UNREADY:
      return 'Unready: All other players -1 happiness';
    case CHARACTER_READINESS_LEVEL.READY:
      return 'Ready: +1 Happiness';
    case CHARACTER_READINESS_LEVEL.PERFECT:
      return 'Perfect: +2 Happiness';
    case CHARACTER_READINESS_LEVEL.OVERREADY:
      return 'Overready: +0 Happiness (-1 Happiness when OTHER Player\'s Characters level up)';
    default:
      return 'Unknown readiness level';
  }
}


