import EventEmitter from "events";
import { Character, Encounter, Tag, Location } from "../models";
import AppManager from "./appManager";

export interface GameEvent {
  id: string;
  timestamp: number;
  type: 'encounter_start' | 'encounter_end' | 'tag_discovered' | 'combat_start' | 'combat_end' | 'challenge_attempt' | 'location_change' | 'conversation' | 'quest_progress' | 'character_action';
  description: string;
  details: {
    encounter?: Encounter;
    tag?: Tag;
    character?: Character;
    location?: Location;
    outcome?: 'success' | 'failure' | 'partial';
    participants?: string[];
    [key: string]: any;
  };
}

export interface GameContext {
  currentLocation: Location | null;
  currentEncounter: Encounter | null;
  recentEvents: GameEvent[];
  discoveredTags: Tag[];
  activeCharacters: Character[];
  currentTheme: string;
  sessionDuration: number;
  mood: 'tense' | 'relaxed' | 'excited' | 'mysterious' | 'humorous' | 'dramatic';
}

class GameContextManager extends EventEmitter {
  private appManager: AppManager;
  private gameEvents: GameEvent[] = [];
  private maxEvents = 50; // Keep last 50 events for context

  constructor(appManager: AppManager) {
    super();
    this.appManager = appManager;
  }

  // Log a new game event
  logEvent(event: Omit<GameEvent, 'id' | 'timestamp'>): void {
    const gameEvent: GameEvent = {
      ...event,
      id: this.generateEventId(),
      timestamp: Date.now()
    };

    this.gameEvents.push(gameEvent);
    
    // Keep only the most recent events
    if (this.gameEvents.length > this.maxEvents) {
      this.gameEvents = this.gameEvents.slice(-this.maxEvents);
    }

    this.emit('gameEvent', gameEvent);
  }

  // Get current game context for LLM prompts
  getGameContext(): GameContext {
    const campaignManager = this.appManager.getCampaignManager();
    const playerManager = this.appManager.getPlayerManager();
    
    return {
      currentLocation: campaignManager.getCurrentEncounter()?.location || null,
      currentEncounter: campaignManager.getCurrentEncounter() || null,
      recentEvents: this.gameEvents.slice(-10), // Last 10 events
      discoveredTags: this.appManager.getTagManager().getCollectedTags(),
      activeCharacters: playerManager.getPlayers().flatMap(p => p.characters),
      currentTheme: campaignManager.getCurrentEncounter()?.location?.themes?.[0] || 'fantasy',
      sessionDuration: Date.now() - (this.gameEvents[0]?.timestamp || Date.now()),
      mood: this.inferCurrentMood()
    };
  }

  // Create a narrative summary for LLM context
  getContextSummary(): string {
    const context = this.getGameContext();
    const recentEvents = context.recentEvents.slice(-5);
    
    let summary = "";
    
    if (context.currentLocation) {
      summary += `Current Location: ${context.currentLocation.name} - ${context.currentLocation.description}\n`;
    }
    
    if (context.currentEncounter) {
      summary += `Current Encounter: ${context.currentEncounter.name} - ${context.currentEncounter.description}\n`;
    }
    
    summary += `Current Theme: ${context.currentTheme}\n`;
    summary += `Current Mood: ${context.mood}\n`;
    
    if (recentEvents.length > 0) {
      summary += `\nRecent Events:\n`;
      recentEvents.forEach(event => {
        summary += `- ${event.description}\n`;
      });
    }
    
    if (context.discoveredTags.length > 0) {
      const recentTags = context.discoveredTags.slice(-5);
      summary += `\nRecently Discovered: ${recentTags.map(t => t.name).join(', ')}\n`;
    }
    
    return summary;
  }

  // Helper to infer current mood from recent events
  private inferCurrentMood(): 'tense' | 'relaxed' | 'excited' | 'mysterious' | 'humorous' | 'dramatic' {
    const recentEvents = this.gameEvents.slice(-3);
    
    for (const event of recentEvents.reverse()) {
      if (event.type === 'combat_start') return 'tense';
      if (event.type === 'combat_end' && event.details.outcome === 'success') return 'excited';
      if (event.type === 'tag_discovered' && event.details.tag?.name.toLowerCase().includes('mystery')) return 'mysterious';
      if (event.details.outcome === 'failure') return 'dramatic';
    }
    
    return 'relaxed';
  }

  private generateEventId(): string {
    return `evt_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
  }

  // Convenience methods for common events
  logEncounterStart(encounter: Encounter): void {
    this.logEvent({
      type: 'encounter_start',
      description: `Started encounter: ${encounter.name} at ${encounter.location.name}`,
      details: { encounter, location: encounter.location }
    });
  }

  logTagDiscovered(tag: Tag, context?: string): void {
    this.logEvent({
      type: 'tag_discovered',
      description: `Discovered ${tag.name}${context ? ` ${context}` : ''}`,
      details: { tag }
    });
  }

  logCombatStart(enemies: string[]): void {
    this.logEvent({
      type: 'combat_start',
      description: `Combat started against ${enemies.join(', ')}`,
      details: { participants: enemies }
    });
  }

  logCombatEnd(outcome: 'success' | 'failure', details?: string): void {
    this.logEvent({
      type: 'combat_end',
      description: `Combat ended in ${outcome}${details ? `: ${details}` : ''}`,
      details: { outcome }
    });
  }

  logConversation(participants: string[], topic: string): void {
    this.logEvent({
      type: 'conversation',
      description: `Conversation about ${topic}`,
      details: { participants, topic }
    });
  }
}

export default GameContextManager;
