import React from "react";
import "./notebook.scss";
import { ATTRIBUTE_CONTRIBUTOR, CharacterNotebookEntry } from "../../models";
import { useCharacterManager, usePlayerManager } from "../managerContexts";
import CImage from "./cimage";
import AttributeDisplay from "./attributeDisplay";
import FillBar from "./fillBar";
import RangedFillBar from "./RangedFillBar";
import Cbox from "./cbox";
import {
	CHARACTER_READINESS_THRESHOLDS,
	getCharacterReadinessLevel,
	CHARACTER_READINESS_LEVEL,
} from "../../consts/characterConsts";

const NotebookCharacter: React.FC<{
	characterInfo: CharacterNotebookEntry;
}> = ({ characterInfo }) => {
	const characterManager = useCharacterManager();
	const playerManager = usePlayerManager();

    const clickChar = () => {
        characterInfo.character.experience += 10; // Example increment, adjust as needed
    }

	const getPlayer = () => {
		const player = playerManager.getPlayerAssignedCharacter(
			characterInfo.character
		);
		return player ? `Assigned to ${player.name}` : "Not assigned to any player";
	};

	return (
		<div className="notebook-character">
			<div className="notebook-character-left">
				<Cbox title={characterInfo.character.name} theme="character-info">
					<div className="notebook-left-top-container">
						<div className="notebook-character-icon-container" onClick={clickChar}>
							<CImage imageId={characterInfo.character.icon} />
						</div>
						<div className="notebook-character-stats">
							<div>
								<b>Race:</b>{" "}
								{characterManager.getUFRace(characterInfo.character.race)}
							</div>
							<div>
								<b>Class:</b>{" "}
								{characterManager.getUFClass(
									characterInfo.character.character_class
								)}
							</div>
							
							<div>{getPlayer()}</div>
						</div>

						<Cbox
							title="STATUS"
							theme={`health ${characterInfo.character.injury}`}
						>
							<div
								className={`health-status ${characterInfo.character.injury}`}
							>
								{characterInfo.character.injury?.toUpperCase() ?? "HEALTHY"}
							</div>
						</Cbox>
					</div>
				</Cbox>
				<Cbox title={`LEVEL ${characterInfo.character.level}`} theme="stats">
					<div className="character-level">
						<FillBar
							filledSegments={Math.min(
								characterInfo.character.experience,
								CHARACTER_READINESS_THRESHOLDS.READY_START
							)}
							totalSegments={CHARACTER_READINESS_THRESHOLDS.READY_START}
							theme="character-level"
							
						/>
					</div>
					{characterInfo.character.experience >=
						CHARACTER_READINESS_THRESHOLDS.READY_START && (
						<div className="character-level-up-readiness">
							<RangedFillBar
								filledSegments={characterInfo.character.experience}
								totalSegments={100}
								ranges={[
									{
										start: CHARACTER_READINESS_THRESHOLDS.READY_START,
										end: CHARACTER_READINESS_THRESHOLDS.READY_END,
										backgroundColor: "#c8e6c9",
										fillColor: "#81c784",
										label: "Ready",
									},
									{
										start: CHARACTER_READINESS_THRESHOLDS.PERFECT_START,
										end: CHARACTER_READINESS_THRESHOLDS.PERFECT_END,
										backgroundColor: "#a5d6a7",
										fillColor: "#4caf50",
										label: "Perfect",
									},
									{
										start: CHARACTER_READINESS_THRESHOLDS.READY_SECOND_START,
										end: CHARACTER_READINESS_THRESHOLDS.READY_SECOND_END,
										backgroundColor: "#c8e6c9",
										fillColor: "#81c784",
										label: "Ready",
									},
									{
										start: CHARACTER_READINESS_THRESHOLDS.OVERREADY,
										end: 100,
										backgroundColor: "#ffab91",
										fillColor: "#d84315",
										label: "Overready",
									},
								]}
								theme="level-up-readiness"
								label={`Level up: ${
									getCharacterReadinessLevel(
										characterInfo.character.experience
									) === CHARACTER_READINESS_LEVEL.READY
										? "Ready"
										: getCharacterReadinessLevel(
												characterInfo.character.experience
										  ) === CHARACTER_READINESS_LEVEL.PERFECT
										? "Perfect"
										: "Overready"
								}`}
								showTooltips={true}
							/>
						</div>
					)}
				</Cbox>

				

				<Cbox title="BACKSTORY" theme="backstory">
					<FillBar
						totalSegments={Math.max(
							characterInfo.character.backstory.length,
							3
						)}
						filledSegments={
							characterInfo.character.backstory.filter((bs) => bs.completed)
								.length
						}
						theme="backstory"
						label="Backstory Elements"
					/>
					<div className="notebook-backstory-tags">
						{characterInfo.character.backstory.length > 0 ? (
							<div>SEEKING:</div>
						) : 
						(
							<div>No backstory yet...</div>
						)}
						{characterInfo.character.backstory
							.filter((bs) => !bs.completed)
							.map((story, idx) => (
								<div key={idx} className="text-pill expected">
									{story.tag.name}
								</div>
							))}
					</div>
					<div className="notebook-backstory-tags">
						{characterInfo.character.backstory.filter((bs) => bs.completed)
							.length > 0 && <div>COMPLETED:</div>}
						{characterInfo.character.backstory
							.filter((bs) => bs.completed)
							.map((story, idx) => (
								<div key={idx} className="text-pill completed">
									{story.tag.name}
								</div>
							))}
					</div>
				</Cbox>
			</div>

			<div className="notebook-character-right">
				<Cbox title="ABILITIES" theme="abilities">
					<AttributeDisplay
						hero={characterInfo.character}
						category={ATTRIBUTE_CONTRIBUTOR.ALL}
					/>
				</Cbox>
				<Cbox title="ABILITY DETAILS" theme="ability-details">
					<Cbox
						title={`${characterManager.getUFRace(
							characterInfo.character.race
						)} Abilities`}
						theme="race-abilities"
					>
						<AttributeDisplay
							hero={characterInfo.character}
							category={ATTRIBUTE_CONTRIBUTOR.RACE}
							collapsed={true}
						/>
					</Cbox>

					<Cbox
						title={`${characterManager.getUFClass(
							characterInfo.character.character_class
						)} Abilities`}
						theme="class-abilities"
					>
						<AttributeDisplay
							hero={characterInfo.character}
							category={ATTRIBUTE_CONTRIBUTOR.CHARACTER_CLASS}
							collapsed={true}
						/>
					</Cbox>

					{characterInfo.character.gear.length > 0 && (
						<Cbox title="Equipment Bonuses" theme="gear-abilities">
							<AttributeDisplay
								hero={characterInfo.character}
								category={ATTRIBUTE_CONTRIBUTOR.GEAR}
								collapsed={true}
							/>
						</Cbox>
					)}
				</Cbox>
			</div>
		</div>
	);
};

export default NotebookCharacter;
