import { APP_STATE, DEFAULT_STATE_CONFIG, STATE_CONFIG } from "../consts/states";
import { StateConfig, Graph } from "../models";
import AppManager from "./appManager";

class DataManager {

  // Should this LIVE here? :thinking: 
  campaignGraph: Graph | undefined;
  stateConfigs: Map<APP_STATE, StateConfig> = new Map();
  appManager: AppManager;

  getGraph() : Graph | undefined {  
    return this.campaignGraph;
  }

  constructor(appManager: AppManager) {
    this.appManager = appManager;
    this.init();
  }

  init() {
    this.campaignGraph = new Graph();
   

    this.initStates();
    console.log('init dataManager...');
  }

  getStateProps(state?: APP_STATE) : StateConfig {
    return this.stateConfigs.get(state || this.appManager.state) ?? DEFAULT_STATE_CONFIG;
  }

  initStates() {
    this.stateConfigs.set(APP_STATE.MAIN_MENU, {...DEFAULT_STATE_CONFIG, ...STATE_CONFIG[APP_STATE.MAIN_MENU]});
    this.stateConfigs.set(APP_STATE.CAMPAIGN_SETUP, {...DEFAULT_STATE_CONFIG, ...STATE_CONFIG[APP_STATE.CAMPAIGN_SETUP]});
    this.stateConfigs.set(APP_STATE.PLAYER_RECRUITMENT, {...DEFAULT_STATE_CONFIG, ...STATE_CONFIG[APP_STATE.PLAYER_RECRUITMENT]});
    this.stateConfigs.set(APP_STATE.CHARACTER_CREATION, {...DEFAULT_STATE_CONFIG, ...STATE_CONFIG[APP_STATE.CHARACTER_CREATION]});
    this.stateConfigs.set(APP_STATE.SESSION_OVERVIEW, {...DEFAULT_STATE_CONFIG, ...STATE_CONFIG[APP_STATE.SESSION_OVERVIEW]});
    this.stateConfigs.set(APP_STATE.SESSION, {...DEFAULT_STATE_CONFIG, ...STATE_CONFIG[APP_STATE.SESSION]});
    this.stateConfigs.set(APP_STATE.SCORE, {...DEFAULT_STATE_CONFIG, ...STATE_CONFIG[APP_STATE.SCORE]});
  // Other methods to manage sessions
  }
}

export default DataManager;