import React, { useState, useEffect } from 'react';
import { useAppManager } from '../managerContexts';
import './LLMConfigModal.scss';

interface LLMConfigModalProps {
  isOpen: boolean;
  onClose: () => void;
}

const LLMConfigModal: React.FC<LLMConfigModalProps> = ({ isOpen, onClose }) => {
  const appManager = useAppManager();
  const chatManager = appManager.getChatManager();
  const llmService = chatManager.getLLMService();

  const [apiKey, setApiKey] = useState('');
  const [baseUrl, setBaseUrl] = useState('https://api.openai.com/v1');
  const [model, setModel] = useState('gpt-3.5-turbo');
  const [testStatus, setTestStatus] = useState<'idle' | 'testing' | 'success' | 'error'>('idle');
  const [testMessage, setTestMessage] = useState('');

  useEffect(() => {
    if (isOpen) {
      // Load existing configuration
      const savedApiKey = localStorage.getItem('llm_api_key') || '';
      const savedBaseUrl = localStorage.getItem('llm_base_url') || 'https://api.openai.com/v1';
      const savedModel = localStorage.getItem('llm_model') || 'gpt-3.5-turbo';
      
      setApiKey(savedApiKey);
      setBaseUrl(savedBaseUrl);
      setModel(savedModel);
    }
  }, [isOpen]);

  const handleSave = () => {
    if (apiKey.trim()) {
      llmService.setApiKey(apiKey.trim());
      llmService.setConfiguration({ baseUrl, model });
      
      // Save to localStorage
      localStorage.setItem('llm_base_url', baseUrl);
      localStorage.setItem('llm_model', model);
      
      onClose();
    }
  };

  const handleTest = async () => {
    if (!apiKey.trim()) {
      setTestStatus('error');
      setTestMessage('Please enter an API key first');
      return;
    }

    setTestStatus('testing');
    setTestMessage('Testing connection...');

    try {
      // Temporarily set the configuration for testing
      llmService.setApiKey(apiKey.trim());
      llmService.setConfiguration({ baseUrl, model });

      const testResponse = await llmService.generateContextualResponse({
        tag: { name: 'magic sword' },
       gameContext: {},
        partyTension: 0.5,
      });

      if (testResponse) {
        setTestStatus('success');
        setTestMessage(`Success! Response: "${testResponse.message}"`);
      } else {
        setTestStatus('error');
        setTestMessage('Failed to get response from LLM service');
      }
    } catch (error) {
      setTestStatus('error');
      setTestMessage(`Error: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  };

  if (!isOpen) return null;

  return (
    <div className="llm-config-modal-overlay">
      <div className="llm-config-modal">
        <div className="modal-header">
          <h2>LLM Configuration</h2>
          <button className="close-button" onClick={onClose}></button>
        </div>

        <div className="modal-content">
          <div className="config-section">
            <h3>API Configuration</h3>
           

            <div className="form-group">
              <label htmlFor="apiKey">API Key *</label>
              <input
                id="apiKey"
                type="password"
                value={apiKey}
                onChange={(e) => setApiKey(e.target.value)}
                placeholder="Enter your OpenAI API key"
                className="form-control"
              />
              <small className="form-text">
                Get your API key from <a href="https://platform.openai.com/api-keys" target="_blank" rel="noopener noreferrer">OpenAI Platform</a>
              </small>
            </div>

            <div className="form-group">
              <label htmlFor="baseUrl">Base URL</label>
              <input
                id="baseUrl"
                type="url"
                value={baseUrl}
                onChange={(e) => setBaseUrl(e.target.value)}
                placeholder="https://api.openai.com/v1"
                className="form-control"
              />
              {/* <small className="form-text">
                Use a different URL for OpenAI-compatible services
              </small> */}
            </div>

            <div className="form-group">
              <label htmlFor="model">Model</label>
              <select
                id="model"
                value={model}
                onChange={(e) => setModel(e.target.value)}
                className="form-control"
              >
                <option value="gpt-3.5-turbo">GPT-3.5 Turbo (Recommended)</option>
                <option value="gpt-4">GPT-4</option>
                <option value="gpt-4-turbo">GPT-4 Turbo</option>
              </select>
            </div>

            <div className="test-section">
              <button 
                onClick={handleTest} 
                disabled={testStatus === 'testing'}
                className={`test-button ${testStatus}`}
              >
                {testStatus === 'testing' ? 'Testing...' : 'Test Connection'}
              </button>
              
              {testMessage && (
                <div className={`test-result ${testStatus}`}>
                  {testMessage}
                </div>
              )}
            </div>
          </div>
{/* 
          <div className="features-section">
            <h3>Features</h3>
            <ul>
              <li> <strong>Contextual Responses:</strong> Chat messages that reflect current game state</li>
              <li> <strong>Location Awareness:</strong> Dialogue that considers your current location</li>
              <li> <strong>Mood Adaptation:</strong> Responses that match the current game mood</li>
              <li> <strong>Memory:</strong> References to recent events and discoveries</li>
              <li> <strong>Character Voice:</strong> Different speaking styles for DM, players, and characters</li>
            </ul>
          </div> */}
        </div>

        <div className="modal-footer">
          <button onClick={onClose} className="btn btn-secondary">
            Cancel
          </button>
          <button 
            onClick={handleSave} 
            className="btn btn-primary"
            disabled={!apiKey.trim()}
          >
            Save Configuration
          </button>
        </div>
      </div>
    </div>
  );
};

export default LLMConfigModal;
