import React, { useState, useEffect } from 'react';
import { useCampaignManager } from '../managerContexts';
import { ConditionalTooltip } from './tooltip';
import ComingSoon from './ComingSoon';
import './DMAbilitiesScreen.scss';
import { DMUpgrade, FlavorTagTheme, GameMastery } from '../../models';
import { PROPS_ENCOUNTER_SCORES, COMFORT_EXHAUSTION_BONUS } from '../../consts/score';

// Define the ability meter structure
interface AbilityMeter {
  id: string;
  name: string;
  color: string;
  maxLevel: number;
  currentLevel: number;
  abilities: string[];
  pointsPerLevel: number;
  themes?: FlavorTagTheme[]; // Optional themes for the meter
  hoverText?: string[]; // Optional hover text for each ability
}




const DMAbilitiesScreen: React.FC = () => {
  const campaignManager = useCampaignManager();
  const [availablePoints, setAvailablePoints] = useState(0);
  const [abilityMeters, setAbilityMeters] = useState<AbilityMeter[]>([]);
  const [consumables, setConsumables] = useState<DMUpgrade[]>([]);
  const [gameMasteries, setGameMasteries] = useState<GameMastery[]>([]);

  // Initialize ability meters
  useEffect(() => {
    const initialMeters: AbilityMeter[] = [
      {
        id: 'tones',
        name: 'Tones',
        color: '#9123a1',
        maxLevel: 5,
        currentLevel: campaignManager.getDMAbilityMeterLevel('tones'),
        abilities: ["Comedic", "Dramatic", "Heroic", "Romantic", "Grim"],
        themes: [FlavorTagTheme.COMEDIC, FlavorTagTheme.DRAMATIC, FlavorTagTheme.HEROIC, FlavorTagTheme.ROMANTIC, FlavorTagTheme.GRIM],
        hoverText: ["Unlock the Comedy Tone", "Unlock the Dramatic Tone", "Unlock the Heroic Tone", "Unlock the Romantic Tone", "Unlock the Grim Tone"],
        pointsPerLevel: 1
      },
      {
        id: 'hosting',
        name: 'Hosting',
        color: '#DC143C',
        maxLevel: 4,
        currentLevel: campaignManager.getDMAbilityMeterLevel('hosting'),
        abilities: ['Snacks', 'Breaks', 'Props', 'Comfort'],
        hoverText: [
          'Use snacks to improve party attitude.',
          'Breaks reduce tension, stress, and exhaustion.',
          'Props provide a bonus Encounter Score per Level',
          'Comfort levels increase maximum Party Exhaustion'
        ],
        pointsPerLevel: 1
      },
      {
        id: 'storytelling',
        name: 'Storytelling',
        color: '#4682B4',
        maxLevel: 3,
        currentLevel: campaignManager.getDMAbilityMeterLevel('storytelling'),
        abilities: ['Preparation', 'Living World', 'Dramatic Narration'],
        hoverText: [
          'Describe 1 location and 1 upcoming encounter element at the start of each campaign, +1 score bonus for those prep thingers.',
          'Players gain 1 Attention at the beginning of each event.',
          'Widen the x2 area on the Tension Meter',
        ],
        pointsPerLevel: 1
      },
    ];

    setAbilityMeters(initialMeters);
  }, [campaignManager]);

  // Initialize game masteries
  useEffect(() => {
    const initialGameMasteries: GameMastery[] = [
      {
        id: 'roleplaying',
        name: 'Roleplaying',
        currentLevel: campaignManager.getDMGameMasteryLevel('roleplaying'),
        maxLevel: 3,
        color: '#9d7020',
        rankLabels: ['Novice', 'Adept', 'Expert']
      },
      {
        id: 'combat',
        name: 'Combat',
        currentLevel: campaignManager.getDMGameMasteryLevel('combat'),
        maxLevel: 3,
        color: '#9d7020',
        rankLabels: ['Novice', 'Adept', 'Expert']
      },
      {
        id: 'travel',
        name: 'Travel',
        currentLevel: campaignManager.getDMGameMasteryLevel('travel'),
        maxLevel: 3,
        color: '#9d7020',
        rankLabels: ['Novice', 'Adept', 'Expert']
      }
    ];

    setGameMasteries(initialGameMasteries);
  }, [campaignManager]);

  // Initialize consumables
  useEffect(() => {
    const initialConsumables: DMUpgrade[] = [
      {
        id: 'snacks',
        name: 'Snacks',
        value: campaignManager.getMaxSnacks(),
        cost: 1,
        color: '#FF6347',
        icon: '',
        unlockedBy: 'hosting',
        unlockLevel: 1
      },
      {
        id: 'breaks',
        name: 'Breaks',
        value: campaignManager.getMaxBreaks(),
        cost: 1,
        color: '#32CD32',
        icon: '',
        unlockedBy: 'hosting',
        unlockLevel: 2
      },
      {
        id: 'props',
        name: 'Props',
        value: campaignManager.getCurrentProps(),
        cost: 1,
        color: '#FFD700',
        icon: '',
        unlockedBy: 'hosting',
        unlockLevel: 3
      },
      {
        id: 'comfort',
        name: 'Venue',
        value: campaignManager.getCurrentVenue(),
        cost: 1,
        color: '#FFD700',
        icon: '',
        unlockedBy: 'hosting',
        unlockLevel: 4
      },
    ];

    setConsumables(initialConsumables);
  }, [campaignManager]);



  const isUnlocked = (consumable: DMUpgrade): boolean => {
    // Check if the consumable is unlocked by an ability
    if (consumable.unlockedBy) {
      return campaignManager.isDMUpgradeUnlocked(consumable.id, consumable.unlockedBy, consumable.unlockLevel || 1);
    }
    return true; // If no unlock condition, it's always available
  }

  // Update available points and consumables when abilities change
  useEffect(() => {
    setAvailablePoints(campaignManager.getDMAbilityPoints());

    const handleAbilitiesUpdate = (points: number) => {
      setAvailablePoints(points);
      
      // Also update consumable values when abilities change
      setConsumables(prev => prev.map(consumable => {
        let newValue;
        switch (consumable.id) {
          case 'snacks':
            newValue = campaignManager.getMaxSnacks();
            break;
          case 'breaks':
            newValue = campaignManager.getMaxBreaks();
            break;
          case 'props':
            newValue = campaignManager.getCurrentProps();
            break;
          case 'comfort':
            newValue = campaignManager.getCurrentVenue();
            break;
          default:
            newValue = consumable.value;
        }
        return { ...consumable, value: newValue };
      }));
    };

    campaignManager.on('dmAbilitiesUpdated', handleAbilitiesUpdate);

    return () => {
      campaignManager.off('dmAbilitiesUpdated', handleAbilitiesUpdate);
    };
  }, [campaignManager]);

  const handleAbilityUpgrade = (meterId: string) => {
    const meter = abilityMeters.find(m => m.id === meterId);
    if (!meter || meter.currentLevel >= meter.maxLevel) return;

    const cost = meter.pointsPerLevel;
    if (campaignManager.upgradeDMAbilityMeter(meterId, cost)) {
      setAbilityMeters(prev => 
        prev.map(m => 
          m.id === meterId 
            ? { ...m, currentLevel: m.currentLevel + 1 }
            : m
        )
      );
    }
  };

  const handleConsumablePurchase = (consumableId: string) => {
    const consumable = consumables.find(c => c.id === consumableId);
    if (!consumable) return;

    if (campaignManager.purchaseDMConsumable(consumableId, consumable.cost)) {
      // Update local state with new values
      let newValue;
      switch (consumableId) {
        case 'snacks':
          newValue = campaignManager.getMaxSnacks();
          break;
        case 'breaks':
          newValue = campaignManager.getMaxBreaks();
          break;
        case 'props':
          newValue = campaignManager.getCurrentProps();
          break;
        case 'comfort':
          newValue = campaignManager.getCurrentVenue();
          break;
        default:
          newValue = 0;
      }

      // Update local state
      setConsumables(prev => 
        prev.map(c => 
          c.id === consumableId 
            ? { ...c, value: newValue }
            : c
        )
      );
    }
  };

  const handleGameMasteryUpgrade = (masteryId: string) => {
    const mastery = gameMasteries.find(m => m.id === masteryId);
    if (!mastery || mastery.currentLevel >= mastery.maxLevel) return;

    const cost = 1; // Each game mastery level costs 1 point
    if (campaignManager.upgradeDMGameMastery(masteryId, cost)) {
      setGameMasteries(prev => 
        prev.map(m => 
          m.id === masteryId 
            ? { ...m, currentLevel: m.currentLevel + 1 }
            : m
        )
      );
    }
  };

  const renderAbilityMeter = (meter: AbilityMeter) => {
    const progress = (meter.currentLevel / meter.maxLevel) * 100;
    const canUpgrade = meter.currentLevel < meter.maxLevel && availablePoints >= meter.pointsPerLevel;

    return (
      <div key={meter.id} className="ability-meter">
        <button 
          className={`ability-button ${canUpgrade ? 'can-upgrade' : ''}`}
          onClick={() => handleAbilityUpgrade(meter.id)}
          disabled={!canUpgrade}
          style={{ backgroundColor: meter.color }}
        >
          <span className="ability-name">{meter.name}</span>
          {/* <span className="ability-cost">{meter.pointsPerLevel}pt</span> */}
        </button>
        
        <div className="ability-track">
           <div className="ability-circles">
             <div 
                className={`ability-circle-spacer`}
              
              />
            {Array.from({ length: meter.maxLevel }, (_, i) => (
              <div 
                key={`background-${i}`}
                className={`ability-circle background`}
                style={{ backgroundColor: "black"}}
              />
            ))}
          </div>
           <div className="ability-circles names">
             <div 
                className={`ability-circle-spacer`}
              />
            {Array.from({ length: meter.maxLevel }, (_, i) => (
              <ConditionalTooltip 
                key={`name-${i}`}
                tooltip={meter.hoverText?.[i] || meter.abilities[i]}
                visible={true}
              >
                <div 
                  className={`ability-circle-name ${i < meter.currentLevel ? 'highlighted' : ''}`}
                >
                  <div className="ability-circle-name-text">
                    {meter.abilities[i]}
                  </div>  
                </div>
              </ConditionalTooltip>
            ))}
          </div>
          <div className="ability-progress" style={{ width: `100%`, backgroundColor: '#000000' }}>
          </div>
          
          <div className="ability-circles foreground">
             <div 
                className={`ability-circle-spacer`}
              
              />
            {Array.from({ length: meter.maxLevel }, (_, i) => (
                <div 
                  key={`foreground-${i}`}
                  className={`ability-circle ${i < meter.currentLevel ? 'filled' : ''}`}
                  style={{ backgroundColor: i < meter.currentLevel ? meter.color : 'transparent' }}
                />
            ))}
          </div>

          

          <div className="ability-progress filled" style={{ width: `${progress}%`, backgroundColor: meter.color }}>
          </div>
        </div>
        
        <div className="ability-info">
          <span className="ability-level">{meter.currentLevel}/{meter.maxLevel}</span>
          {meter.currentLevel > 0 && (
            <span className="ability-unlock">{meter.abilities[meter.currentLevel - 1]}</span>
          )}
        </div>
      </div>
    );
  };

  // Define level descriptions for each consumable type
  const getConsumableInfo = (consumable: DMUpgrade) => {
    const propsArray = ["Battlemaps", "Cardboard Buildings", "Miniatures", "3D Printed Everything"];
    const comfortArray = ["Background Music", "Nice Chairs", "Gaming Table", "Custom Game Room"];

    switch (consumable.id) {
      case 'snacks':
        return {
          displayName: 'Snacks',
          currentDescription: consumable.value > 0 ? `${consumable.value} per Encounter ` : 'None',
          currentEffect: 'increases attitude by 1',
          upgradeButtonText: `+1 Snacks`,
          maxLevel: 10 // Arbitrary high number for consumables
        };
      
      case 'breaks':
        return {
          displayName: 'Breaks',
          currentDescription: consumable.value > 0 ? `${consumable.value} per Encounter` : 'None',
          currentEffect: 'decreases tension, stress, exhastion',
          upgradeButtonText: `+1 Breaks`,
          maxLevel: 10
        };
      
      case 'props':
        return {
          displayName: 'Props',
          currentDescription: consumable.value > 0 ? (propsArray[consumable.value - 1] || 'Max Level') : 'None',
          upgradeButtonText: (consumable.value > 0 && consumable.value < propsArray.length) ? `Upgrade to ${propsArray[consumable.value]}` : consumable.value === 0 ? 'Unlock Props' : 'Max Level',
          currentEffect: consumable.value > 0 ? `+${PROPS_ENCOUNTER_SCORES[consumable.value - 1]} Encounter Score` : '',
          maxLevel: propsArray.length
        };
      
      case 'comfort':
        return {
          displayName: 'Venue',
          currentDescription: consumable.value > 0 ? (comfortArray[consumable.value - 1] || 'Max Level') : 'Basic Setup',
          upgradeButtonText: (consumable.value > 0 && consumable.value < comfortArray.length) ? `Upgrade to ${comfortArray[consumable.value]}` : consumable.value === 0 ? 'Unlock Venue' : 'Max Level',
          currentEffect: consumable.value > 0 ? `+${COMFORT_EXHAUSTION_BONUS[consumable.value - 1]} Max Exhaustion` : '',
          maxLevel: comfortArray.length
        };
      
      default:
        return {
          displayName: consumable.name,
          currentDescription: consumable.value > 0 ? `Level ${consumable.value}` : 'None',
          upgradeButtonText: `+1 ${consumable.name}`,
          currentEffect: '',
          maxLevel: 10
        };
    }
  };

  const renderConsumableRow = () => {
    return (
      <div className="consumable-row">
        <div className="consumable-header">
          <span className="section-title">HOSTING UPGRADES</span>
        </div>
        
        <div className="consumable-grid">
          {consumables.map(consumable => {
            const info = getConsumableInfo(consumable);
            const unlocked = isUnlocked(consumable);
            const canPurchase = availablePoints >= consumable.cost && unlocked;
            const isAtMaxLevel = consumable.value >= info.maxLevel;
            
            return (
              <div key={consumable.id} className="consumable-item">
                {/* Category Label */}
                <div className="consumable-category-label">
                  {info.displayName}
                </div>
                
                {/* Current Level/Status */}
                <div className="consumable-current-status">
                  {unlocked ? (
                    <div className="current-info">
                      <span className="current-level">{info.currentDescription}</span>
                      <span className="current-effect">{info.currentEffect}</span>
                    </div>
                  ) : (
                    <span className="locked-status"> Locked</span>
                  )}
                </div>
                
                {/* Upgrade Button or Lock Icon */}
                {unlocked ? (
                  <button
                    className={`consumable-upgrade-button ${canPurchase && !isAtMaxLevel ? 'can-upgrade' : 'disabled'}`}
                    onClick={() => handleConsumablePurchase(consumable.id)}
                    disabled={!canPurchase || isAtMaxLevel}
                   
                  >
                    {isAtMaxLevel ? 'Max Level' : info.upgradeButtonText}
                  </button>
                ) : (
                  <div className="locked-indicator">
                    <span className="lock-icon"></span>
                    <span className="unlock-requirement">Hosting Level {consumable.unlockLevel}</span>
                  </div>
                )}
              </div>
            );
          })}
        </div>
      </div>
    );
  };

  const renderGameMasteryRow = () => {
    return (
      <div className="game-mastery-row">
        <div className="game-mastery-label">
          <span className="ability-name">Game Mastery</span>
        </div>
        
          {gameMasteries.map(mastery => {
            const canUpgrade = mastery.currentLevel < mastery.maxLevel && availablePoints >= 1;
            const currentRank = mastery.currentLevel > 0 ? mastery.rankLabels[mastery.currentLevel - 1] : 'Untrained';
            
            return (
              <div key={mastery.id} className="mastery-container">
                <div className="mastery-label-top">
                  <span className="mastery-name">{mastery.name}</span>
                  <span className="mastery-level">Level {mastery.currentLevel}/{mastery.maxLevel}</span>
                </div>
                <button
                  className={`ability-button ${canUpgrade ? 'can-upgrade' : ''}`}
                  onClick={() => handleGameMasteryUpgrade(mastery.id)}
                  disabled={!canUpgrade}
                  style={{ backgroundColor: mastery.color }}
                >
                  <span className="mastery-rank">{currentRank}</span>
                </button>
              </div>
            );
          })}
        
      </div>
    );
  };

  return (
    <div className="dm-abilities-screen">
      {/* Header with points and info */}
      <div className="abilities-header">
        <div className="available-points">
          <span className="points-label">Available Points:</span>
          <span className="points-value">{availablePoints}</span>
        </div>
       
      </div>

      {/* Ability Meters */}
      <div className="abilities-meters">
        {abilityMeters.map(meter => {
          // Apply ComingSoon overlay to storytelling meter
          if (meter.id === 'storytelling') {
            return (
              <div key={meter.id} style={{ position: 'relative' }}>
                {renderAbilityMeter(meter)}
                <ComingSoon />
              </div>
            );
          }
          return renderAbilityMeter(meter);
        })}
      </div>

      {/* Game Mastery Row */}
      <div className="game-mastery-section" style={{ position: 'relative' }}>
        {renderGameMasteryRow()}
        <ComingSoon />
      </div>

      {/* Consumables Row */}
      <div className="consumables-section">
        {renderConsumableRow()}
      </div>
    </div>
  );
};

export default DMAbilitiesScreen;
