import React from 'react';
import { Campaign } from '../models';
import './campaignDisplay.scss';
import CampaignDisplayBody from './campaign-display-body';
import CampaignDisplayHeader from './campaign-display-header';
import Cimage from './UI/cimage';



interface CampaignDisplayProps {
  campaign: Campaign;
  onSelect?: () => void;
  collapsed?: boolean;
  iconOnly?: boolean;
  onAccept?: () => void; 
  onCancel?: () => void;
  acceptText?: string;
}

const CampaignDisplay: React.FC<CampaignDisplayProps> = ({ campaign, onSelect, collapsed, iconOnly, onAccept, onCancel, acceptText = 'ACCEPT' }) => {

  
  return (
    <>
      {iconOnly ? (
        <div className={`campaign-display ${campaign.theme || ''} icon-only`} onClick={onSelect}>
          <div className="campaign-name">{campaign.name}</div>
          <div className="campaign-icon solo">
            <Cimage imageId={campaign.icon} />
          </div>
        </div>
      ) : collapsed ? (
        <div className={`campaign-display ${campaign.theme || ''} collapsed pill-shaped`} onClick={onSelect}>
          <CampaignDisplayHeader campaign={campaign} />
        </div>
      ) : (
        <div className={`campaign-display ${campaign.theme || ''} full`}>
          <CampaignDisplayHeader campaign={campaign} />
          <CampaignDisplayBody processedEncounters={campaign.processedEncounters} />
          <div className='campaign-display-footer'>
            <div className="ui-actions">
              <button className={`campaign-button cancel ${onCancel ? '' : ' hidden' }`} onClick={onCancel}>Cancel</button>
              <button className="campaign-button accept" onClick={onAccept}>{acceptText}</button>
            </div>
          </div>
        </div>
      )}
    </>
  )
};

export default CampaignDisplay;