
import React, { useState, useEffect } from "react";
import { Encounter, Player, Character, EncounterFlavorTag, FlavorTagSource, TravelResultConfig, EffectData, ATTRIBUTE_TYPE } from '../../models';
import { useAppManager, useCampaignManager, usePlayerManager } from "../managerContexts";
import EncounterHeader from "./encounterHeader";
import FlavorTagPill from "../UI/FlavorTagPill";
import "./gameRoom.scss";
import "./gameEncounterTrap.scss";

interface GameEncounterTrapProps {
  encounter: Encounter;
}

interface TrapResult {
  type: 'success' | 'survived' | 'injured' | 'death';
  minRoll: number;
  maxRoll: number;
  dmRewards: number[];
}

interface CharacterRoll {
  character: Character;
  player: Player;
  roll: number;
  result: TrapResult;
  dmReward: number;
}

const GameEncounterTrap: React.FC<GameEncounterTrapProps> = ({ encounter }) => {
  // Phase management
  const [phase, setPhase] = useState<'setup' | 'disarm' | 'resolved'>('setup');
  
  // Trap configuration
  const [trapResults, setTrapResults] = useState<TrapResult[]>([
    { type: 'death', minRoll: 0, maxRoll: 0, dmRewards: [] },
    { type: 'injured', minRoll: 1, maxRoll: 3, dmRewards: [] },
    { type: 'survived', minRoll: 4, maxRoll: 5, dmRewards: [] },
    { type: 'success', minRoll: 6, maxRoll: 7, dmRewards: [] }
  ]);
  
  // Character states
  const [characterRolls, setCharacterRolls] = useState<CharacterRoll[]>([]);
  const [usedDisarmAttempts, setUsedDisarmAttempts] = useState<Set<string>>(new Set());
  const [highlightedCells, setHighlightedCells] = useState<{character: string, roll: number}[]>([]);
  
  // UI states
  const [showResults, setShowResults] = useState(false);
  const [resultConfig, setResultConfig] = useState<TravelResultConfig | null>(null);
  const [boredomRisks, setBoredomRisks] = useState<{[playerId: string]: number}>({});

  const appManager = useAppManager();
  const campaignManager = useCampaignManager();
  const playerManager = usePlayerManager();
  const characterManager = appManager.getCharacterManager();
  const tagManager = appManager.getTagManager();
  const partyManager = appManager.getPartyManager();

  const players = playerManager.getPlayers();
  const allCharacters = players.flatMap(player => 
    player.characters.map(char => ({ character: char, player }))
  );
  const locationTags = campaignManager.currentEncounter?.location.tags || [];
  const collectedLocationTags = tagManager.locationTagsCollected;
  const unexposedLocationTags = locationTags.filter(tag => 
    !collectedLocationTags.some(collected => collected.name === tag.name)
  );

  // Initialize trap with random DM rewards
  useEffect(() => {
    initializeTrap();
    calculateBoredomRisks();
  }, []);

  useEffect(() => {
    calculateBoredomRisks();
  }, [trapResults]);

  const initializeTrap = () => {
    const newTrapResults = trapResults.map(result => ({
      ...result,
      dmRewards: Array.from({ length: result.maxRoll - result.minRoll + 1 }, () => 
        Math.floor(Math.random() * 6) + 1
      )
    }));
    setTrapResults(newTrapResults);
  };

  const calculateBoredomRisks = () => {
    const successRange = trapResults.find(r => r.type === 'success');
    if (!successRange) return;
    
    const successChance = ((successRange.maxRoll - successRange.minRoll + 1) / 8) * 100;
    const baseBoredomRisk = Math.max(0, successChance - 50); // Higher success = higher boredom risk
    
    const risks: {[playerId: string]: number} = {};
    players.forEach(player => {
      risks[player.id.toString()] = Math.min(90, baseBoredomRisk + (Math.random() * 20 - 10));
    });
    setBoredomRisks(risks);
  };

  const getTensionFromDifficulty = () => {
    const deathRange = trapResults.find(r => r.type === 'death');
    const injuredRange = trapResults.find(r => r.type === 'injured');
    if (!deathRange || !injuredRange) return 0;
    
    const deathChance = ((deathRange.maxRoll - deathRange.minRoll + 1) / 8) * 100;
    const injuryChance = ((injuredRange.maxRoll - injuredRange.minRoll + 1) / 8) * 100;
    
    return Math.floor(deathChance * 2 + injuryChance * 1);
  };

  const getDeathAndInjuryPercentages = () => {
    const deathRange = trapResults.find(r => r.type === 'death');
    const injuredRange = trapResults.find(r => r.type === 'injured');
    
    const deathChance = deathRange ? ((deathRange.maxRoll - deathRange.minRoll + 1) / 8) * 100 : 0;
    const injuryChance = injuredRange ? ((injuredRange.maxRoll - injuredRange.minRoll + 1) / 8) * 100 : 0;
    
    return { deathChance, injuryChance };
  };

  const adjustDifficultyBand = (resultType: string, newMin: number, newMax: number) => {
    if (newMin > newMax || newMax - newMin < 0) return;
    
    const newResults = [...trapResults];
    const resultIndex = newResults.findIndex(r => r.type === resultType);
    if (resultIndex === -1) return;
    
    // Check for overlaps with other bands
    const otherBands = newResults.filter((_, index) => index !== resultIndex);
    const hasOverlap = otherBands.some(band => 
      (newMin >= band.minRoll && newMin <= band.maxRoll) ||
      (newMax >= band.minRoll && newMax <= band.maxRoll) ||
      (newMin <= band.minRoll && newMax >= band.maxRoll)
    );
    
    if (hasOverlap) return;
    
    newResults[resultIndex] = {
      ...newResults[resultIndex],
      minRoll: newMin,
      maxRoll: newMax,
      dmRewards: Array.from({ length: newMax - newMin + 1 }, () => 
        Math.floor(Math.random() * 6) + 1
      )
    };
    
    setTrapResults(newResults);
  };

  const getDisarmChance = (character: Character) => {
    const tools = characterManager.getRemainingAttributes(character, ATTRIBUTE_TYPE.TOOLS) || 0;
    const baseChance = tools * 10; // 10% per tool
    const isTagExposed = unexposedLocationTags.length === 0;
    return Math.min(95, baseChance + (isTagExposed ? 25 : 0));
  };

  const getAssignedLocationTag = (characterIndex: number) => {
    if (unexposedLocationTags.length > 0) {
      return unexposedLocationTags[characterIndex % unexposedLocationTags.length];
    }
    if (collectedLocationTags.length > 0) {
      return collectedLocationTags[characterIndex % collectedLocationTags.length];
    }
    return null;
  };

  const handleDisarmAttempt = (character: Character, player: Player) => {
    const characterId = character.id.toString();
    if (usedDisarmAttempts.has(characterId)) return;
    
    const disarmChance = getDisarmChance(character);
    const roll = Math.random() * 100;
    const success = roll < disarmChance;
    
    setUsedDisarmAttempts(prev => new Set([...prev, characterId]));
    
    // Get character index for tag assignment
    const characterIndex = allCharacters.findIndex(c => c.character.id === character.id);
    const assignedTag = getAssignedLocationTag(characterIndex);
    
    const results: EffectData[] = [];
    
    // Player experience
    playerManager.addPlayerExp(player, 1);
    results.push({
      object: player,
      effects: [{
        effect: "+1 Experience",
        class: "player-exp"
      }]
    });
    
    // Tool loss
    const currentTools = characterManager.getRemainingAttributes(character, ATTRIBUTE_TYPE.TOOLS) || 0;
    const toolsLost = Math.floor(currentTools / 2);
    if (toolsLost > 0) {
      characterManager.setAttributeUsed(character, ATTRIBUTE_TYPE.TOOLS, toolsLost);
      results.push({
        object: character,
        effects: [{
          effect: `-${toolsLost} Tools`,
          class: "character-tools"
        }]
      });
    }
    
    if (success) {
      // Shift all ranges up by 1
      const newResults = trapResults.map(result => ({
        ...result,
        minRoll: Math.min(7, result.minRoll + 1),
        maxRoll: Math.min(7, result.maxRoll + 1)
      }));
      setTrapResults(newResults);
      
      // Expose location tag if not already exposed
      if (assignedTag && unexposedLocationTags.some(tag => tag.name === assignedTag.name)) {
        const flavorTag: EncounterFlavorTag = {
          tag: assignedTag,
          source: FlavorTagSource.LOCATION,
          theme: tagManager.getTheme()
        };
        campaignManager.exposeTag(flavorTag);
        
        results.push({
          object: 'party',
          effects: [{
            effect: `Exposed: ${assignedTag.name}`,
            class: "location-tag"
          }]
        });
      }
      
      showResultsModal("Trap Disarmed!", `${character.name} successfully disarmed the trap!`, results);
    } else {
      // Shift all ranges down by 1
      const newResults = trapResults.map(result => ({
        ...result,
        minRoll: Math.max(0, result.minRoll - 1),
        maxRoll: Math.max(0, result.maxRoll - 1)
      }));
      setTrapResults(newResults);
      
      // Add tension
      partyManager.addPartyTension(10);
      results.push({
        object: 'party',
        effects: [{
          effect: "+10 Tension",
          class: "tension"
        }]
      });
      
      showResultsModal("Disarm Failed", `${character.name} failed to disarm the trap!`, results);
    }
  };

  const handleReactToTrap = () => {
    const rolls: CharacterRoll[] = [];
    
    allCharacters.forEach(({ character, player }) => {
      const roll = Math.floor(Math.random() * 8); // 0-7
      const result = trapResults.find(r => roll >= r.minRoll && roll <= r.maxRoll);
      const dmReward = result?.dmRewards[roll - result.minRoll] || 0;
      
      if (result) {
        rolls.push({
          character,
          player,
          roll,
          result,
          dmReward
        });
      }
    });
    
    setCharacterRolls(rolls);
    setHighlightedCells(rolls.map(r => ({ character: r.character.id.toString(), roll: r.roll })));
    
    // Process results after highlighting
    setTimeout(() => {
      processCharacterRolls(rolls);
    }, 1500);
  };

  const processCharacterRolls = (rolls: CharacterRoll[]) => {
    const results: EffectData[] = [];
    let totalDmRewards = 0;
    
    rolls.forEach(({ character, player, result, dmReward }) => {
      // Apply injuries or death
      if (result.type === 'injured') {
        partyManager.addInjury(character);
        results.push({
          object: character,
          effects: [{
            effect: "Injured",
            class: "character-injury"
          }]
        });
      } else if (result.type === 'death') {
        partyManager.addInjury(character); // This will kill if already injured
        partyManager.addInjury(character); // Ensure death
        results.push({
          object: character,
          effects: [{
            effect: "Died",
            class: "character-death"
          }]
        });
      }
      
      // Add DM rewards
      if (dmReward > 0) {
        totalDmRewards += dmReward;
        results.push({
          object: character,
          effects: [{
            effect: `+${dmReward} DM Points`,
            class: "dm-points"
          }]
        });
      }
      
      // Apply boredom risk
      const boredomRisk = boredomRisks[player.id.toString()] || 0;
      if (Math.random() * 100 < boredomRisk) {
        partyManager.updateAttention(player, -1);
        results.push({
          object: player,
          effects: [{
            effect: "-1 Attention (Boredom)",
            class: "player-attention"
          }]
        });
      }
    });
    
    if (totalDmRewards > 0) {
      campaignManager.addDmExp(totalDmRewards);
    }
    
    setHighlightedCells([]);
    showResultsModal("Trap Results", "Characters have reacted to the trap!", results);
    setPhase('resolved');
  };

  const showResultsModal = (title: string, description: string, results: EffectData[]) => {
    const mockTravelOption = {
      id: "trap_result",
      name: title,
      description: description,
      time: 0,
      tags: 0,
      score: 0,
      boredom: 0,
      exhaustion: 0,
      tagInstructions: "",
      repeatable: false
    };

    const resultConfig: TravelResultConfig = {
      results: results,
      title: title,
      description: description,
      tags: [],
      travelOption: mockTravelOption,
      info: {
        score: 0,
        time: 0,
        boredom: 0,
        exhaustion: 0
      }
    };

    setResultConfig(resultConfig);
    setShowResults(true);
  };

  const renderDifficultyAdjustment = () => {
    const { deathChance, injuryChance } = getDeathAndInjuryPercentages();
    const tension = getTensionFromDifficulty();
    
    return (
      <div className="difficulty-adjustment">
        <h3>Trap Difficulty Configuration</h3>
        <div className="risk-stats">
          <div className="risk-stat">Death: {deathChance.toFixed(1)}%</div>
          <div className="risk-stat">Injury: {injuryChance.toFixed(1)}%</div>
          <div className="risk-stat">Tension: +{tension}</div>
        </div>
        <div className="boredom-risks">
          <h4>Boredom Risks:</h4>
          {players.map(player => (
            <div key={player.id} className="boredom-risk">
              {player.name}: {(boredomRisks[player.id.toString()] || 0).toFixed(1)}%
            </div>
          ))}
        </div>
        <button 
          className="introduce-trap-button"
          onClick={() => setPhase('disarm')}
        >
          Introduce Trap
        </button>
      </div>
    );
  };

  const renderDisarmInterface = () => (
    <div className="disarm-interface">
      <h3>Disarm Attempts</h3>
      <div className="character-disarm-options">
        {allCharacters.map(({ character, player }, index) => {
          const disarmChance = getDisarmChance(character);
          const isUsed = usedDisarmAttempts.has(character.id.toString());
          const assignedTag = getAssignedLocationTag(index);
          const borderColor = disarmChance < 35 ? '#e74c3c' : disarmChance > 75 ? '#27ae60' : '#f39c12';
          
          return (
            <div 
              key={character.id}
              className={`character-disarm-card ${isUsed ? 'disabled' : ''}`}
              style={{ borderColor }}
            >
              <div className="character-info">
                <div className="character-name">{character.name}</div>
                <div className="character-player">({player.name})</div>
              </div>
              {assignedTag && (
                <FlavorTagPill
                  tag={{
                    tag: assignedTag,
                    source: FlavorTagSource.LOCATION,
                    theme: tagManager.getTheme()
                  }}
                  disabled={false}
                  className="assigned-tag"
                  showTooltip={true}
                />
              )}
              <div className="disarm-chance">{disarmChance}% success</div>
              <button 
                className="disarm-button"
                disabled={isUsed}
                onClick={() => handleDisarmAttempt(character, player)}
              >
                {isUsed ? 'Used' : 'Disarm'}
              </button>
            </div>
          );
        })}
      </div>
      <button 
        className="react-to-trap-button"
        onClick={handleReactToTrap}
      >
        React to Trap
      </button>
    </div>
  );

  const renderTrapGrid = () => (
    <div className="trap-grid">
      <div className="grid-labels">
        <div className="result-label success">Success</div>
        <div className="result-label survived">Survived</div>
        <div className="result-label injured">Injured</div>
        <div className="result-label death">Death</div>
      </div>
      <div className="grid-content">
        {/* Render grid rows from 7 to 0 */}
        {Array.from({ length: 8 }, (_, i) => 7 - i).map(rollValue => {
          const result = trapResults.find(r => rollValue >= r.minRoll && rollValue <= r.maxRoll);
          const dmReward = result?.dmRewards[rollValue - result.minRoll] || 0;
          
          return (
            <div key={rollValue} className="grid-row">
              <div className="roll-number">{rollValue}</div>
              {allCharacters.map(({ character }, charIndex) => {
                const isHighlighted = highlightedCells.some(h => 
                  h.character === character.id.toString() && h.roll === rollValue
                );
                
                return (
                  <div 
                    key={`${rollValue}-${character.id}`}
                    className={`grid-cell ${result?.type || ''} ${isHighlighted ? 'highlighted' : ''}`}
                  >
                    {isHighlighted && (
                      <div className="character-marker"></div>
                    )}
                    {dmReward > 0 && !isHighlighted && (
                      <div className="dm-reward-marker">{dmReward}</div>
                    )}
                  </div>
                );
              })}
            </div>
          );
        })}
        {/* Character headers at bottom */}
        <div className="character-headers">
          <div className="roll-number-header">#</div>
          {allCharacters.map(({ character, player }) => (
            <div key={character.id} className="character-header">
              <div className="character-name">{character.name}</div>
              <div className="character-player">({player.name})</div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );

  return (
    <div className="game-encounter-trap">
      <EncounterHeader
        title={`Trap - ${encounter.location.name}`}
        description="Configure the trap difficulty and manage character survival!"
        childClass="small-font"
      />
      
      <div className="trap-content">
        {renderTrapGrid()}
        
        <div className="trap-controls">
          {phase === 'setup' && renderDifficultyAdjustment()}
          {phase === 'disarm' && renderDisarmInterface()}
          {phase === 'resolved' && (
            <button 
              className="end-trap-button"
              onClick={() => campaignManager.backToOverview()}
            >
              End Trap Encounter
            </button>
          )}
        </div>
      </div>

      {showResults && resultConfig && (
        <div className="modal-overlay">
          <div className="modal-container">
            <div className="modal-header">
              {resultConfig.title}
              <button className="close-button" onClick={() => setShowResults(false)}></button>
            </div>
            <div className="modal-description">{resultConfig.description}</div>
            <div className="trap-results">
              {resultConfig.results.map((result, index) => (
                <div key={index} className="effect-display">
                  <div className="effect-target">
                    {typeof result.object === 'string' ? result.object : result.object.name}
                  </div>
                  <div className="effect-list">
                    {result.effects?.map((effect, effectIndex) => (
                      <div key={effectIndex} className={`effect ${effect.class}`}>
                        {effect.effect}
                      </div>
                    ))}
                  </div>
                </div>
              ))}
            </div>
            <div className="modal-actions">
              <button onClick={() => setShowResults(false)}>OK</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default GameEncounterTrap;