import React, { useEffect, useState } from 'react';
import './playerCard.scss';
import { Character, NOTEBOOK_CATEGORY, Player, PlayerStats } from '../models';
import Cimage from './UI/cimage';
import Schedule from './schedule';
import CharacterCard from './characterCard';
import { useAppManager, useCharacterManager, usePlayerManager, useUiManager } from './managerContexts';
import MoreInfo from './UI/moreInfoButton';
import { APP_STATE } from '../consts/states';
import FillBar from './UI/fillBar';

interface PlayerCardProps {
  player: Player;
  isSelected: boolean;
  showSchedule?: boolean;
  showCharacters?: boolean;
  showPreferences?: boolean;
  onSelect: () => void;
  overlayStyle?: 'assign' | 'unassign' | 'none';
  parentUpdate?: () => void;
  showStats?: boolean;
 
}

const delightColors = ['#CC0000', '#CC6600', '#CCCC00', '#66CC00', '#00CC00'];
const delightBorderColors = ['#990000', '#994C00', '#999900', '#4C9900', '#009900'];

const delightColors2 = ['#FFCCCC', '#FFDDCC', '#FFFFCC', '#DDFFCC', '#CCFFCC'];
const delightBorderColors2 = ['#660000', '#663300', '#666600', '#336600', '#006600'];

const PlayerCard: React.FC<PlayerCardProps> = ({ player, parentUpdate, isSelected, onSelect, showCharacters, showSchedule, showPreferences, overlayStyle, showStats }) => {
  const uiManager = useUiManager();
  const appManager = useAppManager();
  const [update, setUpdate] = useState(false);
  const [showTooltipLikes, setShowTooltipLikes] = useState(false);
  const [showTooltipHates, setShowTooltipHates] = useState(false);
  const [showTooltipTraits, setShowTooltipTraits] = useState(false);
  const [prevEnjoyment, setPrevEnjoyment] = useState(player.playerStats.enjoyment);
  const [isFlashing, setIsFlashing] = useState(false);


  const mainLike = player.preferences.reduce((prev, current) => (current[1] > prev[1] ? current : prev), player.preferences[0]);
  const mainHate = player.preferences.reduce((prev, current) => (current[1] < prev[1] ? current : prev), player.preferences[0]);

  const handleMouseEnterLikes = () => setShowTooltipLikes(true);
  const handleMouseLeaveLikes = () => setShowTooltipLikes(false);

  
  const handleMouseEnterHates = () => setShowTooltipHates(true);
  const handleMouseLeaveHates = () => setShowTooltipHates(false);

  const handleMouseEnterTraits = () => setShowTooltipTraits(true);
  const handleMouseLeaveTraits = () => setShowTooltipTraits(false);


  const allLikes = player.preferences.filter(pref => pref[1] > 0);
  const allHates = player.preferences.filter(pref => pref[1] < 0);
 

  const text = overlayStyle === 'assign' ? 'Assign' : 'Remove';
  const overlay = overlayStyle === 'none' ? '' : 
    <div className={`overlay ${overlayStyle}`}>
      <div className="overlay-text">
        {text}  
      </div>
    </div>;
  const characterManager = useCharacterManager();
  const playerManager = usePlayerManager();

  const assignedCharClicked = (character: Character) => {
    if(appManager.state === APP_STATE.CHARACTER_CREATION) {
      unassignCharacter(character);
    }else {
      
      uiManager.showBasicNotebook(NOTEBOOK_CATEGORY.CHARACTER, character);
    }
  }

  const unassignCharacter = (character: Character) => {
      playerManager.toggleSinglePlayerForCharacter(player, character);
      setUpdate(!update);
      if(parentUpdate){
        parentUpdate();
      }
    }

    useEffect(() => {
      if (prevEnjoyment !== player.playerStats.enjoyment) {
        setIsFlashing(true);
        setTimeout(() => setIsFlashing(false), 500);
        setPrevEnjoyment(player.playerStats.enjoyment);
      }
    }, [player.playerStats.enjoyment]);
    
  // useEffect(() => {
  //   const forceUpdate = () => {
  //     setUpdate(!update);
  //   }
  //   uiManager.on('playerUpdate', forceUpdate);
  //   return () => {
  //     uiManager.off('playerUpdate', forceUpdate);
  //   };

  // }, [uiManager, update]);
  
  
  const showNotebookForPlayer = () => {
    uiManager.showBasicNotebook(NOTEBOOK_CATEGORY.PLAYER, player)
  }

  const renderPlayerStats = () => {
    if (!showStats) return null;
    
    return (
      <div className="player-stats">
      
        <div className="stat-item enjoyment">
            <div className="stat-item-emoji"></div>
           
            {Array.from({ length: 5 }).map((_, index) => {
              const isFilled = index <= player.playerStats.enjoyment;
         
              
              return (
                <span
                  key={index}
                  className={`delight-circle ${isFilled ? 'filled' : ''} ${isFlashing ? 'flash' : ''}`}
                  style={isFilled ? {
                    backgroundColor: delightColors[player.playerStats.enjoyment] || '',
                    borderColor: delightBorderColors[player.playerStats.enjoyment] || '',
                    boxShadow: `0 0 10px ${delightColors[player.playerStats.enjoyment] || ''}`
                  } : {}}
                ></span>
              );
            })}
          <div className="stat-item-emoji"></div>
        </div>
        <div className="stat-item engagement">
          <FillBar totalSegments={5} filledSegments={player.playerStats.involvement} theme="engagement" label="ATTENTION" />
        </div>
        {/* <div className="stat-item stress">
          <FillBar totalSegments={3} filledSegments={player.playerStats.stress} theme="engagement" label="STRESS" />
        </div> */}
      </div>
    );
  };



  return (
    <div 
      className={`player-card ${isSelected ? 'selected' : ''}`} 
      onClick={onSelect}
      style={{
        backgroundColor: delightColors2[player.playerStats.enjoyment] || '',
        borderColor: delightBorderColors2[player.playerStats.enjoyment] || '',
     //   boxShadow: `0 0 10px ${delightColors2[player.playerStats.enjoyment] || ''}`
      }}
      
      
      >
      <MoreInfo callback={showNotebookForPlayer} />
      <div className="player-card-header">
        <div className="player-icon">
          <Cimage imageId={player.icon} />
        </div>
        <div className="player-upper-info">
          <div className="player-name">
            {player.name}
          </div>
          {showSchedule && (
            <div className="player-schedule-container">
              <Schedule dayData={player.schedule} />
            </div>
          )}
        </div>
      </div>
      {showPreferences && (
        <div className="player-preferences-section">
           {player.traits.length > 0 && (
            <div className="preferences-column">
                <div className="preferences-title">
                  Traits
                </div>
                <div className="preferences-traits " onMouseEnter={handleMouseEnterTraits} onMouseLeave={handleMouseLeaveTraits}>
                  {player.traits.map((trait, idx) => (
                    <div key={idx} className="trait-item text-pill trait">
                      {trait.name}
                    </div>
                  ))}
                </div>
            
              {showTooltipTraits && (
              <div className="tooltip traits">
                <div className="tooltip-content-single">
                  {player.traits.map((trait, idx) => (
                  <div key={idx} className="tooltip-trait-item">
                    <b>{trait.name}</b>: {trait.description}
                  </div>
                ))}
                </div>
              </div>
            )}
            </div>
           )}
          <div className="preferences-column">
            <div className="preferences-title">Likes</div>
            <div className="preferences-main">{mainLike[0].name}</div>
            <div className="preferences-dots"  onMouseEnter={handleMouseEnterLikes} onMouseLeave={handleMouseLeaveLikes}>
              {allLikes.map((like, idx) => (
                <div key={idx} className="preference-dot like" title={like[0].name} />
              ))}
            </div>
            {showTooltipLikes && (
              <div className="tooltip like">
                <div className="tooltip-content">
                  {player.preferences.filter(pref => pref[1] > 0).map((pref, idx) => (
                    <div key={idx} className="tooltip-pill">{pref[0].name}</div>
                  ))}
                </div>
              </div>
            )}
          </div>
          <div className="preferences-column" >
            <div className="preferences-title">Hates</div>
            <div className="preferences-main">{mainHate[0].name}</div>
            <div className="preferences-dots" onMouseEnter={handleMouseEnterHates} onMouseLeave={handleMouseLeaveHates}>
              {allHates.map((hate, idx) => (
                <div key={idx} className="preference-dot hate" title={hate[0].name} />
              ))}
            </div>
            {showTooltipHates && (
              <div className="tooltip hate">
                <div className="tooltip-content">
                  {player.preferences.filter(pref => pref[1] < 0).map((pref, idx) => (
                    <div key={idx} className="tooltip-pill">{pref[0].name}</div>
                  ))}
                </div>
              </div>
            )}
          </div>
        
        </div>
      )}
      {renderPlayerStats()}
      {showCharacters && (
        <div className="character-row-container">
          <div className="character-row-display">
            {player.characters.map((character) => {
              
              return (
                <CharacterCard
                  key={character.id}
                  character={character}
                  isSelected={false}
                  onSelect={() => { assignedCharClicked(character) }}
                  showAbilities={false}
                  collapsed={true}
                />
              );
            })}
          
          </div>
          </div>
      )}
      {overlayStyle && overlay}
    </div>
  );
}

export default PlayerCard;