  perceptionEvent(character: Character, perceptionTags: PerceptionTags) {
    let messageString = "";
    let campaignChatTrigerred = false;
    perceptionTags.tags.forEach((flavorTag) => {

      if (this.tagResolved_badFunctionDontUse(flavorTag.tag)) {
        campaignChatTrigerred = true;
      }
    })
    if (campaignChatTrigerred) {
      return;
    }

    switch (perceptionTags.type) {
      case TAGS_TYPE.CURRENT_LOCATION:
        messageString = `${perceptionTags.tags[0].tag.name}? Pretty much what I would expect from a ${perceptionTags.name}.`;
        break;
      case TAGS_TYPE.BRANCH_LOCATION:
        messageString = `Hmm. Sounds like we know where to look for ${perceptionTags.tags[0].tag.name}...`;
        break;
      case TAGS_TYPE.CHARACTER_BACKSTORY:
        messageString = `OH. MY. GOSH.`;
        break;
      case TAGS_TYPE.CHARACTER_BACKSTORY_PROGRESS:
        messageString = `AT LAST! My character has been searching for ${perceptionTags.tags[0].tag.name}.`;
        break;
      case TAGS_TYPE.MAGIC_ITEM:
        messageString = `Magic item? I'm intrigued.`;
        break;
      case TAGS_TYPE.PLOT_HOOK:

        const conversation: ChatMessage[] = [{
          message: "I have a feeling this is going to be important",
          speaker: {
            type: CHAT_TYPE.PLAYER,
            id: character.id
          }
        }
          , {
          message: "... in a few months.",
          speaker: {
            type: CHAT_TYPE.PLAYER,
            id: this.playerManager.getRandomPlayer().id,
          }
        }]

        this.launchNewConversation(conversation);
        return;



    }

    const newMessage: ChatMessage = {
      message: messageString,
      speaker: {
        type: CHAT_TYPE.PLAYER,
        id: character.id
      }
    }
    this.fireNewChatMessage(newMessage);

  }


