  getThemePostFix(flavorTag: EncounterFlavorTag) {
    switch (flavorTag.theme) {
      case 'comedic':
        return ' in a comedic way';
      case 'heroic':
        return ' in a heroic way';
      case 'serious':
        return ' in a serious way';
      case 'dramatic':
        return ' in a dramatic way';
      case 'grim':
        return ' in a grim way';
      case 'romantic':
        return ' in a romantic way';
      case 'fantasy':
        return ' in a fantasy way';
      case 'horror':
        return ' in a horror way';
      default:
    }
    return '';
  }

  getEncounterTagPrefix(flavorTag: EncounterFlavorTag) {
    // Add an indication if the tag is an expected location tag or a diffeent one if its an outstanding encounter tag. Otherwise nothing. 
    const tagSource = flavorTag.source;
    if(tagSource === FlavorTagSource.LOCATION)
      return 'Set the Scene: ';
    else if(tagSource === FlavorTagSource.ENCOUNTER)
      return 'Encounter Event: '
    return '';
  }

  triggerCharacterBackstoryResponse(flavorTag: EncounterFlavorTag, character: Character) {
    let messageString = "";
    let player = this.playerManager.getPlayerAssignedCharacter(character) || this.playerManager.getRandomPlayer();
    let chatType = CHAT_TYPE.DM;
    let chatId = 0;
    // Randomize some responses to fulfilling a characters backstory, using any themes as extra flavor in some cases
    switch (Math.floor(Math.random() * 5)) {
      case 0:
        messageString = `I never expected to find ${flavorTag.tag.name} here.`;
        chatType = CHAT_TYPE.PLAYER;
        chatId = player.id;
        break;
      case 1:
        messageString = `${character.name} you discovered that ${flavorTag.tag.name} were looking for`;
        break;
      case 2:
        messageString = `${flavorTag.tag.name} has been on your ${character.name}'s mind for a while...behold, ${flavorTag.tag.name}!`;
        break;
      case 3:
        messageString = `I HAVE BEEN WAITING FOR ${flavorTag.tag.name.toUpperCase()} FOREVER!`;
        chatType = CHAT_TYPE.CHARACTER;
        chatId = player.id;
        break;
      default:
        messageString = `You find ${flavorTag.tag.name} in a place you never expected.`;
        break;
    }

    const newMessage: ChatMessage = {
      message: messageString,
      speaker: {
        type: chatType,
        id: chatId
      }
    }
    this.fireNewChatMessage(newMessage);


  }


