  themeConversation(flavorTag: EncounterFlavorTag, playerId: number) {
    let messageString = "";
    let chatSpeaker = playerId;
    let type = CHAT_TYPE.PLAYER;

    switch (flavorTag.theme) {
      case 'comedic':
        messageString = `Heheh..."${flavorTag.tag.name}"`;
        break;
      case 'grim':
        messageString = `I need to tell you something disturbing about ${flavorTag.tag.name}.`;
        type = CHAT_TYPE.DM;
        break;
      case 'fantasy':
        messageString = `I sometimes daydream about ${flavorTag.tag.name}.`
        break;
      case 'heroic':
        messageString = `Yeah! ${flavorTag.tag.name}!!!`;
        type = CHAT_TYPE.CHARACTER;
        break;
      case 'horror':
        messageString = `A chill descends on the party as they think about ${flavorTag.tag.name}.`;
        type = CHAT_TYPE.DM;
        break;
      case 'romantic':
        messageString = `OOoooooOOOoo ${flavorTag.tag.name}. Kissy kissy!`;
        break;
      case 'serious':
        messageString = `We should focus on ${flavorTag.tag.name}.`;
        break;
      case 'dramatic':
        messageString = `BEHOLD! ${flavorTag.tag.name}!!!`
        type = CHAT_TYPE.CHARACTER;
        break;
    }

    const newChat = {
      message: messageString,
      speaker: {
        type,
        id: chatSpeaker,
      }
    }
    this.fireNewChatMessage(newChat);
  }




  sayCombatModifiedMessage(side: 'heroes' | 'enemies', flavor: Tag, enemy: Tag, modifier: number) {
    let messageString = "";
    if (flavor.name === "DM") {
      if (side === 'heroes') {
        messageString = `The ${enemy.name} are not as strong as you first thought...`;
      } else {
        messageString = `Oops, the ${enemy.name} have more HP than regular...`;
      }
    } else {
      if (modifier < 0) {
        messageString = `Nevermind about the ${flavor.name}...`;
      } else {
        if (side === 'heroes') {
          if (flavor.enemy || flavor.friendly) {
            messageString = `Some ${flavor.name} are distracting the ${enemy.name}!`;
          } else {
            messageString = `The ${enemy.name} get stuck trying to avoid the ${flavor.name}.`;
          }
        } else {
          if (flavor.enemy || flavor.friendly) {
            messageString = `The ${enemy.name} seem to have some ${flavor.name} on their side...`;
          } else {
            messageString = `The ${enemy.name} use the ${flavor.name} to their advantage.`;
          }
        }
      }
    }




    const newChat = {
      message: messageString,
      speaker: {
        type: CHAT_TYPE.DM,
        id: 0,
      }
    }
    this.fireNewChatMessage(newChat);

  }



  combatIntroMessage(enemy: Tag) {
    const playerAttitude = this.playerManager.getGroupAttitudeForTag(this.playerManager.getPlayers(), enemy);
    let messageString = "";
    let playerToSpeak = this.playerManager.getRandomPlayer();
    if (playerAttitude === ATTITUDE.LIKE) {
      messageString = `Yay! ${enemy.name} are my favorite!`;
      playerToSpeak = this.playerManager.getPlayerWithAttitudeForTag(enemy, ATTITUDE.LIKE) || playerToSpeak;
    } else if (playerAttitude === ATTITUDE.HATE) {
      playerToSpeak = this.playerManager.getPlayerWithAttitudeForTag(enemy, ATTITUDE.HATE) || playerToSpeak;
      messageString = `Ugh. I hate ${enemy.name}. Why do we even have them in the game?`;
    } else if (playerAttitude === ATTITUDE.CONFLICTED) {
      messageString = `Well, I know some people don't like ${enemy.name}, but I like em.`;
      playerToSpeak = this.playerManager.getPlayerWithAttitudeForTag(enemy, ATTITUDE.LIKE) || playerToSpeak;
    } else {
      messageString = `I figured we'd fight ${enemy.name} eventually.`;
    }
    const newChat = {
      message: messageString,
      speaker: {
        type: CHAT_TYPE.PLAYER,
        id: playerToSpeak.id,
      }
    }


    this.dmMessage('You guys enter combat with some ' + enemy.name);

    setTimeout(() => {
      this.fireNewChatMessage(newChat);
    }, 5000);

  }






