import { ScoreEntry } from '../models';

/**
 * Calculate the total score from an array of ScoreEntry objects.
 * Handles both additive points and multiplier entries correctly.
 * 
 * @param scoreEntries Array of score entries to calculate total from
 * @returns Total calculated score
 */
export function calculateTotalScore(scoreEntries: ScoreEntry[]): number {
  return scoreEntries.reduce((sum, entry) => {
    if (entry.multiplier) {
      return sum * entry.points;
    }
    return sum + entry.points;
  }, 0);
}

/**
 * Get score entries filtered by type (additive vs multiplier)
 * 
 * @param scoreEntries Array of score entries to filter
 * @param isMultiplier Whether to return multiplier entries (true) or additive entries (false)
 * @returns Filtered array of score entries
 */
export function getScoreEntriesByType(scoreEntries: ScoreEntry[], isMultiplier: boolean): ScoreEntry[] {
  return scoreEntries.filter(entry => Boolean(entry.multiplier) === isMultiplier);
}

/**
 * Get only the additive score entries (non-multiplier)
 * 
 * @param scoreEntries Array of score entries to filter
 * @returns Array of additive score entries
 */
export function getAdditiveScoreEntries(scoreEntries: ScoreEntry[]): ScoreEntry[] {
  return getScoreEntriesByType(scoreEntries, false);
}

/**
 * Get only the multiplier score entries
 * 
 * @param scoreEntries Array of score entries to filter
 * @returns Array of multiplier score entries
 */
export function getMultiplierScoreEntries(scoreEntries: ScoreEntry[]): ScoreEntry[] {
  return getScoreEntriesByType(scoreEntries, true);
}

/**
 * Calculate just the additive portion of the score (before multipliers)
 * 
 * @param scoreEntries Array of score entries
 * @returns Sum of all additive points
 */
export function calculateAdditiveScore(scoreEntries: ScoreEntry[]): number {
  return getAdditiveScoreEntries(scoreEntries).reduce((sum, entry) => sum + entry.points, 0);
}

/**
 * Calculate the final multiplier from all multiplier entries
 * 
 * @param scoreEntries Array of score entries
 * @returns Final multiplier value (product of all multiplier points)
 */
export function calculateMultiplier(scoreEntries: ScoreEntry[]): number {
  return getMultiplierScoreEntries(scoreEntries).reduce((product, entry) => product * entry.points, 1);
}
