import { useCallback } from "react";
import { Tag, ThemeConfig, TRAVEL_OPTION, TravelOption } from "../../models";
import { useAppManager, useCampaignManager } from "../managerContexts";
import { ConditionalTooltip } from "./tooltip";
import TooltipWrapper from "./TooltipWrapper";

interface TravelOptionProps {
	travelOption: TravelOption;
	theme?: ThemeConfig;
	onClick: () => void;
	length?: string;
}

const TravelOptionDisplay: React.FC<TravelOptionProps> = ({
	travelOption,
	theme,
	onClick,
	length,
}) => {
	const {
		id,
		name,
		description,
		time,
		tags,
		score,
		boredom,
		tag,
		exhaustion,
		travelOptionType,
	} = travelOption;
	const appManager = useAppManager();
const campaignManager = useCampaignManager();
	const eventTag = campaignManager.getEventTag(travelOption);
	const hasTravelTag = (tag: Tag) => {
		// Uncomment the line below and remove the true return when ready to use the actual function
			 return appManager.getTagManager().collectedTags.includes(tag);
		//return true;
	};

	const displayValue = (value: number | undefined) => {
		if (value === -1) return "?";
		return value;
	};
	// Determine if the option is locked
	const isLocked = eventTag && !hasTravelTag(eventTag);

	const getTagClassType = (type: TRAVEL_OPTION | undefined) => {
		switch (type) {
			case TRAVEL_OPTION.EXPLORE_CURRENT_LOCATION:
				return "type-explore-current";
			case TRAVEL_OPTION.EXPLORE_UPCOMING_LOCATION:
				return "type-explore-upcoming";
			case TRAVEL_OPTION.CAMP:
				return "type-camp";
			case TRAVEL_OPTION.AMBUSH:
				return "type-ambush";
			case TRAVEL_OPTION.AMBUSH2:
				return "type-ambush";
			case TRAVEL_OPTION.TRADERS:
				return "type-traders";
			case TRAVEL_OPTION.SIDE_QUEST:
				return "type-sidequest";
			case TRAVEL_OPTION.MAGIC_CACHE:
				return "type-magic-item";
			case TRAVEL_OPTION.CHARACTER_BACKSTORY:
				return "type-backstory";

			default:
				return "";
		}
	};

	return (
		<div
			className={`travel-option ${length} ${isLocked ? "locked" : "unlocked"}`}
			onClick={onClick}
		>
			{isLocked && (
				
				<div className="locked-banner"><TooltipWrapper
					content={`Unlocked by introducing ${eventTag?.name}`}
					
					preservePositioning={false}
					>	
					<span>UNDISCOVERED</span>
					</TooltipWrapper>
				</div>
			
			)}
			<div className="travel-option-header">
				<span className="travel-option-name">{name}</span>
				{eventTag && (
					<ConditionalTooltip
						tooltip={`Unlocked from previous discussions about ` + eventTag.name}
						visible={!isLocked}
						
						preservePositioning={true}
						translate={[0, -40]}
					>
						<span
							className={`travel-option-tag text-pill type-travel ${
								hasTravelTag(eventTag) ? "" : "missing"
							}`}
						>
							{eventTag.name}
						</span>
					</ConditionalTooltip>
				)}
				<div className="travel-option-description">{description}</div>
			</div>{" "}
			<ConditionalTooltip
				tooltip="Journey Length increase"
				visible={!isLocked}
				preservePositioning={true}
				translate={[100, -80]}
			>
				<span className="travel-option-detail">:{displayValue(time)}</span>
			</ConditionalTooltip>
			<ConditionalTooltip
				tooltip="Boredom Risk Increase"
				visible={!isLocked}
				preservePositioning={true}
				translate={[-110, -80]}
			>
				<span className="travel-option-detail time">
					:{displayValue(boredom)}
				</span>
			</ConditionalTooltip>
			{tags > 0 && (
				<ConditionalTooltip
					tooltip={travelOption.tagInstructions || "Location tags"}
					visible={!isLocked}
					preservePositioning={true}
					translate={[-135, 10]}
				>
					<span
						className={`travel-option-detail tags text-pill ${isLocked ? 'disabled' : '' } ${getTagClassType(
							travelOption.travelOptionType
						)}`}
					>
						<div className="travel-option-tag-label">{tags}</div>
					</span>
				</ConditionalTooltip>
			)}{" "}
			{score !== undefined && score !== 0 && (
				<ConditionalTooltip
					tooltip="Impact on campaign score"
					visible={!isLocked}
					preservePositioning={true}
				>
					<span className="travel-option-detail score">
						Score: {displayValue(score)}
					</span>
				</ConditionalTooltip>
			)}{" "}
			{exhaustion !== undefined && exhaustion !== 0 && (
				<ConditionalTooltip
					tooltip="Party Exhaustion increase"
					visible={!isLocked}
					preservePositioning={true}
				>
					<span className="travel-option-detail score">
						Exhaustion: {displayValue(exhaustion)}
					</span>
				</ConditionalTooltip>
			)}
		</div>
	);
};

export default TravelOptionDisplay;
