import { TAG } from '../consts/tags';
import { ATTRIBUTE_TYPE, Building, BUILDING_EFFECT_TYPE, BUILDING_TYPE, BuildingEffect, Campaign, Character, EffectData, EncounterFlavorTag, FlavorTagSource, Player, ResultConfig, Tag, TownBuilding } from '../models';
import AppManager from './appManager';
import CampaignManager from './campaignManager';
import PartyManager from './partyManager';
import PlayerManager from './playerManager';
import TagManager from './tagManager';
import UiManager from './uiManager';

class TownManager {

private appManager: AppManager;
private partyManager: PartyManager;
private playerManager: PlayerManager;
private campaignManager: CampaignManager
private tagManager: TagManager;

constructor(appManager: AppManager) {
    this.appManager = appManager;
    this.tagManager = appManager.getTagManager();
    this.partyManager = this.appManager.getPartyManager();
    this.playerManager = this.appManager.getPlayerManager();
    this.campaignManager = this.appManager.getCampaignManager();
}

  getTagsForBuilding(building: Building): EncounterFlavorTag[] {
        let tags: Tag[] = [...building.tags];

        // filter out any tags already claimed
        const collectedTags = this.tagManager.getCollectedTags();
        tags = tags.filter(tag => !collectedTags.some(collectedTag => collectedTag.name === tag.name));

        if (building.addFriendlyTags) {
           const friendlyTags = this.tagManager.getRandomFriendlyTags(1, false);
           tags = [...tags, ...friendlyTags];
        }

        if(building.allowMagicItems) {
            const magicTags = this.tagManager.getRandomMagicItemTag(1);
            tags = [...tags, ...magicTags];
        }
        
        const randomTags = [];

        // Encounter tags
      //  const encounterTags = this.tagManager.getOutstandingEncounterTags();

       // location tags
        const locationTags = this.tagManager.getOutstandingLocationTags();
       
       // foreshadowing tags
        const upcomingEncounterTags = this.tagManager.getUpcomingEncounterTags();

       // backstory tags
        const backstoryTags = this.tagManager.getOutstandingBackstoryTags()
       // rumor tags

       const nbRandomTags = (building.nbTags || 5) - tags.length;
        const allTags = [...locationTags, ...upcomingEncounterTags, ...backstoryTags,]//...encounterTags,];
        const randomTagSet = new Set<Tag>(tags);
        while (randomTagSet.size < nbRandomTags && randomTagSet.size < allTags.length) {
            const randomIndex = Math.floor(Math.random() * allTags.length);
            const randomTag = allTags[randomIndex];
            randomTagSet.add(randomTag);
        }

        // Turn all the tags into flavor tags
        let flavorTags: EncounterFlavorTag[] = [];
        randomTagSet.forEach(tag => {
            flavorTags.push({
                tag: tag,
                source: this.tagManager.getFormattedSource(tag, FlavorTagSource.ALL),
                theme: this.tagManager.getTheme()
            });
        });


        return flavorTags;
}


// When character is sliding towards building
townBuildingAssigned(character: Character, building: TownBuilding): void {
    const player = this.playerManager.getPlayerAssignedCharacter(character);
     
    if(player) {
      this.partyManager.playersTakeAction([player.name], true, 1);
      this.partyManager.stressAction([player]);
    }

    this.appManager.getChatManager().playerMessage(`${character.name} will head towards the ${building.building.name}`, player?.id);


}


// when building is activated
townBuildingResolved(character: Character, building: TownBuilding, position: number): ResultConfig {
    const player = this.playerManager.getPlayerAssignedCharacter(character);
    const allEffects: EffectData[] = [];

    // Track effects for character and party separately
    let characterEffects: {effect: string, class: string}[] = [];
    let partyEffects: {effect: string, class: string}[] = [];
    let playerEffects: {effect: string, class: string}[] = [];


    // Player gets player Experience
    playerEffects.push({
        effect: `+1 Player experience`,
        class: 'player-experience'
    });

      playerEffects.push({
        effect: `+1 Attention`,
        class: 'player-attention'
    });

    partyEffects.push({
        effect: `other players -1 Attention`,
        class: 'party-experience'
    });

    // Process building effects
    [...building.effects, ...(building.positionEffects[position] || [])].forEach((effect) => {
        if (effect.chance && Math.random() > effect.chance) {
            return;
        }

        const effectStr = this.formatBuildingEffect(effect);
        const effectClass = this.getBuildingEffectClass(effect);

        switch (effect.type) {
            case BUILDING_EFFECT_TYPE.HEAL:
            case BUILDING_EFFECT_TYPE.RESURRECT:
            case BUILDING_EFFECT_TYPE.INJURE:
            case BUILDING_EFFECT_TYPE.USE_MAGIC:
            case BUILDING_EFFECT_TYPE.USE_TOOLS:
            case BUILDING_EFFECT_TYPE.REPLENISH_MAGIC:
            case BUILDING_EFFECT_TYPE.REPLENISH_TOOLS:
                characterEffects.push({effect: effectStr, class: effectClass});
                break;
 // case BUILDING_EFFECT_TYPE.ADD_INVOLVEMENT:
            case BUILDING_EFFECT_TYPE.ADD_TENSION:
            case BUILDING_EFFECT_TYPE.ADD_ENJOYMENT:
            // case BUILDING_EFFECT_TYPE.ADD_SCORE:
             partyEffects.push({effect: effectStr, class: effectClass});
                 break;
 // case BUILDING_EFFECT_TYPE.ADD_PLAYER_POINT:
            // case BUILDING_EFFECT_TYPE.CAMPAIGN_POINT:
            // break;  
        }

        // Apply effect
        this.applyBuildingEffect(effect, character, building);
    });

    // Add effects to results if they exist
    if (characterEffects.length > 0) {
        allEffects.push({
            object: character,
            effects: characterEffects
        });
    }

    if (playerEffects.length > 0 && player) {
        allEffects.push({
            object: player,
            effects: playerEffects
        });
    }

    if (partyEffects.length > 0) {
        allEffects.push({
            object: 'party',
            effects: partyEffects
        });
    }

    building.capacity = 0;

    // Return ResultConfig
    return {
        results: allEffects,
        title: `${building.building.name} Results`,
        description: `${character.name} visits the ${building.building.name}`,
        tags: this.getTagsForBuilding(building.building),
        travelOption: {
            tagInstructions: "Select a topic",
        },
        info: {
            building: building.building.name,
            character: character.name,
            player: player?.name
        }
    };
}

private applyBuildingEffect(effect: BuildingEffect, character: Character, building: TownBuilding) {
    // Initialize attributeLoss if it doesn't exist
    if (!character.attributeLoss) {
        character.attributeLoss = {};
    }

    switch(effect.type) {
        case BUILDING_EFFECT_TYPE.HEAL:
            // Remove injury effects
            character.injury = undefined;
            break;
        case BUILDING_EFFECT_TYPE.RESURRECT:
            // Remove all negative effects
            character.injury = undefined;
            character.attributeLoss = {};
            break;
        case BUILDING_EFFECT_TYPE.INJURE:
            character.injury = "injured";
            break;
        case BUILDING_EFFECT_TYPE.ADD_TENSION:
            this.partyManager.addPartyTension(effect.value);
            break;
        // case BUILDING_EFFECT_TYPE.ADD_SCORE:
        //   //  this.partyManager.addPartyScore(effect.value);
        //     break;
        case BUILDING_EFFECT_TYPE.ADD_ENJOYMENT:
            this.partyManager.addPartyEnjoyment(effect.value);
            break;
        case BUILDING_EFFECT_TYPE.CAMPAIGN_POINT:
            this.campaignManager.addEncounterPoints(effect.value);
            break;
        // case BUILDING_EFFECT_TYPE.ADD_PLAYER_POINT:
        //     const player = this.playerManager.getPlayerAssignedCharacter(character);
        //     if (player) {
        //         player.playerStats.experience += effect.value;
        //     }
        //     break;
        case BUILDING_EFFECT_TYPE.REPLENISH_MAGIC:
            // Remove magic losses
            character.attributeLoss[ATTRIBUTE_TYPE.MAGIC] = 0;
            break;
        case BUILDING_EFFECT_TYPE.REPLENISH_TOOLS:
            // Remove tool losses 
            character.attributeLoss[ATTRIBUTE_TYPE.TOOLS] = 0;
            break;
        case BUILDING_EFFECT_TYPE.USE_MAGIC:
            // Add to magic loss
            character.attributeLoss[ATTRIBUTE_TYPE.MAGIC] = (character.attributeLoss[ATTRIBUTE_TYPE.MAGIC] || 0) + effect.value;
            break;
        case BUILDING_EFFECT_TYPE.USE_TOOLS:
            // Add to tool loss
            character.attributeLoss[ATTRIBUTE_TYPE.TOOLS] = (character.attributeLoss[ATTRIBUTE_TYPE.TOOLS] || 0) + effect.value;
            break;
        case BUILDING_EFFECT_TYPE.ADD_TREASURE:
            // Add treasure to character
            this.partyManager.addTreasurePoints(effect.value);
    }
}

private formatBuildingEffect(effect: BuildingEffect): string {
    switch(effect.type) {
        case BUILDING_EFFECT_TYPE.HEAL:
            return `any Injuries Healed`;
        case BUILDING_EFFECT_TYPE.RESURRECT:
            return `Resurrected if dead`;
        case BUILDING_EFFECT_TYPE.INJURE:
            return `Injured!`;
        case BUILDING_EFFECT_TYPE.ADD_TENSION:
            return `${effect.value > 0 ? '+' : ''}${effect.value} tension`;
        case BUILDING_EFFECT_TYPE.ADD_ENJOYMENT:
            return `${effect.value > 0 ? '+' : ''}${effect.value} attitude`;
        // case BUILDING_EFFECT_TYPE.ADD_INVOLVEMENT:
        //     return `${effect.value > 0 ? '+' : ''}${effect.value} involvement`;
        case BUILDING_EFFECT_TYPE.ADD_REWARDS:
            return `${effect.value} rewards`;
        case BUILDING_EFFECT_TYPE.REPLENISH_MAGIC:
            return `Replenished `;
        case BUILDING_EFFECT_TYPE.REPLENISH_TOOLS:
            return `Replenished `;
        case BUILDING_EFFECT_TYPE.USE_MAGIC:
            return `Used `;
        case BUILDING_EFFECT_TYPE.USE_TOOLS:
            return `Used `;
        // case BUILDING_EFFECT_TYPE.CAMPAIGN_POINT:
        //     return `DM gained ${effect.value} exp`;
        // case BUILDING_EFFECT_TYPE.ADD_PLAYER_POINT:
        //     return `Gained ${effect.value} exp`;
        case BUILDING_EFFECT_TYPE.ADD_TREASURE:
            return `Found treasure (${effect.value})`;
        // case BUILDING_EFFECT_TYPE.ADD_SCORE:
        //     return `+${effect.value} Party Score`;
        default:
            return 'Unknown effect';
    }
}

private getBuildingEffectClass(effect: BuildingEffect): string {
    switch(effect.type) {
        case BUILDING_EFFECT_TYPE.HEAL:
        case BUILDING_EFFECT_TYPE.REPLENISH_MAGIC:
        case BUILDING_EFFECT_TYPE.REPLENISH_TOOLS:
        case BUILDING_EFFECT_TYPE.RESURRECT:
            return 'positive';
        case BUILDING_EFFECT_TYPE.INJURE:
        case BUILDING_EFFECT_TYPE.ADD_TENSION:
        case BUILDING_EFFECT_TYPE.USE_TOOLS:
        case BUILDING_EFFECT_TYPE.USE_MAGIC:
            return 'negative';
        
        //case BUILDING_EFFECT_TYPE.CAMPAIGN_POINT: 
        // //case BUILDING_EFFECT_TYPE.ADD_SCORE:
        //case BUILDING_EFFECT_TYPE.ADD_PLAYER_POINT:
        case BUILDING_EFFECT_TYPE.ADD_REWARDS:
        case BUILDING_EFFECT_TYPE.ADD_TREASURE:
        case BUILDING_EFFECT_TYPE.ADD_ENJOYMENT:
            return 'reward';
        default:
            return 'neutral';
    }
}
}

export default TownManager;
