
import React from 'react';
import './encounter-display.css';
import { Encounter, NOTEBOOK_CATEGORY } from '../models';
import { useUiManager } from './managerContexts';
import CImage from './UI/cimage';

interface EncounterDisplayProps {
    encounter:Encounter;
}

const EncounterDisplay: React.FC<EncounterDisplayProps> = ({ encounter }) => {
    const uiManager = useUiManager()
    const handleClicked = () => {
    uiManager.showBasicNotebook(NOTEBOOK_CATEGORY.ENCOUNTER, encounter)
}

    return (
        <div className={`encounter-display ${encounter.location.themes[0]}`} onClick={handleClicked}>
           <div className="encounter-name">
                {encounter.name}
            </div> 
            <div className = "encounter-event-icons">
                {encounter.events.map((event, index) => (
                    <div key={index} className="encounter-event-icon">
                        <CImage imageId={uiManager.getEventIcon(event.type)} />
                    </div>
                ))}
            </div>
            {/* <MoreInfo
                callback={handleClickedMoreInfo}
            /> */}
        </div>
    );
};

export default EncounterDisplay;