import React, { useState, useEffect, useMemo, useCallback } from "react";
import { useAppManager } from "../managerContexts";
import { ChatMessage, Quest, QuestNode, Tag } from "../../models";
import "./QuestProgressModal.scss";
import FlavorTagPill from "./FlavorTagPill";
import CImage from "./cimage";
import ConversationSection from "./ConversationSection";
import RewardDisplay from "./RewardDisplay";

interface QuestProgressModalProps {
	quest: Quest;
	currentNode: QuestNode;
	collectedTags: Tag[];
	onClose: () => void;
	onAccept: () => void;
}

const QuestProgressModal: React.FC<QuestProgressModalProps> = ({
	quest,
	currentNode,
	collectedTags,
	onClose,
	onAccept,
}) => {
	const appManager = useAppManager();
	const questManager = appManager.getQuestManager();
	const [conversationComplete, setConversationComplete] = useState(false);
	const [speedUp, setSpeedUp] = useState(false);
	const [selectedTagForConversation, setSelectedTagForConversation] =
		useState<Tag | null>(null);

	// Helper function to find a node by its tag
	const findNodeByTag = useCallback(
		(node: QuestNode, tag: Tag): QuestNode | null => {
			if (node.tag.name === tag.name) {
				return node;
			}
			for (const child of node.children) {
				const found = findNodeByTag(child, tag);
				if (found) return found;
			}
			return null;
		},
		[]
	);

	// Get the conversation messages for the current node
	const messages = useMemo((): ChatMessage[] => {
		if (selectedTagForConversation && collectedTags.length > 0) {
			// Show conversation for selected collected tag
			const tagNode = findNodeByTag(
				quest.startingNode,
				selectedTagForConversation
			);
			return tagNode?.conversation || [];
		} else {
			// Show current node conversation
			return currentNode.conversation || [];
		}
	}, [
		currentNode,
		selectedTagForConversation,
		quest.startingNode,
		collectedTags,
		findNodeByTag,
	]);

	// Determine if quest is complete
	const isQuestComplete = useMemo(() => {
		return questManager.isQuestComplete(quest.id);
	}, [questManager, quest.id]);

	// Combine starting node tag with collected tags for conversation selection
	const availableTagsForConversation = useMemo(() => {
		const startingTag = quest.startingNode.tag;
		const allTags = [startingTag, ...collectedTags];

		// Remove duplicates by tag name
		const uniqueTags = allTags.filter(
			(tag, index, array) =>
				array.findIndex((t) => t.name === tag.name) === index
		);

		return uniqueTags;
	}, [quest.startingNode.tag, collectedTags]);

	// Get upcoming quest topics (current node's children)
	const upcomingQuestTopics = useMemo(() => {
		const activeQuest = questManager
			.getActiveQuests()
			.find((aq) => aq.quest.id === quest.id);
		if (!activeQuest || isQuestComplete) {
			return [];
		}

		return activeQuest.currentNode.children.map((child) => child.tag);
	}, [questManager, quest.id, isQuestComplete]);

	// Set the most recent tag for conversation by default
	useEffect(() => {
		if (
			availableTagsForConversation.length > 0 &&
			!selectedTagForConversation
		) {
			// Default to the most recently collected tag, or starting tag if none collected
			const defaultTag =
				collectedTags.length > 0
					? collectedTags[collectedTags.length - 1]
					: quest.startingNode.tag;
			setSelectedTagForConversation(defaultTag);
		}
	}, [
		availableTagsForConversation,
		selectedTagForConversation,
		collectedTags,
		quest.startingNode.tag,
	]);

	const handleClick = () => {
		if (!conversationComplete) {
			setSpeedUp(true);
		}
	};

	const handleModalClick = (e: React.MouseEvent) => {
		e.stopPropagation();
		handleClick();
	};

	const handleTagClick = (tag: Tag) => {
		setSelectedTagForConversation(tag);
		setConversationComplete(false);
		setSpeedUp(false);
	};

	const handleAccept = () => {
		if (conversationComplete) {
			if (isQuestComplete) {
				// Apply quest rewards
				questManager.completeQuest(quest.id);

				// The QUEST MANAGER is kind of a bookkeeper and it shouldnt know about all the 
				// extra managers and game junk. So lets extract the rewards here: 
				const rewards = quest.rewards;
				const treasureReward = rewards.find(r => r.type === "treasure");
				const scoreReward = rewards.find(r => r.type === "score");
				
				if(treasureReward) {
					appManager.getPartyManager().addTreasurePoints(treasureReward.amount);
				}
				if(scoreReward) {
					appManager.getCampaignManager().addEncounterPoints(scoreReward.amount);
				}



			} else {
				// Activate quest and add first tag to pool
				questManager.activateQuest(quest.id);
			}
			onAccept();
			onClose();
		}
	};

	const formatCostDescription = (cost: any) => {
		switch (cost.type) {
			case "exhaustion":
				return `Increases party exhaustion by ${cost.amount}`;
			case "points":
				return `Costs ${cost.amount} encounter points`;
			case "tension":
				return `Increases party tension by ${cost.amount}`;
			default:
				return `${cost.type}: ${cost.amount}`;
		}
	};

	const formatRewardDescription = (reward: any) => {
		switch (reward.type) {
			case "gmPoints":
				return `Grants ${reward.amount} GM points`;
			case "treasure":
				return `Adds ${reward.amount} treasure`;
			case "score":
				return `Adds ${reward.amount} to campaign score`;
			case "reward":
				return `Provides ${reward.amount} party rewards`;
			default:
				return `${reward.type}: ${reward.amount}`;
		}
	};

	return (
		<div className="modal-overlay blue-theme" onClick={handleClick}>
			<div className="modal-container questprogress" onClick={handleModalClick}>
				{/* Header strip with quest info */}
				<div className="questprogress-header">
					<div className="header-background">
						{quest.icon && <CImage imageId={quest.icon} />}
					</div>
					<div className="header-overlay"></div>
					<div className="header-content">
						<div className="header-left">
							<div className="quest-info">
								<div className="quest-title">{quest.name}</div>
								<div className="quest-description">{quest.description}</div>
							</div>
						</div>
						<div className="header-right">
							<button className="close-button" onClick={onClose}>
								
							</button>
						</div>
					</div>
				</div>

				{/* Quest Progress Section */}
				<div className="quest-progress-section">
					{/* Quest Progress Row - Always show at least starting node */}
					<div className="progress-row">
						<div className="progress-label">
							<h4>Quest Progress</h4>
						</div>
						<div className="progress-tags">
							{availableTagsForConversation.map((tag, index) => {
								const isStartingTag = tag.name === quest.startingNode.tag.name;
								const isCompleted = collectedTags.some(
									(ct) => ct.name === tag.name
								);

								return (
									<div
										key={index}
										className={`progress-tag-item ${
											selectedTagForConversation?.name === tag.name
												? "selected"
												: ""
										} ${isStartingTag ? "starting-tag" : ""} ${
											isCompleted ? "completed-tag" : ""
										}`}
										onClick={() => handleTagClick(tag)}
									>
										<FlavorTagPill
											tag={{
												tag,
												theme: "none" as any,
												source: "QUEST" as any,
											}}
											showTooltip={false}
										/>
									</div>
								);
							})}
						</div>
					</div>

					{/* Upcoming Quest Topics Row */}
					{/* {upcomingQuestTopics.length > 0 && !isQuestComplete && (
						<div className="progress-row">
							<div className="progress-label">
								<h4>Upcoming Quest Topics</h4>
							</div>
							<div className="progress-tags">
								{upcomingQuestTopics.map((tag, index) => (
									<div key={index} className="progress-tag-item upcoming-tag">
										<FlavorTagPill
											tag={{
												tag,
												theme: "none" as any,
												source: "QUEST" as any,
											}}
											showTooltip={false}
										/>
									</div>
								))}
							</div>
						</div>
					)} */}
				</div>

				{/* Side-by-side content */}
				<div className="questprogress-body">
					{/* Left side - Conversation */}
					<div className="conversation-section">
						<div className="conversation-content">
							{messages.length > 0 ? (
								<ConversationSection
									title="Quest Dialogue"
									messages={messages}
									onComplete={() => setConversationComplete(true)}
									speedUp={speedUp}
								/>
							) : (
								<div className="no-conversation">
									No dialogue available for this quest step.
								</div>
							)}
						</div>
					</div>

					{/* Right side - Quest Details */}
					<div className="quest-details-section">
						<div className="section-header">
							<h3>{isQuestComplete ? "Quest Completed!" : "Quest Details"}</h3>
						</div>
						<div className="quest-details-content">
							{!isQuestComplete ? (
								<>
									{/* Quest Cost */}
									<div className="quest-info-block">
										<h4>Quest Cost</h4>
										{quest.cost.map((cost, index) => (
											<div key={index} className="cost-item">
												<div className="cost-label">
													{cost.type.charAt(0).toUpperCase() +
														cost.type.slice(1)}{" "}
													+{cost.amount}
												</div>
												{/* <div className="cost-description">{formatCostDescription(cost)}</div> */}
											</div>
										))}
									</div>

									{/* Quest Rewards */}
									<div className="quest-info-block">
										<h4>Quest Rewards</h4>
										{quest.rewards.map((reward, index) => (
											<div key={index} className="reward-item">
												<RewardDisplay reward={reward} />
											</div>
										))}
									</div>
								</>
							) : (
								/* Quest Complete - Highlight Rewards */
								<div className="quest-complete-rewards">
									<div className="quest-info-block highlighted">
									
											<div className="quest-info-block">
										<h4>Quest Rewards</h4>
										{quest.rewards.map((reward, index) => (
											<div key={index} className="reward-item">
												<RewardDisplay reward={reward} />
											</div>
										))}
									</div>
									</div>
								</div>
							)}
						</div>
					</div>
				</div>

				{/* Footer with OK button */}
				<div className="questprogress-footer">
					<button
						className={`questprogress-accept-button ${
							!conversationComplete ? "disabled" : ""
						}`}
						onClick={handleAccept}
						disabled={!conversationComplete}
					>
						{isQuestComplete ? "CLAIM REWARDS" : "CONTINUE"}
					</button>
				</div>
			</div>
		</div>
	);
};

export default QuestProgressModal;
