import React from 'react';
import { ChatEvent, CHAT_EVENT_TYPE } from '../../models';
import { ENCOUNTER_EVENT_TYPE } from '../../consts/general';
import './chatEventTile.scss';

interface ChatEventTileProps {
  event: ChatEvent;
}

const ChatEventTile: React.FC<ChatEventTileProps> = ({ event }) => {
  const getEventTypeClass = (): string => {
    switch (event.type) {
      case CHAT_EVENT_TYPE.CAMPAIGN_START:
        return 'campaign-start';
      case CHAT_EVENT_TYPE.ENCOUNTER_EVENT:
        // Use the encounter event type for styling
        const encounterType = event.data?.eventType as ENCOUNTER_EVENT_TYPE;
        return `encounter-event ${encounterType || 'unknown'}`;
      case CHAT_EVENT_TYPE.LOCATION_CHANGE:
        return 'location-change';
      case CHAT_EVENT_TYPE.COMBAT_START:
        return 'combat-start';
      case CHAT_EVENT_TYPE.COMBAT_END:
        return 'combat-end';
      default:
        return 'generic';
    }
  };



  return (
    <div className={`chat-event-tile ${getEventTypeClass()}`}>
     
      <div className="event-content">
        <div className="event-title">
        {event.subtitle || event.title}
        </div>
        {event.subtitle && (
          <div className="event-subtitle"> 
             {event.title}
          </div>
        )}
      </div>
      
    </div>
  );
};

export default ChatEventTile;