import React, { useState } from 'react';
import { Character } from '../../models';
import { 
  getCharacterReadinessLevel, 
  getCharacterReadinessClass,
  CHARACTER_READINESS_THRESHOLDS 
} from '../../consts/characterConsts';
import CImage from './cimage';
import RangedFillBar from './RangedFillBar';
import './CharacterLevelRow.scss';

interface CharacterLevelRowProps {
  character: Character;
  isSelected: boolean;
  onToggle: () => void;
}

const CharacterLevelRow: React.FC<CharacterLevelRowProps> = ({
  character,
  isSelected,
  onToggle
}) => {
  const [hoverTooltip, setHoverTooltip] = useState<string>('');

  const ranges = [
    {
      start: 0,
      end: CHARACTER_READINESS_THRESHOLDS.VERY_UNREADY - 1,
      backgroundColor: '#ffcdd2',
      fillColor: '#f44336',
      label: 'Very Unready: All other players -2 happiness'
    },
    {
      start: CHARACTER_READINESS_THRESHOLDS.VERY_UNREADY,
      end: CHARACTER_READINESS_THRESHOLDS.UNREADY - 1,
      backgroundColor: '#ffe0b2',
      fillColor: '#ff9800',
      label: 'Unready: All other players -1 happiness'
    },
    {
      start: CHARACTER_READINESS_THRESHOLDS.READY_START,
      end: CHARACTER_READINESS_THRESHOLDS.READY_END,
      backgroundColor: '#c8e6c9',
      fillColor: '#8bc34a',
      label: 'Ready: +1 Happiness'
    },
    {
      start: CHARACTER_READINESS_THRESHOLDS.PERFECT_START,
      end: CHARACTER_READINESS_THRESHOLDS.PERFECT_END,
      backgroundColor: '#4caf50',
      fillColor: '#2e7d32',
      label: 'Perfect: +2 Happiness'
    },
    {
      start: CHARACTER_READINESS_THRESHOLDS.READY_SECOND_START,
      end: CHARACTER_READINESS_THRESHOLDS.READY_SECOND_END,
      backgroundColor: '#c8e6c9',
      fillColor: '#8bc34a',
      label: 'Ready: +1 Happiness'
    },
    {
      start: CHARACTER_READINESS_THRESHOLDS.OVERREADY,
      end: 100,
      backgroundColor: '#ffab91',
      fillColor: '#ff5722',
      label: 'Overready: +0 Happiness (-1 Happiness when OTHER Player\'s Characters level up)'
    }
  ];

  const handleHover = (rangeLabel: string) => {
    setHoverTooltip(`Level Up: Full Attention\n${rangeLabel}`);
  };

  const handleLeave = () => {
    setHoverTooltip('');
  };

  const readinessClass = getCharacterReadinessClass(character.experience);

  return (
    <div className="character-level-row">
      <button 
        className={`character-toggle ${isSelected ? 'selected' : ''} ${readinessClass}`}
        onClick={onToggle}
      >
        <div className="character-icon">
          <CImage imageId={character.icon} />
        </div>
        <div className="character-name">{character.name}</div>
      </button>
      
      <div className={`experience-bar ${isSelected ? 'selected' : ''}`}>
        <RangedFillBar
          totalSegments={100}
          filledSegments={character.experience}
          ranges={ranges}
          onHover={handleHover}
          onLeave={handleLeave}
        />
      </div>
      
      {hoverTooltip && (
        <div className="tooltip">
          {hoverTooltip.split('\n').map((line, index) => (
            <div key={index}>{line}</div>
          ))}
        </div>
      )}
    </div>
  );
};

export default CharacterLevelRow;
