import { Tag } from "../../models";
import TooltipWrapper from "./TooltipWrapper";

interface TooltipProps {
  theme: string;
  tag: Tag;
  message: string;
}

// Conditional tooltip component that only shows the tooltip when visible is true
export const ConditionalTooltip: React.FC<{
  tooltip: string;
  visible: boolean;
  preservePositioning?: boolean;
  translate?: [number, number];
  children: React.ReactNode;
  // MAXIM: Note that we dont pass everything to the tooltip wrapper and just use the defaults, theres no reason not to if needed though
}> = ({ tooltip, visible, preservePositioning, translate, children }) => {
  if (visible) {
    return (
      <TooltipWrapper
        content={tooltip}
        preservePositioning={preservePositioning}
        translate={translate}
      >
        {children}
      </TooltipWrapper>
    );
  }
  return <>{children}</>;
};


const Tooltip: React.FC<TooltipProps> = ({ theme, tag, message }) => {
  return (
    <div className={`tooltip theme-${theme}`}>
      <div className="tooltip-content">
        {message}
      </div>
    </div>
  );
};

export default Tooltip;