import React from 'react';
import TooltipWrapper from './TooltipWrapper';

interface RangeConfig {
  start: number;
  end: number;
  backgroundColor: string;
  fillColor: string;
  label: string;
}

interface RangedFillBarProps {
  totalSegments: number;
  filledSegments: number;
  ranges: RangeConfig[];
  theme?: string;
  label?: string;
  onHover?: (rangeLabel: string) => void;
  onLeave?: () => void;
  showTooltips?: boolean;
}

const RangedFillBar: React.FC<RangedFillBarProps> = ({ 
  totalSegments, 
  filledSegments, 
  ranges, 
  theme = '', 
  label,
  onHover,
  onLeave,
  showTooltips = false
}) => {
  
  const handleSegmentHover = (segmentIndex: number) => {
    if (onHover) {
      const range = ranges.find(r => segmentIndex >= r.start && segmentIndex <= r.end);
      if (range) {
        onHover(range.label);
      }
    }
  };

  const getSegmentStyle = (segmentIndex: number) => {
    const range = ranges.find(r => segmentIndex >= r.start && segmentIndex <= r.end);
    const isFilled = segmentIndex < filledSegments;
    
    if (range) {
      return {
        backgroundColor: isFilled ? range.fillColor : range.backgroundColor,
      };
    }
    
    return {
      backgroundColor: isFilled ? '#4CAF50' : '#e0e0e0',
    };
  };

  const getTooltipContent = (segmentIndex: number) => {
    if (!showTooltips) return '';
    
    const baseText = "Level Up: Full Attention\n";
    
    if (segmentIndex < 20) {
      return baseText + "Very Unready: All other players -2 happiness";
    } else if (segmentIndex < 50) {
      return baseText + "Unready: All other players -1 happiness";
    } else if (segmentIndex < 65 || (segmentIndex >= 75 && segmentIndex <= 90)) {
      return baseText + "Ready: +1 Happiness";
    } else if (segmentIndex >= 65 && segmentIndex < 75) {
      return baseText + "Perfect: +2 Happiness";
    } else if (segmentIndex > 90) {
      return baseText + "Overready: +0 Happiness (-1 Happiness when OTHER Player's Characters level up)";
    }
    return baseText;
  };

  const segments = Array.from({ length: totalSegments }, (_, index) => {
    const segment = (
      <div
        key={index}
        className={`fill-bar-segment ranged ${index < filledSegments ? 'filled' : ''}`}
        style={getSegmentStyle(index)}
        onMouseEnter={() => handleSegmentHover(index)}
        onMouseLeave={onLeave}
      />
    );

    if (showTooltips) {
      return (
        <TooltipWrapper
          key={index}
          content={getTooltipContent(index)}
          position="top"
          showHelpIcon={false}
        >
          {segment}
        </TooltipWrapper>
      );
    }

    return segment;
  });

  return (
    <div className={`fill-bar ranged-fill-bar`}>
      {label && <div className={`fill-bar-label text-theme-${theme || ''}`}>{label}</div>}
      <div className={`fill-bar-container ranged`}>
        {segments}
      </div>
    </div>
  );
};

export default RangedFillBar;
