import React from 'react';
import { NAV_ENTRY } from '../../models';
import { useAppManager } from '../managerContexts';
import { APP_STATE } from '../../consts/states';

interface NavBarProps {
   buttonList: string[];
   highlight?: string;
}

const NavBar: React.FC<NavBarProps> = ({buttonList, highlight}) => {
    const appManager = useAppManager();

    const navClicked = (navEntry: string) => {
        switch (navEntry) {
          case NAV_ENTRY.PLAYER_RECRUITMENT:
            
            appManager.state = APP_STATE.PLAYER_RECRUITMENT;
            break;
          case NAV_ENTRY.CHARACTER_ASSIGNMENT:
            appManager.state = APP_STATE.CHARACTER_CREATION;
            break;
          case NAV_ENTRY.CAMPAIGN:
            appManager.state = APP_STATE.CAMPAIGN_SETUP;
            break;
          default:
            console.log('No entry');
        }
      }



    const renderButton = (buttonName: string) => {

      let highlightClass = buttonName === highlight ? 'highlight' : '';
        switch (buttonName) {
          case NAV_ENTRY.PLAYER_RECRUITMENT:
            return <button className={`nav-button player ${highlightClass}`} key={buttonName} onClick={() => navClicked(buttonName)} >Player Recruitment</button>;
          case NAV_ENTRY.CHARACTER_ASSIGNMENT:
            return <button className={`nav-button character ${highlightClass}`} key={buttonName}onClick={() => navClicked(buttonName)}>Character Assignment</button>;
          case NAV_ENTRY.CAMPAIGN:
            return <button className={`nav-button campaign ${highlightClass}`} key={buttonName}onClick={() => navClicked(buttonName)}>Campaign Select</button>;
          default:
            return null;
        }
      };
    
      return (
        <div className="nav-bar-container">
          {buttonList.map(buttonName => renderButton(buttonName))}
        </div>
      );
};

export default NavBar;
