import React, { useEffect, useState } from 'react';
import './playerRecruitment.scss';
import AppManager from '../managers/appManager';
import PlayerManager from '../managers/playerManager';
import { NAV_ENTRY, NOTEBOOK_CATEGORY, NotebookState, Player } from '../models';
import PlayerCard from './playerCard';
import Schedule from './schedule';

import { useUiManager } from './managerContexts';
import NotebookGroup from './UI/notebookGroup';
import Notebook from './UI/notebook';
import NavBar from './UI/navBar';

interface PlayerRecruitmentProps {
  appManager: AppManager; 
  playerManager: PlayerManager;
}


const PlayerRecruitmentComponent: React.FC<PlayerRecruitmentProps> = ({appManager, playerManager}) => {
  const [selectedPlayers, setSelectedPlayers] = useState<number[]>(playerManager.getSelectedPlayerIds());
  const [groupNotebookContent, setGroupNotebookContent] = useState<NotebookState[]>([]);
  const recruitablePlayers : Player[] = playerManager?.getRecruitablePlayers() || [];
  const uiManager = useUiManager();

  const toggleSelectPlayer = (playerId: number) => {
    setSelectedPlayers((prevSelectedPlayers) => {
      const newSelectedPlayers = Array.from(prevSelectedPlayers);

      const index = newSelectedPlayers.indexOf(playerId);
      if (index > -1) {
        newSelectedPlayers.splice(index, 1);
      } else {
        newSelectedPlayers.push(playerId);
      }
      playerManager.setSelectedPlayers(newSelectedPlayers);

      return newSelectedPlayers;
    });
    
    const playerToShow = playerManager.getRecruitablePlayers()[playerId];
  };

  const numberOfSelectedPlayers = selectedPlayers.length;
  const dayData = playerManager.aggregateSchedule();

 useEffect(() => {
  const makeGroupNotebookContent = (players: number[]) => {

    let content;
    let notebookTitle ='';

    
    let secondaryEntries: { category: string; content: { player: Player; }; title: string; }[] = []
    content = {players: players.map((player) => playerManager.getRecruitablePlayers()[player])};
    notebookTitle = "Group Info";
    content.players.forEach((player) => {
      secondaryEntries.push({
        category: NOTEBOOK_CATEGORY.PLAYER,
        content: {player: player},
        title: player.name.split(' ').map((n) => n[0]).join('')
      })
    });

    const notebookState: NotebookState = {
      category: NOTEBOOK_CATEGORY.GROUP,
      content: content,
      title: notebookTitle,
    } 

    return [notebookState, ...secondaryEntries];
  // return [notebookState];
  }
  // set selected players when the component mounts
  playerManager.setSelectedPlayers(selectedPlayers);
    setGroupNotebookContent(makeGroupNotebookContent(selectedPlayers));
  }, [playerManager, selectedPlayers]);


  useEffect(() => {
    
    const handleRemovePlayer = (player: Player) => {
      setSelectedPlayers((prevSelectedPlayers) => {
        const newSelectedPlayers = Array.from(prevSelectedPlayers);
        const index = newSelectedPlayers.indexOf(player.id);
        if (index > -1) {
          newSelectedPlayers.splice(index, 1);
        }
        playerManager.setSelectedPlayers(newSelectedPlayers);
        return newSelectedPlayers;
    });
  }

    uiManager.on('removePlayer', handleRemovePlayer)
    return () => {
     uiManager.off('removePlayer', handleRemovePlayer);
    } 
  
  }, []);



  return (

    <div className="player-recruitment-mode">
      <div className="player-status-container">
        <div className="player-recruitment-left-container">
          <Notebook
              notebookPages={groupNotebookContent}
              singleColumn
            />
      
        </div>
        <NavBar buttonList={[NAV_ENTRY.CAMPAIGN, NAV_ENTRY.CHARACTER_ASSIGNMENT]} highlight={NAV_ENTRY.CHARACTER_ASSIGNMENT}/>
      </div>
      <div className="player-grid-container">
        <div className="player-recruitment-status">
          {numberOfSelectedPlayers === 0 ? 'Select players to recruit' : 'Click "Accept Group" to continue'}
        </div>
        <div className="player-grid">
          {recruitablePlayers.map((player) => (
            <PlayerCard
              key={player.id}
              player={player}
              isSelected={selectedPlayers.includes(player.id)}
              onSelect={() => toggleSelectPlayer(player.id)}
              showSchedule={true}
              showPreferences={true}
            />
          ))}
        </div>
      </div>
    </div>
  );
}

export default PlayerRecruitmentComponent;
