import React, { useState, useMemo } from "react";
import "./WelcomeModal.scss";
import CImage from "./cimage";
import { useAppManager } from "../managerContexts";
import ConversationSection from "./ConversationSection";

interface WelcomeModalProps {
	onClose: () => void;
}

// Game Info Component
interface GameInfoPanelProps {
	handleOpenCampaignNotes: () => void;
}

const GameInfoPanel: React.FC<GameInfoPanelProps> = ({ handleOpenCampaignNotes }) => {
	return (
		<div className="game-info-panel">
			<div className="info-content">
				<div className="info-section">
					<h3>Welcome to Dungeon Master Sim</h3>
					<p>
						Take the role of Dungeon Master and run a Campaign by ushering your
						players through a series of Encounters.
					</p>
					<p>
						Encounters are made up of Events, each with their own rules, goals,
						and basis in real-life tabletop rpgs.
					</p>
					<p>
						Keep your players happy by engaging in topics they like, awarding treasure, and attitude bonuses. Players will get bored if their Characters are ignored. 
					</p>
					<p>
						Score points by building <span className="inline-dice-symbols">
							<CImage imageId="die_d4" />
							<CImage imageId="die_d6" />
							<CImage imageId="die_d20" />
						</span> during events, and then collect them by hitting key encounter points.  
					</p>
					<div className="button-container-center">
						<button 
							className="welcome-button info"
							onClick={() => {
								// Close this modal and open the Encounter notebook
								handleOpenCampaignNotes();
							}}
						>
							Open Campaign Notes
						</button>
					</div>
				</div>

				<div className="info-section highlight">
					<h3>Gameplay Tips</h3>
					<ul>
						<li>Pay attention to your players' preferences and attitudes</li>
						<li>Balance different encounter types to keep things interesting</li>
						<li>Use the campaign notes to track important story elements</li>
						<li>Experiment with different approaches to see what works</li>
					</ul>
				</div>
			</div>
		</div>
	);
};

const WelcomeModal: React.FC<WelcomeModalProps> = ({ onClose }) => {
	const appManager = useAppManager();
	const chatManager = appManager.getChatManager();
	const [conversationComplete, setConversationComplete] = useState(false);
	const [speedUp, setSpeedUp] = useState(false);

	// Get the welcome conversation
	const messages = useMemo(() => {
		return chatManager.getConversationById("WELCOME_CONVERSATION") || [];
	}, [chatManager]);

	const handleClick = () => {
		if (!conversationComplete) {
			setSpeedUp(true);
		}
	};

	const handleModalClick = (e: React.MouseEvent) => {
		e.stopPropagation();
		handleClick();
	};

	const handleOpenCampaignNotes = () => {
		// Close this modal and open the Campaign Notes
		onClose();
		appManager.getCampaignManager().displayNewEncounter();
	};

	const handleContinue = () => {
		onClose();
	};

	return (
		<div className="modal-overlay blue-theme" onClick={handleClick}>
			<div className="modal-container welcome" onClick={handleModalClick}>
				{/* Header */}
				<div className="welcome-header">
					<div className="header-background">
						<CImage imageId="logo.png" />
					</div>
					<div className="header-overlay"></div>
					<div className="header-content">
						<h1 className="welcome-title">Dungeon Master Sim v0.0.1</h1>
						<p className="welcome-subtitle">
							Prepare for an unfinished experience!
						</p>
					</div>
				</div>
				
				{/* Side-by-side content */}
				<div className="welcome-body">
					{/* Right side - Game Information */}
					<div className="info-section">
						<div className="info-content">
							<GameInfoPanel handleOpenCampaignNotes={handleOpenCampaignNotes} />
						</div>
					</div>
					
					{/* Left side - Conversation */}
					<div className="conversation-section">
						<div className="conversation-content">
							{messages.length > 0 ? (
								<ConversationSection
									messages={messages}
									title="Welcome"
									onComplete={() => setConversationComplete(true)}
									speedUp={speedUp}
								/>
							) : (
								<div className="no-conversation">
									<p>What is happening? Why did Maxim break this? Ugh.</p>
								</div>
							)}
						</div>
					</div>
				</div>

				{/* Footer */}
				<div className="welcome-footer">
					<button
						className={`welcome-button ${
							!conversationComplete ? "disabled" : ""
						}`}
						onClick={handleContinue}
						disabled={!conversationComplete}
					>
						BEGIN THE ADVENTURE!
					</button>
				</div>
			</div>
		</div>
	);
};

export default WelcomeModal;