import React, { useState, useEffect, useRef } from "react";
import { ChatMessage, CHAT_TYPE } from "../../models";
import { useAppManager } from "../managerContexts";
import CImage from "./cimage";
import "./ConversationSection.scss";

interface ConversationSectionProps {
	messages: ChatMessage[];
	title: string;
	onComplete: () => void;
	speedUp: boolean;
}

// Conversation Display Component
interface ConversationDisplayProps {
	messages: ChatMessage[];
	onComplete: () => void;
	speedUp: boolean;
}

const ConversationDisplay: React.FC<ConversationDisplayProps> = ({
	messages,
	onComplete,
	speedUp,
}) => {
	const [currentMessageIndex, setCurrentMessageIndex] = useState(0);
	const [messageStates, setMessageStates] = useState<
		Array<{ text: string; complete: boolean }>
	>([]);
	const intervalRef = useRef<NodeJS.Timeout | null>(null);
	const uiManager = useAppManager().getUiManager();
	const currentMessage = messages[currentMessageIndex];

	// Initialize message states
	useEffect(() => {
		setMessageStates(messages.map(() => ({ text: "", complete: false })));
	}, [messages]);

	useEffect(() => {
		if (!currentMessage) {
			onComplete();
			return;
		}

		const animateCurrentMessage = () => {
			// Split text into words while preserving spaces (same as main chat)
			const words = currentMessage.message.split(/(?<=\s)|(?=\s)/);
			let wordIndex = 0;
			let visibleWords = words.map((word) => ({ word, visible: false }));

			const animateWords = () => {
				if (wordIndex < words.length) {
					if (!visibleWords[wordIndex].word.match(/^\s+$/)) {
						visibleWords[wordIndex].visible = true;
						const displayText = visibleWords
							.map(({ word, visible }) =>
								word.match(/^\s+$/)
									? word
									: `<span class="chat-word ${
											visible ? "visible" : ""
									  }">${word}</span>`
							)
							.join("");

						setMessageStates((prev) => {
							const newStates = [...prev];
							newStates[currentMessageIndex] = {
								text: displayText,
								complete: false,
							};
							return newStates;
						});
					}
					wordIndex++;

					const delay = speedUp ? 5 : 10; // Match main chat's 10ms interval
					intervalRef.current = setTimeout(animateWords, delay);
				} else {
					// Message animation complete
					setMessageStates((prev) => {
						const newStates = [...prev];
						newStates[currentMessageIndex] = {
							text: currentMessage.message,
							complete: true,
						};
						return newStates;
					});

					if (currentMessageIndex < messages.length - 1) {
						// Move to next message after a pause
						const pauseDelay = speedUp ? 50 : 800;
						intervalRef.current = setTimeout(() => {
							setCurrentMessageIndex((prev) => prev + 1);
						}, pauseDelay);
					} else {
						// All messages complete
						setTimeout(() => onComplete(), 500);
					}
				}
			};

			animateWords();
		};

		animateCurrentMessage();

		return () => {
			if (intervalRef.current) {
				clearTimeout(intervalRef.current);
			}
		};
	}, [
		currentMessageIndex,
		currentMessage,
		speedUp,
		messages.length,
		onComplete,
	]);

	useEffect(() => {
		if (speedUp) {
			// Complete all animations immediately
			if (intervalRef.current) {
				clearTimeout(intervalRef.current);
			}
			setMessageStates(
				messages.map((msg) => ({ text: msg.message, complete: true }))
			);
			setCurrentMessageIndex(messages.length - 1);
			setTimeout(() => onComplete(), 100);
		}
	}, [speedUp, messages, onComplete]);

	const getSpeakerClass = (speaker: any) => {
		if (speaker.type === CHAT_TYPE.DM) return "dm";
		if (speaker.type === CHAT_TYPE.PLAYER) return "player";
		return "character";
	};

	return (
		<div className="conversation-display chat-expanded-messages">
			{messages.map((message, messageIdx) => {
				const messageState = messageStates[messageIdx];
				const shouldShow = messageIdx <= currentMessageIndex;

				if (!shouldShow) return null;

				return (
					<div
						key={messageIdx}
						className={`chat-message-container ${getSpeakerClass(
							message.speaker
						)} ${messageState?.complete ? "complete" : "current"}`}
					>
						<div className="chat-message-display">
							<div className="chat-message-speaker-container">
								<div className="chat-message-speaker">
									{uiManager.getMessageSpeakerName(message.speaker)}
								</div>
								<div className="chat-message-icon">
									<CImage
										imageId={
											uiManager.getMessageIconId(message.speaker) || "icon_dm"
										}
									/>
								</div>
							</div>
							<div className="chat-messager-content">
								<div className="chat-message-text">
									<div
										className="chat-message-words"
										dangerouslySetInnerHTML={{
											__html: messageState?.text || message.message,
										}}
									/>
								</div>
							</div>
						</div>
					</div>
				);
			})}
		</div>
	);
};

const ConversationSection: React.FC<ConversationSectionProps> = ({
	messages,
	title,
	onComplete,
	speedUp,
}) => {
	const hasMessages = messages.length > 0;

	return (
		<div className="conversation-section">
			{/* <div className="section-header">
				<h3>{title}</h3>
			</div> */}
			<div className="conversation-content">
				{hasMessages ? (
					<ConversationDisplay
						messages={messages}
						onComplete={onComplete}
						speedUp={speedUp}
					/>
				) : (
					<div className="no-conversation">
						<p>This tag was discovered without recorded conversation.</p>
					</div>
				)}
			</div>
		</div>
	);
};

export default ConversationSection;