import React, { createContext, useContext } from 'react';
import CharacterManager from '../managers/characterManager';
import AppManager from '../managers/appManager';
import CampaignManager from '../managers/campaignManager';
import PartyManager from '../managers/partyManager';
import UiManager from '../managers/uiManager';
import DataManager from '../managers/dataManager';
import SessionManager from '../managers/sessionManager';
import PlayerManager from '../managers/playerManager';


const AppManagerContext = createContext<AppManager | undefined>(undefined);
const DataManagerContext = createContext<DataManager | undefined>(undefined);
const CampaignManagerContext = createContext<CampaignManager | undefined>(undefined);
const SessionManagerContext = createContext<SessionManager | undefined>(undefined);
const PartyManagerContext = createContext<PartyManager | undefined>(undefined);
const UiManagerContext = createContext<UiManager | undefined>(undefined);
const CharacterManagerContext = createContext<CharacterManager | undefined>(undefined);
const PlayerManagerContext = createContext<PlayerManager | undefined>(undefined);

export const CharacterManagerProvider: React.FC<{ manager: CharacterManager, children: React.ReactNode }> = ({ manager, children }) => {
  return (
    <CharacterManagerContext.Provider value={manager}>
      {children}
    </CharacterManagerContext.Provider>
  );
};

export const PlayerManagerProvider: React.FC<{ manager: PlayerManager, children: React.ReactNode }> = ({ manager, children }) => {
    return (
    <PlayerManagerContext.Provider value={manager}>
      {children}
    </PlayerManagerContext.Provider>
  );
};

