import React, { useEffect, useState } from 'react';
import './campaignSetup.scss';
import { APP_STATE } from '../consts/states';
import { Player, Campaign, NAV_ENTRY } from '../models';
import { usePlayerManager, useAppManager, useCampaignManager } from './managerContexts';
import PlayerCard from './playerCard';
import CampaignDisplay from './campaignDisplay';
import NavBar from './UI/navBar';

interface CampaignSetupProps {
  openCampaign?: number;
}

const CampaignSetupComponent: React.FC<CampaignSetupProps> = ({openCampaign}) => {

  const playerManager = usePlayerManager()
  const appManager = useAppManager();
  const campaignManager = useCampaignManager();


  const [availableCampaigns] = useState<Campaign[]>(campaignManager.getAvailableCampaigns());
  const [selectedCampaign, setSelectedCampaign] = useState<number | undefined>(openCampaign);

  const [players] = useState<Player[]>(playerManager.getPlayers());

const unsetSelectedCampaign = () => {
  setSelectedCampaign(undefined);
}

const setMySelectedCampaign = (index: number) => {
  
  setSelectedCampaign(index);
}

  const handleAcceptCampaign = (campaignId: number) => {
    campaignManager.startCampaign(campaignId);
    if(playerManager.readyForGame()){
      appManager.state = APP_STATE.SESSION;
    }
  }


  useEffect(() => {
    
  }, [selectedCampaign]);

  return (
    <div className="campaign-setup-container">
      <div className="player-list-container">
      <NavBar buttonList={[NAV_ENTRY.PLAYER_RECRUITMENT, NAV_ENTRY.CHARACTER_ASSIGNMENT]} />

        <div className="player-list">
          {players.map((player) => (
            <PlayerCard 
              key={player.id}
              player={player}
              isSelected={false}
              onSelect={() => {}}
              showCharacters={true}
              showSchedule={false}
            >
            </PlayerCard>
          ))}
        </div>
      </div>
      <div className="campaign-display-container">
        <div className="campaign-list-container"> 
            <div className="campaign-list">
              {availableCampaigns.map((campaign, index) => (
                <CampaignDisplay
                  key={index} 
                  collapsed={selectedCampaign !== index}
                  campaign={campaign}
                  onSelect={() => setMySelectedCampaign(index)}
                  onAccept={() => handleAcceptCampaign(index)}
                  onCancel={() => unsetSelectedCampaign()}
                />
              ))}
          </div>
        </div>
      </div>
    </div>
  );
}

export default CampaignSetupComponent;
