  constructor(appManager: AppManager) {
    super();
    this.appManager = appManager;
    this.playerManager = appManager.getPlayerManager();
    this.tagManager = appManager.getTagManager();
    this.currentCampaign = CAMPAIGNS[0]; // Default to the first campaign
    this.init();


  }

  partyManager() {

    return this.appManager.getPartyManager();
  }

  init() {

    this.dmTricks = [

    ]

    // Initialize consumables based on current hosting level
    this.resetConsumables();
  }

  // Initialize/reset consumables
  private resetConsumables(): void {
    const hostingLevel = this.getDMAbilityMeterLevel('hosting');
    
    // Reset max values based on hosting level
    this.maxSnacks = hostingLevel >= 1 ? 1 : 0;
    this.maxBreaks = hostingLevel >= 2 ? 1 : 0;
    this.maxProps = hostingLevel >= 3 ? 1 : 0;
    this.maxVenue = hostingLevel >= 4 ? 1 : 0;
    
    // Set remaining/current to max (full reset)
    this.remainingSnacks = this.maxSnacks;
    this.remainingBreaks = this.maxBreaks;
    this.currentProps = this.maxProps;
    this.currentVenue = this.maxVenue;
  }



  // ---------------------------------------------------------------------------------------------------------
  // DM SCREEN EVENTS
  // ---------------------------------------------------------------------------------------------------------



  redeemTheme(theme: string) {
    delete this.toneCounts[theme as FlavorTagTheme];
    switch (theme) {
      case FlavorTagTheme.COMEDIC:
        this.partyManager().addPartyEnjoyment(1);
        break;
      // case FlavorTagTheme.HEROIC:
      //   this.partyManager().addPartyEnjoyment(1);
      //   break;
      case FlavorTagTheme.HEROIC:
        this.partyManager().setPartyTension(50);
        break;
      case FlavorTagTheme.DRAMATIC:
        this.partyManager().updateAllAttention(10);
        break;
      case FlavorTagTheme.GRIM:
        this.adjustTensionMeterForGrimTone();
        break;
      case FlavorTagTheme.ROMANTIC:
        this.adjustTensionMeterForRomanticTone();
        break;
      // case FlavorTagTheme.FANTASY:
      //   this.partyManager().addPartyEnjoyment(1);
      //   break;
      // case FlavorTagTheme.HORROR:
      //   this.partyManager().addPartyEnjoyment(1);
      //   break;
      case FlavorTagTheme.NONE:
        this.partyManager().addPartyEnjoyment(1);
        break;
    }
    this.emit('dmScreenUpdated', this.toneCounts);
  }

  // This occurs when a user hits a theme that has been filled. 
  continueTheme(theme: string) {
    // this.partyManager().addPartyScore(10);
  }



  updateFlavorTagThemesTally(tags: EncounterFlavorTag[]) {
    tags.forEach(tag => {
      if (tag.theme === FlavorTagTheme.NONE) {
        return;
      }
      this.addTheme(tag.theme);
    });
  }

  themedUiClicked(theme: string) {

    this.addTheme(theme);
    this.emit('dmScreenUpdated', this.toneCounts);
  }

  addTheme(theme: string) {
    if (theme === FlavorTagTheme.NONE) {
      return;
    }
    if (this.toneCounts[theme as FlavorTagTheme] === undefined) {
      this.toneCounts[theme as FlavorTagTheme] = 1;
    }
    else if ((this.toneCounts[theme as FlavorTagTheme] || 0) >= THEME_FILLED_REQ) {
      this.continueTheme(theme);
    } else {
      this.toneCounts[theme as FlavorTagTheme] = Math.min(THEME_FILLED_REQ, Math.max(0, (this.toneCounts[theme as FlavorTagTheme] || 0) + 1));
    }


    this.emit('dmScreenUpdated', this.toneCounts);
  }



  spendDMPoints(points: number) {
    this.dmPoints += points;
    this.emit('dmScreenUpdated', this.dmPoints);
  }

  addDMPoints(points: number) {
    this.dmPoints += points;
    this.emit('dmScreenUpdated', this.dmPoints);
    console.log(`Awarded ${points} DM point(s). Total: ${this.dmPoints}`);
  }

  getDMPoints() {
    return this.dmPoints;
  }

  // DM Abilities System Methods
  getDMAbilityPoints() {
    return this.dmAbilityPoints;
  }

  addDMAbilityPoints(points: number) {
    this.dmAbilityPoints += points;
    this.emit('dmAbilitiesUpdated', this.dmAbilityPoints);
    console.log(`Awarded ${points} DM ability point(s). Total: ${this.dmAbilityPoints}`);
  }

  spendDMAbilityPoints(points: number) {
    if (this.dmAbilityPoints >= points) {
      this.dmAbilityPoints -= points;
      this.emit('dmAbilitiesUpdated', this.dmAbilityPoints);
      return true;
    }
    return false;
  }

  // Simplified snacks management
  getMaxSnacks(): number {
    return this.maxSnacks;
  }

  getRemainingSnacks(): number {
    return this.remainingSnacks;
  }

  setMaxSnacks(value: number): void {
    this.maxSnacks = value;
    // Ensure remaining doesn't exceed max
    this.remainingSnacks = Math.min(this.remainingSnacks, this.maxSnacks);
    this.emit('dmAbilitiesUpdated', this.dmAbilityPoints);
  }

  // Simplified breaks management
  getMaxBreaks(): number {
    return this.maxBreaks;
  }

  getRemainingBreaks(): number {
    return this.remainingBreaks;
  }

  setMaxBreaks(value: number): void {
    this.maxBreaks = value;
    // Ensure remaining doesn't exceed max
    this.remainingBreaks = Math.min(this.remainingBreaks, this.maxBreaks);
    this.emit('dmAbilitiesUpdated', this.dmAbilityPoints);
  }

  useSnacks() {
    if (this.remainingSnacks > 0) {
      this.remainingSnacks -= 1;
      this.showSnacksModal();
      this.emit('dmAbilitiesUpdated', this.dmAbilityPoints);
    }
  }

  useBreak() {
    if (this.remainingBreaks > 0) {
      this.remainingBreaks -= 1;
      this.showBreakModal();
      this.emit('dmAbilitiesUpdated', this.dmAbilityPoints);
    }
  }

