  isInitialEncounter() {
    return this.currentEncounter?.name === this.currentCampaign?.startingEncounter?.name;
  }

  getCompletedEvents() {
    return this.completedEventIndexes;
  }

  getEncounterAdvanceReadinessText() {
    const currentEncounterEstablished = (this.getExpectedEncounterTags().length === 0);
    const branches = this.currentEncounter?.branches || [];
    const someBranchesReady = branches.some(branch => this.tagManager.encounterTagsExpected[branch.name].length >= NB_EXPECTED_ENCOUNTER_TAGS - 1);
    const allBranchesReady = branches.every(branch => this.tagManager.encounterTagsExpected[branch.name].length >= NB_EXPECTED_ENCOUNTER_TAGS - 1);
    if (!currentEncounterEstablished) {
      return 'Players will be confused. They expect more from the current encounter.';
    }
    if (!someBranchesReady) {
      return 'Players don\'t have enough information to choose what to do next.';
    }
    if (!allBranchesReady) {
      return 'Players are ready to move on, but feel some choices are unclear.';
    }
    return 'Players are ready to move on.';

  }

  encounterEventSelected(eventIndex: number) {
    if (this.encounterState === undefined) {
      return;
    }
    if (this.completedEventIndexes.includes(eventIndex)) {
      return;
    }
    this.appManager.state = APP_STATE.SESSION
    this.completedEventIndexes.push(eventIndex);
    this.encounterState.eventIndex = eventIndex;
    
    // Trigger encounter event chat tile
    const currentEvent = this.getCurrentEvent();
    if (currentEvent) {
      this.appManager.getChatEventManager().addEncounterEvent(currentEvent.type, currentEvent.name);
    }
    
    this.appManager.getUiManager().updateEncounterState(this.encounterState);
  }

