  getTensionMultiplier(tensionValue?: number): number {
    const tension = tensionValue ?? this.partyManager().getPartyTension();
    const tensionDescriptions = this.getTensionDescriptions();
    for (const tensionDesc of tensionDescriptions) {
      if (tension <= tensionDesc.max) {
        return tensionDesc.scoreMultiplier;
      }
    }
    return 0;
  }

