import React from 'react';
import { FlavorTagSource, FlavorTagTheme, ModalConfig } from '../../models';
import CImage from './cimage';
import './SetSceneModal.scss';
import TagPill from './TagPill';

interface SetSceneModalProps {
  modalConfig: ModalConfig;
  onClose: () => void;
  onOptionSelect: (optionId: string) => void;
}

const SetSceneModal: React.FC<SetSceneModalProps> = ({ modalConfig, onClose, onOptionSelect }) => {
  if (!modalConfig.sceneData) return null;

  const { location, encounter } = modalConfig.sceneData;

  return (
    <div className="modal-overlay">
      <div className="modal-container setscene">
        <div className="modal-header">
          <h2>{modalConfig.title}</h2>
          <button className="close-button" onClick={onClose}></button>
        </div>
        
        <div className="setscene-content">
          <div className="setscene-image">
            <CImage imageId={location.icon} />
          </div>
          
          <div className="setscene-info">
            <h3 className="location-name">{location.name}</h3>
            <p className="location-description">{location.description}</p>
            
            <div className="encounter-info">
              <h4>Set the Scene</h4>
              <p>Now that the Location is fully described, your players can imagine the details more clearly.</p>
              <p>You will gain a multiplier during encounter scoring for the rest of this Encounter.</p>
            </div>
            
            <div className="location-tags">
              
              <div className="tags-grid">
                {location.tags.map((tag, index) => (
                  <TagPill 
                    key={index}
                    tag={tag}
                    disabled={true}
                    highlighted={false}
                    showTooltip={false}
                    theme={FlavorTagTheme.NONE}
                    source={FlavorTagSource.LOCATION}
                    className="setscene-tag"/>
                ))}
              </div>
            </div>
          </div>
        </div>
        
        <div className="setscene-actions">
          {modalConfig.options.map((option) => (
            <button
              key={option.id}
              className="setscene-continue-button"
              onClick={() => onOptionSelect(option.id)}
            >
              {option.title}
            </button>
          ))}
        </div>
      </div>
    </div>
  );
};

export default SetSceneModal;
