import React from 'react';
import { FlavorTagTheme } from '../../models';
import TooltipWrapper from '../UI/TooltipWrapper';
import { ConditionalTooltip } from '../UI/tooltip';

interface ToneTrackerProps {
  themes: Record<string, number>;
  maxSegments: number;
  onRedeem: (theme: string) => void;
}

const ToneTracker: React.FC<ToneTrackerProps> = ({ themes, maxSegments, onRedeem }) => {


  const getTooltipForTone = (theme: string, count: number) => {

    if(count < maxSegments) {
      
    switch (theme) {
     case FlavorTagTheme.COMEDIC:
      return "When filled: All players gain +1 attitude.";
     case FlavorTagTheme.DRAMATIC:
      return "When filled: All players gain full attention.";
     case FlavorTagTheme.HEROIC: 
   
      return "When filled: Set Tension to 50"
     case FlavorTagTheme.GRIM  : 
     return "When filled: High-tension rewards increased";
     case FlavorTagTheme.ROMANTIC : 
     return "When filled: Low-tension rewards increased";
     
     case FlavorTagTheme.NONE: 
     default:
      return `defaultTooltip`;
    }
    }


    switch (theme) {
     case FlavorTagTheme.COMEDIC:
      return "Click: All players gain +1 attitude.";
     case FlavorTagTheme.DRAMATIC:
      return "Click: All players gain full attention.";
     case FlavorTagTheme.FANTASY: 
     case FlavorTagTheme.HEROIC: 
      return "Click: Set Tension to 50"
     case FlavorTagTheme.GRIM  : 
     return "Click: Adjust high-tension rewards";
     case FlavorTagTheme.ROMANTIC : 
     return "Click: Adjust low-tension rewards";
     case FlavorTagTheme.HORROR : 
     case FlavorTagTheme.NONE: 
     default:
      return `defaultTooltip`;
    }


  }





  return (
    <div className="tone-tracker">
      <div className="tone-header">
        <TooltipWrapper
          content="Once a tone is established, it will generate party points. Tones can also be redeemed for unique effects once established."
          position="top"
          
          >
        <span className="tone-title">Session Tones</span>
       </TooltipWrapper>
      </div>
      <div className="tone-grid">
        {Object.entries(themes).map(([theme, count]) => (
          <TooltipWrapper

            key={theme}
            content={getTooltipForTone(theme, count)}
            translate={[-100, -10]}
         
          >
          <div 
            key={theme} 
            className={`tone-item theme-${theme} muted ${count >= maxSegments ? 'active' : ''}`} 
            onClick={count >= maxSegments ? () => onRedeem(theme) : undefined}
          >
            <div className="tone-item-header">
              <span className="tone-name">{theme}</span>
              {count < maxSegments && (
                <span className="tone-count">{count}/{maxSegments}</span>
                )}
              
            </div>
            <div className="tone-progress-container">
              <div 
                className={`tone-progress-bar theme-${theme}`}
                style={{ width: `${(count / maxSegments) * 100}%` }}
              />
            </div>
          </div>
          </TooltipWrapper>
        ))}
      </div>
    </div>
  );
};

export default ToneTracker;