import React, { useEffect, useRef, useState } from 'react';
import './chat.scss';
import CImage from './cimage';
import { CHAT_TYPE, ChatMessage, ChatEvent } from '../../models';
import ChatMessageDisplay from './chatmessagedisplay';
import ChatEventTile from './chatEventTile';
import { useAppManager } from '../managerContexts';

interface ChatProps {
 // expanded: boolean; 
}

const Chat: React.FC<ChatProps> = () => {
  const containerRef = useRef<HTMLDivElement>(null);
  const animationIntervalRef = useRef<NodeJS.Timeout | null>(null);
  const appManager = useAppManager();
  const chatManager = appManager.getChatManager();
  const chatEventManager = appManager.getChatEventManager();
  
  // Initialize with existing message history from ChatManager
  const [messages, setMessages] = useState<ChatMessage[]>(() => chatManager.getRecentMessages());
  const [chatEvents, setChatEvents] = useState<ChatEvent[]>(() => chatEventManager.getEventHistory());
  const [currentMessage, setCurrentMessage] = useState<ChatMessage | null>(null);
  const [displayedText, setDisplayedText] = useState('');
  const [isAnimating, setIsAnimating] = useState(false);
  const [hasMoreMessages, setHasMoreMessages] = useState(false);
  const [highlightedMessages, setHighlightedMessages] = useState<Record<string, { highlighted: boolean; pulsing: boolean }>>({});
  const [isUserScrolling, setIsUserScrolling] = useState(false);
  const scrollTimeoutRef = useRef<NodeJS.Timeout | null>(null);

  // Handle new incoming messages
  useEffect(() => {
    const handleNewMessage = (message: ChatMessage & { 
      hasMoreMessages?: boolean;
      messageId?: string;
      conversationIds?: string[];
      isLastInConversation?: boolean;
    }) => {
      // Sync with ChatManager's recent messages to maintain persistence
      setMessages(chatManager.getRecentMessages());
      setCurrentMessage(message);
      setHasMoreMessages(!!message.hasMoreMessages);
      
      // Handle highlighting logic - only highlight the latest message
      const currentMessages = chatManager.getRecentMessages();
      const messageIndex = currentMessages.length - 1; // Latest message index
      
      // Always highlight only the latest message with pulsing glow
      const msgId = `message-${messageIndex}`;
      setHighlightedMessages(prev => ({ ...prev, [msgId]: { highlighted: true, pulsing: true } }));
      
      // Clear highlight after 3 seconds
      setTimeout(() => {
        setHighlightedMessages(prev => {
          const newState = { ...prev };
          delete newState[msgId];
          return newState;
        });
      }, 3000);
      
      animateMessage(message.message);
    };

    chatManager.on('chat-message', handleNewMessage);
    return () => {
      chatManager.off('chat-message', handleNewMessage);
    };
  }, [chatManager]);

  // Handle new chat events
  useEffect(() => {
    const handleNewEvent = (event: ChatEvent) => {
      setChatEvents(prev => [...prev, event]);
    };

    chatEventManager.on('chat-event', handleNewEvent);
    return () => {
      chatEventManager.off('chat-event', handleNewEvent);
    };
  }, [chatEventManager]);

  // Detect user scrolling to prevent auto-scroll interference
  useEffect(() => {
    const container = containerRef.current;
    if (!container) return;

    const handleScroll = () => {
      const isAtBottom = container.scrollHeight - container.scrollTop - container.clientHeight < 10;
      
      if (!isAtBottom) {
        setIsUserScrolling(true);
        
        // Clear existing timeout
        if (scrollTimeoutRef.current) {
          clearTimeout(scrollTimeoutRef.current);
        }
        
        // Reset user scrolling state after 2 seconds of no scrolling
        scrollTimeoutRef.current = setTimeout(() => {
          setIsUserScrolling(false);
        }, 2000);
      } else {
        setIsUserScrolling(false);
      }
    };

    container.addEventListener('scroll', handleScroll);
    
    return () => {
      container.removeEventListener('scroll', handleScroll);
      if (scrollTimeoutRef.current) {
        clearTimeout(scrollTimeoutRef.current);
      }
    };
  }, []);

  // Sync with ChatManager history on mount and periodically
  useEffect(() => {
    const syncMessages = () => {
      const managerMessages = chatManager.getRecentMessages();
      setMessages(managerMessages);
    };
    
    // Initial sync
    syncMessages();
    
    // Optional: Periodic sync to catch any missed updates
    const syncInterval = setInterval(syncMessages, 1000);
    
    return () => {
      clearInterval(syncInterval);
    };
  }, [chatManager]);
  
  // Smart auto-scroll - only scroll to bottom if user is already at or near bottom
  useEffect(() => {
    if (containerRef.current && !isUserScrolling) {
      const container = containerRef.current;
      const isNearBottom = container.scrollHeight - container.scrollTop - container.clientHeight < 50;
      
      if (isNearBottom) {
        // Use setTimeout to ensure DOM has updated before scrolling
        setTimeout(() => {
          if (containerRef.current) {
            containerRef.current.scrollTop = containerRef.current.scrollHeight;
          }
        }, 10);
      }
    }
  }, [messages, chatEvents, isUserScrolling]);

  // Message animation logic
  const animateMessage = (text: string) => {
    setIsAnimating(true);
    setDisplayedText('');
    
    // Clear any existing animation
    if (animationIntervalRef.current) {
      clearInterval(animationIntervalRef.current);
    }
    
    // Split text into words while preserving spaces
    const words = text.split(/(?<=\s)|(?=\s)/);
    let wordIndex = 0;
    let visibleWords = words.map(word => ({ word, visible: false }));
    
    animationIntervalRef.current = setInterval(() => {
      if (!visibleWords[wordIndex].word.match(/^\s+$/)) {
        visibleWords[wordIndex].visible = true;
        setDisplayedText(
          visibleWords
            .map(({ word, visible }) => 
              word.match(/^\s+$/) ? word :
              `<span class="chat-word ${visible ? 'visible' : ''}">${word}</span>`
            )
            .join('')
        );
      }
      wordIndex++;
      
      if (wordIndex >= words.length) {
        if (animationIntervalRef.current) {
          clearInterval(animationIntervalRef.current);
          animationIntervalRef.current = null;
        }
        setIsAnimating(false);
        
        // Scroll to bottom when animation completes
        setTimeout(() => {
          if (containerRef.current) {
            containerRef.current.scrollTop = containerRef.current.scrollHeight;
          }
        }, 50);
      }
    }, 10); // Slightly faster interval to account for animation duration
  };

  // Handle message box click
  const handleMessageClick = () => {
    if (isAnimating) {
      // Complete current message animation immediately
      if (animationIntervalRef.current) {
        clearInterval(animationIntervalRef.current);
        animationIntervalRef.current = null;
      }
      setIsAnimating(false);
      setDisplayedText(currentMessage?.message || '');
    } else if (hasMoreMessages) {
      // Advance to next message
     chatManager.advanceQueue();
    }
  };






const renderMessage = (chatMessage: ChatMessage, index: number, isCurrentMessage: boolean = false, isLastMessage: boolean = false) => {
  // Show animated text for current message, full text for completed messages
  const messageToDisplay = isCurrentMessage && isAnimating ? {
    ...chatMessage,
    message: displayedText
  } : chatMessage;
  
  // Generate consistent message ID for highlighting
  const messageId = `message-${index}`;
  const highlightState = highlightedMessages[messageId];
  
  // Build CSS classes for highlighting
  const cssClasses = [
    'chat-new-message',
    isCurrentMessage && isAnimating ? 'animating' : '',
    highlightState?.highlighted ? 'highlighted' : '',
    highlightState?.pulsing ? 'pulsing' : ''
  ].filter(Boolean).join(' ');
  
  // Determine if we should show the ellipsis (only on the most recent message if more are coming)
  const showEllipsis = isLastMessage && hasMoreMessages;
  
  // Determine ellipsis position based on message type
  const isPlayerOrCharacter = chatMessage.speaker.type === CHAT_TYPE.PLAYER || chatMessage.speaker.type === CHAT_TYPE.CHARACTER;
  const ellipsisClass = isPlayerOrCharacter ? 'chat-queue-indicator-right' : 'chat-queue-indicator';
  
  return (
    <div 
      key={messageId}
      className={cssClasses}
      onClick={isCurrentMessage ? handleMessageClick : undefined}
      style={{ cursor: isCurrentMessage && (isAnimating || hasMoreMessages) ? 'pointer' : 'default' }}
    >
      <ChatMessageDisplay 
        chatMessage={messageToDisplay}
        newMessage={isCurrentMessage}
      />
      {showEllipsis && (
        <div className={`${ellipsisClass} ${showEllipsis ? 'visible' : ''}`}>
          ...
        </div>
      )}
    </div>
  );
};





  // Combine messages and events in chronological order
  const renderChatContent = () => {
    const allItems: Array<{ type: 'message' | 'event'; data: ChatMessage | ChatEvent; timestamp: number; originalIndex?: number }> = [
      ...messages.map((msg, index) => ({ 
        type: 'message' as const, 
        data: msg, 
        timestamp: msg.timestamp || Date.now() - (messages.length - index) * 1000, // Fallback timestamps for older messages
        originalIndex: index
      })),
      ...chatEvents.map(event => ({ 
        type: 'event' as const, 
        data: event, 
        timestamp: event.timestamp 
      }))
    ];
    
    // Sort by timestamp - true chronological order
    allItems.sort((a, b) => a.timestamp - b.timestamp);
    
    return allItems.map((item, index) => {
      if (item.type === 'event') {
        return (
          <ChatEventTile 
            key={`event-${item.timestamp}`}
            event={item.data as ChatEvent}
          />
        );
      } else {
        const message = item.data as ChatMessage;
        const messageIndex = item.originalIndex!;
        const isCurrentMessage = !!currentMessage && message === currentMessage;
        // Check if this is the last message in the entire flow (not just messages)
        const isLastInFlow = index === allItems.length - 1 && item.type === 'message';
        return renderMessage(message, messageIndex, isCurrentMessage, isLastInFlow);
      }
    });
  };

  return (
    <div className="chat-exterior">
      <div className="chat-messages-container" ref={containerRef}>
        {renderChatContent()}
      </div>
    </div>
  );
}

export default Chat;

