import React, { useEffect, useState } from 'react';
import './notebook.scss'; 

import { useCharacterManager, usePlayerManager } from '../managerContexts';
import { GroupNotebookEntry, Player } from '../../models';
import Schedule from '../schedule';


interface NoteBookGroupProps {
    groupInfo:GroupNotebookEntry;
}

const NotebookGroup: React.FC<NoteBookGroupProps> = ({ groupInfo }) => {

    const [playerGroup, setPlayerGroup] = useState<Player[]>(groupInfo.players);
    const playerManager = usePlayerManager();
    const [groupAttitudes, setGroupAttitudes] = useState(playerManager.getGroupAttitudes());
    const [dayData, setDayData] = useState(playerManager.aggregateSchedule());


    useEffect(() => {
        const updateGroupAttitudes = () => {
            setGroupAttitudes(playerManager.getGroupAttitudes());
          };
        updateGroupAttitudes();
        setPlayerGroup(groupInfo.players);
        setDayData(playerManager.aggregateSchedule());
    }, [groupInfo, playerManager]);

    return (
    <div className="notebook-group-container">
       <div className = "notebook-group-top-content">
            <div className="notebook-group-players notebook-text large">
                <u>Players</u>
                <div className = "notebook-group-players-list">
                    {playerGroup.length !== 0 &&  playerGroup.map((player, idx) => (
                                <div key={idx} className="notebook-text player-name">
                                {player.name}
                                </div>
                            ))}
                </div>
            </div>
        </div>

        <div className="player-attitude">
                <div className="attitude-column">
                  <div className="attitude-title notebook-text large"><u>Group Likes</u></div>
                  {groupAttitudes.likes.map((like, index) => (
                    <div key={index} className="attitude-item like">
                        <div className="attitude-item-text">
                      {like}
                      </div>
                    </div>
                  ))}
                </div>
                {groupAttitudes.conflicted.length > 0 && (
              <div className="attitude-column">
                  <div className="attitude-title notebook-text large"><u>Conflicted</u></div>
                  {groupAttitudes.conflicted.map((conflicted, index) => (
                    <div key={index} className="attitude-item conflicted">
                       <div className="attitude-item-text">
                      {conflicted}
                      </div>
                    </div>
                  ))}
                </div>
              )}
                <div className="attitude-column">
                  <div className="attitude-title notebook-text large"><u>Group Hates</u></div>
                  {groupAttitudes.hates.map((hate, index) => (
                    <div key={index} className="attitude-item hate">
                         <div className="attitude-item-text">
                      {hate}
                      </div>
                    </div>
                  ))}
                </div>
               
              </div>
             

           
           
    </div>
    );
};

export default NotebookGroup;




