import React from "react";
import { useCampaignManager } from "../managerContexts";
import CImage from "./cimage";
import DiceScore from "./DiceScore";

interface CampaignHeaderProps {
  encounterScore: number;
  campaignScore: number;
  onClick?: () => void;
  className?: string;
}

const CampaignHeader: React.FC<CampaignHeaderProps> = ({
  encounterScore,
  campaignScore,
  onClick,
  className = ""
}) => {
  const campaignManager = useCampaignManager();

  return (
    <div className={`campaign-header ${className}`} >
      <div className="campaign-header-dm-points">
        <div className="campaign-header-dm-points-label">POTENTIAL SCORE</div>
        <div className="campaign-header-dm-point">
          <DiceScore
            key={encounterScore} // Add a key prop that changes with the score
            score={encounterScore}
          />
        </div>
      </div>
      <div className="campaign-header-center-container" onClick={onClick}>
        <div
          className={`campaign-display ${
            campaignManager.getCurrentCampaign().theme
          } icon-only`}
        >
          <div className={"campaign-info left"}>
            <div className="campaign-info title">
              {campaignManager.getCurrentCampaign().name}
            </div>
            <div className="campaign-info hooks">PLOT HOOKS</div>
            <div className="circle-display">
              {Array.from({ length: 4 }, (_, index) => (
                <div
                  key={index}
                  className={`circle ${
                    index < campaignManager.getCampaignProgress()
                      ? "filled"
                      : ""
                  }`}
                />
              ))}
            </div>
          </div>

          <div className="campaign-icon solo">
            <CImage imageId={campaignManager.getCurrentCampaign().icon} />
          </div>
        </div>
      </div>
      <div className="campaign-header-dm-points">
        <div className="campaign-header-dm-points-label">CAMPAIGN TOTAL</div>
        <div className="campaign-header-dm-point">
          <DiceScore
            key={campaignScore} // Add a key prop that changes with the score
            score={campaignScore}
          />
        </div>
      </div>
    </div>
  );
};

export default CampaignHeader;
