import React, { useState, useEffect, useRef } from "react";
import {
	ModalConfig,
	ChatMessage,
	CHAT_TYPE,
	FlavorTagSource,
	FlavorTagTheme,
	Tag,
	ScoreEntry,
} from "../../models";
import "./EncounterTagModal.scss";
import DiceScore from "./DiceScore";
import CImage from "./cimage";
import FlavorTagPill from "./FlavorTagPill";
import ConversationSection from "./ConversationSection";
import { useAppManager } from "../managerContexts";
import { calculateTotalScore } from "../../utils/scoreUtils";
import RewardFillBar from "./RewardFillBar";

interface EncounterTagModalProps {
	modalConfig: ModalConfig;
	onClose: () => void;
	onOptionSelect: (optionId: string) => void;
}

const EncounterTagModal: React.FC<EncounterTagModalProps> = ({
	modalConfig,
	onClose,
	onOptionSelect,
}) => {
	const uiManager = useAppManager().getUiManager();
	const [conversationComplete, setConversationComplete] = useState(false);
	const [scoringComplete, setScoringComplete] = useState(false);
	const [speedUp, setSpeedUp] = useState(false);
	const [claimedRewards, setClaimedRewards] = useState<number[]>([]);
	const [claimedDMPoints, setClaimedDMPoints] = useState<number[]>([]);
	const appManager = useAppManager();
	const tagManager = appManager.getTagManager();
	const campaignManager = appManager.getCampaignManager();

	// Extract tag data safely with null checks
	const tag = modalConfig.tagData?.tag;
	const chatTrigger = modalConfig.tagData?.chatTrigger;
	const encounter = modalConfig.tagData?.encounter;

	// Memoize messages to prevent recalculation
	const messages = React.useMemo((): ChatMessage[] => {
		if (!chatTrigger) return [];

		if (Array.isArray(chatTrigger)) {
			return chatTrigger;
		}

		if (chatTrigger.conversation) {
			// Handle conversation reference
			return [];
		}

		// just a string.
		if (chatTrigger.length > 0) {
			return [
				{
					message: chatTrigger,
					speaker: { type: CHAT_TYPE.DM, id: 0 },
				},
			];
		}

		if (chatTrigger.message) {
			return [
				{
					message: chatTrigger.message,
					speaker: chatTrigger.speaker || { type: CHAT_TYPE.DM, id: 0 },
				},
			];
		}

		return [];
	}, [chatTrigger]);

	// Memoize scoring data to prevent regeneration with random values
	const scoringData = React.useMemo((): ScoreEntry[] => {
		if (!tag) return [];

		return (
			campaignManager.getEncounterScoring(tag) || [
				{ description: "Encounter", points: 25, isPositive: true },
				{ description: "Event ", points: 15, isPositive: true },
				{ description: "Narrative Cohesion", points: 10, isPositive: true },
				{ description: "Time Efficiency", points: -5, isPositive: false },
				{ description: "Creative Solution", points: 20, isPositive: true },
			]
		);
	}, [campaignManager, tag]);
	const totalScore = React.useMemo(
		() => calculateTotalScore(scoringData),
		[scoringData]
	);
	
	// Get current campaign score
	const currentCampaignScore = campaignManager.getCampaignScore();
	const newTotalCampaignScore = currentCampaignScore + totalScore;

	// Early return after hooks
	if (!modalConfig.tagData || !tag) return null;

	const handleClick = () => {
		if (!conversationComplete || !scoringComplete) {
			setSpeedUp(true);
		}
	};

	const handleModalClick = (e: React.MouseEvent) => {
		e.stopPropagation();
		handleClick();
	};

	const encounterTagSatisfied = (tag: Tag) =>
		!tagManager.getOutstandingEncounterTags().includes(tag);
	const nbEncounterTagsSatisfied = (encounter?.tags || []).filter((encTag) =>
		encounterTagSatisfied(encTag)
	).length;

	const canProceed = conversationComplete && scoringComplete;

	const handleClickContinue = () => {
		if (canProceed) {
			campaignManager.bankCampaignScore(calculateTotalScore(scoringData));
			campaignManager.clearEncounterScore();
			onOptionSelect(modalConfig.options[0]?.id || "continue");
			onClose();
		}
	}
	

	return (
		<div className="modal-overlay blue-theme" onClick={handleClick}>
			<div className="modal-container encountertag" onClick={handleModalClick}>
				{/* Header strip with encounter info */}
				<div className="encountertag-header">
					<div className="header-background">
						{encounter?.icon && <CImage imageId={encounter.icon} />}
					</div>
					<div className="header-overlay"></div>
					<div className="header-content">
						<div className="header-left">
							<div className="encounter-info">
								<h2 className="encounter-title">
									{encounter?.name || "Unknown Encounter"}
								</h2>
								<div className="tag-discovery">
									<span className="tag-name">{tag.name}</span>
									{tag.type && <span className="tag-type">{tag.type}</span>}
								</div>
							</div>
						</div>
						<div className="header-right">
							<div className="encounter-tags">
								{encounter?.tags?.map((encTag, index) => (
									<FlavorTagPill
										key={index}
										tag={{
											tag: {
												...encTag,
											},
											source: encounterTagSatisfied(encTag)
												? FlavorTagSource.ENCOUNTER
												: FlavorTagSource.ENCOUNTER_EXPECTED,
											theme: FlavorTagTheme.NONE,
										}}
										off={true}
										showTooltip={false}
										className="encounter-tag"
									/>
								))}
							</div>
						</div>
					</div>
				</div>

				{/* Side-by-side content */}
				<div className="encountertag-body">
					{/* Left side - Conversation */}
					<ConversationSection
						messages={messages}
						title={`Encounter Topic ${nbEncounterTagsSatisfied} : ${tag.name}`}
						onComplete={() => setConversationComplete(true)}
						speedUp={speedUp}
					/>

					{/* Right side - Scoring Ledger */}
					<div className="scoring-section">
						<div className="section-header">
							<h3>Encounter Scoring</h3>
						</div>
						<div className="scoring-content">
							<ScoringLedger
								entries={scoringData}
								totalScore={totalScore}
								currentCampaignScore={currentCampaignScore}
								newTotalCampaignScore={newTotalCampaignScore}
								onComplete={() => setScoringComplete(true)}
								speedUp={speedUp}
							/>
						</div>
					</div>
					
				</div>

				{/* Footer with Campaign Reward and Continue button */}
				<div className="encountertag-footer">
					{/* Full-width Campaign Reward FillBar */}
					<div className="campaign-reward-section">
						<RewardFillBar 
							initialCampaignScore={currentCampaignScore}
							newScore={totalScore}
							onRewardClaim={(rewardIndex) => {
								setClaimedRewards(prev => [...prev, rewardIndex]);
								uiManager.showSelectQuestModal();
							}}
							onDMPointClaim={(dmPointIndex) => {
								// Award 1 DM ability point (for spending in skill screen)
								campaignManager.addDMAbilityPoints(1);
								// Mark this DM point as claimed
								setClaimedDMPoints(prev => [...prev, dmPointIndex]);
								console.log(`Claimed DM point ${dmPointIndex + 1}!`);
							}}
							claimedRewards={claimedRewards}
							claimedDMPoints={claimedDMPoints}
							showScore={scoringComplete}
							onThresholdCrossed={(threshold) => {
								console.log(`Crossed reward threshold: ${threshold}`);
							}}
						/>
					</div>
					
					{/* Continue Button */}
					<button
						className={`encountertag-continue-button ${
							!canProceed ? "disabled" : ""
						}`}
						onClick={() =>
							canProceed && handleClickContinue()
						}
						disabled={!canProceed}
					>
						{modalConfig.options[0]?.title || "CONTINUE"}
					</button>
				</div>
			</div>
		</div>
	);
};

// Scoring Ledger Component
interface ScoringLedgerProps {
	entries: ScoreEntry[];
	totalScore: number;
	currentCampaignScore: number;
	newTotalCampaignScore: number;
	onComplete: () => void;
	speedUp: boolean;
}

const ScoringLedger: React.FC<ScoringLedgerProps> = ({
	entries,
	totalScore,
	currentCampaignScore,
	newTotalCampaignScore,
	onComplete,
	speedUp,
}) => {
	const [visibleEntries, setVisibleEntries] = useState(0);
	const [showTotal, setShowTotal] = useState(false);
	const intervalRef = useRef<NodeJS.Timeout | null>(null);
	useEffect(() => {
		if (speedUp) {
			// Speed up animation - complete everything immediately
			setVisibleEntries(entries.length);
			setShowTotal(true);
			setTimeout(() => onComplete(), 100);
			return;
		}

		const delay = 800;

		if (visibleEntries < entries.length) {
			intervalRef.current = setTimeout(() => {
				setVisibleEntries((prev) => prev + 1);
			}, delay);
		} else if (!showTotal) {
			intervalRef.current = setTimeout(() => {
				setShowTotal(true);
				setTimeout(() => onComplete(), 500);
			}, delay);
		}

		return () => {
			if (intervalRef.current) {
				clearTimeout(intervalRef.current);
			}
		};
	}, [visibleEntries, showTotal, entries.length, onComplete, speedUp]);

	return (
		<div className="scoring-ledger">
			{" "}
			<div className="score-entries">
				{entries.slice(0, visibleEntries).map((entry, index) => (
					<div 
						key={index} 
						className={`score-entry animate-in ${entry.multiplier ? (entry.points < 1 ? "multiplier-low" : "multiplier-high") : ""}`}
						style={{
							borderColor:  entry.color ? entry.color : undefined,
							boxShadow: entry.color ? `0 0 10px ${entry.color}` : undefined
						}}
					>
						<span className="score-description">{entry.description}</span>
						{entry.multiplier ? (
							<span className={`score-points multiplier-${entry.points < 1 ? 'low' : 'high'}`}>x{entry.points}</span>
						) : (
						<span className="score-points">
							<DiceScore
								score={Math.abs(entry.points)}
								direction="row"
								className={`score-dice ${
									entry.points < 0 ? "negative" : "positive"
								}`}
							/>
						</span>
						)}
					</div>
				))}
			</div>
			{showTotal && (
				<div className="score-total animate-in">
					<div className="total-separator"></div>
					<div className="total-entry glow">
						<span className="total-label">Added Score</span>
						<span
							className={`total-points ${
								totalScore >= 0 ? "positive" : "negative"
							}`}
						>
							<DiceScore
								score={Math.abs(totalScore)}
								direction="row"
								className={`total-dice ${
									totalScore < 0 ? "negative" : "positive"
								}`}
							/>
						</span>
					</div>
					
					{/* Campaign Score Display */}
					<div className="total-entry campaign-score-entry">
						<span className="total-label">Total Score</span>
						<span className="campaign-score-breakdown">
							{/* <span className="current-score">
								<DiceScore score={currentCampaignScore} direction="row" />
							</span>
							<span className="score-separator">+</span>
							<span className="new-score">
								<DiceScore score={totalScore} direction="row" />
							</span>
							<span className="score-separator">=</span> */}
							<span className="final-score">
								<DiceScore score={newTotalCampaignScore} direction="row" />
							</span>
						</span>
					</div>
				</div>
			)}
		</div>
	);
};

export default EncounterTagModal;
