import React from "react";
import { Quest } from "../../models";
import "./SelectQuestModal.scss";
import Cbox from "./cbox";
import CImage from "./cimage";
import RewardDisplay from "./RewardDisplay";
interface SelectQuestModalProps {
	quests: Quest[];
	activeQuestIds: string[];
	onQuestSelect: (questId: string) => void;
	onClose: () => void;
}

const SelectQuestModal: React.FC<SelectQuestModalProps> = ({
	quests,
	activeQuestIds,
	onQuestSelect,
	onClose,
}) => {
	return (
		<div className="select-quest-modal-overlay">
			<div className="quest-modal-container">
				<Cbox title="Quest Opportunity" theme="quests">
					<div className="modal-header">
						<div className="quest-subheader">
							Choose a side-story to introduce to the players.
            
						</div><div className="quest-subheader">  Quest-related topics will grab the players attention.
					</div>	{/* <button className="close-button" onClick={onClose}></button> */}
					</div>

					<div className="quest-tiles">
						{quests.map((quest) => (
							<Cbox
								title={quest.name}
								theme={"quest"}
								onClick={() => onQuestSelect(quest.id)}
							>
								<div
									className="quest-tile-content"
									data-quest-icon={quest.icon || "default"}
								>
									<div className="quest-icon-background">
										{quest.icon && <CImage imageId={quest.icon} />}
									</div>

									<div className="quest-content-overlay">
										<div className="quest-info">
											<div className="quest-cost">
												{quest.cost.map((cost, index) => (
													<span key={index} className="quest-cost-item">
														 +{cost.amount} {cost.type}
													</span>
												))}
											</div>
											
											<div className="quest-description">
												{quest.description}
											</div><div className="quest-rewards">
                        <div className="rewards-title">Rewards:</div>
                        <div className="quest-reward-row">
                          
												{quest.rewards.map((reward, index) => (
													<span key={index} className="quest-reward">
														{/* {reward.description}:{reward.amount} */}
                             <div className="quest-reward-treasure">
                              <RewardDisplay reward={reward} />
                             </div>
                            
													</span>
												))}
                        </div>
											</div>
										</div>

										{activeQuestIds.includes(quest.id) && (
											<div className="active-indicator">ACTIVE</div>
										)}
									</div>
								</div>
							</Cbox>
						))}
					</div>
				</Cbox>
			</div>
		</div>
	);
};

export default SelectQuestModal;
