import { LEVELUP_OPTION } from "../../consts/general";
import { useModal } from "../../hooks/useModal";
import CImage from "./cimage";
import EffectDisplay from "./effectDisplay";
import SetSceneModal from "./SetSceneModal";
import EncounterTagModal from "./EncounterTagModal";
import NextEncounterModal from "./NextEncounterModal";
import WelcomeModal from "./WelcomeModal";
import TagModal from "./TagModal";
import './Modal.scss';
 

export const Modal: React.FC = () => {
    const { modalConfig, handleOptionSelect, closeModal } = useModal();
  
    if (!modalConfig) return null;
  
    // Handle our new modal types
    if (modalConfig.type === 'setscene') {
      return (
        <SetSceneModal
          modalConfig={modalConfig}
          onClose={closeModal}
          onOptionSelect={handleOptionSelect}
        />
      );
    }

    if (modalConfig.type === 'encountertag') {
      return (
        <EncounterTagModal
          modalConfig={modalConfig}
          onClose={closeModal}
          onOptionSelect={handleOptionSelect}
        />
      );
    }

    if (modalConfig.type === 'nextencounter') {
      return (
        <NextEncounterModal
          modalConfig={modalConfig}
          onClose={closeModal}
          onOptionSelect={handleOptionSelect}
        />
      );
    }

    if (modalConfig.type === 'tagmodal' && modalConfig.tagModalConfig) {
      return (
        <TagModal
          modalConfig={modalConfig}
          onClose={closeModal}
        />
      );
    }

// Add new modal types in here. 
// we should probably refactor/unify this! 


    if(modalConfig.type === 'levelup') {
      return (
        <div className="modal-overlay">
          <div className="modal-container">
            <div className="modal-header">
              <h2>{modalConfig.title}</h2>
              <button className="close-button" onClick={closeModal}></button>
            </div>
            <div className="modal-options">
              {modalConfig.options.map((option) => (
                <button
                  key={option.id}
                  className={`modal-option ${option.class || ''}`}
                  onClick={() => handleOptionSelect(option.id)}
                >
                  <div className="option-content">
                  <h3>{option.title}</h3>
                  </div>
                  {option.icon && (
                    option.id === LEVELUP_OPTION.CHARACTER ? 
                      <CImage imageId={option.icon} /> 
                    : 
                      <span className="option-icon">{option.icon}</span>
                  )}
                  <div className="option-content">
                  
                    {option.description && <p>{option.description}</p>}
                  </div>
                </button>
              ))}
            </div>
          </div>
        </div>
      );
    }

    if(modalConfig.type === 'travelresult' && modalConfig.travelConfig) {
      return (
        <div className="modal-overlay">
          <div className="modal-container travel">
            <div className="modal-header">
              {modalConfig.title}
              <button className="close-button" onClick={closeModal}></button>
            </div>
            <div className="travel-results">
              {modalConfig.travelConfig.results.map((result, index) => (
                  <EffectDisplay
                    key={index}
                    effectData={result}
                    />
              ))}
            </div>
            <div className="modal-actions">
              {modalConfig.options.map((option) => (
                <button
                  key={option.id}
                  className="modal-action-button"
                  onClick={() => handleOptionSelect(option.id)}
                >
                  {option.title}
                </button>
              ))}
            </div>
          </div>
        </div>
      );
    }

    if(modalConfig.type === 'puzzle' && modalConfig.effectData) {
      return (
        <div className="modal-overlay">
          <div className="modal-container puzzle">
            <div className="modal-header">
              {modalConfig.title}
              <button className="close-button" onClick={closeModal}></button>
            </div>
            <div className="puzzle-results">
              {modalConfig.effectData.map((result, index) => (
                  <EffectDisplay
                    key={index}
                    effectData={result}
                    />
              ))}
            </div>
            <div className="modal-actions">
              {modalConfig.options.map((option) => (
                <button
                  key={option.id}
                  className="modal-action-button"
                  onClick={() => handleOptionSelect(option.id)}
                >
                  {option.title}
                </button>
              ))}
            </div>
          </div>
        </div>
      );
    }

    if (modalConfig.type === 'welcome') {
      return (
        <WelcomeModal
          onClose={closeModal}
        />
      );
    }

    return (
      <div className="modal-overlay">
        <div className="modal-container">
          <div className="modal-header">
            <h2>{modalConfig.title}</h2>
            <button className="close-button" onClick={closeModal}></button>
          </div>
          <div className="modal-options">
            {modalConfig.options.map((option) => (
              <button
                key={option.id}
                className={`modal-option ${option.class || ''}`}
                onClick={() => handleOptionSelect(option.id)}
              >
                <div className="option-content">
                <h3>{option.title}</h3>
                </div>
                {option.icon && (
                  option.id === LEVELUP_OPTION.CHARACTER ? 
                    <CImage imageId={option.icon} /> 
                  : 
                    <span className="option-icon">{option.icon}</span>
                )}
                <div className="option-content">
                
                  {option.description && <p>{option.description}</p>}
                </div>
              </button>
            ))}
          </div>
        </div>
      </div>
    );
  };