import { ENCOUNTER_EVENT_TYPE } from "../consts/general";
import { ChatMessage, CHAT_TYPE, Tag, ATTITUDE, Player } from "../models";
import { ChatRuleEngine } from "./chatRuleEngine";



const PROMPT_CHANCE = {
  MENTION_LOCATION: 20,
  MENTION_PLOT: 0,
  DM_CONVERSATION: 0,
  PLAYER_CONVERSATION: 0,
  REFERENCE_TOPIC: 15,
}
export interface LLMResponse {
  message: string;
  speaker: 'dm' | 'player' | 'character';
  speakerId?: number;
  confidence: number; // 0-1, how confident the LLM is in the response
  appliedRules?: string[]; // Debug: which rules were applied
  debugInfo?: string; // Debug: formatted debug string
}

export interface LLMRequest {
  tag: Tag;
  gameContext: Record<string, any>;
  theme?: string;
  participants?: number[];
  speakerType?: 'dm' | 'player' | 'character';
  partyTension: number;
  recentTopics?: string[];
}

class LLMService {
  private apiKey: string | null = null;
  private baseUrl: string = 'https://api.openai.com/v1';
  private model: string = 'gpt-3.5-turbo';
  private enabled: boolean = false;

  constructor() {
    // Check for API key in environment or local storage
    this.loadConfiguration();
  }

  private loadConfiguration(): void {
    // Try to load from environment variables or local storage
    const apiKey = process.env.REACT_APP_OPENAI_API_KEY || localStorage.getItem('llm_api_key');
    if (apiKey) {
      this.apiKey = apiKey;
      this.enabled = true;
    }
  }

  public setApiKey(apiKey: string): void {
    this.apiKey = apiKey;
    this.enabled = true;
    localStorage.setItem('llm_api_key', apiKey);
  }

  public setConfiguration(config: { baseUrl?: string; model?: string }): void {
    if (config.baseUrl) this.baseUrl = config.baseUrl;
    if (config.model) this.model = config.model;
  }

  public isEnabled(): boolean {
    return this.enabled && this.apiKey !== null;
  }

  public async generateContextualResponse(request: LLMRequest): Promise<LLMResponse | null> {
    if (!this.isEnabled()) {
      return null;
    }

    try {
      // Get applicable rules first to capture debug info
      const applicableRules: any = [];

      const prompt = this.buildPrompt(request);

      console.log('LLM Prompt:', prompt);

      const response = await this.callLLM(prompt);
      const parsedResponse = this.parseResponse(response, request);


      return parsedResponse;
    } catch (error) {
      console.error('LLM Service Error:', error);
      return null;
    }
  }

  private getGameContextPrompt(tag: Tag, context: Record<string, any> = {}) {



    switch (context.eventType) {
      case ENCOUNTER_EVENT_TYPE.CHALLENGE:
        
          return `The player(s) or DM must discuss how ${tag.name} helps them overcome a challenge with the description: ${context.challengeDescription || ' something difficult'}  ${context.attribute || ' skills'}`
        
        break;
      case ENCOUNTER_EVENT_TYPE.ROLEPLAY:
      default:
        return `The player(s) or DM should casually mention ${tag.name}`
    }
  }

  private buildPrompt(request: LLMRequest): string {
    const { tag, gameContext, theme, participants, speakerType, partyTension, recentTopics } = request;


    let themeString = ''
    if (theme && theme !== 'none') {
      themeString = `The ${tag.name} should be treated with a ${theme} attitude`
    }

    let tensionString = ``;
    if (Math.random() * 3 < 1) {
      if (partyTension < 10) {
        tensionString = `the party is bored and the message should reflect this`
      } else if (partyTension > 80) {
        tensionString = 'the party mood is very tense and the message tone should reflect this.'
      }
    }

    let locationString = '';

    if (Math.random() * 100 < PROMPT_CHANCE.MENTION_LOCATION) {
      locationString = `The current location ${gameContext.location} should be mentioned.`
    }

let basicInstructions = `The message should be short and nonspecific, like a casual remark.`

let recentTopic = ''
if(recentTopics && Math.random() * 100 < PROMPT_CHANCE.REFERENCE_TOPIC) {
  const relatedTopic = recentTopics[Math.floor((Math.random() * recentTopics.length))];
  recentTopic = `the message should refer to ${relatedTopic} as potentially related`
}
    let prompt = `You are a helpful assistant for a tabletop RPG game. 
    Generate short contextual dialogue for the following situation. 
    
    GAME CONTEXT: ${this.getGameContextPrompt(tag, gameContext)}
    ${themeString}
    ${tensionString}
    ${locationString}
    ${recentTopic}
    ${basicInstructions}
`;

let conversationChance = Math.random() * 100; 
if(conversationChance <= PROMPT_CHANCE.DM_CONVERSATION){
  prompt +=
`return a JSON object representing the conversation as an array with the structure such that each message in the converstation is an object with this structure :

{
  "message": "The actual dialogue text",
  "speaker": "dm" | "player" | "character",
  "speakerId": ${participants?.[0] || 0},
  }`  

} else {
  // conversationChance -= PROMPT_CHANCE.DM_CONVERSATION;
  // if(conversationChance <= PROMPT_CHANCE.PLAYER_CONVERSATION) {

  // }else {
    prompt += 

`Return a JSON object with this structure:
{
  "message": "The actual dialogue text",
  "speaker": ${speakerType},
  "speakerId": ${participants?.[0]},
}`
  }



    return prompt;
  }

  private async callLLM(prompt: string): Promise<any> {
    if (!this.apiKey) {
      throw new Error('No API key configured');
    }

    const response = await fetch(`${this.baseUrl}/chat/completions`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${this.apiKey}`
      },
      body: JSON.stringify({
        model: this.model,
        messages: [
          {
            role: 'user',
            content: prompt
          }
        ],
        temperature: 0.8,
        max_tokens: 150
      })
    });

    if (!response.ok) {
      throw new Error(`LLM API Error: ${response.status} ${response.statusText}`);
    }

    const data = await response.json();
    return data.choices[0].message.content;
  }

  private parseResponse(responseText: string, request: LLMRequest): LLMResponse {
    try {
      // Try to parse as JSON first
      const parsed = JSON.parse(responseText);
      // If response is an array, then it is a conversation 
      if (Array.isArray(parsed)) {
        
      }
      // Otherwise, treat as single message object


      return {
        message: parsed.message || responseText,
        speaker: parsed.speaker || 'dm',
        speakerId: parsed.speakerId,
        confidence: parsed.confidence || 0.7
      };
    } catch (error) {
      // Fallback to treating the whole response as a message
      return {
        message: responseText.trim(),
        speaker: 'dm',
        confidence: 0.5
      };
    }
  }

  // Generate fallback responses that are more contextual than the current generic ones
  public generateFallbackResponse(topic: Tag, gameContext: string): ChatMessage {
    const contextualResponses = [
      `Oh! That ${topic.name} is interesting given our current situation...`,
      `${topic.name}... that reminds me of something important.`,
      `Hmm, ${topic.name} could be significant here.`,
      `I wonder if that ${topic.name} is connected to what we're doing...`,
      `${topic.name}? That's unexpected in this context.`
    ];

    const message = contextualResponses[Math.floor(Math.random() * contextualResponses.length)];

    return {
      message,
      speaker: {
        type: CHAT_TYPE.DM,
        id: 0
      }
    };
  }

  // Convert LLMResponse to ChatMessage format
  public toLLMResponseToChatMessage(response: LLMResponse): ChatMessage {
    let chatType: CHAT_TYPE;
    switch (response.speaker) {
      case 'player':
        chatType = CHAT_TYPE.PLAYER;
        break;
      case 'character':
        chatType = CHAT_TYPE.CHARACTER;
        break;
      default:
        chatType = CHAT_TYPE.DM;
    }

    // Add debug information to the message if available
    const debugSuffix = response.debugInfo || '';
    const finalMessage = response.message + debugSuffix;

    return {
      message: finalMessage,
      speaker: {
        type: chatType,
        id: response.speakerId || 0
      }
    };
  }

}

export default LLMService;
