import React, { useEffect, useState } from 'react';
import './rewardModal.scss';
import { useAppManager, useCampaignManager, usePartyManager, useUiManager } from '../managerContexts';
import { ATTRIBUTE_CONTRIBUTOR, HeroCombatant, REWARD_TYPE, RewardItem } from '../../models';
import { REWARD_ITEMS } from '../../consts/rewards';
import CImage from '../UI/cimage';
import AttributeDisplay from '../UI/attributeDisplay';



interface RewardModalProps {
    rewardsAcceptedCallback: (acceptedRewards: RewardItem[], rewardDifference: number, advanceEvent: boolean) => void;
}

  //const REWARDS_PER_RESULT = [2,3,4,5,6,6,6,6]
  


  const RewardModal: React.FC<RewardModalProps> = ({rewardsAcceptedCallback}) => {
    
    const uiManager = useUiManager();
    const partyManager = usePartyManager();
    const campaignManager = useCampaignManager();
    const appManager= useAppManager();
    const tagManager = appManager.getTagManager();


    const [visible, setVisible] = React.useState(false);
    const [rewardTotal, setRewardTotal] = React.useState(0);
    const [selectedItems, setSelectedItems] = useState<RewardItem[]>([]);
    const currentTotal = selectedItems.reduce((sum: any, item: { cost: any; }) => sum + item.cost, 0);
    const [currentAvailableRewards, setCurrentAvailableRewards] = useState<RewardItem[]>([]);
    const [heroCombatants, setHeroCombatants] = useState<HeroCombatant[]>(partyManager.generateHeroes([]));
    const [nextEventOnAccept, setNextEventOnAccept] = useState(false);
   const getPanelState = () => {
    if (currentTotal < rewardTotal) return 'low';
    if (currentTotal === rewardTotal) return 'match';
    return 'high';
  };
  const toggleItem = (item: RewardItem) => {




    if (selectedItems.includes(item)) {
      setSelectedItems(selectedItems.filter(i => i !== item));
    } else {
        if (currentTotal + item.cost > rewardTotal + 2) {
            return; // Don't allow selection if it exceeds the total
        }
      setSelectedItems([...selectedItems, item]);
    }
  };

    useEffect(() => {
        if(visible) {
            const availableRewards = [...REWARD_ITEMS]
            const expectedGear = tagManager.magicItemRumors;
            setSelectedItems([]);
          
            // for XP, add a reward for a random player (lowest level) to level up
            // or an expensive one for all characters to level up.
            if(expectedGear.length > 0) {
                availableRewards.push({
                    name: "Magic Item",
                    value: 1,
                    type: REWARD_TYPE.ITEM,
                    cost: 2,
                    description: expectedGear[0].name,
                    tag: expectedGear[0],
                })
            }

            setCurrentAvailableRewards(availableRewards);
        }
    }, [campaignManager, visible]);
 

    useEffect(() => {

        const handleShowRewards = (rewardTotal: number, advanceEvent: boolean) => {
            if (rewardTotal > 0) {
                setVisible(true);
                setRewardTotal(rewardTotal);
                setNextEventOnAccept(advanceEvent);
            } else {
                setVisible(false);
            }
        }
        uiManager.on('showRewardModal', handleShowRewards);

        return () => {
            uiManager.off('showRewardModal', handleShowRewards);
        };
    }, [uiManager]);

  const handleCloseCallback = () => {
    // Handle the close callback here if needed
    rewardsAcceptedCallback(selectedItems, currentTotal - rewardTotal, nextEventOnAccept); 
    setVisible(false);
   
}
  const getPanelLowText = (rewardTotal: number, currentTotal: number) => {
    if (rewardTotal - currentTotal === 1) {
        return `All players `;
    }
    return `All players `;
  }
  const getPanelHighText = (rewardTotal: number, currentTotal: number) => {
    if (currentTotal - rewardTotal   === 1) {
        return `-50% Intensity`;
    }
    return `-100% Intensity`;
  }

    if(!visible) {
        return null;
    }
    return (
        <div className="reward-modal-container">
            <div className="reward-modal-body">
                <div className = "reward-modal-title">
                    <div className="reward-modal-title-text">Reward your players</div>
                    <div className="reward-modal-instructions">Based on the diffifculty of the event, they expect the equivalent of {rewardTotal} rewards.</div>
                </div>
                <div className="reward-panels">
                    <div className={`panel too-low ${getPanelState() === 'low' ? 'active' : ''}`}>
                    <div className="panel-title">Too Low</div>
                    {getPanelState() === 'low' && 
                        <div className="panel-contents">
                            <div className="panel-value">{currentTotal}</div>
                            <div className="panel-low-text">{getPanelLowText(rewardTotal, currentTotal)}</div>
                            <button className="accept-button" onClick={() => handleCloseCallback()}>ACCEPT</button>
                        </div>
                        
                    }
                    </div>
                    <div className={`panel match ${getPanelState() === 'match' ? 'active' : ''}`}>
                    <div className="panel-title">Expected</div>
                    <div className="panel-value expected">{rewardTotal}</div>
                    {getPanelState() === 'match' && 
                        <button className="accept-button" onClick={() => handleCloseCallback()}>ACCEPT</button>
                    }
                    </div>
                    <div className={`panel too-high ${getPanelState() === 'high' ? 'active' : ''}`}>
                    <div className="panel-title">Too High</div>
                    {getPanelState() === 'high' && 
                     <div className="panel-contents">
                            <div className="panel-value">{currentTotal}</div>
                            <div className="panel-high-text">{getPanelHighText(rewardTotal, currentTotal)}</div>
                            <button className="accept-button" onClick={() => handleCloseCallback()}>ACCEPT</button>
                        </div>
                    }
                    </div>
                </div>
            
                <div className="reward-items-list">
                    {currentAvailableRewards.map(item => (
                    <div 
                        key={item.name}
                        className={`reward-item ${selectedItems.includes(item) ? 'selected' : ''}`}
                        onClick={() => toggleItem(item)}
                    >
                        <div className="item-header">
                        <span className="item-name">{item.name}</span>
                        <span className="item-cost">{item.cost}</span>
                        </div>
                        <div className="item-description">{item.description}</div>
                    </div>
                    ))}
                </div>
                <div className="reward-modal-heroes">

                
                    {heroCombatants && heroCombatants.map((hero, index) => {
                        return (
                            <div key={hero.name} className="reward-modal-hero-row">
                                <div className = "reward-modal-hero-container">
                                    <div className="reward-modal-hero">
                                        <div className="reward-modal-hero-name">{hero.name}</div>
                                        <div className="combat-character-icon">
                                            <CImage imageId={hero.icon} />
                                        </div>
                                        { hero.character && hero.character.injury === 'injured' && (
                                            <div className="reward-modal-hero-injury"></div>
                                        )}
                                        { hero.character && hero.character.injury === 'dead' && (
                                            <div className="reward-modal-hero-injury dead"></div>
                                        )}
                                    </div>
                                    <div className="reward-modal-hero-attributes">
                                        <AttributeDisplay hero={hero} category={ATTRIBUTE_CONTRIBUTOR.ALL} showAttributeLoss={true} />
                                    </div>
                                </div>
                            </div>
                        )})}	
                    </div>
            </div>
        </div>
      );
}
  export default RewardModal;