import React from 'react';
import { Character } from '../../models';
import './characterSelector.scss';
import CharacterToken from './characterToken';

interface CharacterSelectorProps {
  characters: Character[];
  selectedCharacter: Character | null;
  onCharacterClick: (character: Character, elementRect: DOMRect) => void;
  exhaustion?: number;
  onRandomClick?: () => void;
  onEndClick?: () => void;
}

const CharacterSelector: React.FC<CharacterSelectorProps> = ({
  characters,
  selectedCharacter,
  onCharacterClick,
  exhaustion,
  onRandomClick,
  onEndClick
}) => {
  return (
    <div className="character-holder">
      <div className="characters-title">
        {exhaustion === 0 ? 'Click a Character' : `Next action ${exhaustion} Party Exhaustion`}
      </div>
      
      <div className="characters-display">
        <div className="characters-row">
          {onRandomClick && (
            <div className="selector-button random-button" onClick={onRandomClick}>
              RANDOM
            </div>
          )}
          <div className = "character-selector-line">
          {characters.map((character, index) => (
            <CharacterToken
              key={index}
              character={character}
              isSelected={selectedCharacter === character}
              onClick={(e) => onCharacterClick(character, e.currentTarget.getBoundingClientRect())}
              data-character-id={String(character.id)}
            />

          ))}
          </div>
          {onEndClick && (
            <div className="selector-button end-button" onClick={onEndClick}>
              LEAVE
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default CharacterSelector;
