import React, { useState } from 'react';
import TagCollectionsScreen from './TagCollectionsScreen';
import DMAbilitiesScreen from './DMAbilitiesScreen';
import './DMCollectionsModal.scss';

interface DMCollectionsModalProps {
  onClose: () => void;
  initialView?: 'collections' | 'abilities';
}

const DMCollectionsModal: React.FC<DMCollectionsModalProps> = ({
  onClose,
  initialView = 'collections'
}) => {
  const [activeView, setActiveView] = useState<'collections' | 'abilities'>(initialView);

  const handleBackdropClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      onClose();
    }
  };

  const renderContent = () => {
    switch (activeView) {
      case 'collections':
        return (
          <div className="collections-content">
            <TagCollectionsScreen onClose={() => {}} isEmbedded={true} />
          </div>
        );
      case 'abilities':
        return (
          <div className="abilities-content">
            <DMAbilitiesScreen />
          </div>
        );
      default:
        return null;
    }
  };

  return (
    <div className="dm-collections-modal-overlay" onClick={handleBackdropClick}>
      <div className={`dm-collections-modal-container ${activeView === 'collections' ? 'green-theme' : 'blue-theme'}`}>
        {/* Header */}
        <div className="dm-collections-modal-header">
          <h2>{activeView === 'collections' ? 'Topics & Themes' : 'DM Abilities'}</h2>
          <button className="close-button" onClick={onClose}></button>
        </div>

        {/* Content */}
        <div className="dm-collections-modal-content">
          {renderContent()}
        </div>

        {/* Footer Navigation */}
        {/* <div className="dm-collections-modal-footer">
          <button
            className={`footer-tab green-theme ${activeView === 'collections' ? 'active' : ''}`}
            onClick={() => setActiveView('collections')}
          >
            Topics and Themes
          </button>
          <button
            className={`footer-tab blue-theme ${activeView === 'abilities' ? 'active' : ''}`}
            onClick={() => setActiveView('abilities')}
          >
            DM Abilities
          </button>
        </div> */}
      </div>
    </div>
  );
};

export default DMCollectionsModal;
