import { useState, useRef, useEffect } from "react";
import {
  Encounter,
  RPEncounterFlavorTag,
  FlavorTagSource,
  FlavorTagTheme,
  Tag,
  ATTITUDE,
} from "../../models";
import EncounterHeader from "./encounterHeader";
import InstructionsPanel from "../UI/InstructionsPanel";
import {
  useAppManager,
  useCampaignManager,
  useCharacterManager,
  usePlayerManager,
  useUiManager,
} from "../managerContexts";
import FillBar from "../UI/fillBar";
import PartyManager from "../../managers/partyManager";
import CImage from "../UI/cimage";
import CharacterManager from "../../managers/characterManager";
import PlayerManager from "../../managers/playerManager";
import FlavorTagPill from "../UI/FlavorTagPill";
import TooltipWrapper from "../UI/TooltipWrapper";
import DiceScore from "../UI/DiceScore";

interface GameEncounterRoleplayProps {
  encounter: Encounter;
}

const bandLabels = [
  { text: "Organic Conversation", top: "4%", score: 2 },
  { text: "Guided Interactions", top: "39%", score: 1 },
  { text: "Off Topic", top: "74%", score: 0 },
];

const GameEncounterRoleplay: React.FC<GameEncounterRoleplayProps> = ({
  encounter,
}) => {
  const uiManager = useUiManager();
  const campaignManager = useCampaignManager();
  const appManager = useAppManager();
  const chatManager = appManager.getChatManager();
  const partyManager = appManager.getPartyManager();
  const characterManager = useCharacterManager();
  const playerManager = usePlayerManager();
  const tagManager = appManager.getTagManager();
  const [positions, setPositions] = useState<{ top: string; left: string }[]>(
    []
  );
  const [flavorTags, setFlavorTags] = useState<RPEncounterFlavorTag[]>(() =>
    campaignManager.generateRoleplayEventTags()
  );
  const [freeTags, setFreeTags] = useState<number>(5);

  const containerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const newPositions: { top: string; left: string }[] = [];
    const maxRetries = 100;
    const bands = [
      { min: 5, max: 20 }, // Top band
      { min: 40, max: 55 }, // Middle band
      { min: 77, max: 88 }, // Bottom band
    //  { min: 0, max: 0 }, // Off-topic band
    ];

    const isOverlap = (
      pos: { top: string; left: string },
      top: string,
      left: string
    ) =>
      Math.abs(parseFloat(pos.top) - parseFloat(top)) < 10 &&
      Math.abs(parseFloat(pos.left) - parseFloat(left)) < 15;

    flavorTags.forEach(() => {
      let currentTry = 0;
      let top: string, left: string;
      let overlap;
      do {
        currentTry++;
        const bandIndex = Math.floor(Math.random() * 3);
        const band = bands[bandIndex];
        top = `${band.min + Math.random() * (band.max - band.min)}%`;
        left = `${Math.random() * 80}%`;
        // eslint-disable-next-line no-loop-func
        overlap = newPositions.some((pos) => isOverlap(pos, top, left));
      } while (overlap && currentTry < maxRetries);
      newPositions.push({ top, left });
    });

    setPositions(newPositions);
  }, [flavorTags]);

  useEffect(() => {
    const adjustTooltips = () => {
      const container = containerRef.current;
      if (!container) return;

      const containerRect = container.getBoundingClientRect();

      // Handle both FlavorTagPill tooltips and direct tooltip elements
      const allElements = container.querySelectorAll(
        "[data-tooltip], .encounter-rp-icon, .encounter-flavor-tag"
      );

      allElements.forEach((element) => {
        try {
          // Find tooltip element - could be .tooltip-text or [role="tooltip"]
          const tooltipText = element.querySelector(
            '.tooltip-text, [role="tooltip"]'
          ) as HTMLElement;
          if (!tooltipText) return;

          // Make tooltip temporarily visible for measurement
          tooltipText.style.visibility = "hidden";
          tooltipText.style.display = "block";
          tooltipText.style.opacity = "1";

          const tooltipRect = tooltipText.getBoundingClientRect();
          const elementRect = element.getBoundingClientRect();

          // Calculate available space in each direction
          const spaceAbove = elementRect.top - containerRect.top;
          const spaceBelow = containerRect.bottom - elementRect.bottom;
          const spaceLeft = elementRect.left - containerRect.left;
          const spaceRight = containerRect.right - elementRect.right;

          // Position horizontally based on available space
          if (tooltipRect.width > spaceRight && spaceLeft > spaceRight) {
            // More space on left than right
            tooltipText.style.left = "auto";
            tooltipText.style.right = "0";
            tooltipText.style.transform = "translateX(0)";
          } else if (tooltipRect.width > spaceLeft && spaceRight > spaceLeft) {
            // More space on right than left
            tooltipText.style.left = "0";
            tooltipText.style.right = "auto";
            tooltipText.style.transform = "translateX(0)";
          } else {
            // Center if enough space
            tooltipText.style.left = "50%";
            tooltipText.style.right = "auto";
            tooltipText.style.transform = "translateX(-50%)";
          }

          // Position vertically based on available space
          if (tooltipRect.height > spaceBelow && spaceAbove > spaceBelow) {
            // More space above than below
            tooltipText.style.bottom = "100%";
            tooltipText.style.top = "auto";
          } else {
            // Default to below
            tooltipText.style.top = "100%";
            tooltipText.style.bottom = "auto";
          }

          // Reset visibility
          tooltipText.style.visibility = "visible";
          tooltipText.style.display = "";
          tooltipText.style.opacity = "";
        } catch (error) {
          // Ignore any getBoundingClientRect errors that might occur
          console.debug("Tooltip positioning skipped:", error);
        }
      });
    };

    // Initial adjustment after a short delay to ensure elements are rendered
    const timeoutId = setTimeout(adjustTooltips, 100);

    // Adjust on resize
    window.addEventListener("resize", adjustTooltips);

    return () => {
      window.removeEventListener("resize", adjustTooltips);
      clearTimeout(timeoutId);
    };
  }, [positions, flavorTags]);

  const getScoreForPosition = (position: string): number => {
    const topPercentage = parseFloat(position);

    // Check which band the tag is in
    if (topPercentage <= 20) {
      return bandLabels[0].score; // Top band - Organic Conversation
    } else if (topPercentage <= 55) {
      return bandLabels[1].score; // Middle band - Guided Interactions
    } else {
      return bandLabels[2].score; // Bottom band - Off Topic
    }
  };

  const handleTagClick = (flavorTag: RPEncounterFlavorTag, index: number) => {
    const tagPosition = positions[index];
    const score = getScoreForPosition(tagPosition.top);

    setFreeTags(freeTags - 1);

    if (freeTags > 0) {
     // partyManager.addPartyScore(score);
     campaignManager.collectEncounterPoints(score);
    }

    //chatManager.roleplayingEvent(flavorTag);

    campaignManager.selectRoleplayFlavorTag(flavorTag, freeTags);
    setFlavorTags(campaignManager.generateRoleplayEventTags());
  };
  const description = campaignManager.getCurrentEvent()?.flavorText || "";

  const instructions =
    freeTags > 0
      ? description
      : `You may keep roleplaying, but your players are growing bored.`;
  const getPlayerAttitudesStyle = (tag: Tag) => {
    return uiManager.getPlayerAttitudesStyle(tag);
  };

  const pressHurryButton = () => {
    if (freeTags > 0) {
      partyManager.addPartyTension(10);
      setFreeTags(freeTags - 1);

      chatManager.dmMessage([
        "Lets moooove along!",
        "Blah Blah blah",
        "Theres a lot more game to get to.",
      ]);
      setFlavorTags(campaignManager.generateRoleplayEventTags());
    } else {
      campaignManager.backToOverview();
    }
  };

  const getLegend = () => {
    const characters = characterManager.getPlayerCharacters();

    return (
      <div className="roleplay-legend">
        <div className="legend-section">
          <div className="legend-row">
            <div className="sample-tag text-pill type-location">Location</div>
            <div className="sample-desc">Describe the environment</div>
          </div>
          <div className="legend-row">
            <div className="sample-tag text-pill type-encounter">
              Foreshadow
            </div>
            <div className="sample-desc">Hint about an upcoming encounter</div>
          </div>
          <div className="legend-row">
            <div className="sample-tag text-pill type-all">New Topic</div>
            <div className="sample-desc">Introduce/entertain new ideas</div>
          </div>
          <div className="legend-row">
            <div className="sample-tag text-pill type-rumor">Resolve Topic</div>
            <div className="sample-desc">
              Integrate/confirm introduced topics
            </div>
          </div>
        </div>

        <div className="legend-section">
          <div className="legend-column">
            <div className="legend-watchfor-container">
              <div className="sample-tag text-pill type-backstory">
                Backstory
              </div>
              <div className="sample-tag text-pill type-magic-item">
                Magic Item
              </div>
              <div className="sample-tag text-pill type-plot-hook">
                Plot Hooks
              </div>
            </div>
          </div>
        </div>

        <div className="legend-section attitudes">
          <div className="legend-player-row">
            <div className="legend-player-column">
              <div className="legend-title">Player Attitudes:</div>
              <div className="attitude-row">
                <span className="attitude like">Like</span>
                <span className="attitude hate">Hate</span>
                <span className="attitude neutral">Mixed</span>
              </div>
            </div>
            <div className="legend-player-column">
              <div className="encounter-rp-icon-container">
                <div className="legend-player-row">
                  {characters.map((character, index) => (
                    <CImage
                      key={`char-${character.id}-${index}`}
                      imageId={character.icon}
                    />
                  ))}
                </div>
              </div>

              <div className="legend-character-info">+2/-1 Attention</div>
            </div>
          </div>
        </div>
      </div>
    );
  };
  const getInstructions = () => {
    const instructions = [
      "Click on the topics below to discuss.",
      "Players will lose attention after losing patience.",
      "Increase tension by hurrying the players.",
    ];
    return <InstructionsPanel instructions={instructions} />;
  };

  return (
    <div className="encounter-roleplay-container">
      <EncounterHeader
        title={`Roleplay in ${encounter.location.name}`}
        description={instructions}
        leftContent={getInstructions()}
        rightContent={
          <div className="encounter-roleplay-skill-container">
            <FillBar
              totalSegments={5}
              filledSegments={freeTags}
              theme="engagement"
              label="Party Patience"
            />
            <div
              className={`encounter-roleplay-hurry-button`}
              onClick={() => pressHurryButton()}
            >
              {freeTags > 0 ? "Hurry Players" : "End Roleplay"}
            </div>
            <div className="encounter-roleplay-hurry-text">
              {freeTags > 0 ? "+10 Tension" : ""}
            </div>
          </div>
        }
        instructions={[true, false]}
      />
      <div
        className={`encounter-roleplay-flavor-tags-container  ${
          freeTags <= 0 ? "disabled" : ""
        } `}
        ref={containerRef}
      >
        {bandLabels.map((label, index) => (
          <div
            key={`band-label-${index}`}
            className={`band-label ${freeTags <= 0 ? "disabled" : ""} `}
            style={{ top: label.top }}
          >
            {label.text}
            <DiceScore
              score={label.score}
              className="band-score"
              />
          </div>
        ))}
        {flavorTags.map((flavorTag, index) => {
          if (flavorTag.tag) {
            return (
              <FlavorTagPill
                key={index}
                tag={flavorTag.tag}
                style={positions[index]}
                onClick={() => handleTagClick(flavorTag, index)}
                className={`text-pill theme-${
                  flavorTag.tag.theme
                } type-${flavorTag.tag.source} ${getPlayerAttitudesStyle(
                  flavorTag.tag.tag
                )}`}
                tooltipText={tagManager.getTooltipText(flavorTag)}
              />
            );
          } else if (flavorTag.type === "treasure") {
            // Convert treasure type to FlavorTagSource.TREASURE
            return (
              <FlavorTagPill
                key={index}
                tag={{
                  tag: { name: "Treasure" },
                  source: FlavorTagSource.TREASURE,
                  theme: FlavorTagTheme.NONE,
                }}
                style={positions[index]}
                onClick={() => handleTagClick(flavorTag, index)}
                tooltipText={tagManager.getTooltipText(flavorTag)}
                className="encounter-flavor-tag"
              />
            );
          } else if (flavorTag.type === "dmpoint") {
            // Convert dmpoint type to FlavorTagSource.DM_POINT
            return (
              <FlavorTagPill
                key={index}
                tag={{
                  tag: { name: "DM Point" },
                  source: FlavorTagSource.DM_POINT,
                  theme: FlavorTagTheme.NONE,
                }}
                style={positions[index]}
                onClick={() => handleTagClick(flavorTag, index)}
                tooltipText={tagManager.getTooltipText(flavorTag)}
                className="encounter-flavor-tag"
              />
            );
          } else {
            return (
              <div
                key={index}
                className={`encounter-rp-icon theme-character`}
                style={positions[index]}
                data-tooltip={`${tagManager.getTooltipText(flavorTag)}`}
                onClick={() => handleTagClick(flavorTag, index)}
              >
                <TooltipWrapper
                  content={`Click to focus on ${
                    flavorTag.character?.name
                  }. This may bore some of your players, but ${
                    playerManager.getPlayerAssignedCharacter(
                      flavorTag.character
                    )?.name
                  } will like it.`}
                  preservePositioning={true}
                  translate={[-30, -150]}
                >
                  <div className="encounter-rp-icon-container">
                    <CImage
                      imageId={flavorTag.character?.icon || "icon_character"}
                    />
                  </div>
                </TooltipWrapper>
              </div>
            );
          }
        })}
      </div>
    </div>
  );
};

export default GameEncounterRoleplay;
