import React, { useEffect, useMemo, useState } from "react";
import "./notebook.scss";
import CImage from "./cimage";
import {
	useAppManager,
	useCampaignManager,
	useUiManager,
} from "../managerContexts";
import FillBar from "./fillBar";
import { CAMPAIGNS, NB_EXPECTED_ENCOUNTER_TAGS } from "../../consts/campaigns";
import UiManager from "../../managers/uiManager";
import MoreInfo from "./moreInfoButton";
import {
	EncounterEvent,
	NOTEBOOK_CATEGORY,
	Tag,
	FlavorTagSource,
	FlavorTagTheme,
} from "../../models";
import { EventType } from "@testing-library/react";
import { ENCOUNTER_EVENT_TYPE } from "../../consts/general";
import CampaignDisplay from "../campaignDisplay";
import TagPill from "./TagPill";
import FlavorTagPill from "./FlavorTagPill";
import DiceScore from "./DiceScore";
import CampaignHeader from "./CampaignHeader";
import Tooltip from "./tooltip";
import TooltipWrapper from "./TooltipWrapper";
import Cbox from "./cbox";

const EncounterOverview: React.FC = () => {
	const campaignManager = useCampaignManager();
	const uiManager = useUiManager();
	const appManager = useAppManager();
	const tagManager = appManager.getTagManager();
	const [encounter, setEncounter] = useState(
		campaignManager.getCurrentEncounter()
	);
	const [expectedEncounterTags, setExpectedEncounterTags] = useState(
		tagManager.getOutstandingEncounterTags()
	);
	const [establishedEncounterTags, setEstablishedEncounterTags] = useState(
		tagManager.encounterTagsCollected[encounter?.name || ""] || []
	);
	const [locationTagsEstablished, setLocationTagsEstablished] = useState(
		tagManager.locationTagsCollected
	);
	const [locationTags, setLocationTags] = useState(
		campaignManager.getEncounterLocationTags()
	);
	const [completedEvents, setCompletedEvents] = useState(
		campaignManager.getCompletedEvents()
	);
	const [campaignModalVisible, setCampaignModalVisible] = useState(false);

	const [campaignScore, setCampaignScore] = useState(
		campaignManager.getCampaignScore()
	);
	const [encounterScore, setEncounterScore] = useState(
		campaignManager.getEncounterPoints()
	);
	const [remainingSnacks, setRemainingSnacks] = useState(
		campaignManager.getRemainingSnacks()
	);
	const [remainingBreaks, setRemainingBreaks] = useState(
		campaignManager.getRemainingBreaks()
	);

	const setSceneTags = useMemo(
		() => encounter?.location.tags.sort() || [],
		[encounter?.location.tags]
	);

	useEffect(() => {
		const updateEncounterState = () => {
			setCompletedEvents(campaignManager.getCompletedEvents());
			setExpectedEncounterTags(campaignManager.getExpectedEncounterTags());
			setEstablishedEncounterTags(
				tagManager.encounterTagsCollected[encounter?.name || ""] || []
			);
			setLocationTags(campaignManager.getEncounterLocationTags());
			setLocationTagsEstablished(tagManager.locationTagsCollected);
			setEncounter(campaignManager.getCurrentEncounter());
			setEncounterScore(campaignManager.getEncounterPoints());
			setCampaignScore(campaignManager.getCampaignScore());
			setRemainingSnacks(campaignManager.getRemainingSnacks());
			setRemainingBreaks(campaignManager.getRemainingBreaks());
		};

		campaignManager.on("encounterUpdated", updateEncounterState);

		return () => {
			campaignManager.off("updateEncounterState", updateEncounterState);
		};
	}, [encounter, campaignManager, tagManager]);

	// Quest event listeners
	useEffect(() => {
		const handleShowQuestTerminal = (data: { quest: any; node: any }) => {
			uiManager.showQuestTerminalModal(data.quest, data.node);
		};

		campaignManager.on("showQuestTerminal", handleShowQuestTerminal);

		return () => {
			campaignManager.off("showQuestTerminal", handleShowQuestTerminal);
		};
	}, [campaignManager, uiManager]);

	// DM Abilities event listeners
	useEffect(() => {
		const updateDMAbilities = () => {
			setRemainingSnacks(campaignManager.getRemainingSnacks());
			setRemainingBreaks(campaignManager.getRemainingBreaks());
		};

		campaignManager.on("dmAbilitiesUpdated", updateDMAbilities);

		return () => {
			campaignManager.off("dmAbilitiesUpdated", updateDMAbilities);
		};
	}, [campaignManager]);

	const sortedSetSceneTags = useMemo(() => {
		return [...setSceneTags].sort((a, b) => a.name.localeCompare(b.name));
	}, [setSceneTags]);

	if (!encounter) {
		return <div className="encounter-overview-container">No Encounter</div>;
	}
	const uniqueEventTypes = Array.from(
		new Set(encounter.events.map((event) => event.type))
	);
	const currentLocationTheme = encounter.location.themes[0];

	// Get the preferred event index (first unactivated event)
	const getPreferredEventIndex = (): number => {
		for (let i = 0; i < encounter.events.length; i++) {
			if (!completedEvents.includes(i)) {
				return i;
			}
		}
		return -1; // All events completed
	};

	const preferredEventIndex = getPreferredEventIndex();

	// const combinedTags = campaignManager.getTagsForEncounter(encounter);
	//const encounterBranches = encounter.branches;

	const clickEvent = (index: number, isPreferred: boolean) => {
		campaignManager.encounterEventSelected(index);
		completedEvents.push(index);
		if (isPreferred) {
			campaignManager.addEncounterPoints(1);
		}
		setCompletedEvents([...completedEvents]);
	};

	const getEventDescription = (eventType: ENCOUNTER_EVENT_TYPE) => {
		switch (eventType) {
			case ENCOUNTER_EVENT_TYPE.COMBAT:
				return "Combat";

			case ENCOUNTER_EVENT_TYPE.ROLEPLAY:
				return "Roleplaying";

			case ENCOUNTER_EVENT_TYPE.PUZZLE:
				return "Puzzle";
			case ENCOUNTER_EVENT_TYPE.DUNGEON:
				return "Dungeon Crawl";

			case ENCOUNTER_EVENT_TYPE.PERCEPTION:
				return "Perception Check";

			case ENCOUNTER_EVENT_TYPE.TOWN:
				return "Town Stuff";

			case ENCOUNTER_EVENT_TYPE.TRAVEL:
				return "Travel";

			case ENCOUNTER_EVENT_TYPE.CHALLENGE:
				return "Skill Challenge";

			case ENCOUNTER_EVENT_TYPE.TRAP:
				return "Trap";

			case ENCOUNTER_EVENT_TYPE.CHARACTER_BACKSTORY:
				return "Backstory";

			case ENCOUNTER_EVENT_TYPE.STORY:
				return "Story";

			case ENCOUNTER_EVENT_TYPE.EXPLORATION:
				return "Exploration";
		}
	};

	const eventsList = () => {
		if (encounter.events.length === 0) {
			return null;
		}
		return (
			<div className="encounter-eventlist">
				{encounter.events.map((event, index) => (
					<div
						key={index}
						className={`event-entry ${event.type} ${
							completedEvents.includes(index) ? "completed" : ""
						}`}
						onClick={() => clickEvent(index, index === preferredEventIndex)}
					>
						<div className="event-icon">
							<CImage imageId={uiManager.getEventIcon(event.type)} />
						</div>
						{index === preferredEventIndex && (
							<div className="preferred-event-score">
								<TooltipWrapper
									content="Gain a bonus point for choosing this event next."
									preservePositioning={true}
									translate={[270, -120]}
								>
									<DiceScore score={1} />
								</TooltipWrapper>
							</div>
						)}
						<div className="event-name">{event.name}</div>
						{/* <div className="event-description">{getEventDescription(event)}</div> */}

						{event.flavorText && (
							<div className="event-flavor-text">{event.flavorText}</div>
						)}
					</div>
				))}
			</div>
		);
	};

	const campaignDisplay = () => {
		return (
			<div className="session-control-campaign">
				<CampaignHeader
					encounterScore={encounterScore}
					campaignScore={campaignScore}
					onClick={() => setCampaignModalVisible(true)}
					className="header overview"
				/>
			</div>
		);
	};

	const getReadiness = (nbExpectedTags: number) => {
		if (nbExpectedTags === NB_EXPECTED_ENCOUNTER_TAGS) {
			return "WELL PREPARED";
		} else if (nbExpectedTags === NB_EXPECTED_ENCOUNTER_TAGS - 1) {
			return "READY";
		} else if (nbExpectedTags === NB_EXPECTED_ENCOUNTER_TAGS - 2) {
			return "INFORMED";
		}

		return "NOT READY";
	};
	const hackToBranch = (branch: any) => {
		// Show results and advance to branch
		tagManager.showNextEncounterModal();
	};

	const advanceEncounter = () => {
		// Show the next encounter selection modal instead of directly advancing
		tagManager.showNextEncounterModal();
	};

	const encounterTagSatisfied = (tag: Tag) =>
		!tagManager.getOutstandingEncounterTags().includes(tag);
	const nbEncounterTagsSatisfied = (encounter?.tags || []).filter((encTag) =>
		encounterTagSatisfied(encTag)
	).length;

	return (
		<div className="encounter-overview-container">
			<div className="encounter-icon-container">
				<CImage imageId={encounter.icon} />
			</div>
			{!campaignModalVisible && (
				<div className={`encounter-overview-nonmodal `}>
					{/* <div
						className={`encounter-overview-exposition-button`}
						onClick={() => campaignManager.handleExpositionClicked()}
					>
						<div className="encounter-overview-exposition-label">
							EXPOSITION
						</div>
						<div className="encounter-overview-exposition-effect">
							{campaignManager.getEncounterExpositionText()}
						</div>
					</div> */}
					<div className="encounter-overview-abilities-row">

						<Cbox
							title="Offer Snacks"
							theme="snacks"
							onClick={() => {
								if (remainingSnacks > 0) {
									campaignManager.useSnacks();
									// Update state directly for immediate UI feedback
									setRemainingSnacks(remainingSnacks - 1);
								}
							}}
						>
<div className="ability-container">
										
										<div className="ability-info">
											Increase Attitude 
										</div>
									</div>
								
									{campaignManager.hasHostingAbility(0) ? (
										<div className="ability-amount-label-container">
											<div className="ability-amount-label">
												{remainingSnacks}/
												{campaignManager.getMaxSnacks()}
											</div>
										</div>
									) : (
										<div className="lock-icon-container">
											<div className="lock-icon"></div>
										</div>
									)}
								</Cbox>						<Cbox
							title="Take a Break"
							theme="breaks"
							onClick={() => {
								if (remainingBreaks > 0) {
									campaignManager.useBreak();
									// Update state directly for immediate UI feedback
									setRemainingBreaks(remainingBreaks - 1);
								}
							}}
						>
							<div className="ability-container">
								
								<div className="ability-info">
									Reduce Exhaustion, Tension, Attention, Stress
								</div>
							</div>
							{campaignManager.hasHostingAbility(1) ? (
								<div className="ability-amount-label-container">
									<div className="ability-amount-label">
										{remainingBreaks}/
										{campaignManager.getMaxBreaks()}
									</div>
								</div>
							) : (
								<div className="lock-icon-container">
									<div className="lock-icon"></div>
								</div>
							)}
						</Cbox>

					</div>
					{/* Quest Debug Button */}
					{/* <div
						className="encounter-ability-button quest-debug"
						onClick={() => uiManager.showSelectQuestModal()}
					>
						<div className="ability-container">
							<div className="ability-label">Debug Quests</div>
							<div className="ability-info">Launch & manage quests</div>
						</div>
						<div className="quest-icon-container">
							<div className="quest-icon"></div>
						</div>
					</div> */}

					{campaignDisplay()}
					<div className="encounter-overview-contents">
						<div className="encounter-overview-info-column">
							<div className="encounter-overview-encounter-info">
								<div className="encounter-header">
									<MoreInfo
										callback={() =>
											uiManager.showBasicNotebook(
												NOTEBOOK_CATEGORY.ENCOUNTER,
												encounter
											)
										}
										classname="encounter"
									/>
									{encounter.name}
								</div>

								<div className="encounter-overview-subtitle small">
									ENCOUNTER TOPICS
								</div>
								<div className="encounter-overview-encounter-info-tags-row">
									{establishedEncounterTags.map((tag, index) => (
										<TagPill
											key={index}
											tag={tag}
											source={FlavorTagSource.ENCOUNTER}
											theme={currentLocationTheme as FlavorTagTheme}
											tooltipClassName={"info-tooltip"}
											tooltipText="Expectation fulfilled"
										/>
									))}
									{expectedEncounterTags.map((tag, index) => (
										<TagPill
											key={index}
											tag={tag}
											source={FlavorTagSource.ENCOUNTER_EXPECTED}
											theme={currentLocationTheme as FlavorTagTheme}
											tooltipClassName={"info-tooltip"}
											tooltipText="Players still expect to encounter this"
										/>
									))}
								</div>
							</div>
						</div>

						<div className="encounter-overview-info-column">
							<div className="encounter-overview-encounter-info">
								<div className="encounter-header">
									<MoreInfo
										callback={() =>
											uiManager.showBasicNotebook(
												NOTEBOOK_CATEGORY.ENCOUNTER,
												encounter
											)
										}
										classname="location"
									/>
									{encounter.location.name}
								</div>
								<div className="encounter-overview-encounter-info">
									{
										<FillBar
											totalSegments={locationTags.length}
											filledSegments={locationTagsEstablished.length}
											theme={currentLocationTheme}
											label={`LOCATION ELEMENTS`}
										/>
									}
									<div className="encounter-overview-encounter-info-tags-row">
										<div className="encounter-overview-encounter-info-tags">
											{sortedSetSceneTags.map((tag, index) => (
												// <div
												// 	key={index}
												// 	className={`text-pill  ${
												// 		tagManager.locationTagsCollected.includes(tag)
												// 			? "type-location"
												// 			: ""
												// 	}`}
												// >
												// 	{tag.name}
												// </div>
												<TagPill
													key={index}
													tag={tag}
													source={
														tagManager.locationTagsCollected.includes(tag)
															? FlavorTagSource.LOCATION
															: FlavorTagSource.NONE
													}
													theme={currentLocationTheme as FlavorTagTheme}
													tooltipText={
														tagManager.locationTagsCollected.includes(tag)
															? "This has been discussed and established"
															: "This part of the location has not yet been described"
													}
												/>
											))}
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
					<div className="encounter-overview-events-container">
						<div className="encounter-overview-event-header">
							ENCOUNTER EVENTS
						</div>
						<div className="encounter-overview-events-row">
							<div className="encounter-overview-legend">
								{uniqueEventTypes.map((event, index) => (
									<div key={index} className="event-legend-entry">
										<div className="event-icon">
											<CImage imageId={uiManager.getEventIcon(event)} />
										</div>

										<div className="event-name">
											{getEventDescription(event)}
										</div>
										{/* <div className="event-description">{getEventDescription(event)}</div> */}
									</div>
								))}

								<div className="event-legend-entry">
									<div className="preferred-event-score">
										<DiceScore score={1} />
									</div>
									<div className="event-name">Maxim's Choice</div>
								</div>
							</div>
							{eventsList()}
						</div>
					</div>
					{encounter.branches && encounter.branches.length > 0 && (
						<div className="encounter-overview-bottom-container">
							<div className="encounter-overview-upcoming-branches-container">
								<div className={`encounter-overview-upcoming-branches-header`}>
									UPCOMING ENCOUNTERS
								</div>
								<div className="encounter-overview-upcoming-upcoming-branches">
									{encounter.branches.map((branch, index) => (
										<div
											key={index}
											className={`encounter-overview-upcoming-branch text-theme-${branch.location.themes[0]}`}
										>
											<div className="encounter-overview-upcoming-branch-header">
												<div
													className="encounter-overview-upcoming-travel-header-text encounter"
													onClick={() => hackToBranch(branch)}
												>
													{branch.name}
												</div>
												<div className="encounter-overview-upcoming-travel-header-text">
													{branch.location.name}
												</div>
											</div>
											<FillBar
												totalSegments={4}
												filledSegments={Math.min(
													4,
													tagManager.encounterTagsCollected[branch.name].length
												)}
												theme={branch.location.themes[0]}
												label={branch.name}
											/>

											<div className="encounter-overview-upcoming-branch-tags">
												{(() => {
													const expectedTags =
														tagManager.encounterTagsExpected[branch.name];
													const collectedTags =
														tagManager.encounterTagsCollected[branch.name] ||
														[];
													//    const authoredTags = tagManager.encounterTagsExpected(branch.name).filter((tag) => !expectedTags.includes(tag));
													const totalSlots =
														NB_EXPECTED_ENCOUNTER_TAGS - expectedTags.length;
													const fillerTags = Array(totalSlots).fill({
														name: "?",
													});
													return [
														//     ...authoredTags.map((tag, index) => (
														//     <TagPill
														//       key={`authored-${index}`}
														//       tag={tag}
														//       source={FlavorTagSource.FORESHADOWING_EXPECTED}
														//       theme={branch.location.themes[0] as FlavorTagTheme}
														//       tooltipClassName={"info-tooltip"}
														//       tooltipText="This topic can still be exposed to the players to inform them of the upcoming encounter"
														//     />
														//   )),
														...collectedTags.map((tag, index) => (
															<TagPill
																key={`collected-${index}`}
																tag={tag}
																source={FlavorTagSource.FORESHADOWING}
																theme={
																	branch.location.themes[0] as FlavorTagTheme
																}
																tooltipClassName={"info-tooltip"}
																tooltipText="Players have been led to expect this if/when they get to this encounter."
															/>
														)),
														...expectedTags
															.filter(
																(tag) =>
																	!collectedTags.some(
																		(collected) => collected.name === tag.name
																	)
															)
															.map((tag, index) => (
																<TagPill
																	key={`expected-tag-${index}`}
																	tag={tag}
																	source={
																		FlavorTagSource.FORESHADOWING_EXPECTED
																	}
																	theme={
																		branch.location.themes[0] as FlavorTagTheme
																	}
																	tooltipClassName={"info-tooltip"}
																	tooltipText="This topic can still be exposed to the players to inform them of the upcoming encounter"
																/>
															)),
														...fillerTags.map((tag, index) => (
															<TagPill
																key={`filler-${index}`}
																tag={tag}
																source={FlavorTagSource.NONE}
																theme={
																	branch.location.themes[0] as FlavorTagTheme
																}
																tooltipClassName={"info-tooltip"}
																tooltipText="This topic is yet to be determined. Opportunities to choose a topic may arise."
															/>
														)),
													];
												})()}
											</div>
											<div className="encounter-overview-upcoming-footer">
												<div className="encounter-overview-upcoming-travel-footer-text">
													{getReadiness(
														tagManager.encounterTagsCollected[branch.name]
															.length
													)}
												</div>
											</div>
										</div>
									))}
								</div>
							</div>
							<div className="encounter-overview-advance-container">
								<div className="encounter-overview-advance-text">
									ADVANCE ENCOUNTER
								</div>
								<div
									className="encounter-overview-advance-button"
									onClick={advanceEncounter}
								>
									<div className="encounter-overview-advance-button-text">
										PLAYERS CHOOSE
									</div>
									<div className="encounter-overview-advance-info">
										{campaignManager.getEncounterAdvanceReadinessText()}
									</div>
								</div>
							</div>
						</div>
					)}
				</div>
			)}

			{campaignModalVisible && (
				<CampaignDisplay
					campaign={campaignManager.getCurrentCampaign()}
					collapsed={false}
					onAccept={() => setCampaignModalVisible(false)}
					acceptText="Close"
				/>
			)}
		</div>
	);
};

export default EncounterOverview;
