import React, { useState } from 'react';
import { ChatRuleEngine } from '../services/chatRuleEngine';
import './ChatRuleTestPanel.scss';

interface ChatRuleTestPanelProps {
  onClose: () => void;
}

export const ChatRuleTestPanel: React.FC<ChatRuleTestPanelProps> = ({ onClose }) => {
  const [testContext, setTestContext] = useState({
    speakerType: 'character' as 'player' | 'character' | 'dm',
    playerAttitude: 5,
    mood: 'neutral' as 'positive' | 'negative' | 'neutral',
    theme: 'adventure' as string
  });
  const [testResults, setTestResults] = useState<string[]>([]);
  const [debugMode, setDebugMode] = useState(true);

  const runTest = () => {
    const ruleEngine = new ChatRuleEngine();
    ruleEngine.setDebugMode(debugMode);
    const applicableRules: any = [];
    
    const results = applicableRules.map((rule: any) => 
      `${rule.name}: ${rule.action.instruction}`
    );
    
    setTestResults(results);
  };

  const resetRules = () => {
    const ruleEngine = new ChatRuleEngine();
    ruleEngine.setDebugMode(debugMode);
    const allRules: any = [];
    setTestResults([`Rules loaded: ${allRules.length} total rules`]);
  };

  return (
    <div className="chat-rule-test-panel">
      <div className="panel-header">
        <h3>Chat Rule Test Panel</h3>
        <button onClick={onClose} className="close-btn"></button>
      </div>
      
      <div className="test-controls">
        <div className="control-group">
          <label>Speaker Type:</label>
          <select 
            value={testContext.speakerType} 
            onChange={(e) => setTestContext({...testContext, speakerType: e.target.value as any})}
          >
            <option value="player">Player</option>
            <option value="character">Character</option>
            <option value="dm">DM</option>
          </select>
        </div>
        
        <div className="control-group">
          <label>Player Attitude (1-10):</label>
          <input 
            type="range" 
            min="1" 
            max="10" 
            value={testContext.playerAttitude}
            onChange={(e) => setTestContext({...testContext, playerAttitude: parseInt(e.target.value)})}
          />
          <span>{testContext.playerAttitude}</span>
        </div>
        
        <div className="control-group">
          <label>Mood:</label>
          <select 
            value={testContext.mood} 
            onChange={(e) => setTestContext({...testContext, mood: e.target.value as any})}
          >
            <option value="positive">Positive</option>
            <option value="negative">Negative</option>
            <option value="neutral">Neutral</option>
          </select>
        </div>
        
        <div className="control-group">
          <label>Theme:</label>
          <select 
            value={testContext.theme} 
            onChange={(e) => setTestContext({...testContext, theme: e.target.value})}
          >
            <option value="adventure">Adventure</option>
            <option value="combat">Combat</option>
            <option value="social">Social</option>
            <option value="mystery">Mystery</option>
            <option value="exploration">Exploration</option>
          </select>
        </div>
        
        <div className="control-group">
          <label>Debug Mode:</label>
          <input 
            type="checkbox" 
            checked={debugMode}
            onChange={(e) => setDebugMode(e.target.checked)}
          />
          <span>{debugMode ? 'Enabled' : 'Disabled'}</span>
        </div>
        
        <div className="button-group">
          <button onClick={runTest} className="test-btn">Test Rules</button>
          <button onClick={resetRules} className="reset-btn">Reset Rules</button>
        </div>
      </div>
      
      <div className="test-results">
        <h4>Active Rule Effects:</h4>
        {testResults.length > 0 ? (
          <ul>
            {testResults.map((result, index) => (
              <li key={index}>{result}</li>
            ))}
          </ul>
        ) : (
          <p>Run a test to see which rules apply...</p>
        )}
      </div>
      
      <div className="rule-examples">
        <h4>Example Rule Behaviors:</h4>
        <ul>
          <li><strong>Third Person Speech:</strong> Characters sometimes speak in third person when attitude is high</li>
          <li><strong>Player Narration:</strong> Players describe actions narratively based on mood</li>
          <li><strong>Attitude Responses:</strong> High attitude = enthusiastic, Low attitude = reluctant</li>
          <li><strong>Mood Adaptation:</strong> Positive mood adds optimistic language</li>
          <li><strong>Theme Matching:</strong> Combat themes use action words, social themes use diplomatic language</li>
        </ul>
      </div>
    </div>
  );
};
