import React, { ReactNode } from 'react';
import './encounterHeader.scss';

interface EncounterHeaderProps {
  title: string;
  description?: string;
  leftContent?: ReactNode;
  rightContent?: ReactNode;
  centerContent?: ReactNode;
  instructions?: boolean[];
  childClass?: string;
}

const EncounterHeader: React.FC<EncounterHeaderProps> = ({
  title,
  description,
  leftContent,
  rightContent,
  centerContent,
  instructions = [false,false],
  childClass = '',
}) => {
  return (
    <div className="encounter-header">
      <div className="encounter-header-content">
        {leftContent && (
          <div className={`encounter-header-left ${instructions[0] ? 'with-instructions' : ''}  ${childClass ? childClass : '' }  `}>
            {leftContent}
          </div>
        )}
        
        <div className="encounter-header-center">
          {centerContent ? (
            centerContent
          ) : (
            <>
              <div className="encounter-header-title">{title}</div>
              {description && (
                <div className="encounter-header-description">{description}</div>
              )}
            </>
          )}
        </div>

        {rightContent && (
          <div className={`encounter-header-right ${instructions[1] ? 'with-instructions' : ''} ${childClass ? childClass : '' }`}>
            {rightContent}
          </div>
        )}
      </div>
    </div>
  );
};

export default EncounterHeader;
