import React, { useState, useEffect, useCallback } from "react";
import {
  Encounter,
  Player,
  EncounterFlavorTag,
  FlavorTagSource,
  EffectData,
} from "../../models";
import {
  useAppManager,
  useCampaignManager,
  usePlayerManager,
  useUiManager,
} from "../managerContexts";
import EncounterHeader from "./encounterHeader";
import FlavorTagPill from "../UI/FlavorTagPill";
import DiceScore from "../UI/DiceScore";
import Cimage from "../UI/cimage";
import "./gameRoom.scss";
import "./gameEncounterPuzzle.scss";
import TooltipWrapper from "../UI/TooltipWrapper";

// Configuration constants
const SHORT_PAUSE_MS = 1000;
const TINY_PAUSE_MS = 250; // Used for small delays in animations
const TENSION_SQUARES_ENABLED = false;
const TENSION_SQUARES_CHANCE = 0.4;
const NO_HINTS_SCORE = 6;

type PuzzleDifficulty = "simple" | "clever" | "tricky";
type PuzzlePhase = "setup" | "instructions" | "solving" | "player-stopped";
type GridSquareType =
  | "answer"
  | "hint"
  | "frustration"
  | "exposed"
  | "unexposed"
  | "directionless";
type TensionLevel = "boring" | "low" | "high" | "crisis";
type HintDirection = "up" | "down" | "left" | "right";

interface GameEncounterPuzzleProps {
  encounter: Encounter;
}

interface GridCell {
  type: GridSquareType;
  tag?: EncounterFlavorTag;
  hintDirection?: HintDirection;
  playerIcon?: Player;
  visited: boolean;
  pinned: boolean; // Stays constant between solving attempts
  scoreReward: number; // 0-4 points
  tensionLevel?: TensionLevel;
  isAnswer: boolean;
  isFrustration: boolean;
  isTemporaryFrustration: boolean;
  isFlashing?: boolean;
}

const GameEncounterPuzzle: React.FC<GameEncounterPuzzleProps> = ({
  encounter,
}) => {
  // Phase management
  const [currentPhase, setCurrentPhase] = useState<PuzzlePhase>("setup");

  // Setup phase state
  const [difficulty, setDifficulty] = useState<PuzzleDifficulty>("simple");
  const [hintsCount, setHintsCount] = useState(3);

  // Grid configuration based on difficulty
  const getGridConfig = (diff: PuzzleDifficulty) => {
    switch (diff) {
      case "simple":
        return { rows: 4, cols: 4, reward: 2, extraFrustration: 0 };
      case "clever":
        return { rows: 4, cols: 5, reward: 4, extraFrustration: 1 };
      case "tricky":
        return { rows: 5, cols: 5, reward: 6, extraFrustration: 2 };
    }
  };

  // Grid state
  const [grid, setGrid] = useState<GridCell[][]>([]);
  const [selectedPlayer, setSelectedPlayer] = useState<Player | null>(null);
  const [currentScore, setCurrentScore] = useState(0);
  const [soCloseUsed, setSoCloseUsed] = useState(false);
  const [soCloseCount, setSoCloseCount] = useState(0);
  const [highlightedCells, setHighlightedCells] = useState<
    { row: number; col: number }[]
  >([]);
  const [playerPosition, setPlayerPosition] = useState<{
    row: number;
    col: number;
  } | null>(null);
  const [usedPlayers, setUsedPlayers] = useState<Set<number>>(new Set());
  const [isAnimating, setIsAnimating] = useState(false);
  const [buttonsEnabled, setButtonsEnabled] = useState(true);
  const [currentStep, setCurrentStep] = useState<string>("");
  const [adjustHintsMode, setAdjustHintsMode] = useState(false);
  const [floatingIconPosition, setFloatingIconPosition] = useState<{
    x: number;
    y: number;
    targetRow: number;
    targetCol: number;
    player: Player;
    isAnimating: boolean;
  } | null>(null);
  const [currentStepPlayerIcon, setCurrentStepPlayerIcon] = useState<{
    player: Player;
    isVisible: boolean;
  } | null>(null);
  const [gridPlayerIcon, setGridPlayerIcon] = useState<{
    player: Player;
    row: number;
    col: number;
    isVisible: boolean;
  } | null>(null);
  // Store the generated hint tags to maintain consistency between setup and grid
  const [generatedHintTags, setGeneratedHintTags] = useState<any[]>([]);

  const appManager = useAppManager();
  const campaignManager = useCampaignManager();
  const playerManager = usePlayerManager();
  const uiManager = useUiManager();
  const tagManager = appManager.getTagManager();
  const partyManager = appManager.getPartyManager();
  const chatManager = appManager.getChatManager();

  const players = playerManager.getPlayers();

  // Helper functions
  const getSoCloseLimit = (): number => {
    // Eventually this will come from campaignManager
    // For now, hardcoded to 2
    return 2;
  };

  const getSoCloseRemaining = (): number => {
    return getSoCloseLimit() - soCloseCount;
  };

  const hasHintSquares = (): boolean => {
    for (let row = 0; row < grid.length; row++) {
      for (let col = 0; col < grid[row].length; col++) {
        if (grid[row][col].type === "hint") {
          return true;
        }
      }
    }
    return false;
  };

  const calculateScore = (
    hints: number,
    isNoHintsAvailable: boolean = false,
    puzzleDifficulty: PuzzleDifficulty = "tricky"
  ): number => {
    // if (hints === 0 && isNoHintsAvailable) return NO_HINTS_SCORE;
    // let score = hints >= 10 ? 1 : 1 + Math.floor((10 - hints) / 1) * 0.5;
    // if (hints <= 1) score += hints === 0 ? 5 : 2;

    // Apply difficulty multiplier
    switch (puzzleDifficulty) {
      case "simple":
        // score *= 0.5; // Half the score
        return 4;

      case "clever":
        //   score *= 0.75; // Three-quarters the score
        return 6;

      case "tricky":
        return 8;
      // Keep current values (multiplier of 1.0)
    }
    return 1;
    //  return Math.round(score * 10) / 10; // Round to 1 decimal place
  };

  const calculateAbandonmentCost = (
    currentScore: number
  ): { attitude: number; tension: number } => {
    console.log("calculateAbandonmentCost called with score:", currentScore);
    return {
      attitude: 1,
      tension: 20,
    };
  };

  const getValidHintDirections = (
    row: number,
    col: number,
    gridConfig: ReturnType<typeof getGridConfig>
  ): HintDirection[] => {
    const directions: HintDirection[] = [];
    if (row > 0) directions.push("up");
    if (row < gridConfig.rows - 1) directions.push("down");
    if (col > 0) directions.push("left");
    if (col < gridConfig.cols - 1) directions.push("right");
    return directions;
  };

  const getNextDirection = (
    current: HintDirection,
    valid: HintDirection[]
  ): HintDirection => {
    const order: HintDirection[] = ["up", "right", "down", "left"];
    const currentIndex = order.indexOf(current);

    for (let i = 1; i <= 4; i++) {
      const nextIndex = (currentIndex + i) % 4;
      const nextDir = order[nextIndex];
      if (valid.includes(nextDir)) return nextDir;
    }
    return current; // Fallback
  };

  const getTensionDisplay = (level: TensionLevel): string => {
    switch (level) {
      case "boring":
        return "--";
      case "low":
        return "-";
      case "high":
        return "+";
      case "crisis":
        return "++";
    }
  };

  const getTensionValue = (level: TensionLevel): number => {
    switch (level) {
      case "boring":
        return -5;
      case "low":
        return -2;
      case "high":
        return 2;
      case "crisis":
        return 5;
    }
  };

  const getTensionColor = (level: TensionLevel): string => {
    return level === "boring" || level === "low" ? "black" : "orange";
  };

  // Helper function to generate available hint tags (shared between setup and grid generation)
  const getAvailableHintTags = useCallback(() => {
    const allCollectedTags = tagManager.getCollectedTags();
    const currentLocationTagsCollected = tagManager.locationTagsCollected;

    // Filter out location tags from collected tags to get available hint tags
    const availableHintTags = allCollectedTags.filter(
      (tag) =>
        !currentLocationTagsCollected.some(
          (locTag: any) => locTag.name === tag.name
        )
    );

    // Shuffle and return the tags (we'll slice as needed)
    return [...availableHintTags].sort(() => Math.random() - 0.5);
  }, [tagManager]);

  // Grid generation functions
  const createEmptyGrid = (rows: number, cols: number): GridCell[][] => {
    const newGrid: GridCell[][] = [];
    for (let row = 0; row < rows; row++) {
      newGrid[row] = [];
      for (let col = 0; col < cols; col++) {
        newGrid[row][col] = {
          type: "unexposed",
          visited: false,
          pinned: false,
          scoreReward: 0,
          isAnswer: false,
          isFrustration: false,
          isTemporaryFrustration: false,
        };
      }
    }
    return newGrid;
  };

  const generatePinnedGrid = useCallback((): GridCell[][] => {
    const gridConfig = getGridConfig(difficulty);
    const newGrid = createEmptyGrid(gridConfig.rows, gridConfig.cols);
    const totalCells = gridConfig.rows * gridConfig.cols;
    const usedPositions = new Set<string>();

    // Get current values
    const currentLocationTags =
      campaignManager.currentEncounter?.location.tags || [];
    const outstandingLocationTags = tagManager.getOutstandingLocationTags();
    const currentAnswerTag = campaignManager.getCurrentEvent()?.tags?.[0] ?? {
      name: "MAXIM Fix Me",
      description: "Placeholder answer tag",
      theme: "neutral",
    };
    const currentAnswerTags = [currentAnswerTag];

    // Helper for valid hint directions
    const getValidHintDirections = (
      row: number,
      col: number
    ): HintDirection[] => {
      const directions: HintDirection[] = [];
      if (row > 0) directions.push("up");
      if (row < gridConfig.rows - 1) directions.push("down");
      if (col > 0) directions.push("left");
      if (col < gridConfig.cols - 1) directions.push("right");
      return directions;
    };

    // Helper to get random empty position
    const getRandomEmptyPosition = () => {
      const emptyCells: { row: number; col: number }[] = [];
      for (let row = 0; row < gridConfig.rows; row++) {
        for (let col = 0; col < gridConfig.cols; col++) {
          const key = `${row}-${col}`;
          if (!usedPositions.has(key)) {
            emptyCells.push({ row, col });
          }
        }
      }
      if (emptyCells.length === 0) return null;
      return emptyCells[Math.floor(Math.random() * emptyCells.length)];
    };

    // 1. Add answer squares (from encounter.tags)
    currentAnswerTags.forEach((tag) => {
      const pos = getRandomEmptyPosition();
      if (pos) {
        const flavorTag = tag.name
          ? tagManager.tagToEncounterFlavorTag(
              tag,
              FlavorTagSource.PUZZLE_ANSWER
            )
          : {
              tag: {
                name: "MAXIM Fix Me",
                description: "Placeholder answer tag",
                theme: "neutral",
              },
              source: "ENCOUNTER" as any,
              theme: tagManager.getTheme(),
            };
        newGrid[pos.row][pos.col] = {
          type: "answer",
          tag: flavorTag,
          visited: false,
          pinned: true,
          scoreReward: 0,
          isAnswer: true,
          isFrustration: false,
          isTemporaryFrustration: false,
        };
        usedPositions.add(`${pos.row}-${pos.col}`);
      }
    });

    // 2. Add location tags (exposed ones become hints, unexposed stay as location tags)
    currentLocationTags.forEach((tag) => {
      const pos = getRandomEmptyPosition();
      if (pos) {
        const isExposed = !outstandingLocationTags.some(
          (collected) => collected.name === tag.name
        );
        const validDirections = getValidHintDirections(pos.row, pos.col);
        const randomDirection =
          validDirections[Math.floor(Math.random() * validDirections.length)];

        const flavorTag = tagManager.tagToEncounterFlavorTag(
          tag,
          FlavorTagSource.LOCATION
        );
        newGrid[pos.row][pos.col] = {
          type: isExposed ? "hint" : "unexposed",
          tag: flavorTag,
          hintDirection: isExposed ? randomDirection : undefined,
          visited: false,
          pinned: true,
          scoreReward: 0,
          isAnswer: false,
          isFrustration: false,
          isTemporaryFrustration: false,
        };
        usedPositions.add(`${pos.row}-${pos.col}`);
      }
    });

    // 3. Add hint squares (using stored hint tags for consistency)
    const hintsToAdd = Math.min(hintsCount, generatedHintTags.length);

    for (let i = 0; i < hintsToAdd; i++) {
      const pos = getRandomEmptyPosition();
      if (pos) {
        const validDirections = getValidHintDirections(pos.row, pos.col);
        const randomDirection =
          validDirections[Math.floor(Math.random() * validDirections.length)];
        const hintTag = tagManager.tagToEncounterFlavorTag(
          generatedHintTags[i],
          FlavorTagSource.PUZZLE
        );

        newGrid[pos.row][pos.col] = {
          type: "hint",
          tag: hintTag,
          hintDirection: randomDirection,
          visited: false,
          pinned: true,
          scoreReward: 0,
          isAnswer: false,
          isFrustration: false,
          isTemporaryFrustration: false,
        };
        usedPositions.add(`${pos.row}-${pos.col}`);
      }
    }

    // 4. Add frustration squares
    const baseFrustration = Math.floor(Math.random() * 4) + 1; // 1-4
    const totalFrustration = baseFrustration + gridConfig.extraFrustration;

    for (let i = 0; i < totalFrustration; i++) {
      const pos = getRandomEmptyPosition();
      if (pos) {
        newGrid[pos.row][pos.col] = {
          type: "frustration",
          visited: false,
          pinned: true,
          scoreReward: 0,
          isAnswer: false,
          isFrustration: true,
          isTemporaryFrustration: false,
        };
        usedPositions.add(`${pos.row}-${pos.col}`);
      }
    }

    // 5. Add score rewards with guaranteed distribution
    const scoreRewardCoverage = 0.5; // 50% of grid squares should have score rewards
    const allPositions: { row: number; col: number }[] = [];
    for (let row = 0; row < gridConfig.rows; row++) {
      for (let col = 0; col < gridConfig.cols; col++) {
        allPositions.push({ row, col });
      }
    }

    const targetScoreSquares = Math.floor(totalCells * scoreRewardCoverage);
    const shuffledPositions = allPositions
      .sort(() => Math.random() - 0.5)
      .filter((s) => !newGrid[s.row][s.col].isFrustration);

    // Define guaranteed rewards based on grid size
    const guaranteedRewards: number[] = [];

    // Always have one big prize (4 points for largest puzzle, 3 for others)
    const bigPrize = difficulty === "tricky" ? 6 : 4;
    guaranteedRewards.push(bigPrize);

    // At least 2 three-point rewards (but only if we have big prize of 4)
    if (difficulty === "tricky") {
      guaranteedRewards.push(3, 3);
    }

    // At least 3 two-point rewards
    guaranteedRewards.push(2, 2, 2);

    // Fill remaining squares with 1-point rewards up to target coverage
    const remainingSquares = targetScoreSquares - guaranteedRewards.length;
    for (let i = 0; i < remainingSquares; i++) {
      guaranteedRewards.push(1);
    }

    // Apply the rewards to random positions
    for (
      let i = 0;
      i < guaranteedRewards.length && i < shuffledPositions.length;
      i++
    ) {
      const pos = shuffledPositions[i];
      newGrid[pos.row][pos.col].scoreReward = guaranteedRewards[i];
    }
    // be sure the answer has a big prize:
    const answerPos = allPositions.find(
      (pos) => newGrid[pos.row][pos.col].isAnswer
    );
    if (answerPos) {
      newGrid[answerPos.row][answerPos.col].scoreReward = bigPrize;
    }

    // 6. Add tension levels to some squares if enabled
    if (TENSION_SQUARES_ENABLED) {
      const tensionLevels: TensionLevel[] = ["boring", "low", "high", "crisis"];

      allPositions.forEach((pos) => {
        const cell = newGrid[pos.row][pos.col];
        if (
          !cell.isAnswer &&
          !cell.isFrustration &&
          Math.random() < TENSION_SQUARES_CHANCE
        ) {
          cell.tensionLevel =
            tensionLevels[Math.floor(Math.random() * tensionLevels.length)];
        }
      });
    }

    return newGrid;
  }, [difficulty, hintsCount, campaignManager, tagManager, generatedHintTags]);

  const generateNonPinnedCells = (baseGrid: GridCell[][]): GridCell[][] => {
    const gridConfig = getGridConfig(difficulty);
    const newGrid = baseGrid.map((row) => row.map((cell) => ({ ...cell })));

    // Find non-pinned positions
    const nonPinnedPositions: { row: number; col: number }[] = [];
    for (let row = 0; row < gridConfig.rows; row++) {
      for (let col = 0; col < gridConfig.cols; col++) {
        if (!newGrid[row][col].pinned) {
          nonPinnedPositions.push({ row, col });
        }
      }
    }

    // Generate relevant tags for non-pinned positions
    if (nonPinnedPositions.length > 0) {
      const relevantTags = tagManager.generateRelevantFlavorTags(
        nonPinnedPositions.length,
        [
          FlavorTagSource.ENCOUNTER,
          FlavorTagSource.PUZZLE_ANSWER,
          FlavorTagSource.LOCATION,
          FlavorTagSource.QUEST,
          FlavorTagSource.QUEST_TERMINAL,
          FlavorTagSource.FORESHADOWING_RANDOM,
        ]
      );

      nonPinnedPositions.forEach((pos, index) => {
        if (index < relevantTags.length) {
          newGrid[pos.row][pos.col] = {
            type: "unexposed",
            tag: relevantTags[index],
            visited: false,
            pinned: false,
            scoreReward: newGrid[pos.row][pos.col].scoreReward, // Keep existing score rewards
            tensionLevel: newGrid[pos.row][pos.col].tensionLevel, // Keep existing tension
            isAnswer: false,
            isFrustration: false,
            isTemporaryFrustration: false,
          };
        }
      });
    }

    return newGrid;
  };

  // Initialize pinned grid on phase change
  useEffect(() => {
    if (currentPhase === "instructions") {
      const pinnedGrid = generatePinnedGrid();
      setGrid(pinnedGrid);
      setCurrentStep(
        `The players are considering what they know so far. You will need to call on one of them to start.`
      );
    }
  }, [currentPhase, difficulty, hintsCount, generatePinnedGrid]);

  // Generate and store hint tags when setup changes
  useEffect(() => {
    const hintTags = getAvailableHintTags();
    setGeneratedHintTags(hintTags);
  }, [difficulty, hintsCount, tagManager, getAvailableHintTags]);

  // Ensure hints count doesn't exceed available collected tags
  useEffect(() => {
    const allCollectedTags = tagManager.getCollectedTags();
    const currentLocationTagsCollected = tagManager.locationTagsCollected;
    const maxAvailableHints =
      allCollectedTags.length - currentLocationTagsCollected.length;

    if (hintsCount > maxAvailableHints) {
      setHintsCount(Math.max(0, maxAvailableHints));
    }
  }, [difficulty, tagManager, hintsCount]);

  // Phase handlers
  const handlePresentPuzzle = () => {
    partyManager.exhaustParty(getExhaustionCost());
    setCurrentPhase("instructions");
  };

  const handlePlayersAttempt = () => {
    // Generate full grid with all cells populated when starting solving phase
    const fullGrid = generateNonPinnedCells(grid);
    setGrid(fullGrid);
    setCurrentPhase("solving");
  };

  const handleHintDirectionChange = (row: number, col: number) => {
    if (
      currentPhase !== "instructions" &&
      !(currentPhase === "solving" && !selectedPlayer)
    )
      return;

    const gridConfig = getGridConfig(difficulty);
    const validDirections = getValidHintDirections(row, col, gridConfig);
    const currentDirection = grid[row][col].hintDirection;

    if (currentDirection) {
      const nextDirection = getNextDirection(currentDirection, validDirections);

      setGrid((prevGrid) => {
        const newGrid = [...prevGrid];
        newGrid[row][col] = {
          ...newGrid[row][col],
          hintDirection: nextDirection,
        };
        return newGrid;
      });
    }
  };

  const handlePlayerSelect = (player: Player) => {
    if (currentPhase !== "solving" || usedPlayers.has(player.id) || isAnimating)
      return;

    setSelectedPlayer(player);
    setSoCloseUsed(false);
    setPlayerPosition(null);
    setIsAnimating(true);
    setButtonsEnabled(false); // Disable buttons during animation
    setCurrentStep(
      `${player.name} looks through their notes fom earlier in the session...`
    );

    // Show player icon in current-step container
    setCurrentStepPlayerIcon({
      player: player,
      isVisible: true,
    });

    // Mark player as used
    setUsedPlayers((prev) => new Set([...prev, player.id]));

    // Start floating icon animation
    //startFloatingIconAnimation(player);

    // // Clear temporary frustration and visited states
    // setGrid((prevGrid) => {
    //   const newGrid = prevGrid.map((row) =>
    //     row.map((cell) => ({
    //       ...cell,
    //       visited: false,
    //       isTemporaryFrustration: false,
    //       playerIcon: undefined,
    //     }))
    //   );
    //   return newGrid;
    // });

    // Clear any existing grid player icon
    setGridPlayerIcon(null);

    // Start the solving attempt directly with current grid
    setTimeout(() => {
      // // choose one of five random strings here
      // const randomIdeas = [
      //   `${player.name} has an idea...`,
      //   `${player.name} points to something they wrote on their notes from earlier`,
      //   `${player.name} considers thoughtfully for a minute and makes a totally random guess`,

      // ];

      // setCurrentStep(randomIdeas[Math.floor(Math.random() * randomIdeas.length)]);
      startSolvingAttempt(player, grid);
    }, TINY_PAUSE_MS);
  };

  const startFloatingIconAnimation = (player: Player) => {
    // Get player button position (this would need to be calculated based on the player button)
    // For now, we'll start from a fixed position and animate to grid
    const startX = 50; // This should be calculated from player button position
    const startY = 200;

    setFloatingIconPosition({
      x: startX,
      y: startY,
      targetRow: -1,
      targetCol: -1,
      player: player,
      isAnimating: true,
    });

    // After a short delay, move to grid center
    setTimeout(() => {
      setFloatingIconPosition((prev) =>
        prev
          ? {
              ...prev,
              x: 500, // This should be calculated based on grid position
              y: 300,
            }
          : null
      );
    }, 100);

    // Clear the floating icon after animation
    setTimeout(() => {
      setFloatingIconPosition(null);
    }, 1500);
  };

  const startSolvingAttempt = (player: Player, currentGrid: GridCell[][]) => {
    const gridConfig = getGridConfig(difficulty);

    // Find a random non-frustration square
    const validSquares: { row: number; col: number }[] = [];
    for (let row = 0; row < gridConfig.rows; row++) {
      for (let col = 0; col < gridConfig.cols; col++) {
        const cell = currentGrid[row][col];
        if (!cell.isFrustration && !cell.isTemporaryFrustration) {
          validSquares.push({ row, col });
        }
      }
    }

    if (validSquares.length > 0) {
      const randomSquare =
        validSquares[Math.floor(Math.random() * validSquares.length)];

      setTimeout(() => {
        activateGridSquare(randomSquare.row, randomSquare.col, player);
      }, SHORT_PAUSE_MS);
    }
  };

  const activateGridSquare = (
    row: number,
    col: number,
    player: Player,
    shouldAnimate: boolean = true
  ) => {
    const cell = grid[row][col];

    // Set grid player icon to fade in at this position
    setGridPlayerIcon({
      player: player,
      row: row,
      col: col,
      isVisible: true,
    });

    // Animate floating icon to this grid square
    if (shouldAnimate) {
      animateIconToGridSquare(row, col, player);
    }

    if (shouldAnimate) {
      // 5 Random options:
      const randomMessages = [
        `${player.name} ponders ${cell.tag?.tag.name || "the puzzle"}`,
        `${player.name} examines the possibility of ${
          cell.tag?.tag.name || "a bug (this is a bug...really)"
        }`,
        `${player.name} looks into ${cell.tag?.tag.name || "the puzzle"}`,
        `${player.name} has an idea...`,
        `${player.name} points to something they wrote on their notes from earlier`,
        `${player.name} considers thoughtfully for a minute and makes a totally random guess`,
      ];

      setCurrentStep(
        randomMessages[Math.floor(Math.random() * randomMessages.length)]
      );

      // Flash the grid square before activation
      setGrid((prevGrid) => {
        const newGrid = [...prevGrid];
        newGrid[row][col] = {
          ...newGrid[row][col],
          isFlashing: true,
        };
        return newGrid;
      });

      setTimeout(() => {
        // If this is a "So Close" activation, reset the soCloseUsed flag
        if (soCloseUsed) {
          setSoCloseUsed(false);
        }

        // Remove flash and mark as visited
        setGrid((prevGrid) => {
          const newGrid = [...prevGrid];
          newGrid[row][col] = {
            ...newGrid[row][col],
            visited: true,
            playerIcon: player,
            isTemporaryFrustration: true,
            isFlashing: false,
          };
          return newGrid;
        });

        setPlayerPosition({ row, col });

        // Handle different cell types after animation
        setTimeout(() => {
          processGridCellActivation(row, col, player, cell);
        }, SHORT_PAUSE_MS);
      }, SHORT_PAUSE_MS);
    } else {
      // Immediate activation without animation
      setGrid((prevGrid) => {
        const newGrid = [...prevGrid];
        newGrid[row][col] = {
          ...newGrid[row][col],
          visited: true,
          playerIcon: player,
          isTemporaryFrustration: true,
        };
        return newGrid;
      });

      setPlayerPosition({ row, col });
      processGridCellActivation(row, col, player, cell);
    }
  };

  const animateIconToGridSquare = (
    row: number,
    col: number,
    player: Player
  ) => {
    // Calculate grid square position (this would need to be calculated based on actual grid position)
    const gridSquareX = 500 + col * 195; // Approximate based on 155px + 40px gap
    const gridSquareY = 300 + row * 195;

    setFloatingIconPosition((prev) =>
      prev
        ? {
            ...prev,
            x: gridSquareX,
            y: gridSquareY,
            targetRow: row,
            targetCol: col,
          }
        : null
    );

    // Clear the floating icon after reaching target
    setTimeout(() => {
      setFloatingIconPosition(null);
    }, 1000);
  };

  const processGridCellActivation = (
    row: number,
    col: number,
    player: Player,
    cell: GridCell
  ) => {
    // Handle different cell types
    if (cell.type === "hint") {
      setCurrentStep(`${player.name} found a hint!`);
      handleHintActivation(row, col, player);
    } else if (cell.isFrustration) {
      setCurrentStep(`${player.name} is annoyed by the puzzle.`);
      handleFrustrationActivation(player);
    } else if (cell.isTemporaryFrustration) {
      setCurrentStep(
        `${player.name} already came to this conclusion earlier and is frustrated.`
      );
      handleFrustrationActivation(player);
    } else if (cell.isAnswer) {
      setCurrentStep(`${player.name} found the answer!`);
      handleAnswerActivation(player);
    } else if (cell.type === "unexposed") {
      setCurrentStep(`${player.name} considers a new topic!`);
      handleUnexposedTagActivation(row, col, player);
      setButtonsEnabled(true);
    } else if (cell.type === "exposed" || cell.type === "directionless") {
      setCurrentStep(`${player.name} needs to choose a think...`);
      handleDirectionlessActivation(row, col, player);
    } else {
      setCurrentStep(`${player.name} explores the square...`);
      setIsAnimating(false);
      // setButtonsEnabled(true);
    }

    // Handle score reward

    // Handle score reward
    if (cell.scoreReward > 0) {
      setCurrentScore((prev) => prev + cell.scoreReward);
      // Award points to campaign
      campaignManager.addEncounterPoints(cell.scoreReward);

      // Remove the score reward from the grid cell by updating state
      setGrid((prevGrid) => {
        const newGrid = [...prevGrid];
        newGrid[row][col] = {
          ...newGrid[row][col],
          scoreReward: 0, // Remove the score reward
        };
        return newGrid;
      });
      cell.scoreReward = 0; // Also reset the cell directly
    }

    // Handle tension
    if (cell.tensionLevel) {
      const tensionChange = getTensionValue(cell.tensionLevel);
      partyManager.addPartyTension(tensionChange);
    }
  };

  const handleHintActivation = (row: number, col: number, player: Player) => {
    const cell = grid[row][col];
    const direction = cell.hintDirection;

    if (direction) {
      const nextPos = getNextPosition(row, col, direction);
      if (nextPos) {
        // Add exhaustion for hint usage
        partyManager.exhaustParty(1);

        setCurrentStep(
          `Hint points ${direction}! ${player.name} is following the hint...`
        );

        setTimeout(() => {
          setHighlightedCells([nextPos]);
          setCurrentStep(`${player.name} is moving to the indicated square...`);

          setTimeout(() => {
            setHighlightedCells([]);
            // Mark current position as visited before moving
            setGrid((prevGrid) => {
              const newGrid = [...prevGrid];
              newGrid[row][col] = {
                ...newGrid[row][col],
                visited: true,
              };
              return newGrid;
            });

            // Update grid player icon position for the move
            setGridPlayerIcon((prev) =>
              prev
                ? {
                    ...prev,
                    row: nextPos.row,
                    col: nextPos.col,
                  }
                : null
            );

            activateGridSquare(nextPos.row, nextPos.col, player, true);
          }, SHORT_PAUSE_MS * 2);
        }, SHORT_PAUSE_MS);
      } else {
        setIsAnimating(false);
        setButtonsEnabled(true);
      }
    } else {
      setIsAnimating(false);
      setButtonsEnabled(true);
    }
  };

  const getNextPosition = (
    row: number,
    col: number,
    direction: HintDirection
  ): { row: number; col: number } | null => {
    const gridConfig = getGridConfig(difficulty);

    switch (direction) {
      case "up":
        return row > 0 ? { row: row - 1, col } : null;
      case "down":
        return row < gridConfig.rows - 1 ? { row: row + 1, col } : null;
      case "left":
        return col > 0 ? { row, col: col - 1 } : null;
      case "right":
        return col < gridConfig.cols - 1 ? { row, col: col + 1 } : null;
    }
  };

  const handleFrustrationActivation = (player: Player) => {
    // Apply effects
    partyManager.addEnjoyment(player, -1);
    partyManager.updateAttention(player, -1);
    const results: EffectData[] = [];
    results.push({
      object: player,
      effects: [
        { effect: "-1 Attention", class: "player-attention" },
        { effect: "-1 Attitude", class: "player-attitude" },
      ],
    });

    // Show modal using uiManager
    uiManager.showModal({
      title: `${player.name} became frustrated!`,
      type: "puzzle",
      effectData: results,
      options: [
        {
          id: "continue",
          title: "Continue",
          description: `${player.name} became frustrated!`,
          callback: () => {
            setIsAnimating(false);
            setButtonsEnabled(true);
            setCurrentPhase("player-stopped");
          },
        },
      ],
      onClose: () => {
        setIsAnimating(false);
        setButtonsEnabled(true);
        setCurrentPhase("player-stopped");
      },
    });
  };

  const handleAnswerActivation = (player: Player) => {
    const score = calculateScore(hintsCount, false, difficulty);
    // Apply effects
    campaignManager.addEncounterPoints(score);
    // Add player experience (implement in player manager)
    const results: EffectData[] = [];
    playerManager.addPlayerExp(player, 1);
    results.push({
      object: player,
      effects: [
        {
          effect: "+1 Experience",
          class: "player-exp",
        },
      ],
    });
    players.forEach((p) => {
      if (p.id !== player.id) {
        partyManager.addEnjoyment(p, 1);
        results.push({
          object: p,
          effects: [
            {
              effect: "+1 Attitude",
              class: "player-attitude",
            },
          ],
        });
      }
    });

    // Show modal using uiManager
    // uiManager.showModal({
    //   title: "Puzzle Solved!",
    //   type: "puzzle",
    //   options: [
    //     {
    //       id: "continue",
    //       title: "Return to Encounter",
    //       description: `Gained ${score} campaign score and experience!`,
    //       callback: () => {
    //         campaignManager.backToOverview();
    //       },
    //     },
    //   ],
    //   onClose: () => {
    //     campaignManager.backToOverview();
    //   },
    // });
    uiManager.showRewardModal(getGridConfig(difficulty).reward, true);

    setIsAnimating(false);
    setButtonsEnabled(true);
  };

  const handleUnexposedTagActivation = (
    row: number,
    col: number,
    player: Player
  ) => {
    const cell = grid[row][col];
    if (cell.tag) {
      // Expose the tag
      campaignManager.exposeTag(cell.tag);

      // Convert to directionless and pin
      setGrid((prevGrid) => {
        const newGrid = [...prevGrid];
        newGrid[row][col] = {
          ...newGrid[row][col],
          type: "directionless",
          pinned: true,
        };
        return newGrid;
      });

      let playerActionString = `${player.name} investigated ${cell.tag.tag.name}`;
      if (Math.random() > 0.5) {
        playerActionString = `${player.name} thought ${cell.tag.tag.name} was related somehow`;
      }

      // End player's journey here when they expose a tag
      setCurrentStep(playerActionString);
      setIsAnimating(false);
      setButtonsEnabled(true);
      setCurrentPhase("player-stopped");
    } else {
      setIsAnimating(false);
      setButtonsEnabled(true);
      setCurrentPhase("player-stopped");
    }
  };

  const handleDirectionlessActivation = (
    row: number,
    col: number,
    player: Player
  ) => {
    // Player stops at directionless squares and must choose what to do next
    setCurrentStep(`${player.name} needs to choose what to do next...`);
    setIsAnimating(false);
    setButtonsEnabled(true);
    setCurrentPhase("player-stopped");
  };

  const handleSoClose = () => {
    if (
      !selectedPlayer ||
      !playerPosition ||
      soCloseUsed ||
      soCloseCount >= getSoCloseLimit()
    )
      return;

    //all other players lose one attention
    players.forEach((p) => {
      if (p.id !== selectedPlayer.id) {
        partyManager.updateAttention(p, -1);
      }
    });
    // reduce tension by 5, then 10, then 15 etc.
    partyManager.addPartyTension(-5 * (soCloseCount + 1));

    setSoCloseUsed(true);
    setSoCloseCount((prev) => prev + 1);
    setButtonsEnabled(false); // Disable buttons during So Close selection
    chatManager.dmMessage(
      `Ah, close...${
        grid[playerPosition.row][playerPosition.col].tag?.tag.name || "that"
      } was misleading but you are close...`
    );
    setCurrentStep(`nudge ${selectedPlayer.name} in the right direction`);
    const allAdjacentPositions = getDiagonalAndOrthogonalPositions(
      playerPosition.row,
      playerPosition.col
    );

    // filter out answer grid
    const highlightCells = allAdjacentPositions.filter(
      (cell) => !grid[cell.row][cell.col].isAnswer
    );

    setHighlightedCells(highlightCells);
  };

  const getDiagonalAndOrthogonalPositions = (
    row: number,
    col: number
  ): { row: number; col: number }[] => {
    const gridConfig = getGridConfig(difficulty);
    const positions: { row: number; col: number }[] = [];

    for (let r = row - 1; r <= row + 1; r++) {
      for (let c = col - 1; c <= col + 1; c++) {
        if (
          r >= 0 &&
          r < gridConfig.rows &&
          c >= 0 &&
          c < gridConfig.cols &&
          !(r === row && c === col)
        ) {
          positions.push({ row: r, col: c });
        }
      }
    }

    return positions;
  };

  const handleAbandonPuzzle = (collectedScore: number) => {
    // Force re-calculation by directly accessing current score
    const cost = calculateAbandonmentCost(collectedScore);
    partyManager.addPartyTension(-cost.tension);
    // Generate effects for the modal
    const results: any[] = [];
    players.forEach((p) => {
      partyManager.addEnjoyment(p, -cost.attitude);
      results.push({
        object: p,
        effects: [
          {
            effect: `-${cost.attitude} Attitude`,
            class: "player-attitude",
          },
        ],
      });
    });

    partyManager.addPartyTension(cost.tension);
    results.push({
      object: "party",
      effects: [
        {
          effect: `+${cost.tension}% Tension`,
          class: "tension",
        },
      ],
    });

    // Show modal and return to encounter overview
    uiManager.showModal({
      title: "Puzzle Abandoned",
      options: [
        {
          id: "ok",
          title: "Back to Session",
          callback: () => {
            campaignManager.backToOverview();
          },
        },
      ],
      type: "puzzle",
      effectData: results,
      onClose: () => {
        campaignManager.backToOverview();
      },
    });
  };

  const convertDirectionlessToHints = () => {
    const gridConfig = getGridConfig(difficulty);
    setGrid((prevGrid) => {
      const newGrid = [...prevGrid];

      for (let row = 0; row < gridConfig.rows; row++) {
        for (let col = 0; col < gridConfig.cols; col++) {
          const cell = newGrid[row][col];
          if (cell.type === "directionless") {
            const validDirections = getValidHintDirections(
              row,
              col,
              gridConfig
            );
            const randomDirection =
              validDirections[
                Math.floor(Math.random() * validDirections.length)
              ];

            newGrid[row][col] = {
              ...newGrid[row][col],
              type: "hint",
              hintDirection: randomDirection,
            };
          }
        }
      }

      return newGrid;
    });
  };

  // New handlers for player-stopped phase
  const handleAdjustHints = () => {
    const gridConfig = getGridConfig(difficulty);

    // Add party exhaustion for using adjust hints
    partyManager.exhaustParty(10);

    setAdjustHintsMode(true);
    setCurrentStep("Click on hint squares to rotate their directions.");
  };

  const handleNextPlayer = () => {
    const gridConfig = getGridConfig(difficulty);
    setPlayerPosition(null);
    convertDirectionlessToHints();
    // Convert ALL directionless squares to hints (consistent with handleAdjustHints)
    setGrid((prevGrid) => {
      const newGrid = [...prevGrid];

      for (let row = 0; row < gridConfig.rows; row++) {
        for (let col = 0; col < gridConfig.cols; col++) {
          const cell = newGrid[row][col];
          cell.visited = false;
          if (cell.type === "directionless") {
            const validDirections = getValidHintDirections(
              row,
              col,
              gridConfig
            );
            const randomDirection =
              validDirections[
                Math.floor(Math.random() * validDirections.length)
              ];

            newGrid[row][col] = {
              ...newGrid[row][col],
              type: "hint",
              hintDirection: randomDirection,
            };
          }
        }
      }

      return newGrid;
    });

    // Reset selected player so UI properly shows no player selected state
    setSelectedPlayer(null);
    setCurrentStepPlayerIcon(null);
    setGridPlayerIcon(null);
    setCurrentPhase("solving");
    setAdjustHintsMode(false);
  };

  const handlePlayerStoppedHintRotation = (row: number, col: number) => {
    if (!adjustHintsMode || currentPhase !== "player-stopped") return;

    const gridConfig = getGridConfig(difficulty);
    const validDirections = getValidHintDirections(row, col, gridConfig);
    const currentDirection = grid[row][col].hintDirection;

    if (currentDirection) {
      const nextDirection = getNextDirection(currentDirection, validDirections);

      setGrid((prevGrid) => {
        const newGrid = [...prevGrid];
        newGrid[row][col] = {
          ...newGrid[row][col],
          hintDirection: nextDirection,
        };
        return newGrid;
      });
    }
  };

  const getPuzzleHintCost = (difficulty: PuzzleDifficulty) => {
    switch (difficulty) {
      case "simple":
        return 1;
      case "clever":
        return 2;
      case "tricky":
        return 3;
    }
  };

  const getExhaustionCost = () => {
    //cost is puzzle difficulty * 1 * hints

    return hintsCount * getPuzzleHintCost(difficulty);
  };

  // Render functions
  const renderSetupPhase = () => {
    // Calculate maximum available hints based on collected tags
    const allCollectedTags = tagManager.getCollectedTags();
    const currentLocationTagsCollected = tagManager.locationTagsCollected;
    const maxAvailableHints =
      allCollectedTags.length - currentLocationTagsCollected.length;
    const effectiveMaxHints = Math.min(8, maxAvailableHints);

    // Get the actual hint tags that would be used (using stored tags for consistency)
    const hintTagsToShow = generatedHintTags.slice(0, effectiveMaxHints);

    return (
      <div className="puzzle-setup-modal">
        <div className="setup-header">
          <h2>Adjust Difficulty and Hints</h2>
        </div>

        <div className="difficulty-options">
          {(["simple", "clever", "tricky"] as PuzzleDifficulty[]).map(
            (diff) => {
              const iconSquares =
                diff === "simple" ? 4 : diff === "clever" ? 6 : 9;
              const diffConfig = getGridConfig(diff);

              return (
                <div
                  key={diff}
                  className={`difficulty-option ${
                    difficulty === diff ? "selected" : ""
                  }`}
                  onClick={() => setDifficulty(diff)}
                >
                  <div className="difficulty-name">{diff.toUpperCase()}</div>
                  <div className="difficulty-icon">
                    {Array.from({ length: iconSquares }).map((_, i) => (
                      <div key={i} className="icon-square" />
                    ))}
                  </div>
                  <div className="party-reward">
                    <span className="reward-label">Party Reward</span>
                    <span className="reward-value">{diffConfig.reward}</span>
                  </div>
                </div>
              );
            }
          )}
        </div>

        {maxAvailableHints > 0 && (
          <div className="hints-control">
            <div className="hints-header">
              <span>Hints: {hintsCount}</span>

              <div className="hint-arrows">
                <button
                  className="hint-arrow"
                  onClick={() =>
                    setHintsCount(Math.min(effectiveMaxHints, hintsCount + 1))
                  }
                  disabled={hintsCount >= effectiveMaxHints}
                >
                  
                </button>
                <button
                  className="hint-arrow"
                  onClick={() => setHintsCount(Math.max(0, hintsCount - 1))}
                  disabled={hintsCount <= 0}
                >
                  
                </button>
              </div>
            </div>
            {hintsCount >= effectiveMaxHints && (
              <div className="hints-limit-info">
                (Limited by previously discussed topics)
              </div>
            )}
            {/* Display available hint tags */}
            {hintsCount > 0 && (
              <span className="hint-info">
                You've been leaving clues and crafting this puzzle all
                <br /> along using the following topics as hints.
              </span>
            )}
            <div className="available-hint-tags">
              {hintTagsToShow.slice(0, hintsCount).map((tag, index) => (
                <FlavorTagPill
                  key={index}
                  tag={tagManager.tagToEncounterFlavorTag(
                    tag,
                    FlavorTagSource.PUZZLE
                  )}
                  showTooltip={false}
                />
              ))}
              {hintsCount === 0 && <span className="no-hints"></span>}
            </div>
            {hintsCount > 0 && (
              <span className="exhaustion-info">
                Exhaustion:{getExhaustionCost()}
              </span>
            )}

            {/* Score display */}
            <div className="score-display">
              <span>Solving score: </span>
              <DiceScore
                score={calculateScore(
                  hintsCount,
                  maxAvailableHints === 0,
                  difficulty
                )}
              />
            </div>
          </div>
        )}

        {maxAvailableHints === 0 && (
          <div className="no-hints-display">
            <div className="score-info">
              <div className="hint-rewards-note">No hints available.</div>
              <div className="hint-rewards-sub">
                <i>
                  Discuss topics with your players before asking them to solve
                  puzzles!
                </i>
              </div>

              {/* Score display for no hints */}
              <div className="score-display">
                <span>Score if players solve: </span>
                <DiceScore score={calculateScore(0, true, difficulty)} />
              </div>
            </div>
          </div>
        )}

        <div className="setup-actions">
          <button className="present-puzzle-btn" onClick={handlePresentPuzzle}>
            PRESENT PUZZLE
          </button>
        </div>
      </div>
    );
  };

  const renderInstructionsPhase = () => {
    return (
      <div className="puzzle-instructions">
        <div className="instructions-header">
          <h3>
            {campaignManager.getCurrentEvent()?.tags?.[0]?.name || ""} Puzzle
          </h3>
        </div>
        <div className="instructions-content">
          <p>
            In addition to clues you have left so far leading up to this puzzle,
            the players will use things they have learned about the current
            location to make guesses. Direct them to a desired outcome by
            modifying where the clues lead. Be careful, certain paths or cirular
            logic leads to frustration.
          </p>
        </div>
        <button className="players-attempt-btn" onClick={handlePlayersAttempt}>
          BEGIN
        </button>
      </div>
    );
  };

  const renderSolvingPhase = (currentScore: number) => {
    const abandonmentCost = calculateAbandonmentCost(currentScore);
    return (
      <div className="puzzle-solving">
        <div className="solving-header">
          <h3>Prompt a player to solve the puzzle</h3>
        </div>

        {currentStep && (
          <div className="current-step">
            {currentStep}
            {currentStepPlayerIcon && currentStepPlayerIcon.isVisible && (
              <div className="current-step-player-icon">
                <Cimage imageId={currentStepPlayerIcon.player.icon} />
              </div>
            )}
          </div>
        )}

        <div className="player-selection">
          {players.map((player) => {
            const isUsed = usedPlayers.has(player.id);
            const isSelected = selectedPlayer?.id === player.id;

            return (
              <button
                key={player.id}
                className={`player-btn ${isSelected ? "selected" : ""} ${
                  isUsed ? "used" : ""
                }`}
                onClick={() => handlePlayerSelect(player)}
                disabled={isUsed || !buttonsEnabled}
              >
                <div className="player-icon-container">
                  <div className="player-icon-display">
                    <Cimage imageId={player.icon} />
                  </div>
                </div>
                <span className="player-name">{player.name}</span>
                {isUsed && <span className="used-indicator"></span>}
              </button>
            );
          })}
        </div>

        <button
          className="abandon-btn"
          onClick={() => handleAbandonPuzzle(currentScore)}
          disabled={!buttonsEnabled}
        >
          Abandon Puzzle
          <div className="btn-subtitle">
            -{abandonmentCost.attitude} attitude
          </div>
          {abandonmentCost.tension > 0 && (
            <div className="btn-subtitle">
              -{abandonmentCost.tension} tension
            </div>
          )}
        </button>
      </div>
    );
  };

  const renderPlayerStoppedPhase = (currentScore: number) => {
    const abandonmentCost = calculateAbandonmentCost(currentScore);
    console.log(
      "renderPlayerStoppedPhase - currentScore:",
      currentScore,
      "abandonmentCost:",
      abandonmentCost
    );
    const availablePlayers = players.filter(
      (player) => !usedPlayers.has(player.id)
    );

    return (
      <div className="puzzle-solving">
        {currentStep && <div className="current-step">{currentStep}</div>}

        <div className="player-stopped-options">
          {availablePlayers.length > 0 && (
            <button
              className="next-player-btn"
              onClick={handleNextPlayer}
              disabled={!buttonsEnabled || availablePlayers.length === 0}
            >
              Next Player
              <div className="btn-subtitle">continue with another player</div>
            </button>
          )}
          {getSoCloseRemaining() > 0 && (
            <button
              className="so-close-btn"
              onClick={handleSoClose}
              disabled={!buttonsEnabled || soCloseCount >= getSoCloseLimit()}
            >
              Suggest
              <div className="btn-subtitle">
                Guide current player to an adjacent topic.
              </div>
              <div className="btn-subtitle">Other players lose 1 attention</div>
            </button>
          )}
          {(availablePlayers.length > 0 || getSoCloseRemaining() > 0) && (
            <button
              className="adjust-hints-btn"
              onClick={handleAdjustHints}
              disabled={!buttonsEnabled || !hasHintSquares()}
            >
              Adjust Hints
              <div className="btn-subtitle">modify hint directions</div>
            </button>
          )}
          <button
            className="abandon-btn"
            onClick={() => handleAbandonPuzzle(currentScore)}
            disabled={!buttonsEnabled}
          >
            Abandon Puzzle
            <div className="btn-subtitle">
              -{abandonmentCost.attitude} attitude
            </div>
            {abandonmentCost.tension > 0 && (
              <div className="btn-subtitle">
                -{abandonmentCost.tension} tension
              </div>
            )}
          </button>
        </div>

        {/* <div className="current-score">Current Score: {currentScore}</div> */}
      </div>
    );
  };

  const getGridCellTooltipText = (
    cell: GridCell,
    row: number,
    col: number
  ): string => {
    if (cell.type === "answer") {
      return "This is the answer! Guide your players here with a trail of clues.";
    }
    if (cell.type === "hint") {
      if (currentPhase === "instructions") {
        return (
          "Prior discussions of " +
          (cell.tag?.tag.name || " this ") +
          " will help the players solve the puzzle. Click to set the direction that this clue leads to."
        );
      } else if (currentPhase === "solving" && !selectedPlayer) {
        return (
          "Prior discussions of " +
          (cell.tag?.tag.name || " this ") +
          " will help the players solve the puzzle. Click to adjust the hint direction before selecting a player."
        );
      } else if (currentPhase === "player-stopped" && adjustHintsMode) {
        return (
          "Click to rotate the hint direction for " +
          (cell.tag?.tag.name || " this hint") +
          "."
        );
      } else {
        return (
          "Prior discussions of " +
          (cell.tag?.tag.name || " this ") +
          " will help the players solve the puzzle."
        );
      }
    }
    if (cell.type === "unexposed") {
      if (currentPhase === "instructions") {
        return "You aren't yet sure what conclusions your players may come to.";
      }

      // if this is a highlighted cell in so close mode indicate that we can click on it to expose the tag

      if (highlightedCells.some((pos) => pos.row === row && pos.col === col)) {
        return "Click to expose this tag and see what your players think.";
      }

      return "Players may come to the conclusion that this is relevant";
    }
    if (cell.type === "directionless") {
      return (
        "Your players arent sure how " +
        (cell.tag?.tag.name || "stuff ") +
        " relates to the puzzle, so you aren't sure how they will react."
      );
    }
    if (cell.type === "exposed") {
      return "Exposed Tag: Relevance unknown.";
    }
    if (cell.type === "frustration") {
      return "A player reaching this will become frustrated and lose one attitude.";
    }
    return "Empty Square: No special properties.";
  };

  const renderGridCell = (cell: GridCell, row: number, col: number) => {
    const isHighlighted = highlightedCells.some(
      (pos) => pos.row === row && pos.col === col
    );
    const isPlayerPosition =
      playerPosition?.row === row && playerPosition?.col === col;

    // Determine if this cell is clickable
    const isClickable =
      // So Close mode - highlighted cells are clickable
      ((currentPhase === "solving" || currentPhase === "player-stopped") &&
        isHighlighted &&
        !isAnimating) ||
      // Instructions phase - hint cells are clickable
      (currentPhase === "instructions" && cell.type === "hint") ||
      // Solving phase with no player selected - hint cells are clickable
      (currentPhase === "solving" && !selectedPlayer && cell.type === "hint") ||
      // Player-stopped phase in adjust hints mode - hint cells are clickable
      (currentPhase === "player-stopped" &&
        adjustHintsMode &&
        cell.type === "hint");

    return (
      <TooltipWrapper
        key={`tooltip-${row}-${col}`}
        content={getGridCellTooltipText(cell, row, col)}
        position="top"
        translate={[-50, -50]}
      >
        <div
          key={`${row}-${col}`}
          className={`grid-cell ${cell.type} ${
            isHighlighted ? "highlighted" : ""
          } ${isPlayerPosition ? "player-position" : ""} ${
            cell.isFlashing ? "flashing" : ""
          }`}
          style={{
            cursor: isClickable ? "pointer" : "default",
          }}
          onClick={() => {
            // Prioritize highlighted cell activation (So Close mode) over hint rotation
            if (
              (currentPhase === "solving" ||
                currentPhase === "player-stopped") &&
              isHighlighted &&
              !isAnimating
            ) {
              setHighlightedCells([]);
              //     setButtonsEnabled(true); // Re-enable buttons after So Close selection
              activateGridSquare(row, col, selectedPlayer!);
            } else if (
              currentPhase === "instructions" &&
              cell.type === "hint"
            ) {
              handleHintDirectionChange(row, col);
            } else if (
              currentPhase === "solving" &&
              !selectedPlayer &&
              cell.type === "hint"
            ) {
              handleHintDirectionChange(row, col);
            } else if (
              currentPhase === "player-stopped" &&
              adjustHintsMode &&
              cell.type === "hint"
            ) {
              handlePlayerStoppedHintRotation(row, col);
            }
          }}
        >
          {/* Score reward display */}
          {cell.scoreReward > 0 && (
            <div className="score-reward">
              <DiceScore score={cell.scoreReward} />
            </div>
          )}

          {/* Tension display */}
          {cell.tensionLevel && (
            <div
              className="tension-indicator"
              style={{ color: getTensionColor(cell.tensionLevel) }}
            >
              {getTensionDisplay(cell.tensionLevel)}
            </div>
          )}

          <div className="cell-tag">
            {cell.type === "hint" ? (
              <div className="hint-tag">
                {cell.tag && (
                  <FlavorTagPill tag={cell.tag} showTooltip={false} />
                )}
              </div>
            ) : cell.type === "exposed" ? (
              <div className="exposed-tag">
                {cell.tag && (
                  <FlavorTagPill tag={cell.tag} showTooltip={false} />
                )}
              </div>
            ) : cell.type === "directionless" ? (
              <div className="exposed-tag">
                {cell.tag && (
                  <FlavorTagPill tag={cell.tag} showTooltip={false} />
                )}
              </div>
            ) : cell.tag ? (
              <FlavorTagPill tag={cell.tag} showTooltip={false} />
            ) : cell.type === "unexposed" ? (
              <div className="unexposed-tag">
                <FlavorTagPill
                  tag={tagManager.tagToEncounterFlavorTag(
                    { name: "?" },
                    FlavorTagSource.NONE
                  )}
                  showTooltip={false}
                />
              </div>
            ) : null}
          </div>

          {/* Hint direction arrow */}
          {cell.type === "hint" && cell.hintDirection && (
            <div
              className={`grid-hint-arrow ${cell.hintDirection} ${
                cell.visited ? "visited" : ""
              }`}
            >
              {cell.hintDirection === "up" && ""}
              {cell.hintDirection === "down" && ""}
              {cell.hintDirection === "left" && ""}
              {cell.hintDirection === "right" && ""}
            </div>
          )}

          {/* Player icon and visited overlay */}
          {cell.visited && cell.type !== "directionless" && (
            <div className="visited-overlay">
              {/* {cell.playerIcon && (
                <div
                  className={
                    isPlayerPosition
                      ? "current-player-marker"
                      : "visited-player-marker"
                  }
                >
                  {cell.playerIcon.name.charAt(0)}
                </div>
              )} */}
            </div>
          )}

          {/* Answer indicator */}
          {cell.isAnswer && <div className="answer-indicator"></div>}

          {/* Frustration indicator */}
          {cell.isFrustration && (
            <div className="frustration-indicator">?!</div>
          )}
        </div>
      </TooltipWrapper>
    );
  };

  const renderGrid = () => {
    if (grid.length === 0) return null;

    const gridConfig = getGridConfig(difficulty);

    return (
      <div className="grid-container">
        <div className="grid-hint-instructions">
          Click hints and location clues{" "}
          <span className="hint-symbol">
            <span className="hint-pill">
              <span className="hint-arrow-symbol"></span>
            </span>
          </span>{" "}
          to indicate where discussions will lead.
        </div>
        <div
          className="puzzle-grid"
          style={{
            gridTemplateRows: `repeat(${gridConfig.rows}, 1fr)`,
            gridTemplateColumns: `repeat(${gridConfig.cols}, 1fr)`,
            position: "relative", // Add relative positioning for absolute child
          }}
        >
          {grid.map((row, rowIndex) =>
            row.map((cell, colIndex) =>
              renderGridCell(cell, rowIndex, colIndex)
            )
          )}

          {/* Grid player icon overlay - positioned absolutely */}
          {gridPlayerIcon && gridPlayerIcon.isVisible && (
            <div
              className="grid-player-icon"
              style={{
                position: "absolute",
                left: `${(gridPlayerIcon.col / gridConfig.cols) * 100}%`,
                top: `${(gridPlayerIcon.row / gridConfig.rows) * 100}%`,
                width: `${100 / gridConfig.cols}%`,
                height: `${100 / gridConfig.rows}%`,
              }}
            >
              <Cimage imageId={gridPlayerIcon.player.icon} />
            </div>
          )}
        </div>

        {/* Legend */}
        <div className="grid-legend">
          <div className="legend-item">
            <span className="legend-frustration">{"?!"}</span>
            <span className="legend-text">
              Frustration: -1 active player Attitude
            </span>
          </div>
          <div className="legend-item">
            <span className="legend-dice-score">
              <DiceScore score={4} />
            </span>{" "}
            and{" "}
            <span className="legend-dice-score">
              <DiceScore score={1} />
            </span>{" "}
            Interesting topics: adds potential score
          </div>
        </div>
      </div>
    );
  };
  
  const getPuzzleTitle = () => {
    const event = campaignManager.getCurrentEvent();
    if (event && event.flavorText) {
      return event.flavorText;
    }
    return "Puzzle Event";
  }


  return (
    <div className="game-encounter-puzzle">
      <EncounterHeader
        title={getPuzzleTitle()}
        description="You've been leaving clues leading up to this all along."
        childClass="small-font"
      />

      <div className="puzzle-content">
        {currentPhase !== "setup" && (
          <div className="puzzle-left">{renderGrid()}</div>
        )}

        {currentPhase === "setup" ? (
          <div className="puzzle-setup-container">{renderSetupPhase()}</div>
        ) : (
          <div className="puzzle-right">
            {currentPhase === "instructions" && renderInstructionsPhase()}
            {currentPhase === "solving" && renderSolvingPhase(currentScore)}
            {currentPhase === "player-stopped" &&
              renderPlayerStoppedPhase(currentScore)}
          </div>
        )}
      </div>

      {/* Floating player icon animation */}
      {floatingIconPosition && floatingIconPosition.isAnimating && (
        <div
          className="floating-player-icon"
          style={{
            position: "absolute",
            left: floatingIconPosition.x,
            top: floatingIconPosition.y,
            zIndex: 1000,
            transition: "all 1s ease-in-out",
          }}
        >
          <Cimage imageId={floatingIconPosition.player.icon} />
        </div>
      )}
    </div>
  );
};

export default GameEncounterPuzzle;
