import React from 'react';
import { ModalConfig } from '../../models';
import EffectDisplay from './effectDisplay';
import FlavorTagPill from './FlavorTagPill';
import TooltipWrapper from './TooltipWrapper';

interface TagModalProps {
  modalConfig: ModalConfig;
  onClose: () => void;
}

const TagModal: React.FC<TagModalProps> = ({ modalConfig, onClose }) => {
  if (!modalConfig.tagModalConfig) {
    return null;
  }

  const {
    title,
    description,
    tags,
    effects,
    selectedTags,
    remainingClicks,
    tagInstructions,
    onTagClick,
    onClose: configOnClose
  } = modalConfig.tagModalConfig;

  const handleClose = () => {
    configOnClose();
    onClose();
  };

  return (
    <div className="modal-overlay">
      <div className="modal-container">
        <div className="modal-header">
          <h2>{title}</h2>
          {description && (
            <div className="modal-description">
              {description}
            </div>
          )}
          <button className="close-button" onClick={handleClose}></button>
        </div>

        {/* Effects Display */}
        {effects && effects.length > 0 && (
          <div className="modal-effects">
            {effects.map((effect, index) => (
              <EffectDisplay key={index} effectData={effect} />
            ))}
          </div>
        )}

        {/* Tags Section */}
        {tags.length > 0 && remainingClicks > 0 && (
          <div className="modal-tag-container">
            <div className="clicks-remaining">
              <TooltipWrapper
                content="Click on the tags to reveal them."
                maxWidth="200"
                preservePositioning={true}
                translate={[0, -80]}
              >
                {remainingClicks}
              </TooltipWrapper>
            </div>
            {tagInstructions && (
              <div className="tag-instructions">
                {tagInstructions}
              </div>
            )}
            <div className="modal-tags-container">
              <div className="modal-tags">
                {tags.map((tag) => {
                  const tagUsed = selectedTags.includes(tag.tag);
                  return (
                    <FlavorTagPill
                      key={tag.tag.name}
                      tag={tag}
                      onClick={onTagClick}
                      className={`${tagUsed ? "used" : ""}`}
                      disabled={tagUsed}
                      showTooltip={false}
                    />
                  );
                })}
              </div>
            </div>
          </div>
        )}

        {/* No Tags Warning */}
        {remainingClicks > 0 && tags.length === 0 && (
          <div className="remaining-clicks-warning">
            Nothing new to present to the players.
          </div>
        )}

        {/* Actions */}
        {(tags.length === 0 || remainingClicks === 0) && (
          <div className="modal-actions">
            <button
              className={`modal-action-button ${remainingClicks > 0 ? 'disabled' : ''}`}
              onClick={handleClose}
              disabled={remainingClicks > 0}
            >
              Close
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default TagModal;
