import React, { useEffect, useState } from "react";
import {
  Player,
  Character,
  NOTEBOOK_CATEGORY,
  ATTRIBUTE_CONTRIBUTOR,
} from "../../models";
import CImage from "../UI/cimage";
import CharacterManager from "../../managers/characterManager";
import { useCharacterManager, useUiManager } from "../managerContexts";
import AttributeDisplay from "../UI/attributeDisplay";
import FillBar from "../UI/fillBar";
import { PLAYER_EXP_LEVEL } from "../../consts/general";

interface PlayerDisplayProps {
  player: Player;
}
const delightColors = ["#CC0000", "#CC6600", "#CCCC00", "#66CC00", "#00CC00"];
const delightBorderColors = [
  "#990000",
  "#994C00",
  "#999900",
  "#4C9900",
  "#009900",
];

const delightColors2 = [
  "#FFCCCC",
  "#FFDDCC",
  "#FFFFCC",
  "#DDFFCC",
  "#CCFFCC",
  "#aaFFaa",
];
const delightBorderColors2 = [
  "#AA0000",
  "#663300",
  "#666600",
  "#336600",
  "#006600",
  "#008800",
];
const PlayerDisplay: React.FC<PlayerDisplayProps> = ({ player }) => {
  const characterManager = useCharacterManager();
  const [showPlayerTooltip, setShowPlayerTooltip] = useState(false);
  const [showCharacterTooltip, setShowCharacterTooltip] = useState<
    number | null
  >(null);
  const [characters, setCharacters] = useState<Character[]>(player.characters);
  const uiManager = useUiManager();
  const [enjoyment, setEnjoyment] = useState(player.playerStats.enjoyment);
  const [attention, setAttention] = useState(player.playerStats.involvement);
  const [stress, setStress] = useState(player.playerStats.stress);

  const getStatColor = (value: number, isStress: boolean = false) => {
    if (isStress) {
      // Stress: yellow -> orange -> red
      if (value < 30) return "#4CAF50";
      if (value < 60) return "#ffff00";
      return "#ff4d4d";
    } else {
      // Regular stats: red -> orange -> green
      if (value < 0.3) return "#ff4d4d";
      if (value < 0.6) return "#ffa500";
      return "#4CAF50";
    }
  };

  useEffect(() => {
    const updateStats = () => {
      setEnjoyment(player.playerStats.enjoyment);
      setAttention(player.playerStats.involvement);
      setStress(player.playerStats.stress);
      setCharacters(player.characters);
      console.log("player updated " + player.name);
      console.log(player.playerStats);
    };

    uiManager.on("playerUpdate", updateStats);

    // Cleanup listener on unmount to avoid memory leaks
    return () => {
      uiManager.off("playerUpdate", updateStats);
    };
  }, [uiManager, player.playerStats, player.characters, player.name]);

  const playerLikes = player.preferences.filter((pref) => pref[1] > 0);
  const playerHates = player.preferences.filter((pref) => pref[1] < 0);
  const playerTraits = player.traits;

  return (
    <div
      className="player-display"
      onMouseEnter={() => setShowPlayerTooltip(true)}
      onMouseLeave={() => setShowPlayerTooltip(false)}
      onClick={() =>
        uiManager.showBasicNotebook(NOTEBOOK_CATEGORY.PLAYER, player)
      }
    >
      <div className="character-level-icon-column">
        <div className="player-diamond-holder">
          <div
            className="player-diamond"
            style={{
              backgroundColor: delightColors2[player.playerStats.enjoyment],
              borderColor: delightBorderColors2[player.playerStats.enjoyment],
            }}
          ></div>
        </div>
        <div className="player-icon-container">
          <CImage imageId={player.icon} />
        </div>
        <div className="character-level">
          <FillBar
            filledSegments={player.playerStats.experience}
            totalSegments={PLAYER_EXP_LEVEL[player.playerStats.level]}
            theme="player-level"
            label={`LEVEL ${player.playerStats.level}`}
          />
        </div>
      </div>
      <div className="character-icons">
        {characters.map((char, index) => (
          <div key={char.id} className="character-icon-container">
            {showCharacterTooltip === index && (
              <div className="character-tooltip">
                <div className="tooltip-header">{char.name}</div>
                <div className="tooltip-content">
                  <AttributeDisplay
                    hero={char}
                    category={ATTRIBUTE_CONTRIBUTOR.ALL}
                    showAttributeLoss={true}
                    overviewDisplay={true}
                  />
                </div>
              </div>
            )}
            <div
              className="character-icon"
              onMouseEnter={() => setShowCharacterTooltip(index)}
              onMouseLeave={() => setShowCharacterTooltip(null)}
              onClick={(e) => {
                e.stopPropagation();
                uiManager.showBasicNotebook(NOTEBOOK_CATEGORY.CHARACTER, char);
              }}
            >
              <CImage imageId={char.icon} />
            </div>
          </div>
        ))}
      </div>

      <div className="player-stats">
        <div className="player-stat-label name">{player.name}</div>
        <div
          className="player-stat-label"
          style={{
            color: getStatColor(enjoyment / player.playerStats.maxEnjoyment),
          }}
        >
          attitude
        </div>
        <div className="stat-bar">
          <div
            className="fill happiness"
            style={{
              width: `${(enjoyment / 5) * 100}%`,
              backgroundColor: getStatColor(
                enjoyment / player.playerStats.maxEnjoyment
              ),
            }}
          />
        </div>
        <div
          className="player-stat-label"
          style={{
            color: getStatColor(attention / player.playerStats.maxInvolvement),
          }}
        >
          attention
        </div>
        <div className="stat-bar">
          <div
            className="fill attention"
            style={{
              width: `${(attention / 5) * 100}%`,
              backgroundColor: getStatColor(
                attention / player.playerStats.maxInvolvement
              ),
            }}
          />
        </div>
        <div
          className="player-stat-label"
          style={{
            color: getStatColor(
              Math.max(0, stress - player.playerStats.stressBuffer),
              true
            ),
          }}
        >
          stress
        </div>
        <div>
          <div className="stat-item stress">
            <div
              className="stress-value"
              style={{
                color: getStatColor(
                  Math.max(0, stress - player.playerStats.stressBuffer),
                  true
                ),
              }}
            >
              {Math.max(
                0,
                player.playerStats.stress - player.playerStats.stressBuffer
              )}
              %
            </div>
            <div className="stress-buffer">
              {player.playerStats.stress < player.playerStats.stressBuffer && (
                <div className="stress-buffer-value">
                  {"["}
                  {Math.max(
                    0,
                    player.playerStats.stressBuffer - player.playerStats.stress
                  )}
                  {"]"}
                </div>
              )}
            </div>
          </div>
        </div>
      </div>

      {showPlayerTooltip && showCharacterTooltip === null && (
        <div className="player-tooltip">
          <div className="tooltip-header">{player.name}</div>
          <div className="tooltip-content">
            Likes:
            <div className="preference-list">
              {playerLikes.map((like, index) => (
                <div key={index} className="text-pill preference-item like">
                  {like[0].name}
                </div>
              ))}
            </div>
          </div>
          <div className="tooltip-content">
            Hates:
            <div className="preference-list">
              {playerHates.map((hate, index) => (
                <div key={index} className="text-pill preference-item hate">
                  {hate[0].name}
                </div>
              ))}
            </div>
          </div>
          {playerTraits.length > 0 && (
            <div className="tooltip-content">
              Player Traits:
              <div className="preference-list">
                {playerTraits.map((trait, index) => (
                  <div key={index} className="preference-item trait ">
                    {trait.name}:<div>{trait.description}</div>
                  </div>
                ))}
              </div>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default PlayerDisplay;
