import React, { useEffect, useState } from 'react';
import { useAppManager, useCampaignManager } from "../managerContexts";
import { APP_STATE } from "../../consts/states";
import { Tag, FlavorTagSource, FlavorTagTheme } from '../../models';
import TooltipWrapper from '../UI/TooltipWrapper';
import TagPill from '../UI/TagPill';
import DiceScore from '../UI/DiceScore';
import NeedleGauge from '../UI/needleGauge';
import StressInfo from './stressInfo';

interface BottomBarProps {
    
}


const BottomBar: React.FC<BottomBarProps> = () => {
    const appManager = useAppManager();
    const campaignManager = useCampaignManager();
    const tagManager = appManager.getTagManager();

    const uiManager = appManager.getUiManager();

    const encounter = campaignManager.getCurrentEncounter();
    const campaignScore = campaignManager.getCampaignScore();
    const partyManager = appManager.getPartyManager();
    // State for encounter score that updates when it changes
    const [encounterScore, setEncounterScore] = useState(campaignManager.getEncounterPoints());
      const [needleGaugeUpdate, setNeedleGaugeUpdate] = useState(0); // Force needle gauge re-render
      const [partyTension, setPartyTension] = useState(
        partyManager.getPartyTension()
      );

    // Listen for encounter score updates
    useEffect(() => {
        const updateEncounterScore = () => {
            setEncounterScore(campaignManager.getEncounterPoints());
        };

        // Listen to campaign manager events that might change the encounter score
        campaignManager.on('dmScreenUpdated', updateEncounterScore);
        campaignManager.on('encounterScoreUpdated', updateEncounterScore);
        
        return () => {
            campaignManager.off('dmScreenUpdated', updateEncounterScore);
            campaignManager.off('encounterScoreUpdated', updateEncounterScore);
        };
    }, [campaignManager]);

     useEffect(() => {
        const updateNeedleGauge = () => {
          setNeedleGaugeUpdate((prev) => prev + 1); // Force needle gauge re-render
        };
        campaignManager.on("tensionDescriptionsUpdated", updateNeedleGauge);
        return () => {
          campaignManager.off("tensionDescriptionsUpdated", updateNeedleGauge);
        };
      }, [campaignManager]);


  useEffect(() => {
    const forceUpdate = () => {
      setPartyTension(partyManager.getPartyTension());
    };
    uiManager.on("playerUpdate", forceUpdate);
    return () => {
      uiManager.off("playerUpdate", forceUpdate);
    };
  }, [uiManager, partyManager]);
    // Get tag data for current encounter
    const establishedEncounterTags: Tag[] = encounter 
        ? tagManager.encounterTagsCollected[encounter.name || ""] || []
        : [];
    const expectedEncounterTags: Tag[] = encounter 
        ? tagManager.encounterTagsExpected[encounter.name || ""]?.filter(
            (tag: Tag) => !establishedEncounterTags.includes(tag)
          ) || []
        : [];

    // Get location tag data
    const locationTags: Tag[] = encounter 
        ? campaignManager.getEncounterLocationTags()
        : [];
    const establishedLocationTags: Tag[] = encounter 
        ? tagManager.locationTagsCollected || []
        : [];

    if (!encounter || appManager.state !== APP_STATE.SESSION) {
        return <></>;
    }

    const encounterBranches = encounter?.branches;

    // Calculate progress for current encounter
    const totalEncounterTags = (establishedEncounterTags.length + expectedEncounterTags.length);
    const totalLocationTags = locationTags.length;
    const collectedEncounterTags = establishedEncounterTags.length;
    const collectedLocationTags = establishedLocationTags.length;
    const totalTags = totalEncounterTags + totalLocationTags;
    const collectedTags = collectedEncounterTags + collectedLocationTags;
    const percentComplete = totalTags > 0 ? Math.round((collectedTags / totalTags) * 100) : 0;

    // Render checkboxes (dots)
    const renderTagDots = (total: number, collected: number, className: string) => {
        const dots = [];
        for (let i = 0; i < total; i++) {
            dots.push(
                <div 
                    key={i} 
                    className={`tag-dot ${i < collected ? 'filled' : 'hollow'} ${className}`} 
                />
            );
        }
        return dots;
    };

    
    // Get campaign name
    const campaignName = campaignManager.getCampaignName() || "Campaign";

    // Render encounter section with tooltip
    const renderEncounterSection = () => {
        return (
            <TooltipWrapper
                content={
                    <div className="encounter-tooltip-content">
                        <div className="tooltip-section">
                            <h4>Encounter Tags ({collectedEncounterTags}/{totalEncounterTags})</h4>
                            <div className="tooltip-tags">
                                {establishedEncounterTags.map((tag, index) => (
                                    <TagPill
                                        key={index}
                                        tag={tag}
                                        source={FlavorTagSource.ENCOUNTER}
                                        theme={encounter?.location.themes[0] as FlavorTagTheme}
                                        tooltipClassName={"info-tooltip"}
                                        tooltipText="Expectation fulfilled"
                                    />
                                ))}
                                {expectedEncounterTags.map((tag, index) => (
                                    <TagPill
                                        key={index}
                                        tag={tag}
                                        source={FlavorTagSource.ENCOUNTER_EXPECTED}
                                        theme={encounter?.location.themes[0] as FlavorTagTheme}
                                        tooltipClassName={"info-tooltip"}
                                        tooltipText="Players still expect to encounter this"
                                    />
                                ))}
                            </div>
                        </div>
                        <div className="tooltip-section">
                            <h4>Location Tags ({collectedLocationTags}/{totalLocationTags})</h4>
                            <div className="tooltip-tags">
                                {locationTags.map((tag, index) => (
                                    <TagPill
                                        key={index}
                                        tag={tag}
                                        source={
                                            establishedLocationTags.includes(tag)
                                                ? FlavorTagSource.LOCATION
                                                : FlavorTagSource.LOCATION_EXPECTED
                                        }
                                        theme={encounter?.location.themes[0] as FlavorTagTheme}
                                        tooltipClassName={"info-tooltip"}
                                        tooltipText={
                                            establishedLocationTags.includes(tag)
                                                ? "This location element has been established."
                                                : "Describe the location using these elements."
                                        }
                                    />
                                ))}
                            </div>
                        </div>
                    </div>
                }
            >
                <div className="bottom-bar-encounter">
                    <div className="encounter-header-row">
                        <span className="encounter-name">{encounter?.name}</span>
                        <span className="encounter-percent">{percentComplete}%</span>
                    </div>
                    <div className="encounter-dots-row">
                        <div className="encounter-dots-section">
                            <span className="dots-label">Encounter Topics:</span>
                            <div className="dots-container">
                                {renderTagDots(totalEncounterTags, collectedEncounterTags, 'encounter-dot')}
                            </div>
                        </div>
                        <div className="location-dots-section">
                            <span className="dots-label">{encounter.location.name}:</span>
                            <div className="dots-container">
                                {renderTagDots(totalLocationTags, collectedLocationTags, 'location-dot')}
                            </div>
                        </div>
                    </div>
                </div>
            </TooltipWrapper>
        );
    };

    // Render next encounters section with tooltip
    const renderNextEncountersSection = () => {
        if (!encounterBranches || encounterBranches.length === 0) {
            return null;
        }

        return (
            <TooltipWrapper
                content={
                    <div className="next-encounters-tooltip-content">
                        <h4>Upcoming Encounters</h4>
                        {encounterBranches.map((branch, index) => {
                            const branchCollectedTags = tagManager.encounterTagsCollected[branch.name || ""] || [];
                            const branchExpectedTags = tagManager.encounterTagsExpected[branch.name || ""] || [];
                            
                            return (
                                <div key={index} className="tooltip-branch">
                                    <h5>{branch.name} ({branchCollectedTags.length} exposed)</h5>
                                    <div className="tooltip-tags">
                                        {branchCollectedTags.map((tag: Tag, tagIndex: number) => (
                                            <TagPill
                                                key={`collected-${tagIndex}`}
                                                tag={tag}
                                                source={FlavorTagSource.FORESHADOWING}
                                                theme={branch.location.themes[0] as FlavorTagTheme}
                                                tooltipClassName={"info-tooltip"}
                                                tooltipText="Players have been led to expect this if/when they get to this encounter."
                                            />
                                        ))}
                                        {branchExpectedTags.filter((tag: Tag) => !branchCollectedTags.some(ct => ct.name === tag.name)).map((tag: Tag, tagIndex: number) => (
                                            <TagPill
                                                key={`expected-${tagIndex}`}
                                                tag={tag}
                                                source={FlavorTagSource.FORESHADOWING_EXPECTED}
                                                theme={branch.location.themes[0] as FlavorTagTheme}
                                                tooltipClassName={"info-tooltip"}
                                                tooltipText="This topic can still be exposed to the players to inform them of the upcoming encounter"
                                            />
                                        ))}
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                }
            >
                <div className="bottom-bar-next-encounters">
                    <div className="next-encounters-header">
                        <span className="next-encounters-title">Upcoming Encounters</span>
                    </div>
                    <div className="next-encounters-row">
                        {encounterBranches.map((branch, index) => {
                            const branchCollectedTags = tagManager.encounterTagsCollected[branch.name || ""]?.length || 0;
                            const branchExpectedTags = tagManager.encounterTagsExpected[branch.name || ""]?.length || 0;
                            const branchTotalTags = Math.max(4, branchCollectedTags + branchExpectedTags); // Minimum 4 slots
                            
                            return (
                                <div key={index} className="next-encounter-item">
                                    <span className="next-encounter-name">{branch.name}</span>
                                    <div className="dots-container">
                                        {renderTagDots(branchTotalTags, branchCollectedTags, 'next-encounter-dot')}
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                </div>
            </TooltipWrapper>
        );
    };

    // Render combined scoring section with event, tension, and campaign scores
    const renderScoringSection = () => {
        const event = campaignManager.getCurrentEvent();
        
        return (
            <div className="bottom-bar-scoring">
                {/* Event Scoring - Left Side */}
                {event && (
                    <div className="scoring-event-side">
                        <div className="event-header-row">
                            <span className="event-name">ENCOUNTER SCORE</span>
                        </div>
                        <div className="event-score-row">
                            
                            <DiceScore score={encounterScore}/>
                        </div>
                    </div>
                )}
                
                {/* Tension Gauge - Middle */}
                <div className="scoring-tension-side">
                    <div className="tension-header-row">
                        <span className="tension-name">TENSION</span>
                    </div>
                    <div className="tension-gauge-container">
                        <div className="bottom-bar-stress-needle">
                             <StressInfo stress={partyTension} />
                            <NeedleGauge
                                key={needleGaugeUpdate}
                                value={partyTension / 10}
                                max={10}
                                colors={campaignManager.getTensionColors()}
                                descriptions={campaignManager
                                    .getTensionDescriptions()
                                    .map((desc) => desc.description)}
                                segmentRanges={campaignManager
                                    .getTensionRanges()
                                    .map((range) => range / 10)}
                            />
                        </div>
                    </div>
                </div>
                
                {/* Campaign Scoring - Right Side */}
                <div className="scoring-campaign-side">
                    <div className="campaign-header-row">
                        <span className="campaign-name">QUEST SCORE</span>
                    </div>
                    <div className="campaign-score-row">
                        <DiceScore score={campaignScore}/>
                    </div>
                </div>
            </div>
        );
    };



    return (
        <div className="bottom-bar-container">
            <div className="bottom-bar-content">
                {renderEncounterSection()}
                {renderScoringSection()}
                {renderNextEncountersSection()}
            </div>
        </div>
    );
}
export default BottomBar; 